/*新增投顾*/

addNewInvestService.$inject = ['api'];

function addNewInvestService(api) {
  var vm = this;

  vm.businessAdviserAdd = businessAdviserAdd;
  vm.businessAdviserMod = businessAdviserMod;

  //新增投顾信息
  function businessAdviserAdd(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'businessService',
      url: 'businessAdviserAdd',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  //编辑投顾信息
  function businessAdviserMod(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'businessService',
      url: 'businessAdviserMod',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  return vm;
}

module.exports = addNewInvestService;



