/* 导出 controller*/

require('./style.less');
window.ExcellentExport = require('ExcellentExport'); //导出
var modalOptions = require('jsDir/modal_options'),
orderStatisticsDownloadTempl = require('./orderStatisticsDownload.html');

orderStatisticsDownloadCtrl.$inject = ['modalService', 'orderStatisticsDownloadService'];

function orderStatisticsDownloadCtrl(modalService, orderStatisticsDownloadService) {
  var vm = this;

  /*函数声明*/
  vm.openDownModalFunc = openDownModalFunc;

  /*函数定义*/
  function openDownModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '确认导出';
    options.template = orderStatisticsDownloadTempl;
    options.size = 'md';
    options.controller = downModalController;
    options.modalParams = data; //传递正常客户和限制客户参数
    modalService.openModal(options);
  }

  /*controller定义*/
  function downModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.currentSession = ra.modalParams.current_session;
    ra.cancelBtnText = cancelBtnText;
    ra.content = '下载进度...0%';
    ra.orderStatisticsData = '';
    ra.downFlag = true;

    ra.cancel = cancel;
    ra.close = close;

    init();

    function init() {
      // 0：机构订单统计查询，1：投顾订单统计查询
      if (ra.currentSession == 0) {
        orderStatisticsDownloadService.branchOrderStatisticQryFunc(
          {
            tenant_id: ra.modalParams.tenant_id,
            branch_no: ra.modalParams.branch_no,
            service_type: ra.modalParams.service_type,
            start_time: ra.modalParams.start_time,
            end_time: ra.modalParams.end_time,
            page_no: ra.modalParams.page_no,
            page_size: ra.modalParams.page_size
          },
          function(response) {
            handleDataListFunc(response.data_list);
            ra.downFlag = false;
            ra.content = '下载进度...100%';
          },
          function(err) {
            ra.content = '查询失败！';
          }
        );
      } else if (ra.currentSession == 1) {
        orderStatisticsDownloadService.brokerOrderStatisticQryFunc(
          {
            tenant_id: ra.modalParams.tenant_id,
            branch_no: ra.modalParams.branch_no,
            service_type: ra.modalParams.service_type,
            broker_name: ra.modalParams.brokerName,
            start_time: ra.modalParams.start_time,
            end_time: ra.modalParams.end_time,
            page_no: ra.modalParams.page_no,
            page_size: ra.modalParams.page_size
          },
          function(response) {
            handleDataListFunc(response.data_list);
            ra.downFlag = false;
            ra.content = '下载进度...100%';
          },
          function(err) {
            ra.content = '查询失败！';
          }
        );
      }
    }

    function handleDataListFunc(dataList) {
      ra.orderStatisticsData = [];
      // 非汇总数据
      if (dataList.length) {
        dataList.forEach(function(item) {
          if (item.order_statistic_list && item.order_statistic_list.length) {
            item.order_statistic_list.forEach(function(per) {
              ra.orderStatisticsData.push({
                branchName: item.branch_name,
                brokerName: item.broker_name,
                serviceType: per.title,
                orderNum: per.total_order_num,
                orderPrice: per.total_order_price,
                orderTradeNum: per.success_order_num,
                orderTradePrice: per.success_order_price,
                orderCancelNum: per.cancel_order_num,
                orderCancelPrice: per.cancel_order_price,
                orderBackNum: per.unsubscribe_order_num,
                orderBackPrice: per.unsubscribe_order_price
              })
            })
          } else {
            ra.orderStatisticsData.push({
              branchName: item.branch_name,
              brokerName: item.broker_name
            })
          }
        })
      }
    }
  

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = orderStatisticsDownloadCtrl;



