// 观点标签设置

require('locale_zh');

var func = require('func');

combineShelfCheckCtrl.$inject = ['modalService', 'combineShelfCheckService', 'combineCheckModalCtrl', 'combineRemarkModalCtrl', 'combineDownModalCtrl', 'combineChangeModalCtrl'];

function combineShelfCheckCtrl(modalService, combineShelfCheckService, combineCheckModalCtrl, combineRemarkModalCtrl, combineDownModalCtrl, combineChangeModalCtrl) {
  var vm = this;

  vm.tenant_id = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.branchNo = JSON.parse(sessionStorage.getItem('branchNoData'));

  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.keyword = '';
  vm.combineList = [];

  vm.searchFunc = searchFunc;
  vm.pageChangeFunc = pageChangeFunc;
  vm.openCombineCheckModal = openCombineCheckModal; // 开启审核弹窗
  vm.openCombineRemarkModal = openCombineRemarkModal; // 开启审核意见弹窗
  vm.openCombineDownModal = openCombineDownModal; // 开启组合强制下架弹窗
  vm.openCombineChangeModal = openCombineChangeModal; // 开启组合修改弹窗

  combineQry();
  
  //组合列表查询
  function combineQry() {
    combineShelfCheckService.combineQry({
      tenant_id: vm.tenant_id,
      audit_type: '3,4', // 包含组合上下架、组合修改
      combi_name: vm.keyword,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage
    }, function (res) {
      console.log('res', res);
      vm.combineList = res.data_list;
      vm.totalItems = res.total_num || 0;
      vm.combineList.length && vm.combineList.forEach(function(item) {
        var remark = JSON.parse(item.combine[0].remark);
        item.applyTime = remark.applyTime;
        item.reason = remark.reason;
        item.change = {
          oldState: remark.snapshot,
          newState: remark.modify_data
        };
      });
    }, function(err) {
      console.log('err', err);
      vm.combineList = [];
      vm.totalItems = 0;
    })
  }

  // 打开审核弹窗，type：1通过，2不通过
  function openCombineCheckModal(type, data) {
    combineCheckModalCtrl.openCombineCheckModalFunc(type, data, function(result) {
      combineQry();
    });
  }

  // 查看审核意见弹窗
  function openCombineRemarkModal(content) {
    combineRemarkModalCtrl.openCombineRemarkModalFunc(content);
  }

  // 打开组合强制下架弹窗
  function openCombineDownModal() {
    combineDownModalCtrl.openCombineDownModalFunc();
  }

  // 打开组合修改弹窗
  function openCombineChangeModal(data) {
    combineChangeModalCtrl.openCombineChangeModalFunc(data);
  }

  function searchFunc() {
    vm.currentPage = 1;
    combineQry()
  }

  function pageChangeFunc() {
    combineQry()
  }
}

module.exports = combineShelfCheckCtrl;



