viewpointTagSettingRoute.$inject = ['$stateProvider'];

function viewpointTagSettingRoute($stateProvider) {
  $stateProvider.state('home.viewpointTagSetting', {
    url: '/viewpointTagSetting',
    templateUrl: './views/viewpointTagSetting.html',
    controller: 'viewpointTagSettingCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = viewpointTagSettingRoute;



