/* 直播管理 module */

liveManageService.$inject = ['api'];

function liveManageService(api) {
  var vm = this;

  vm.funcChatTimetableList = funcChatTimetableList;
  vm.manageOpLiveChatNum = manageOpLiveChatNum;

  // //获取课程数据
  // function funcChatTimetableList(data, cb1) {
  //   api.postRequest({
  //     prefix_type: 'vipstu/manage',
  //     url: 'func_chat_timetable_list',
  //     data: data,
  //     successCallback: function (result) {
  //       angular.isFunction(cb1) && cb1(result);
  //     }
  //   });
  // }

  //获取课程数据
  function funcChatTimetableList(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage_list_live_chat',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  // 设置直播参与人数
  function manageOpLiveChatNum(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage_op_live_chat_num',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  return vm;
}

module.exports = liveManageService;



