operatorAccreditService.$inject = ['api'];
function operatorAccreditService(api) {
  var vm = this;

  vm.operatorAccredit = operatorAccredit;
  vm.branchQry = branchQry;
  vm.roleQry = roleQry;
  vm.operatorQry = operatorQry;
  vm.operatorBranchSet = operatorBranchSet;
  vm.operatorRoleSet = operatorRoleSet;

  //增加操作员信息
  function operatorAccredit(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorAdd',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //数据权限获取
  function branchQry(data, enBranchNo, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchQry',
      data: data,
	  enBranchNo: enBranchNo,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //角色获取
  function roleQry(data, cb1) {
    api.postRequest({
      prefix_type: 'roleService',
      url: 'roleQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //获取当前操作员的营业部权限、角色权限
  function operatorQry(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //数据权限设置
  function operatorBranchSet(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorBranchSet',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //角色设置
  function operatorRoleSet(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorRoleSet',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = operatorAccreditService;



