// 问答审核

require('locale_zh');
var func = require('func');

QACheckCtrl.$inject = ['$scope', 'modalService', 'QACheckService', 'QACheckDetailModalCtrl', 'QACheckOperateModalCtrl', '$sce', '$timeout'];

function QACheckCtrl($scope, modalService, QACheckService, QACheckDetailModalCtrl, QACheckOperateModalCtrl, $sce, $timeout) {
  var vm = this;

  vm.accesstoken = sessionStorage.getItem('access_token');
  vm.tenant_id = JSON.parse(sessionStorage.getItem('tenantId'));

  vm.keywords = '';
  vm.ifOpenCheck = false; // 是否勾选开启审核
  vm.selectTab = ''; // 默认选中的tab ''：全部，0：待审核、1：审核通过，2：审核不通过
  vm.moreData = true; // 是否显示“获取更多”;

  vm.dataList = [];
  vm.pageNo = 1;

  vm.selectTabFunc = selectTabFunc;
  vm.openQACheckDetailFunc = openQACheckDetailFunc;
  vm.nextPageFunc = nextPageFunc;
  vm.QACheckSearch = QACheckSearch;
  vm.openCheckOperateFunc = openCheckOperateFunc;
  vm.changeCheckFunc = changeCheckFunc;

  getAutoCheckStatusFunc();
  getAskListFunc();

  // tab选择
  function selectTabFunc(value) {
    vm.pageNo = 1;
    vm.selectTab = value;
    vm.dataList = [];
    getAskListFunc();
  }

  // 获取开启审核状态
  function getAutoCheckStatusFunc() {
    QACheckService.askAutoCheckQryFunc({
      tenant_id: vm.tenant_id
    }, function(res) {
      console.log('res', res);
      vm.ifOpenCheck = res.ask_check_flag == 0 ? false : true;
    }, function(err) {
      console.log('err', err);
    });
  }

  function changeCheckFunc() {
    QACheckService.askAuditCheckFunc({
      tenant_id: vm.tenant_id,
      ask_check_flag: vm.ifOpenCheck ? 1 : 0, // 0-关闭 1-开启
    }, function(res) {
      console.log('res', res);
      modalService.openAlert('提示', res.error_info);
    }, function(err) {
      console.log('err', err);
    });
  }

  // 获取问答列表
  function getAskListFunc() {
    QACheckService.listAnswerOnManageFunc({
      tenant_id: vm.tenant_id,
      key_word: vm.keywords,
      page_no: vm.pageNo,
      page_size: 10,
      audit_states: vm.selectTab
    }, function(res) {
      console.log('res', res);
      if (res.data_list && res.data_list.length == 10) {
        vm.moreData = true;
      } else {
        vm.moreData = false;
      }
      handleAskListFunc(res.data_list);
    }, function(err) {
      console.log('err', err);
    });
  }

  function handleAskListFunc(dataList) {
    if (dataList.length) {
      var content = '';
      var text = '';
      var img = '';
      dataList.forEach(function(item) {
        content = JSON.parse(item.answer_content);
        text = '';
        img = '';
        content.forEach(function(per) {
          if (per && per.type == 'text') {
            text = per.body.replace(/\n/g, '') || '暂无';
            text = $sce.trustAsHtml(autoMatchStockAndEmoji(text));
          }
          if (per.type == 'img' && per.body) {
            img = per.body;
            img.forEach(function(i) {
              i.link = i.link + '/' + vm.accesstoken;
            });
          }
        }) 
        item.text = text;
        item.img = img;
        vm.dataList.push(item);
        console.log('vm.dataList', vm.dataList);
      });
    }
  }

  // 条件搜索
  function QACheckSearch() {
    vm.dataList = [];
    vm.pageNo = 1;
    getAskListFunc();
  }
  
  // 查看更多问答
  function openQACheckDetailFunc(data) {
    QACheckDetailModalCtrl.openQACheckDetailModal(data);
  }

  // 审核弹窗
  function openCheckOperateFunc(type, answerId) {
    QACheckOperateModalCtrl.openQACheckOperateFunc(type, answerId, function() {
      vm.dataList = [];
      vm.pageNo = 1;
      getAskListFunc();
    });
  }

  //分页
  function nextPageFunc() {
    vm.pageNo++;
    getAskListFunc();
  }

  //匹配股票和表情
  function autoMatchStockAndEmoji(str){
    return func.autoMatchStockName(func.showEmoji(str));
  }
}

module.exports = QACheckCtrl;



