/* 操作员管理 module */

require('locale_zh');
var modalOptions = require('jsDir/modal_options');
var func = require('func');

operatorManageCtrl.$inject = ['$cookies', 'operatorManageService','operatorAddCtrl','operatorEditCtrl','operatorAccreditCtrl','operatorFrozenCtrl','operatorUnFrozenCtrl','modalService','operatorDelCtrl'];

function operatorManageCtrl($cookies, operatorManageService,operatorAddCtrl,operatorEditCtrl,operatorAccreditCtrl,operatorFrozenCtrl,operatorUnFrozenCtrl,modalService,operatorDelCtrl) {
  var vm = this;

  /*操作员查询参数*/
  vm.operator_branchNo = '';
  vm.operator_branchName = '--请选择--';
  vm.operator_name = '';
  vm.total_items = 0;
  vm.pageNo = 1;
  vm.branchNo = '';
  vm.isMod = false;
  vm.pagination = {
    pageSize: 5,    //页面大小
    pageNo: 1     //当前页
    //total_items: 0   //总页数
  };
  vm.access_token = sessionStorage.getItem('access_token');
  vm.dicts = [{"code":"1","value":"管理员"},{"code":"2","value":"普通操作员"}];
  vm.dicts_status = [{"code":"0","value":"正常"},{"code":"1","value":"冻结"},{"code":"2","value":"锁定"}];
  vm.operatorInfo = JSON.parse(sessionStorage.getItem("operatorInfo"));

  /* 分页 */
  vm.maxSize = 5;
  vm.currentPage = 1;
  vm.itemPerPage = 5;

  vm.operatorQry = operatorQry;  //点击查询操作员信息
  vm.operatorAddFunc = operatorAddFunc;  //新增操作员
  vm.selectBranchNoFunc = selectBranchNoFunc;
  vm.pageChangeFunc = pageChangeFunc; //换页方法

  /* 打开模态框 */
  vm.operatorAddFunc = operatorAddFunc; //打开增加模态窗
  vm.operatorEditFunc = operatorEditFunc; //打开修改模态窗
  vm.operatorAccreditFunc = operatorAccreditFunc; //打开授权模态窗
  vm.operatorFrozenFunc = operatorFrozenFunc; //打开冻结模态窗
  vm.operatorUnFrozenFunc = operatorUnFrozenFunc; //打开解冻模态窗
  vm.operatorDeleteFunc = operatorDeleteFunc; //删除模态窗

  operatorRefresh();  //刷新显示角色信息

  //打开增加模态窗
  function operatorAddFunc() {
    var data = {
      branch: vm.dataMulList
    };

    operatorAddCtrl.operatorAddFunc(data,
      function(result) {
        vm.operator_code = result.operatorNo;
        vm.operator_branchNo = result.branchNo;
        vm.operator_branchName = result.branchName;
        operatorRefresh();
      }
    );
  }

  //打开修改模态窗
  function operatorEditFunc(item) {
    var data = {
      branch: item.branch,
      type: item.operatorType,
      operatorNo: item.operatorNo,
      name: item.name,
      phoneNo: item.telephone,
      remark: item.remark,
      registerDay: item.registerDay,
      roleRights: item.roleRights,
      rowCount: item.rowCount,
      status: item.status
    };

    operatorEditCtrl.operatorEditFunc(data,
      function() {
        operatorRefresh();
      }
    )
  }

  //打开授权模态窗
  function operatorAccreditFunc(item) {
    var data = {
      branch: item.branch,
      branchNo: item.branchNo,
      operatorNo: item.operatorNo,
      name: item.name
    };

    operatorAccreditCtrl.operatorAccreditFunc(data,
      function() {
        operatorRefresh();
      }
    )
  }

  //打开冻结模态窗
  function operatorFrozenFunc(item) {
    var data = {
      branch: item.branch,
      type: item.operatorType,
      operatorNo: item.operatorNo,
      name: item.name,
      phoneNo: item.telephone,
      remark: item.remark,
      registerDay: item.registerDay,
      roleRights: item.roleRights,
      rowCount: item.rowCount,
      status: item.status
    };

    operatorFrozenCtrl.operatorFrozenFunc(data,
      function(result) {
        operatorRefresh();
      }
    )
  }

  //打开解冻模态窗
  function operatorUnFrozenFunc(item) {
    var data = {
      branch: item.branch,
      type: item.operatorType,
      operatorNo: item.operatorNo,
      name: item.name,
      phoneNo: item.telephone,
      remark: item.remark,
      registerDay: item.registerDay,
      roleRights: item.roleRights,
      rowCount: item.rowCount,
      status: item.status
    };

    operatorUnFrozenCtrl.operatorUnFrozenFunc(data,
      function(result) {
        operatorRefresh();
      }
    )
  }

  //打开删除模态窗
  function operatorDeleteFunc(item) {
    operatorDelCtrl.openOperatoeDelModalFunc(function() {
      operatorManageService.operatorDelete({
        action_in: 3,
        branchName: item.branch,
        branchNo: item.branchNo,
        branchRights: item.branchRights,
        name: item.name,
        operatorNo: item.operatorNo,
        operatorType: item.operatorType,
        registerDay: item.registerDay,
        remark: item.remark,
        roleRights: item.roleRights,
        rowCount: item.rowCount,
        status: item.status,
        status_name: item.status_name,
        telephone: item.telephone
      }, function (response) {
        operatorRefresh();
        var options = angular.copy(modalOptions);
        options.modalTitle = '操作员删除';
        options.modalInfo = '删除成功！';
        modalService.openModal(options);
      });
    })
  }

  //页面刷新
  function operatorRefresh() {
    var branchList = [];
    if (vm.operator_branchNo > 0) {
      branchList.push(vm.operator_branchNo);
    }
    operatorManageService.getOperatorInfo({
      branchNo: vm.operator_branchNo,
      branchNoList: branchList,
      checkEnBranchNo: 1,
      name: vm.operator_name,
      operatorNo: vm.operator_code,
      pageNo: vm.currentPage,
      pageSize: 5,
      remark: ''
    }, function (response) {
      setResponseData(response);
    });
  }

  //页面刷新显示操作员列表信息
  function setResponseData(response) {
    if (response.data != undefined) {
      if (response.data[0] != undefined && 'rowCount' in response.data[0]) {
        vm.total_items = response.data[0].rowCount;
      } else {
        vm.total_items = 0;
      }
    }

    //列表加载匹配信息
    vm.operatorList = response.data;
    vm.branchInfo = JSON.parse(sessionStorage.getItem("allBranchListData"));
    vm.dataMulList = [];
    for (var i = 0; i < vm.branchInfo.length; i++) {
      var obj = {};
      obj.branchNo = vm.branchInfo[i].branchNo;
      obj.branchName = vm.branchInfo[i].branchName;
      vm.dataMulList.push(obj);
    }
    if (vm.operatorList != undefined && vm.operatorList.length > 0) {
      for (var j = 0; j < vm.operatorList.length; j++) {
        if (vm.branchInfo != undefined && vm.branchInfo.length > 0) {
          for (var i = 0; i < vm.branchInfo.length; i++) {
            if (vm.branchInfo[i].branchNo === vm.operatorList[j].branchNo) {
              vm.operatorList[j].branch = vm.operatorList[j].branchNo + "-" + vm.branchInfo[i].branchName;
            }
          }
        }
      }
    }
  }

  function operatorQry() {
    operatorRefresh();
  }

  // 机构选择
  function selectBranchNoFunc(branchNo, branchName) {
    vm.operator_branchNo = branchNo;
    vm.operator_branchName = branchName;
  }

  //分页查询
  function pageChangeFunc() {
    operatorRefresh();
  }

}

module.exports = operatorManageCtrl;



