/* 组织管理 module */

branchManageService.$inject = ['api'];

function branchManageService(api) {
  var vm = this;

  vm.branchInfoQry = branchInfoQry;
  vm.MenuInfoQry = MenuInfoQry;
  vm.allBranchQry = allBranchQry;
  vm.setBranchMenu = setBranchMenu;

  //选中营业部信息查询
  function branchInfoQry(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchInfoQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //选中营业部菜单信息查询
  function MenuInfoQry(data, cb1) {
    api.postRequest({
      prefix_type: 'menuService',
      url: 'menuQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //获取组织权限列表、权限管理
  function allBranchQry(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //保存设置
  function setBranchMenu(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchMenuSet',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = branchManageService;



