  //匹配股票
  autoMatchStock: function(str) {
    var reg = /(\$[^$]*\$)/gi;

    //str = encodeHtml(str)
    return (
      typeof str === 'string' &&
      str.replace(reg, function() {
        var t = arguments[1].replace(/\$/g, '');

        if (t) {
          if (/我的自选股/.test(t)) {
            // 我的自选股
            return '<span class="self-stock">' + t + '</span>';
          } else if (t.indexOf('(') !== -1) {
            // $恒生电子(600570SS)$
            var stock = t.substring(t.indexOf('(') + 1, t.indexOf(')'));
            if (/SH|SS|SZ/gi.test(stock) && stock.length === 8) {
              var code = stock.substring(0, 6); // 000001
              return '<span class="self-stock">' + func.appendStockSuffix(code) + '</span>';
            } else {
              return t;
            }
          } else if (/\.SH|\.SS|\.SZ/gi.test(t)) {
            // 管理端 $600570.SS$
            return '<span class="self-stock">' + func.appendStockSuffix(t) + '</span>';
          } else {
            return '<span class="self-stock">' + t + '</span>';
          }
        } else {
          //$$
          return '<span class="self-stock">' + t + '</span>';
        }
      })
    );
  },

  //匹配股票
  autoMatchStockName: function(str) {
    var reg = /(\$[^$]*\$)/gi;
    if (reg.test(str)) {
      return (
        typeof str === 'string' &&
        str.replace(reg, function() {
          var t = arguments[1].replace(/\$/g, '');
          return '<span class="QACheck-stock">' + t + '</span>';
        })
      );
    } else {
      return str;
    }
    
  },

  //金额为undefined转换,返两位小数
  transMoney: function(number) {
    if (!number) {
      return 0.0;
    } else {
      return parseFloat(number).toFixed(2);
    }
  },

  //金额为undefined转换，返回整数0
  transNum: function(number) {
    if (!number) {
      return '0.00';
    } else {
      return parseInt(number);
    }
  },

  buildEmoji: function(text) {
    return text;
  },

  appendStockSuffix: function(keywordStock) {
    var regSS = /^(((700|730|900|600|601|580)[\d]{3})|60[\d]{4})$/;
    var regSZ = /^(((000|002|200|300|080|031)[\d]{3}|00[\d]{4}))$/;

    if (regSS.test(keywordStock)) {
      keywordStock = keywordStock + '.SS';
    } else if (regSZ.test(keywordStock)) {
      keywordStock = keywordStock + '.SZ';
    }

    return keywordStock;
  },

  // 过滤股票格式
  filterStock: function(str) {
    var len = str.length;
    if (len && str[0] == '$' && str[len - 1] == '$') {
      str = str.replace(/\$/g, '');
    }
    return str;
  },

