/* 角色权限配置 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var rolesModalCtrl = require('./modalPopup/rolesModal/controller');
var rolesModalService = require('./modalPopup/rolesModal/service');
var accreditModalCtrl = require('./modalPopup/accreditModal/controller');
var accreditModalService = require('./modalPopup/accreditModal/service');
var alterModalCtrl = require('./modalPopup/alterModal/controller');
var alterModalService = require('./modalPopup/alterModal/service');

//setter定义模块
module.exports = angular
  .module('rolesManage', [])
  .config(Routing)
  .service('rolesManageService', service)
  .controller('rolesManageCtrl', ctrl)
  .factory('rolesModalCtrl', rolesModalCtrl)
  .service('rolesModalService', rolesModalService)
  .factory('accreditModalCtrl', accreditModalCtrl)
  .service('accreditModalService', accreditModalService)
  .factory('alterModalCtrl', alterModalCtrl)
  .service('alterModalService', alterModalService).name;



