/* 店铺管理 module */

storeManageRoute.$inject = ['$stateProvider'];

function storeManageRoute($stateProvider) {
    $stateProvider
        .state('home.storeManage', {
            url: '/storeManage',
            templateUrl: './views/storeManage.html',
            controller: 'storeManageCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = storeManageRoute;



