viewpointPackageCheckService.$inject = ['api'];
function viewpointPackageCheckService(api) {
  var vm = this;
  vm.getViewpointPkgList = getViewpointPkgList; // 观点包列表

  function getViewpointPkgList(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage_list_viewpoint_pkg_audit',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  

  return vm;
}

module.exports = viewpointPackageCheckService;


