subscribeModalService.$inject = ['api'];
function subscribeModalService(api) {
  var vm = this;
  vm.subscribeMore = subscribeMore;

  function subscribeMore(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'businessService',
      url: 'businessUserSubQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }
  return vm;
}

module.exports = subscribeModalService;



