alterModalService.$inject = ['api'];
function alterModalService(api) {
  var vm = this;
  vm.roleMode = roleMode;//获取修改角色信息

  function roleMode(data, cb1) {
    api.postRequest({
      prefix_type: 'roleService',
      url: 'roleMod',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;

}

module.exports = alterModalService;



