/*投顾详情*/

require('locale_zh');
var func = require('func');

moreInvestInfoCtrl.$inject = ['$state', '$stateParams'];
function moreInvestInfoCtrl($state, $stateParams) {
  var vm = this;

  //接收投顾信息
  vm.investData = JSON.parse($stateParams.params);

  vm.editInvestInfo = editInvestInfo;
  vm.goBack = goBack;
  
  // 返回上一页面
  function goBack() {
    $state.go('home.investManage', {
      params: vm.investData.userStatus
    });
  }

  //编辑投顾信息
  function editInvestInfo() {
    console.log(vm.investData);
    $state.go('home.addNewInvest', {
      params: JSON.stringify(vm.investData),
      type: 1
    });
  }

}

module.exports = moreInvestInfoCtrl;



