/*投顾详情*/

moreInvestInfoRoute.$inject = ['$stateProvider'];

function moreInvestInfoRoute($stateProvider) {
  $stateProvider
    .state('home.moreInvestInfo', {
      url: '/moreInvestInfo?params',
      templateUrl: './views/moreInvestInfo.html',
      controller: 'moreInvestInfoCtrl',
      controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
    });
}

module.exports = moreInvestInfoRoute;



