signProtocolManageRoute.$inject = ['$stateProvider'];

function signProtocolManageRoute($stateProvider) {
  $stateProvider.state('home.signProtocolManage', {
    url: '/signProtocolManage?data',
    templateUrl: './views/signProtocolManage.html',
    controller: 'signProtocolManageCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = signProtocolManageRoute;



