serviceProduct04Route.$inject = ['$stateProvider'];

function serviceProduct04Route($stateProvider) {
    $stateProvider
        .state('home.serviceProduct04', {
            url: '/serviceProduct04',
            templateUrl: './views/serviceProduct04.html',
            controller: 'serviceProduct04Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = serviceProduct04Route;



