require ('./style.less');
var config = require('config');

var modalOptions = require('jsDir/modal_options'),
downloadProgressModalTempl = require('./downloadProgressModal.html'),
  func = require('func');

  downloadProgressModalCtrl.$inject = ['modalService', 'downloadProgressModalService', '$sce'];

function downloadProgressModalCtrl(modalService, downloadProgressModalService, $sce) {
  var vm = this;

  vm.openModal = openModal;

  function openModal(str, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '系统提示';
    options.size = 'md';
    options.template = downloadProgressModalTempl;
    options.modalParams = str;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function () {
      angular.isFunction(callBack) && callBack();
    };

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalTime = modalTime;
    ra.modalParams = modalParams;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;
    
    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    signProtocolDownloadFunc();
    setTimeout(() => {
      close();
    }, 10000);

    function signProtocolDownloadFunc() {
      var accessToken = sessionStorage.getItem('access_token');
      var url = LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download_zip/' + ra.modalParams + '/' + accessToken;
      var $form = $('<form id="download" method="GET"></form>');
      $form.attr('action', url);
      $form.appendTo($('body'));
      $form.submit();

      // downloadProgressModalService.signProtocolDownload(
      //   {
      //     // file_names: ra.modalParams
      //     file_names: '155****5555_111_20045020200319001644080000000001_1.pdf,155****5555_111_20045020200319001644080000000001_1.pdf'
      //   },
      //   function(result) {
      //     console.log(result);
      //     // submit();
      //   },
      //   function(error) {
      //     modalService.openAlert('提示', error.error_info);
      //   }
      // );
    }

    //新增提交
    function submit() {
      close();
    }

    function close() {
      $('#download').remove();
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      $('#download').remove();
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = downloadProgressModalCtrl;


