/* 日志查询 module */

require('locale_zh');
var func = require('func'),
  modalOptions = require('jsDir/modal_options');

logInquiryCtrl.$inject = ['logInquiryService', 'modalService', 'logDetailModalCtrl'];

function logInquiryCtrl(logInquiryService, modalService, logDetailModalCtrl) {
  var vm = this;

  vm.logBranch = '';
  vm.logBranchName = '--请选择--';

  /* 分页 */
  vm.maxSize = 5;
  vm.total_items = '';
  vm.currentPage = 1;
  vm.itemPerPage = 5;
  vm.isMod = false;
  vm.sysDate = '';
  /* 日期选择 */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };
  vm.beginDate = new Date();
  vm.endDate = new Date();
  vm.beginTime = 0;
  vm.finishTime = 0;
  vm.operatorInfo = JSON.parse(sessionStorage.getItem("operatorInfo"));
  vm.logOperatorNo = vm.operatorInfo.operatorNo;

  vm.setResponseDate = setResponseDate; //查询结果
  vm.logSearchFun = logSearchFun; //点击查询
  vm.openStartTimeFunc = openStartTimeFunc; //开始日期
  vm.openEndTimeFunc = openEndTimeFunc; //开始日期
  vm.selectBranchNoFunc = selectBranchNoFunc;
  vm.pageChangeFunc = pageChangeFunc; //换页方法
  vm.logDetail = logDetail; // 双击显示详情

  branchQry();  //查询机构
  operatorLogQry(); //日志列表查询

  // 开始日期与结束日期间隔不能超过3个月
  function dateIntervalsCheck() {
    if (vm.beginDate != null && vm.endDate != null) {
      var date1 = func.formatDate(vm.beginDate, 'yyyyMMdd');
      var date2 = func.formatDate(vm.endDate, 'yyyyMMdd');
      if (date1 > date2) {
        var options = angular.copy(modalOptions);
        options.modalTitle = '警告';
        options.modalInfo = '开始日期大于结束日期！';
        modalService.openModal(options);
        return false;
      }
      else {
        if (date1 > 0 && date2 > 0) {
          var IntervalsDay = date2 - date1;
          if (IntervalsDay > 300) {
            var options = angular.copy(modalOptions);
            options.modalTitle = '警告';
            options.modalInfo = '日期间隔不能大于3个月，请重新选择！';
            modalService.openModal(options);
            return false;
          }
        }
        else {
          var options = angular.copy(modalOptions);
          options.modalTitle = '警告';
          options.modalInfo = '开始日期、结束日期为必选项！';
          modalService.openModal(options);
          return false;
        }
      }
    }
    return true;
  }

  function branchQry() {
    logInquiryService.branchQry({
      branchCode: '',
      branchName: '',
      branchNo: 0,
      branchType: '',
      upperBranchNo: 0
    }, function (response) {
      vm.dataMulList = response.data;
    })
  }

  function operatorLogQry() {
    if (dateIntervalsCheck()) {
      var branchList = [];
      if (vm.logBranch > 0) {
        branchList.push(vm.logBranch);
      }

      logInquiryService.operatorLogQry({
        beginDate: vm.beginDate ? func.formatDate(vm.beginDate, 'yyyyMMdd') : null,
        beginTime: vm.beginTime ? func.timeHMSConvert(vm.beginTime, 'hhmmss') : 0,
        branchNo: vm.logBranch ? vm.logBranch : 0,
        branchNoList: branchList,
        endDate: vm.endDate ? func.formatDate(vm.endDate, 'yyyyMMdd') : null,
        endTime: vm.finishTime ? func.timeHMSConvert(vm.finishTime, 'hhmmss') : 0,
        interfaceUrl: '',
        operatorNo: vm.logOperatorNo,
        pageNo: vm.currentPage,
        pageSize: vm.itemPerPage,
        positionStr: ''
      }, function (response) {
        setResponseDate(response);
      })
    }

  }

  function setResponseDate(response) {
    if (response.data != undefined) {
      if (response.data[0] != undefined && 'rowCount' in response.data[0]) {
        vm.total_items = response.data[0].rowCount;
      } else {
        vm.total_items = 0;
      }
    }
    vm.logListDate = response.data;
    vm.branchInfo = JSON.parse(sessionStorage.getItem("allBranchListData"));
    if (vm.logListDate != undefined && vm.logListDate.length > 0) {
      for (var j = 0; j < vm.logListDate.length; j++) {
        if (vm.branchInfo != undefined && vm.branchInfo.length > 0) {
          for (var i = 0; i < vm.branchInfo.length; i++) {
            if (vm.branchInfo[i].branchNo === vm.logListDate[j].branchNo) {
              vm.logListDate[j].branch = vm.logListDate[j].branchNo + "-" + vm.branchInfo[i].branchName;
            }
          }
        }
      }
    }
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  }

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  }

  function logSearchFun() {
    operatorLogQry();
  }

  // 机构选择
  function selectBranchNoFunc(branchNo, branchName) {
    vm.logBranch = branchNo;
    vm.logBranchName = branchName;
  }

  function pageChangeFunc() {
    operatorLogQry();
  }

  function logDetail(item) {
    console.log(vm.sysDate);
    logDetailModalCtrl.logDetailFunc(item);
  }

}

module.exports = logInquiryCtrl;



