 // 风险等级设置

require('locale_zh');
var func = require('func');

orderCheckCtrl.$inject = ['modalService', 'orderCheckService', 'orderCheckModalCtrl', 'orderRemarkModalCtrl'];

function orderCheckCtrl(modalService, orderCheckService, orderCheckModalCtrl, orderRemarkModalCtrl) {
  var vm = this;

  vm.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));

  vm.orderStatus = '';
  vm.dictsOrderStatus = [
    {label: '待支付', value: '0'},
    {label: '已支付', value: '1'},
    {label: '已失效', value: '2'},
    {label: '待退款', value: '3'},
    {label: '已退款', value: '4'},
    {label: '已评价', value: '5'},
    {label: '待确认', value: '6'},
    {label: '已确认', value: '7'},
    {label: '支付失败', value: '8'}
  ];
  vm.keywords = '';
  vm.recommender = '';
  vm.payType = '';
  vm.dictsPayType = [
    {label: '微信', value: '0'},
    {label: '支付宝', value: '1'},
    {label: '银联', value: '2'},
    {label: '线下转账', value: '3'}
  ];
  vm.orderId = '';
  vm.checkStatus = '';
  vm.dictsCheckStatus = [
    {label: '待审核', value: '0'},
    {label: '审核通过', value: '1'},
    {label: '审核不通过', value: '2'},
  ];
  vm.productType = '';
  vm.dictsProductType = [
    {label: '提问', value: '1'},
    {label: '观点', value: '2'},
    {label: '组合', value: '4,5,6,7'},
    {label: '观点包', value: '8,9,10,11'},
    {label: '量化模型', value: '90,91,92,93'},
    {label: '智能策略', value: '94,95,96,97'}
  ];
  /* 日期选择 */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };
  vm.startTime = '';
  vm.endTime = '';
  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.list = [];

  vm.openStartTimeFunc = openStartTimeFunc; //开始日期选择
  vm.openEndTimeFunc = openEndTimeFunc; //结束日期选择
  vm.resetForm = resetForm;
  vm.searchFunc = searchFunc;
  vm.openCheckModalFunc = openCheckModalFunc;
  vm.openReamrkModalFunc = openReamrkModalFunc;
  vm.pageChangeFunc = pageChangeFunc;

  orderListQryFunc();

  function orderListQryFunc() {
    orderCheckService.orderListQry(
      {
        tenant_id: vm.tenantId,
        order_status: vm.orderStatus,
        keyword: vm.keywords,
        recommend_name: vm.recommender,
        order_type: vm.productType,
        audit_state: vm.checkStatus,
        transaction_id: vm.orderId,
        start_time: vm.startTime && parseInt(Number(vm.startTime) / 1000) || '', 
        end_time: vm.endTime && parseInt(Number(vm.endTime) / 1000) + (60 * 60 * 24 - 1) || '', // 结束日期要算到23：59：59
        third_type: vm.payType,
        page_no: vm.currentPage,
        page_size: vm.itemPerPage
      },
      function(result) {
        // audit_state 0-待审核 1-审核通过 2-审核不通过
        vm.list = result.data_list || [];
        vm.totalItems = result.total_num || 0;
      },
      function(error) {
        modalService.openAlert('提示', error.error_info);
        vm.totalItems = result.total_num || 0;
      }
    );
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  };

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function resetForm() {
    vm.orderStatus = '';
    vm.keywords = '';
    vm.recommender = '';
    vm.payType = '';
    vm.orderId = '';
    vm.checkStatus = '';
    vm.productType = '';
    vm.startTime = '';
    vm.endTime = '';
  }

  function searchFunc() {
    vm.currentPage = 1;
    orderListQryFunc();
  }

  function openCheckModalFunc(id) {
    orderCheckModalCtrl.openModal(id, function() {
      orderListQryFunc();
    });
  }

  function openReamrkModalFunc(remark) {
    orderRemarkModalCtrl.openModal(remark);
  }

  function pageChangeFunc() {
    orderListQryFunc();
  }
}

module.exports = orderCheckCtrl; 


