require ('./style.less');

var modalOptions = require('jsDir/modal_options'),
    addStoreModalTempl = require('./addStoreModal.html'),
    func = require('func');

addStoreModalCtrl.$inject = ['modalService', 'addStoreModalService'];

function addStoreModalCtrl(modalService, addStoreModalService) {
  var vm = this;

  vm.openAddStoreModalFunc = openAddStoreModalFunc;

  function openAddStoreModalFunc(callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '新增店铺';
    options.template = addStoreModalTempl;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function () {
      angular.isFunction(callBack) && callBack();
    };

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.storeName = '';//店铺编号
    ra.manageName = '';//店长姓名
    ra.IDNo = '';//身份证号
    ra.mobile = '';//手机号码
    ra.staffNo = '';//员工工号
    ra.certificateType = '';//资格证书类型
    ra.certificateNo = '';//资格证书编号

    ra.userId = '';//新增店铺入参user_id
    ra.brokerId = '';//新增店铺入参broker_id

    ra.manageNameList = [];//获取联想店长姓名
    ra.modelOptions = { //自动索引组件配置对象
      debounce: {
        default: 500,
        blur: 250
      },
      getterSetter: true
    };

    ra.postManageName = postManagerName;
    ra.selectMatch = selectMatch;
    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    //输入监听调用店长联想
    function postManagerName() {
      if (ra.manageName && ra.manageName != ' ') {
		addStoreModalService.funcBrokerListQuery(
		  {
			tenant_id: JSON.parse(sessionStorage.getItem('tenantId')),
			key_word: ra.manageName
		  },
		  function (result) {
			ra.manageNameList = result.data_list;
			console.log(ra.manageNameList);
		  }
		);
      }
    }

    //输入匹配
    function selectMatch(item) {
      if (item) {
        ra.manageName = item.alias_name;
        ra.userId = item.user_id;
        ra.IDNo = item.id_card_no;
        ra.mobile = item.mobile;
        ra.staffNo = item.employee_no;
        ra.certificateType = item.credentials_type;
        ra.certificateNo = item.credentials_no;
      }
    }

    //新增提交
    function submit() {
      if (ra.storeName.length != 0 && ra.manageName.length != 0) {
        addStoreModalService.funcBrokerAdd(
          {
            broker_name: ra.storeName,
            user_id: ra.userId,
            broker_id: ra.brokerId   //待修改
          },
          function (result) {
            func.openToaster('操作成功！');
            close();
          }
        )
      } else {
        func.openToaster('店铺名称和店长为必填！');
      }
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = addStoreModalCtrl;



