/* 店铺管理 module */

require('locale_zh');

var func = require('func');

storeManageCtrl.$inject = ['modalService', 'storeManageService', 'addStoreModalCtrl', 'editStoreModalCtrl', 'nonUseStoreModalCtrl', 'tagManageModalCtrl'];

function storeManageCtrl(modalService, storeManageService, addStoreModalCtrl, editStoreModalCtrl, nonUseStoreModalCtrl, tagManageModalCtrl) {
  var vm = this;

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 1;
  vm.currentPage = 1;
  vm.itemsPerPage = 10;

  vm.brokerId = ''; //店铺编号
  vm.brokerName = ''; //店铺名称
  vm.manageName = ''; //店长姓名

  vm.defaultStore = {}; // 默认店铺
  vm.selectedStoreName = ''; // 选中的店铺名称
  vm.storeList = []; //店铺列表

  vm.storeInfoQry = storeInfoQry;
  vm.selectedStoreFunc = selectedStoreFunc;
  vm.setDefaultStoreFunc = setDefaultStoreFunc;
  vm.pageChangeFunc = pageChangeFunc;
  vm.openAddStoreModal = openAddStoreModal;
  vm.openTagManageModal = openTagManageModal;
  vm.openEditStoreModal = openEditStoreModal;
  vm.openNonUseModal = openNonUseModal;

  init();

  //初始化
  function init() {
    storeInfoQry();
    getDefaultStoreFunc();
  }

  //获取店铺列表
  function storeInfoQry() {
    storeManageService.funcOperateBrokerSearch(
      {
        tenant_id: JSON.parse(sessionStorage.getItem('tenantId')),
        broker_id: vm.brokerId,
        broker_name: vm.brokerName,
        manager_name: vm.manageName,
        page_count: vm.itemsPerPage,
        page_no: vm.currentPage
      },
      function(result) {
        vm.totalItems = result.total_item_num;
        vm.storeList = result.data_list;
      }
    );
  }

  // 获取默认推荐店铺
  function getDefaultStoreFunc() {
    storeManageService.getDefaultStore(
      {},
      function(result) {
        vm.defaultStore = result;
        vm.selectedStoreName = result.broker_name;
      },
      function(err) {}
    );
  }

  // 选择当前店铺
  function selectedStoreFunc(store) {
    vm.defaultStore = store;
    vm.selectedStoreName = store.broker_name;
  }

  // 设置默认店铺
  function setDefaultStoreFunc() {
    storeManageService.setDefaultStore(
      {
        broker_id: vm.defaultStore.broker_id
      },
      function(res) {
        modalService.openAlert('提示', '设置成功！');
      },
      function(err) {
        modalService.openAlert('提示', '执行失败！');
      }
    );
  }

  //打开新增店铺弹窗
  function openAddStoreModal() {
    addStoreModalCtrl.openAddStoreModalFunc(function() {
      storeInfoQry();
    });
  }

  //打开标签管理弹窗
  function openTagManageModal(item) {
    tagManageModalCtrl.openTagManageModalFunc(item, function() {
      storeInfoQry();
    });
  }

  //打开店铺编辑弹窗
  function openEditStoreModal(item) {
    editStoreModalCtrl.openEditStoreModalFunc(item, function() {
      storeInfoQry();
    });
  }

  //打开店铺停用弹窗
  function openNonUseModal(item, status) {
    nonUseStoreModalCtrl.openNonUseStoreModalFunc(item, status, function() {
      storeInfoQry();
    });
  }

  //换页
  function pageChangeFunc() {
    storeInfoQry();
  }
}

module.exports = storeManageCtrl;



