investorAchievementQryService.$inject = ['api'];
function investorAchievementQryService(api) {
  var vm = this;

  vm.brokerPerformanceQry = brokerPerformanceQry;

  function brokerPerformanceQry(data,cb1,cb2) {
    api.postRequest ({
      prefix_type: 'vipstu',
      url: 'func_broker_performance_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      }
    })
  }

  return vm;
}

module.exports = investorAchievementQryService;


