liveCheckModalService.$inject = ['api'];
function liveCheckModalService(api) {
  var vm = this;
  
  vm.manageLiveChatAudit = manageLiveChatAudit;

  function manageLiveChatAudit(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage_live_chat_audit',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

}

module.exports = liveCheckModalService;


