editCourseModalService.$inject = ['api'];

function editCourseModalService(api) {
  var vm = this;

  vm.getOperatorBrokerList = getOperatorBrokerList;
  vm.funcChatTimetableSave = funcChatTimetableSave;
  vm.funcChatTimetableUpdate = funcChatTimetableUpdate;
  vm.funChatTimetableDel = funChatTimetableDel;

  //获取店铺列表信息
  function getOperatorBrokerList(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu/manage',
      url: 'func_operate_broker_search',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //直播课程新增
  function funcChatTimetableSave(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu/manage',
      url: 'func_chat_timetable_save',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  //更新当前课程安排
  function funcChatTimetableUpdate(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu/manage',
      url: 'func_chat_timetable_update',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  //删除当前课程
  function funChatTimetableDel(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu/manage',
      url: 'func_chat_timetable_del',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  return vm;
}

module.exports = editCourseModalService;



