/*批量新增投顾*/

require('locale_zh');
require('script-loader!ng-table-excel-export/ng-table-excel-export');
require.ensure([], function (require) {
  XLSX = require('xlsx');
}, 'excel');
 var func = require('func');

batchAddNewInvestCtrl.$inject = ['$scope', 'batchAddNewInvestService', '$cookies'];
function batchAddNewInvestCtrl($scope, batchAddNewInvestService, $cookies) {
  var vm = this;
  var wb;//读取完成的数据
  var rABS = false; //是否将文件读取为二进制字符串
  vm.name = '';
  vm.myFile = '';
  vm.excelList = '';
  vm.download = download;//下载excel模板
  vm.saveInvestInfo = saveInvestInfo; //批量保存
  vm.readAsText = readAsText; //显示文本文件
  var certificatedType = {
    '一般证券业务': '1',
    '证券投资咨询业务': '2',
    '证券经纪业务营销': '3',
    '证券经纪人': '4',
    '分析师': '5',
    '投资顾问': '6',
    '保荐代表人': '7',
    '投资主办人': '8'
  };
  var adviserType = {
    '投资顾问助理': '0',
    '投资顾问': '1',
    '理财顾问': '2',
    '高级投顾': '3',
    '明星投顾': '4',
    '资深投顾': '5',
    '钻石投顾': '6',
    '首席投顾': '7'
  };

  //下载excel模板
  function download() {
    window.open('assets/template/batchAddInvestorTemplate.xlsx');
  }

  //批量保存
  function saveInvestInfo() {
    //处理投顾类型值，资格证书类型值
    angular.forEach(vm.list, function (item) {
      item.adviserType = adviserType[item.adviserType];
      item.credentialsType = certificatedType[item.credentialsType];
    });
    batchAddNewInvestService.saveFunc({
      adviserList: vm.list
    }, function (response) {
      func.openToaster('批量新增成功');
    }, function () {
      func.openToaster('操作失败');
    })
  }

  //显示文本文件
  function readAsText(event) {
    var file = event.currentTarget.files[0];
    vm.myFile = event.currentTarget.value;
    if (!file) {
      return;
    }
    var reader = new FileReader();
    reader.onload = function (e) {
      var data = e.target.result;
      if (rABS) {
        wb = XLSX.read(btoa(fixdata(data)), {//手动转化
          type: 'base64'
        });
      } else {
        wb = XLSX.read(data, {
          type: 'binary'
        });
      }
      //wb.SheetNames[0]是获取Sheets中第一个Sheet的名字
      //wb.Sheets[Sheet名]获取第一个Sheet的数据
      vm.adviserInfoList = JSON.stringify(XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]));
      vm.list = JSON.parse(vm.adviserInfoList);
      if (vm.list != undefined && vm.list.length != 0) {
        for (var k = 0; k < vm.list.length; k++) {
          var bbb = vm.list[k];
          for (var j in bbb) {
            var value = bbb[j];
            if (j == "申请人姓名") {
              delete bbb[j];
              j = "applicantName";
              bbb["aliasName"] = value; // 昵称参数同姓名
              bbb[j] = value;
              vm.excelList = bbb;
            }
            if (j == "身份证号") {
              delete bbb[j];
              j = "idCardNo";
              bbb[j] = value;
            }
            if (j == "营业部") {
              delete bbb[j];
              j = "businessDept";
              bbb[j] = value;
            }
            if (j == "投顾类型") {
              delete bbb[j];
              j = "adviserType";
              bbb[j] = value;
            }
            if (j == "邮箱地址") {
              delete bbb[j];
              j = "email";
              bbb[j] = value;
            }
            if (j == "用户昵称") {
              delete bbb[j];
              j = "aliasName";
              bbb[j] = value;
            }
            if (j == "手机号") {
              delete bbb[j];
              j = "mobile";
              bbb[j] = value;
            }
            if (j == "租户ID") {
              delete bbb[j];
              j = "tenantId";
              bbb[j] = value;
            }
            if (j == "机构编号") {
              delete bbb[j];
              j = "branchNo";
              bbb[j] = value;
            }
            if (j == "开户人") {
              delete bbb[j];
              j = "accountHolder";
              bbb[j] = value;
            }
            if (j == "背景介绍") {
              delete bbb[j];
              j = "backgroundIntro";
              bbb[j] = value;
            }
            if (j == "银行账号") {
              delete bbb[j];
              j = "bankAccount";
              bbb[j] = value;
            }
            if (j == "城市") {
              delete bbb[j];
              j = "cityName";
            }
            if (j == "公司名称") {
              delete bbb[j];
              j = "companyName";
              bbb[j] = value;
            }
            if (j == "资格证书号") {
              delete bbb[j];
              j = "credentialsNo";
              bbb[j] = value;
            }
            if (j == "资格证书类型") {
              delete bbb[j];
              j = "credentialsType";
              bbb[j] = value;
            }
            if (j == "员工编号") {
              delete bbb[j];
              j = "employeeNo";
              bbb[j] = value;
            }
            if (j == "投资策略") {
              delete bbb[j];
              j = "investmentStrategy";
              bbb[j] = value;
            }
            if (j == "投资策略简述") {
              delete bbb[j];
              j = "investmentStrategySummary";
              bbb[j] = value;
            }
            if (j == "投资风格") {
              delete bbb[j];
              j = "investmentStyle";
              bbb[j] = value;
            }
            if (j == "开户行") {
              delete bbb[j];
              j = "openingBank";
              bbb[j] = value;
            }
            if (j == "所属机构") {
              delete bbb[j];
              j = "organization";
              bbb[j] = value;
            }
            if (j == "省份") {
              delete bbb[j];
              j = "provinceName";
              bbb[j] = value;
            }
            if (j == "qq号") {
              delete bbb[j];
              j = "qqNo";
              bbb[j] = value;
            }
            if (j == "推荐码") {
              delete bbb[j];
              j = "referralCode";
              bbb[j] = value;
            }
            if (j == "微博号") {
              delete bbb[j];
              j = "weiboNo";
              bbb[j] = value;
            }
            if (j == "微信公众号") {
              delete bbb[j];
              j = "weixinNo";
              bbb[j] = value;
            }
            if (j == "从业年限") {
              delete bbb[j];
              j = "workYear";
              bbb[j] = value;
            }
            if (j == "登录密码") {
              delete bbb[j];
              j = "authPassword";
              bbb[j] = func.MD5(value.trim()).toUpperCase();
            }
            if (j == "微信账户主体") {
              delete bbb[j];
              j = "weixinOwner";
              if (value == '个体') {
                bbb[j] = 0;
              } else {
                bbb[j] = 1;
              }
            }
            if (j == "性别") {
              delete bbb[j];
              j = "sex";
              if (value == '男') { //客户性别 0:男 1:女
                bbb[j] = 0;
              } else {
                bbb[j] = 1;
              }
            }
            if (j == "applicantName" || j == "idCardNo" || j == "businessDept" || j == "investmentStyle" || j == "email" ||
              j == "aliasName" || j == "mobile" || j == "tenantId" || j == "branchNo" || j == "accountHolder" || j == "backgroundIntro" ||
              j == "bankAccount" || j == "cityName" || j == "companyName" || j == "credentialsNo" || j == "credentialsType" ||
              j == "employeeNo" || j == "investmentStrategy" || j == "investmentStrategySummary" || j == "investmentStyle" ||
              j == "openingBank" || j == "organization" || j == "provinceName" || j == "qqNo" || j == "referralCode" || j == "weiboNo" ||
              j == "weixinNo" || j == "workYear" || j == "authPassword" || j == "weixinOwner" || j == "sex" || j == "adviserType" ||
              j == "registerFlag") {
            } else {
              func.openToaster('表格格式出错，请重新下载模板！');
              return;
            }
          }
        }
      } else {
        func.openToaster('表格格式出错，请重新下载模板！');
        // 重置输入框，便于下次上传
        event.target.value = '';
        return;
      }
      // 重置输入框，便于下次上传
      event.target.value = '';
      $scope.$apply();
    };
    if (rABS) {
      reader.readAsArrayBuffer(file);
    } else {
      reader.readAsBinaryString(file);
    }

  }

  //文件流转BinaryString
  function fixdata(data) {
    var o = "",
      l = 0,
      w = 10240;
    for (; l < data.byteLength / w; ++l) o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)));
    o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
    return o;
  }
}

module.exports = batchAddNewInvestCtrl;



