 // 投顾业绩数据查询

require('locale_zh');

var func = require('func');

productAuthorityManageCtrl.$inject = ['modalService', 'productAuthorityManageService'];

function productAuthorityManageCtrl(modalService, productAuthorityManageService) {
  var vm = this;

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));

  vm.selectTab = 3; // 默认选中的tab 3：客户存续期产品、4：客户关注的产品
  vm.customName = '';
  vm.customMobile = '';
  vm.authorityStatus = '';
  vm.dictsAuthorityStatus = [
    {label: '全部', value: ''},
    {label: '正常', value: 1},
    {label: '过期', value: 0},
  ];
  vm.productId = '';
  vm.productName = '';
  vm.productType = '';
  vm.dictsProductType = [
    {label: '全部', value: ''},
    {label: '投顾个人', value: 0},
    {label: '提问', value: 1},
    {label: '观点', value: 2},
    {label: '围观', value: 3},
    {label: '组合', value: 4},
    {label: '观点包', value: 8},
    {label: '量化模型', value: 98},
    {label: '智能策略', value: 99}
  ];
  /* 日期选择 */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };
  vm.startTime = '';
  vm.endTime = '';
  vm.keyword = '';
  vm.dataList = [];
  
  // 排序项
  vm.orderByName = ''; // 1：用户名称，2：手机号，3：产品，4：权限状态、关注状态
  vm.sortOrder = 1; // 1：升序，2：降序
  vm.seqUser = false; // 用户名称当前排序状态
  vm.seqMobile = false; // 手机号当前排序状态
  vm.seqProduct = false; // 产品当前排序状态
  vm.seqAuthState = false; // 权限状态当前排序状态
  vm.seqUpState = false; // 关注状态当前排序状态
  vm.seqProductType = false; // 产品类型当前排序状态
  vm.seqStartTime = false; // 开始时间当前排序状态
  vm.seqEndTime = false; // 结束时间当前排序状态

  vm.openStartTimeFunc = openStartTimeFunc; //开始日期选择
  vm.openEndTimeFunc = openEndTimeFunc; //结束日期选择
  vm.selectTabFunc = selectTabFunc;
  vm.sequenceFunc = sequenceFunc;
  vm.searchFunc = searchFunc;
  vm.pageChangeFunc = pageChangeFunc;

  getCustomerContributionQryFunc('init');
  
  // tab选择
  function selectTabFunc(value) {
    vm.currentPage = 1;
    vm.selectTab = value;
    vm.dataList = [];
    getCustomerContributionQryFunc('init');
  }

  function getCustomerContributionQryFunc(type) {
    if (type === 'init') {
      vm.customName = '';
      vm.customMobile = '';
      vm.authorityStatus = '';
      vm.productId = '';
      vm.productName = '';
      vm.productType = '';
      vm.startTime = '';
      vm.endTime = '';
      vm.keyword = '';
      vm.dataList = [];
      vm.seqUser = false;
      vm.seqMobile = false;
      vm.seqProduct = false;
      vm.seqAuthState = false;
      vm.seqUpState = false;
      vm.seqProductType = false; // 产品类型当前排序状态
      vm.seqStartTime = false; // 开始时间当前排序状态
      vm.seqEndTime = false; // 结束时间当前排序状态
      vm.orderByName = '';
      vm.sortOrder = '';
    }
    productAuthorityManageService.productListQryFunc({
      tenant_id: vm.tenantId,
      interface_flag: vm.selectTab, // 3：存续期产品，4：关注产品
      customer_name: vm.customName,
      customer_mobile: vm.customMobile,
      order_type: vm.productType,
      relation_id: vm.productId,
      relation_name: vm.productName,
      auth_flag: vm.authorityStatus,
      // keyword: vm.keyword,
      sort_type: String(vm.orderByName), // 1：客户名称，2：客户手机号，3：产品名称，4：权限状态、关注状态，6：产品类型，7：开始日期，8：结束日期
      sort_mode: String(vm.sortOrder), // 1：升序，2：降序
      begin_time: vm.startTime && func.formatDate(vm.startTime, 'yyyyMMdd') || '',
      end_time: vm.endTime && func.formatDate(vm.endTime, 'yyyyMMdd') || '',
      page_no: String(vm.currentPage),
      page_count: String(vm.itemPerPage)
    }, function(res) {
      console.log('res', res);
      vm.dataList = res.data_list || [];
      vm.totalItems = res.total_item_num || 0;
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', err.error_info);
      vm.dataList = [];
      vm.totalItems = 0;
    })
  }

  function sequenceFunc(type) {
    switch (type) {
      case 0:
        vm.seqUser = !vm.seqUser;
        vm.sortOrder = vm.seqUser ? 1 : 2;
        vm.orderByName = 1;
        break;
      case 1:
        vm.seqMobile = !vm.seqMobile;
        vm.sortOrder = vm.seqMobile ? 1 : 2;
        vm.orderByName = 2;
        break;
      case 2:
        vm.seqProduct = !vm.seqProduct;
        vm.sortOrder = vm.seqProduct ? 1 : 2;
        vm.orderByName = 3;
        break;
      case 3:
        vm.seqAuthState = !vm.seqAuthState;
        vm.sortOrder = vm.seqAuthState ? 1 : 2;
        vm.orderByName = 4;
        break;
      case 4:
        vm.seqUpState = !vm.seqUpState;
        vm.sortOrder = vm.seqUpState ? 1 : 2;
        vm.orderByName = 4;
        break;
      case 6:
        vm.seqProductType = !vm.seqProductType;
        vm.sortOrder = vm.seqProductType ? 1 : 2;
        vm.orderByName = 6;
        break;
      case 7:
        vm.seqStartTime = !vm.seqStartTime;
        vm.sortOrder = vm.seqStartTime ? 1 : 2;
        vm.orderByName = 7;
        break;
      case 8:
        vm.seqEndTime = !vm.seqEndTime;
        vm.sortOrder = vm.seqEndTime ? 1 : 2;
        vm.orderByName = 8;
        break;
    }
    vm.currentPage = 1;
    getCustomerContributionQryFunc();
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  };

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function searchFunc() {
    vm.currentPage = 1;
    getCustomerContributionQryFunc();
  }

  function pageChangeFunc() {
    getCustomerContributionQryFunc();
  }
}

module.exports = productAuthorityManageCtrl; 


