productAuthorityManageRoute.$inject = ['$stateProvider'];

function productAuthorityManageRoute($stateProvider) {
  $stateProvider.state('home.productAuthorityManage', {
    url: '/productAuthorityManage',
    templateUrl: './views/productAuthorityManage.html',
    controller: 'productAuthorityManageCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = productAuthorityManageRoute;



