addBgModalService.$inject = ['api','img'];
function addBgModalService(api,img) {
  var vm = this;

  vm.getBgListFunc = getBgListFunc; // 获取当前所有背景图
  vm.addPicFunc = addPicFunc; // 新增图片
  vm.upload = upload; // 上传图片

  function getBgListFunc(data, cb1, cb2) {
    api.postRequest ({
      prefix_type: 'vipstu',
      url: 'func_live_bg_list',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  function addPicFunc(data, cb1, cb2) {
    api.postRequest ({
      prefix_type: 'vipstu',
      url: 'func_live_bg_update',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  function upload(data, cb1, cb2) {
    img.postImgRequest ({
      prefix_type: 'fileOpenapi',
      url: 'func_file_upload?sysno=1',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

}

module.exports = addBgModalService;



