/* 操作日志查询 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var logDetailModalCtrl = require('./modalPopup/logDetail/controller');
var logDetailModalService = require('./modalPopup/logDetail/service');

//setter定义模块
module.exports = angular
  .module('logInquiry', [])
  .config(Routing)
  .service('logInquiryService', service)
  .controller('logInquiryCtrl', ctrl)
  .factory('logDetailModalCtrl', logDetailModalCtrl)
  .service('logDetailModalService', logDetailModalService).name;



