/* 日志查询 module */

logInquiryRoute.$inject = ['$stateProvider'];

function logInquiryRoute($stateProvider) {
    $stateProvider
        .state('home.logInquiry', {
            url: '/logInquiry',
            templateUrl: './views/logInquiry.html',
            controller: 'logInquiryCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = logInquiryRoute;



