/* 角色管理 module */

rolesManageRoute.$inject = ['$stateProvider'];

function rolesManageRoute($stateProvider) {
    $stateProvider
        .state('home.rolesManage', {
            url: '/rolesManage',
            templateUrl: './views/rolesManage.html',
            controller: 'rolesManageCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = rolesManageRoute;



