viewpointPackageCheckModalService.$inject = ['api'];
function viewpointPackageCheckModalService(api) {
  var vm = this;
  
  vm.viewpointPkgAudit = viewpointPkgAudit; // 观点包审核

  function viewpointPkgAudit(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage_viewpoint_pkg_audit',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

}

module.exports = viewpointPackageCheckModalService;


