 // 风险等级设置

require('locale_zh');
var func = require('func');

riskLevelSettingCtrl.$inject = ['modalService', 'riskLevelSettingService', 'addRiskLevelModalCtrl'];

function riskLevelSettingCtrl(modalService, riskLevelSettingService, addRiskLevelModalCtrl) {
  var vm = this;

  vm.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.list = [];

  vm.delRisk = delRisk;
  vm.addRisk = addRisk;
  vm.pageChangeFunc = pageChangeFunc;

  getListFunc();
  
  function getListFunc() {
    riskLevelSettingService.riskLevelQryFunc({
      tenant_id: vm.tenantId,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage
    }, function(res) {
      console.log('res', res);
      vm.list = res.data_list || [];
      vm.totalItems = res.total_num || 0;
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', '请求异常');
    })
  }

  function delRisk(data) {
    riskLevelSettingService.riskLevelDelFunc({
      tenant_id: vm.tenantId,
      risk_level_ids: data.risk_level_id,
      operator_no: vm.operatorInfo.operatorNo,
      operator_name: vm.operatorInfo.name
    }, function(res) {
      console.log('res', res);
      modalService.openAlert('提示', res.error_info);
      getListFunc();
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', '请求异常');
    })
  }

  function addRisk() {
    addRiskLevelModalCtrl.openAddRiskLevelModalFunc(function() {
      getListFunc();
    });
  }

  function pageChangeFunc() {
    getListFunc();
  }
}

module.exports = riskLevelSettingCtrl; 


