updatePwdModalService.$inject = ['api'];
function updatePwdModalService(api) {
  var vm = this;

  vm.userSetPwd = userSetPwd;
  
  //获取当前展示投顾信息
  function userSetPwd(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'userCenter',
      url: 'func_user_set_password',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  return vm;
}

module.exports = updatePwdModalService;



