resetPasswordModalService.$inject = ['api'];

function resetPasswordModalService(api) {
  var vm = this;

  vm.resetPassword = resetPassword;

  function resetPassword(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorPwdMod',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }
}

module.exports = resetPasswordModalService;



