/* 金融股票池管理 module */

stockPoolManageRoute.$inject = ['$stateProvider'];

function stockPoolManageRoute($stateProvider) {
  $stateProvider.state('home.stockPoolManage', {
    url: '/stockPoolManage',
    templateUrl: './views/stockPoolManage.html',
    controller: 'stockPoolManageCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = stockPoolManageRoute;



