require('./style.less');

var rightViewTempl = require('./rightView.html');
// 图片文件
var jiansyImg = require('img/jiansy.png');
var jiansyxImg = require('img/jiansyx.png');
var jianyImg = require('img/jiany.png');
var jianyxImg = require('img/jianyx.png');
var jiasyImg = require('img/jiasy.png');
var jiasyxImg = require('img/jiasyx.png');
var jiayImg = require('img/jiay.png');
var jiayxImg = require('img/jiayx.png');
var syImg = require('img/sy.png');
var syxImg = require('img/syx.png');

rightTree.$inject = [];

function rightTree() {
  return {
    restrict: 'E',
    scope: {
      id: '@', //当前节点id
      pid: '@', //父级id
      showLevel: '@', //显示等级,如：3，那么3节点之前都会被显示，在已拥有结果集的刷新的时候
      textField: '@', //控制机构和菜单中显示节点id、name
      ownResultType: '@', //已拥有的结果集是数组还是树
      initShow: '@', //权限树在初始化的时候是打钩，还是打岔
      onlyShowCaption: '=', //是否只显示部门名称
      branchSearch: '=', //通过name、id搜索组织信息
      isRightTree: '=', //是否是权限树，控制rightView中权限和菜单不同icon的显示
      ownResultFlag: '=', //是否有已经拥有权限的结果集
      treeData: '=', //权限和菜单的数据源
      ownResult: '=', //已经拥有的权限的结果集
      addResult: '=', //添加的菜单权限结果集
      delResult: '=', //删除的菜单权限结果集
      itemClicked: '&', //点击权限节点后执行的回调方法
      changeRight: '&', //改变菜单权限后的回调方法
      children: '@', //参数中子节点的名称
      isChange: '=' //是否改变
    },
    template: rightViewTempl,
    link: function($scope) {
      var id = $scope.id;
      var copyPid = '$pid'; //父级id，默认的pid名称为“$pid”
      var pid = $scope.pid ? $scope.pid : copyPid; //如果没有pid，给一个默认的pid名称为“$pid”

      var textField = $scope.textField;
      var fidldArry = textField.split(',');
      var showLevel = parseInt($scope.showLevel);
      var ownResultType = $scope.ownResultType;

      var children = $scope.children || 'children';
      var ownResult = $scope.ownResult;
      var initShow = $scope.initShow; //初始化展示，true是打钩,false是打岔
      var onlyShowCaption = $scope.onlyShowCaption; //是否只显示部门名称

      var ownResultCopy = []; //更新菜单权限后的结果集

      var nodeMap = {}; //接收源数据中的每一个对象，通过其中的ID获取节点数据
      var nodeList = []; //接收源数据中所有对象
      var rootList = []; //接收传入的根数据（机构和菜单）

      //当treeData和ownResult先后改变时通过这两个标识来更新权限显示状态
      var treeOverFlag = false; //当treeData数据改变时置为true，判断是否需要处理ownResult数据的标识
      var ownFlag = false; //判断是否需要处理ownResult数据的标识，

      var itemTreeList = []; //模块父子结构数组

      $scope.showField = showField;
      $scope.getItemIcon = getItemIcon;
      $scope.getRightIcon = getRightIcon;
      $scope.isLeaf = isLeaf;
      $scope.changeItemIcon = changeItemIcon;
      $scope.getItemCallback = getItemCallback;
      $scope.warpCallback = warpCallback;
      $scope.changeDisplayRight = changeDisplayRight;

      //监听传入的机构和菜单结果集
      $scope.$watch('treeData', function(newValue) {
        if (angular.isUndefined(newValue) || newValue.length <= 0) {
          return;
        }
        var data = $scope.treeData;
        nodeMap = {};
        nodeList = [];
        rootList = [];
        if (angular.isArray(data)) {
          //将数据源传入根数据数组，再整理其中的每一条
          for (var i = 0; i < data.length; i++) {
            rootList.push(data[i]);
            scanTree(data[i], 1);
          }
        } else {
          rootList.push(data);
          scanTree(data, 1);
        }
        treeOverFlag = true; //当treeData更新时将此标识置为true，在监听ownResult时通过此标识状态处理ownResult
        if (ownFlag) {
          //当ownResult再次改变时便执行ownResultDeal
          ownResultDeal($scope.ownResult);
        }
      });

      //监听addResult结果集
      $scope.$watch('addResult', function(newValue, oldValue) {
        if (angular.isUndefined(newValue)) {
          return;
        }
        //处理添加菜单权限后的结果集
        ownResultCopy = ownResultCopy.concat(oldValue);
      });

      //监听delResult结果集
      $scope.$watch('delResult', function(newValue, oldValue) {
        if (angular.isUndefined(newValue)) {
          return;
        }
        //处理删除菜单权限后的结果集
        var len = oldValue.length;
        for (var i = 0; i < len; i++) {
          var index = ownResultCopy.indexOf(oldValue[i]);
          if (index >= 0) {
            ownResultCopy.splice(index, 1);
          }
        }
      });

      //监听ownResult结果集
      $scope.$watch(
        'ownResult',
        function(newValue) {
          if (angular.isUndefined(newValue) || newValue == 'change') {
            return;
          }
          if (treeOverFlag) {
            //当treeData改变后，ownResult再改变时处理ownResult
            ownResultDeal($scope.ownResult);
          } else {
            ownFlag = true; //当ownResult改变时此标识状态为true，以便当treeData再改变时对ownResult数据进行处理
          }
        },
        true
      ); //深度监听，监听对象中每一个属性值或数组中个别元素的变化

      //监听onlyShowCaption，是否只显示部门名称
      $scope.$watch('onlyShowCaption', function(newValue) {
        onlyShowCaption = newValue;
      });

      //监听branchSearch，通过name、id匹配对应的组织信息
      $scope.$watch('branchSearch', function(newValue) {
        if (newValue != undefined) {
          itemTreeList = []; //清空父子结构数组
          findParentByNameOrId(newValue);
        }
      });

      //根据搜索的营业部信息获取其所有父级
      function findParentByNameOrId(newValue) {
        angular.forEach(nodeList, function(item) {
          if (item.menuId == newValue || item.menuCaption == newValue || item.branchNo == newValue) {
            //将目标营业部信息存入
            if (itemTreeList.indexOf(newValue) == -1) {
              itemTreeList.push(newValue);
            }
            if (item.$pid) {
              //将父级营业部信息存入
              itemTreeList.push(item.$pid);
              findParentByNameOrId(item.$pid);
            } else {
              findSearchItemByList(itemTreeList.sort(sortNumber));
            }
          }
        });
      }

      //通过要搜索营业部信息的父子列表定位到目标营业部
      function findSearchItemByList(itemTreeList) {
        for (var i = 0; i < itemTreeList.length; i++) {
          angular.forEach(nodeList, function(item) {
            if (itemTreeList[i] == item.menuId || itemTreeList[i] == item.branchNo) {
              getItemCallback('itemClicked', item);
            }
          });
        }
      }

      //整理机构和菜单结果集
      function scanTree(node, lvl) {
        nodeMap[node[id]] = node;
        nodeList.push(node);
        node['$level'] = lvl;
        if (showLevel && lvl <= showLevel) {
          //控制节点显示状态
          node.$isExpend = true; //节点展开（文件夹icon展开）标志，
        } else {
          node.$isExpend = false; //节点关闭（文件夹icon关闭）标志
        }
        var children = node['children'];
        if (children && children.length) {
          //如果包含子节点则递归整理子节点内容
          for (var i = 0; i < children.length; i++) {
            children[i][pid] = node[id]; //处理每个子节点，将每个子节点的pid = 父节点的id
            scanTree(children[i], lvl + 1); //子节点初始为关闭状态
          }
        }
      }

      //整理有权限的菜单结果集，数据来源是ownResult
      function scanSelectTree(node) {
        var children = node['children'];
        if (children && children.length) {
          //如果包含子节点则递归整理子节点内容
          for (var i = 0; i < children.length; i++) {
            children[i][pid] = node[id];
            scanTree(children[i], lvl + 1); //子节点初始为关闭状态
          }
        }
      }

      //整理当前机构拥有的菜单权限
      function ownResultDeal(newValue) {
        loopTree(function(node) {
          //初始化所有的菜单节点为无权限状态，并关闭
          node.$isIcon = 2;
          node.$preIcon = undefined;
        });
        if (ownResultType === 'array') {
          //根据ownResultType绑定的值判断入参数据类型
          ownResultCopy = angular.copy(newValue);
          changeArryDisplayRight(newValue);
        } else {
          ownResultCopy = []; //初始化修改权限后的新结果集
          scanSelectTree(newValue);
          changeDisplayRight(newValue);
        }
      }

      //当已拥有结果集是数组
      function changeArryDisplayRight(ownResult) {
        // var result = $scope.treeData;
        angular.forEach(ownResult, function(targetId) {
          var node = nodeMap[targetId];
          if (!node) {
            //console.error("找不到该节点:" + targetId);
            return;
          }
          if (node.$pid == null || node.$pid == undefined) {
          }
          firstGruntRgihtSelf(node, 1); //改变当前节点
          firstGruntRightParent(node, function(parent) {
            //改变父节点
            var index = ownResultCopy.indexOf(parent);
            if (index < 0) {
              ownResultCopy.push(parent[id]);
            }
          });
          firstGruntRightChildren(node, function(child) {
            //改变子节点
            var index = ownResultCopy.indexOf(child);
            if (index < 0) {
              ownResultCopy.push(child[id]);
            }
          });
        });
      }

      //设置机构或菜单结构分支图片
      function getItemIcon(item, first, middle, last) {
        var isLeaf = $scope.isLeaf(item);
        var iconStr = '';
        if (!isLeaf) {
          //无子节点
          if (item.$level === 1) {
            //如果是一级节点
            iconStr = syImg;
          } else {
            //如果是非一级节点
            if (first && last) {
              //只有一个节点的时候
              iconStr = syImg;
            } else if (last) {
              //末节点
              iconStr = syImg;
            } else if (first) {
              //首节点
              iconStr = syxImg;
            } else {
              //中间节点
              iconStr = syxImg;
            }
          }
        } else {
          //有子节点
          if (item.$level === 1) {
            //如果是一级节点
            if (item.$isExpend) {
              //有子节点,且打开
              if (first && last) {
                //只有一个节点的时候
                iconStr = jianyImg;
              } else if (last) {
                //末节点
                iconStr = jiansyImg;
              } else if (first) {
                //首节点
                iconStr = jianyxImg;
              } else {
                //中间节点
                iconStr = jiansyxImg;
              }
            } else {
              //有子节点，且关闭
              if (first && last) {
                //只有一个节点的时候
                iconStr = jiayImg;
              } else if (last) {
                //末节点
                iconStr = jiasyImg;
              } else if (first) {
                //首节点
                iconStr = jiayxImg;
              } else {
                //中间节点
                iconStr = jiasyxImg;
              }
            }
          } else {
            //如果是非一级节点
            if (item.$isExpend) {
              //有子节点,且打开
              if (first && last) {
                //只有一个节点的时候
                iconStr = jiansyImg;
              } else if (last) {
                //末节点
                iconStr = jiansyImg;
              } else if (first) {
                //首节点
                iconStr = jianyxImg;
              } else {
                //中间节点
                iconStr = jiansyxImg;
              }
            } else {
              //有子节点，且关闭
              if (first && last) {
                //只有一个节点的时候
                iconStr = jiasyImg;
              } else if (last) {
                //末节点
                iconStr = jiasyImg;
              } else if (first) {
                //首节点
                iconStr = jiayxImg;
              } else {
                //中间节点
                iconStr = jiasyxImg;
              }
            }
          }
        }
        return iconStr;
      }

      //点击图标改变菜单权限
      function changeItemIcon(callback, item) {
        changeSelfIcon(item); //改变本节点的权限图标
        changeParentIcon(item); //改变父级节点图标
        changeChildrenIcon(item); //改变子集节点图标
        var root = angular.copy($scope.treeData[0]); //获取最顶级节点信息
        root[children] = null;
        ($scope[callback] || angular.noop)({
          //如果callback不是函数也不会报错
          addResult: $scope.addResult,
          delResult: $scope.delResult,
          item: item,
          root: root
        });
      }

      //改变本节点的权限图标
      function changeSelfIcon(item) {
        switch (item.$isIcon) {
          case undefined:
            item.$isIcon = 2; //打叉
            break;
          case 1:
            item.$isIcon = 2; //打叉
            break;
          case 2:
            item.$isIcon = 1; //打钩
            break;
          case 3:
            item.$isIcon = 1; //打钩
            break;
        }
        updateResult(item);
      }

      //改变父节点的权限图标
      function changeParentIcon(node) {
        firstGruntRightParent(node, function(parentNode) {
          updateResult(parentNode);
        });
      }

      //改变子集节点的图标
      function changeChildrenIcon(item) {
        firstGruntRightChildren(item, function(child) {
          updateResult(child);
        });
      }

      //更新结果集，addResult，delResult
      function updateResult(item) {
        var index = ownResultCopy.indexOf(item[id]);
        var delIndex = $scope.delResult.indexOf(item[id]);
        var addIndex = $scope.addResult.indexOf(item[id]);
        if (index >= 0) {
          //如果在已拥有结果集中存在
          if (item.$isIcon === 1 || item.$isIcon === 3) {
            //如果当前状态为已选择或部分选择
            if (delIndex >= 0) {
              $scope.delResult.splice(delIndex, 1); //如果当前数据在删除数据中，则在删除数据中删除本条
            }
          } else if (item.$isIcon === 2) {
            if (delIndex < 0) {
              $scope.delResult.push(item[id]); //如果当前数据不在删除数据中，则在删除数据中加入本条
            }
          } else {
            return;
          }
        } else {
          //如果不存在
          if (item.$isIcon === 1) {
            //如果当前为新增icon，且当前数据不在新增数据中，则在新增数据中添加本条
            if (addIndex < 0) {
              $scope.addResult.push(item[id]);
            }
          } else if (item.$isIcon === 2) {
            //如果当前为删除icon，且当前数据在删除数据中，则在新增数据中删除本条
            if (addIndex >= 0) {
              $scope.addResult.splice(addIndex, 1);
            }
          } else {
            return;
          }
        }
      }

      //点击获取节点信息
      function getItemCallback(callback, item) {
        if (!$scope[callback]) {
          return;
        }
        if (angular.isDefined($scope.isChange)) {
          //通过isChange值判定是否是打开文件夹的指令样式
          $scope.isChange = item[id];
          if (!(angular.element(document.querySelector('#wenjianjia')) && item.children == null)) {
            item.$isExpend = true;
          }
        }
        ($scope[callback] || angular.noop)({
          //仅当为打开文件夹样式指令时才会执行itemClicked方法
          item: item
        });
      }

      //展示显示子节点内容
      function warpCallback(item, $event) {
        if (!(item.children == null)) {
          itemExpended(item, $event);
        }
        return;
      }

      //节点展开与关闭
      function itemExpended(item, $event) {
        item.$isExpend = !item.$isExpend;
        $event.stopPropagation(); //阻止事件的派发
      }

      //改变权限图标（当已经拥有的结果集是树）
      function changeDisplayRight(ownResult) {
        loopTree(function(node) {
          //重置所有的节点岔并关闭
          node.$isIcon = 2;
          node.$isExpend = false;
          node.$preIcon = undefined;
        });
        firstGruntRight(ownResult, $scope.treeData); //显示结果集权限
      }

      //已拥有结果集赋值权限，首次赋权，将ownResult的内所有对象的id存入ownResultCopy，ownResultCopy不是树结构
      function firstGruntRight(targetResult, result) {
        if (angular.isArray(targetResult)) {
          var len = targetResult.length;
          for (var i = 0; i < len; i++) {
            var index = ownResultCopy.indexOf(targetResult[i]); //判断修改权限后的结果集是否在当前权限集内，没有则添加
            if (index < 0) {
              ownResultCopy.push(targetResult[i][id]);
            }
            if (isLeaf(targetResult[i])) {
              //判断有无子节点
              firstGruntRight(targetResult[i][children], result);
            } else {
              firstGruntRight(targetResult[i], result);
            }
          }
        } else {
          var index = ownResultCopy.indexOf(targetResult);
          if (index < 0) {
            ownResultCopy.push(targetResult[id]);
          }
          var node = nodeMap[targetResult[id]];
          if (!node) {
            return;
          }
          firstGruntRgihtSelf(node, 1); //改变当前节点权限图标，ownResult中当前值是有权限的，所以第二个参数为1
          firstGruntRightParent(node); //改变父节点权限图标
          firstGruntRightChildren(node, function(child) {
            //改变子节点权限图标，
            var index = ownResultCopy.indexOf(child);
            if (index < 0) {
              ownResultCopy.push(child[id]);
            }
          });
        }
      }

      //改变当前节点的权限图标
      function firstGruntRgihtSelf(node, icon) {
        node.$isIcon = icon; //当前节点
        node.$preIcon = icon; //上一次节点图标
        if (showLevel) {
          //如果有显示多少个节点，就按显示多少个节点来，没有就只要赋值权限了就打开
          if (node['$level'] <= showLevel) {
            node.$isExpend = true;
          }
        } else {
          node.$isExpend = true;
        }
      }

      //改变父节点的权限图标
      function firstGruntRightParent(node, callback) {
        var parentId = node[pid];
        if (angular.isDefined(parentId) && parentId !== '' && parentId !== null) {
          var parentNode = nodeMap[parentId];
          var len = parentNode[children].length;
          var gouNum = 0;
          var chaNum = 0;
          var wenNum = 0;
          for (var i = 0; i < len; i++) {
            if (angular.isUndefined(parentNode[children][i].$isIcon) || parentNode[children][i].$isIcon === 1) {
              gouNum++;
            } else if (parentNode[children][i].$isIcon === 2) {
              chaNum++;
            } else {
              wenNum++;
            }
          }
          if (gouNum === len) {
            firstGruntRgihtSelf(parentNode, 1); //当全为打开状态时将当前节点（父节点置为打开）
          } else if (chaNum === len) {
            firstGruntRgihtSelf(parentNode, 2); //当全为关闭状态时将当前节点（父节点置为关闭）
          } else {
            firstGruntRgihtSelf(parentNode, 3); //否则置为问号状态（父节点置为问号）
          }
          if (callback) {
            callback(parentNode);
          }
          firstGruntRightParent(parentNode, callback);
        }
      }

      //改变子节点的权限图标
      function firstGruntRightChildren(node, callback) {
        if (isLeaf(node)) {
          //先判断有无子节点
          var children = node[$scope.children];
          var len = children.length;
          for (var i = 0; i < len; i++) {
            firstGruntRgihtSelf(children[i], node.$isIcon); //通过上一个方法（firstGruntRightSelf）中的第二个参数（打开或关闭）决定
            if (callback) {
              callback(children[i]);
            }
            firstGruntRightChildren(children[i], callback);
          }
        }
      }

      //设置权限icon
      function getRightIcon(item) {
        switch (item.$isIcon) {
          case 1:
            return 'icon-correct'; //打钩
          case 2:
            return 'icon-error'; //打叉
          case 3:
            return 'icon-query'; //打问号
          case undefined:
            return initShow === undefined || initShow === 'true' ? 'icon-correct' : 'icon-error'; //如果initShow为true，那么就打钩，否则打叉
        }
      }

      //统一获取节点id和名称，机构为branchNo、branchName，菜单为menuId、menuCaption
      function showField(item) {
        var fieldStr1 = '';
        //是否只显示部门名称
        if (onlyShowCaption) {
          for (var i = 0; i < fidldArry.length; i++) {
            fieldStr1 = fieldStr1 + ' ' + item[fidldArry[i]];
          }
        } else {
          fieldStr1 = item[fidldArry[1]];
        }
        item['fieldStr'] = fieldStr1;
        return item['fieldStr'];
      }

      //判断有无子节点
      function isLeaf(item) {
        return angular.isDefined(item[children]) && item[children] !== null && item[children].length > 0;
      }

      //循环树,并回调
      function loopTree(callback) {
        for (var i = 0; i < nodeList.length; i++) {
          //根据treeData值内容循环执行回调方法
          callback(nodeList[i]);
        }
      }

      //排序函数
      function sortNumber(a, b) {
        return a - b;
      }
    }
  };
}

module.exports = rightTree;



