customerContributionQryDownloadService.$inject = ['api'];
function customerContributionQryDownloadService(api) {
  var vm = this;
  vm.customerContributionQryFunc = customerContributionQryFunc;

  function customerContributionQryFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'customer_contribution_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = customerContributionQryDownloadService;



