orderInfoManageRoute.$inject = ['$stateProvider'];

function orderInfoManageRoute($stateProvider) {
  $stateProvider.state('home.orderInfoQry', {
    url: '/orderInfoQry',
    templateUrl: './views/orderInfoQry.html',
    controller: 'orderInfoQryCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = orderInfoManageRoute;



