/* 直播监控 module */

require('locale_zh');
var func = require('func');

liveMonitorContent.$inject = ['$sce', '$state', '$stateParams', 'modalService', 'liveMonitorContentService'];

function liveMonitorContent($sce, $state, $stateParams, modalService, liveMonitorContentService) {
  var vm = this;

  //获取branchNo和tenantId
  vm.accesstoken = sessionStorage.getItem('access_token');
  vm.branchNo = JSON.parse(sessionStorage.getItem('branchNoData'));
  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.keyword = ''; //关键字
  vm.topicId = $stateParams.topicId; // 直播话题id
  vm.topicTitle = $stateParams.topic; // 直播话题
  vm.userName = $stateParams.user; // 直播人
  vm.pageNo = 1;
  vm.positionStr = 0;
  vm.moreData = true; // 是否显示“获取更多”;
  vm.moreLiveData = true; // 是否显示“获取更多”;
  vm.currentLiveNumber = 0; // 当前直播人数
  vm.currentSession = 0;

  vm.commentList = []; // 评论列表
  vm.liveList = []; //  直播列表

  vm.deleteComment = deleteComment;
  vm.keywordSearch = keywordSearch;
  vm.nextPageFunc = nextPageFunc;
  vm.setCurrentSession = setCurrentSession;

  
  qryLiveRoomList();

  // 获取直播数据
  function qryLiveRoomList() {
    liveMonitorContentService.getLiveList(
      {
        request_num: 10,
        position_str: vm.positionStr,
        keyword: vm.keyword,
        topic_id: vm.topicId,
        broker_id: -111,
        tenant_id: vm.tenantId,
      },
      function(result) {
        var dataList = result.data_list;
        vm.moreLiveData = dataList.length == 10 ? true : false;
        var data = {};
        dataList.length && angular.forEach(dataList, function(item) {
          // 整理获取到的数据
          data = JSON.parse(item.msg_content);
          item.text = renderLiveData(data).text || '';
          item.img = renderLiveData(data).img || '';
          item.combine = renderLiveData(data).combine || '';
          item.name = renderLiveData(data).name;
          vm.liveList.push(item);
        });
      }
    );
  }

  //获取评论数据
  function qryLiveCommentList() {
    liveMonitorContentService.getLiveCommentList(
      {
        branch_no: vm.branchNo,
        keyword: vm.keyword,
        topic_id: vm.topicId,
        tenant_id: vm.tenantId,
        page_no: vm.pageNo,
        page_count: 10
      },
      function(result) {
        var dataList = result.data_list;
        vm.moreData = dataList.length == 10 ? true : false;
        var data = {};
        dataList.length && angular.forEach(dataList, function(item) {
          //整理获取到的数据
          data = JSON.parse(item.msg_content);
          item.text = renderCommentData(data).text || '';
          item.img = renderCommentData(data).img || '';
          item.combine = renderCommentData(data).combine || '';
          item.name = renderCommentData(data).name;
          item.time = renderCommentData(data).time;
          vm.commentList.push(item);
        });
      }
    );
  }

  //格式化评论数据
  function renderCommentData(data) {
    var commentData = {};
    //获取name数据
    commentData.name = data.fromNick || '匿名';
    //获取time数据
    commentData.time = func.formatDate(data.time, 'yyyy-MM-dd HH:mm:ss');
    //获取body数据
    var dataList = JSON.parse(data.content);
    var contentHtml = '';
    angular.forEach(dataList, function(item) {
      if (item.type == 'text' && item.body.length) {
        contentHtml = func.filterStock(item.body);
        contentHtml = func.showEmoji(contentHtml);
        commentData.text = $sce.trustAsHtml(contentHtml);
      }
      if (item.type == 'img' && item.body.length) {
        commentData.img = item.body[0].link + '/' + vm.accesstoken;
      }
      if (item.type == 'combine' && item.body.length) {
        commentData.combine = item.body[0];
      }
    });
    return commentData;
  }

  //删除评论
  function deleteComment(data) {
    modalService.openConfirm('提示', '是否删除此评论？', function() {
      // 0直播，1互动
      var deleteId = vm.currentSession == 0 ? data.rollingmsg_id : data.chat_msg_id;
      var deleteParams = {
        chat_delete_id_list: [{chat_msg_id: deleteId, tenant_id: vm.tenantId}]
      };
      liveMonitorContentService.deleteLiveComment(
        {
          del_flag: String(vm.currentSession + 1), // 1直播，2互动
          topic_del_ids: JSON.stringify(deleteParams),
          jsonp: 'jsonpcallback'
        },
        function(result) {
          vm.commentList = [];
          func.openToaster('删除成功！');
          keywordSearch();
        }
      );
    })
  }

  //格式化直播数据
  function renderLiveData(data) {
    var liveData = {};
    //获取body数据
    var contentHtml = '';
    angular.forEach(data, function(item) {
      //获取name数据
      if (item.fromNick) {
        liveData.name = item.fromNick || '匿名';
      }
      if (item.type == 'text' && item.body.length) {
        contentHtml = func.filterStock(item.body);
        contentHtml = func.showEmoji(contentHtml);
        liveData.text = $sce.trustAsHtml(contentHtml);
      }
      if (item.type == 'img' && item.body.length) {
        liveData.img = item.body[0].link + '/' + vm.accesstoken;
      }
      if (item.type == 'combine' && item.body.length) {
        liveData.combine = item.body[0];
      }
    });
    return liveData;
  }

  function keywordSearch() {
    if (vm.currentSession == 0) {
      vm.liveList = [];
      vm.moreLiveData = true;
      vm.positionStr = 0;
      qryLiveRoomList();
    } else {
      vm.commentList = [];
      vm.moreData = true;
      vm.pageNo = 1;
      qryLiveCommentList();
    }
  }

  //分页
  function nextPageFunc() {
    if (vm.currentSession == 0) {
      vm.positionStr = vm.liveList[vm.liveList.length - 1].position_str;
      qryLiveRoomList();
    } else {
      vm.pageNo++;
      qryLiveCommentList();
    }
  }

  // 切换
  function setCurrentSession(index) {
    vm.keyword = '';
    vm.currentSession = index;
    if (vm.currentSession === 0) {
      vm.positionStr = 0;
      vm.liveList = []; //  直播列表
      qryLiveRoomList();
    } else {
      vm.pageNo = 1;
      vm.commentList = []; // 评论列表
      qryLiveCommentList();
    }
  }
}

module.exports = liveMonitorContent;



