/* 直播背景图设置 */

require('locale_zh');
require.ensure([], function (require) {
  require('Swiper');
}, 'swiper');

var startImg = require('img/start.png');
var config = require('config');
var func = require('func');

liveBgSettingCtrl.$inject = ['liveBgSettingService', 'addBgModalCtrl', 'modalService'];

function liveBgSettingCtrl(liveBgSettingService, addBgModalCtrl, modalService) {
  var vm = this;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.imgList = [];
  vm.startImg = startImg;

  vm.restartImgFunc = restartImgFunc;
  vm.addBgFunc = addBgFunc;

  getBgPicListFunc();

  function getBgPicListFunc() {
    liveBgSettingService.getBgListFunc({
      tenant_id: vm.tenantId,
    }, function(res) {
      console.log('res', res);
      vm.imgList = [];
      var dataList = res.data_list;
      if (dataList.length) {
        dataList.forEach(function(item) {
          vm.imgList.push({
            liveBgImg: item.live_bg_img,
            url: LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download/' + item.live_bg_img + '/92f17e36e6cf0a8083ff435fc708a372?sysno=1',
            state: item.state
          });
        });
        setSwiper();
      }
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', err.error_info);
    });
  }

  function restartImgFunc(data, type) {
    // 已启用的不可操作
    if (data.state == 1) {
      return;
    }
    var newImgList = [];
    if (type === 1) { // 修改启用图
      vm.imgList.forEach(function(item) {
        if (item.liveBgImg == data.liveBgImg) {
          newImgList.push({
            liveBgImg: item.liveBgImg,
            state: 1
          });
        } else {
          newImgList.push({
            liveBgImg: item.liveBgImg,
            state: 0
          });
        }
      });
    } else { // 删除未启用的图
      vm.imgList.forEach(function(item) {
        if (item.liveBgImg != data.liveBgImg) {
          newImgList.push({
            liveBgImg: item.liveBgImg,
            state: item.state
          });
        }
      });
    }
    liveBgSettingService.updatePicFunc({
      tenant_id: vm.tenantId,
      live_bg_json: JSON.stringify(newImgList)
    }, function(res) {
      console.log('res', res);
      modalService.openAlert('提示', res.error_info);
      getBgPicListFunc();
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', err.error_info);
    });
  }

  //初始化轮播图组件
  function setSwiper() {
    setTimeout(() => {
      mySwiper = new Swiper('.swiper-container', {
        pagination: '.swiper-pagination',
        slidesPerView: 3,
        loop: false,
        prevButton: '.swiper-button-prev',
        nextButton: '.swiper-button-next',
      })
    }, 1000);
  }

  function addBgFunc() {
    addBgModalCtrl.openAddBgModalFunc('', function(res) {
      getBgPicListFunc();
    });
  }

  return vm;
}

module.exports = liveBgSettingCtrl;


