/* 客户管理 module */

customerManageRoute.$inject = ['$stateProvider'];

function customerManageRoute($stateProvider) {
  $stateProvider.state('home.customerManage', {
    url: '/customerManage',
    templateUrl: './views/customerManage.html',
    controller: 'customerManageCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = customerManageRoute;



