/* 主页 module */

homeRoute.$inject = ['$stateProvider'];

function homeRoute($stateProvider) {
    $stateProvider
        .state('home', {
            url: '/home',
            templateUrl: './views/home.html',
            controller: 'homeCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = homeRoute;



