/* 签约统计查询 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var signStatisticsDownloadCtrl = require('./modalPopup/signStatisticsDownload/controller');
var signStatisticsDownloadService = require('./modalPopup/signStatisticsDownload/service');

//setter定义模块
module.exports = angular
  .module('signStatisticsQry', [])
  .config(Routing)
  .controller('signStatisticsQryCtrl', ctrl)
  .service('signStatisticsQryService', service)
  .factory('signStatisticsDownloadCtrl', signStatisticsDownloadCtrl)
  .service('signStatisticsDownloadService', signStatisticsDownloadService).name;



