//按钮权限指令
ifShowEle.$inject = ['$state', '$rootScope'];
function ifShowEle($state, $rootScope) {
  return {
    restrict: 'A',
    link: function (scope, elem, attrs) {
      try {
        var menuIdList = JSON.parse(window.sessionStorage.getItem('menuIdList'));
        if (menuIdList.indexOf(Number(attrs.ifShowEle)) < 0) {
          elem.addClass("ele-hidden");
        }
      } catch (error) {
        console.error('ifShowEle error', error);
      }
    }
  };
}
module.exports = ifShowEle;



