// 风险等级设置

require("locale_zh");
var tinyMce = require("tinymce/tinymce.js");
addProtocolVersionCtrl.$inject = [
  "$sce",
  "$state",
  "$stateParams",
  "modalService",
  "addProtocolVersionService"
];

function addProtocolVersionCtrl(
  $sce,
  $state,
  $stateParams,
  modalService,
  addProtocolVersionService
) {
  var vm = this;

  vm.protocolData = '';
  vm.protocolContent = '';
  vm.operatorInfo = JSON.parse(sessionStorage.getItem("operatorInfo"));
  vm.operatorNo = vm.operatorInfo.operatorNo;
  vm.operatorName = vm.operatorInfo.name;
  vm.tenantId = JSON.parse(sessionStorage.getItem("tenantId"));

  if ($stateParams.data) {
    try {
      vm.protocolData = JSON.parse(decodeURIComponent($stateParams.data));
      vm.protocolContent = vm.protocolData.agreement_content;
      console.log('vm.protocolData', vm.protocolData);
    } catch (error) {
      console.log('error' ,error);
    }
  }

  vm.fileType = vm.protocolData.agreement_type || "";
  vm.dictsFileType = [
    { label: "线下银行卡转账提示", value: "0" },
    { label: "适当性匹配协议", value: "1" },
    { label: "投顾服务协议", value: "2" },
    { label: "风险揭示协议", value: "3" },
    { label: "适当性不匹配协议c1", value: "4" },
    { label: "适当性不匹配协议非c1", value: "5" }
  ];
  vm.fileState = vm.protocolData.agreement_status || "";
  vm.dictsFileState = [
    { label: "启用", value: "0" },
    { label: "停用", value: "1" }
  ];
  vm.fileVersion = vm.protocolData.agreement_version || '';
  vm.fileName = vm.protocolData.agreement_name || '';

  try {
    setTimeout(() => {
      tinyMce.init({
        selector: '.myEditor',
        theme: 'silver',
        language: 'zh_CN',
        plugins: ['table'],
        fontsize_formats: '11px 12px 14px 16px 18px 24px 36px 48px',
        init_instance_callback: function(editor) {
          editor.setContent(vm.protocolContent);
        }
      });
      console.log('2');
    }, 200);
  } catch (error) {
    console.log('error', error);
  }

  vm.backToVersionPage = backToVersionPage;

  function protocolAddFunc(content) {
    addProtocolVersionService.protocolAdd({
      tenant_id: vm.tenantId,
      agreement_name: vm.fileName,
      agreement_type: vm.fileType,
      agreement_version: vm.fileVersion,
      agreement_status: vm.fileState,
      agreement_content: content,
      operator_no: vm.operatorNo,
      operator_name: vm.operatorName
    }, function(res) {
      console.log('res', res);
      if (res.error_no == 0) {
        $state.go('home.protocolVersionManage');
      }
      modalService.openAlert('提示', res.error_info);
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', err.error_info);
    })
  }

  function protocolEditFunc(content) {
    addProtocolVersionService.protocolEdit({
      tenant_id: vm.tenantId,
      agreement_id: vm.protocolData.agreement_id,
      agreement_name: vm.fileName,
      agreement_type: vm.fileType,
      agreement_version: vm.fileVersion,
      agreement_status: vm.fileState,
      agreement_content: content,
      operator_no: vm.operatorNo,
      operator_name: vm.operatorName
    }, function(res) {
      console.log('res', res);
      if (res.error_no == 0) {
        $state.go('home.protocolVersionManage');
      }
      modalService.openAlert('提示', res.error_info);
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', err.error_info);
    })
  }

  // 返回上一页面 type 1：确定 0：取消
  function backToVersionPage(type) {
    if (type) {
      if (vm.fileName && vm.fileType && vm.fileVersion && vm.fileState && tinyMce.activeEditor.getContent()) {
        if (!vm.protocolData) { // 新增
          protocolAddFunc(tinyMce.activeEditor.getContent());
        } else { // 编辑
          protocolEditFunc(tinyMce.activeEditor.getContent());
        }
      } else {
        modalService.openAlert('提示', '请完整填写表单');
      }
    } else {
      $state.go('home.protocolVersionManage');
    }
  }
}

module.exports = addProtocolVersionCtrl;



