require('./style.less');

var modalOptions = require('jsDir/modal_options'),
    tagManageModalTempl = require('./tagManageModal.html'),
    func = require('func');

tagManageModalCtrl.$inject = ['modalService', 'tagManageModalService'];

function tagManageModalCtrl(modalService, tagManageModalService) {
  var vm = this;

  vm.brokerId = '';//当前店铺的brokerId
  vm.tagString = '';//当前店铺的tagString

  vm.openTagManageModalFunc = openTagManageModalFunc;

  function openTagManageModalFunc(item, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '标签管理';
    options.template = tagManageModalTempl;
    options.controller = defaultController;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };

    //处理入参
    vm.brokerId = item.broker_id;
    //判断是否含有brokertags这个key
    if (item.brokertags) {
      vm.tagString = item.brokertags;
    } else {
      vm.tagString = '';
    }

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.brokerId = vm.brokerId;   //传入的broker_id
    if (vm.tagString.length != 0) {
      ra.tagList = vm.tagString.split(',');  //整理传入的标签字符串为数组格式
    } else {
      ra.tagList = [];
    }
    ra.newTag = '';   //新增标签名
    ra.tagString = '';  //提交时入参，新标签字符串

    ra.addTag = addTag;
    ra.deleteTag = deleteTag;
    ra.submitChange = submitChange;
    ra.cancel = cancel;
    ra.close = close;

    //添加标签
    function addTag(tag) {
      if (tag.length != 0 && ra.tagList.indexOf(tag) == -1) {
        ra.tagList.push(tag);
        ra.newTag = '';
      }
    }

    //删除标签
    function deleteTag(index) {
      ra.tagList.splice(index, 1);
    }

    //提交修改
    function submitChange() {
      var list = [];
      //将标签数组转换成字符串
      angular.forEach(ra.tagList, function (item, index) {
        var tagsItem = {
          brokertag_name: item,
          sort: index
        };
        list.push(tagsItem);
      });
      ra.tagString = JSON.stringify(list);

      //执行提交修改
      tagManageModalService.funcBrokertagUpdate(
        {
          broker_id: ra.brokerId,
          broker_tags: ra.tagString
        },
        function (result) {
          func.openToaster('操作成功！');
          close();
        }
      )
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = tagManageModalCtrl;



