 // 风险等级设置

require('locale_zh');
var func = require('func');

basicValueSettingCtrl.$inject = ['modalService', 'basicValueSettingService'];

function basicValueSettingCtrl(modalService, basicValueSettingService) {
  var vm = this;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.vpSurfType = '1'; // 观点，浏览人数设置模式 1：固定值，2：随机值
  vm.vpUpType = '1'; // 观点，点赞人数设置模式 1：固定值，2：随机值
  vm.zbType = '1'; // 直播，参与人数设置 1：固定值，2：随机值

  vm.vpSurfStatic = ''; // 观点，浏览人数固定值
  vm.vpSurfMin = ''; // 观点，浏览人数最小值
  vm.vpSurfMax = ''; // 观点，浏览人数最大值

  vm.vpUpStatic = ''; // 观点，点赞人数固定值
  vm.vpUpMin = ''; // 观点，点赞人数最小值
  vm.vpUpMax = ''; // 观点，点赞人数最大值

  vm.zbStatic = ''; // 直播，参与人数固定值
  vm.zbMin = ''; // 直播，参与人数最小值
  vm.zbMax = ''; // 直播，参与人数最大值

  vm.setBasicValueSetFunc = setBasicValueSetFunc;

  getBasicValueSetFunc();

  // 获取基础值状态
  function getBasicValueSetFunc() {
    basicValueSettingService.basicValueQry(
      {
        tenant_id: vm.tenantId
      },
      function(res) {
        try {
          var data = JSON.parse(res.value); 
          console.log('res value', data);
          vm.vpSurfType = String(data.viewpoint_read.mode) || '1'; // 观点，浏览人数设置模式 1：固定值，2：随机值
          vm.vpUpType = String(data.viewpoint_upvote.mode) || '1'; // 观点，点赞人数设置模式 1：固定值，2：随机值
          vm.zbType = String(data.live_chat_init_num.mode) || '1'; // 直播，参与人数设置 1：固定值，2：随机值

          vm.vpSurfStatic = data.viewpoint_read.static_value; // 观点，浏览人数固定值
          vm.vpSurfMin = data.viewpoint_read.random_min_value; // 观点，浏览人数最小值
          vm.vpSurfMax = data.viewpoint_read.random_max_value; // 观点，浏览人数最大值

          vm.vpUpStatic = data.viewpoint_upvote.static_value; // 观点，点赞人数固定值
          vm.vpUpMin = data.viewpoint_upvote.random_min_value; // 观点，点赞人数最小值
          vm.vpUpMax = data.viewpoint_upvote.random_max_value; // 观点，点赞人数最大值

          vm.zbStatic = data.live_chat_init_num.static_value; // 直播，参与人数固定值
          vm.zbMin = data.live_chat_init_num.random_min_value; // 直播，参与人数最小值
          vm.zbMax = data.live_chat_init_num.random_max_value; // 直播，参与人数最大值
        } catch (error) {
          console.log('basicValueQry error', error);
        }
      }
    )
  }

  // 设置基础值
  function setBasicValueSetFunc() {
    if (vm.vpSurfStatic >= 0 && vm.vpUpStatic >= 0 && vm.zbStatic >= 0 && vm.vpSurfMin >= 0 && vm.vpSurfMax >= 0 && vm.vpUpMin >= 0 && vm.vpUpMax >= 0 && vm.zbMin >= 0 && vm.zbMax >= 0 &&
    vm.vpSurfStatic <=100000 && vm.vpUpStatic <=100000 && vm.zbStatic <=100000 && vm.vpSurfMin <=100000 && vm.vpSurfMax <=100000 && vm.vpUpMin <=100000 && vm.vpUpMax <=100000 && vm.zbMin <=100000 && vm.zbMax <=100000) {
      var data = {
        viewpoint_read: {
          mode: Number(vm.vpSurfType), 
          static_value: Number(vm.vpSurfStatic), 
          random_min_value: Number(vm.vpSurfMin), 
          random_max_value: Number(vm.vpSurfMax)
        },
        viewpoint_upvote: {
          mode: Number(vm.vpUpType), 
          static_value: Number(vm.vpUpStatic), 
          random_min_value: Number(vm.vpUpMin), 
          random_max_value: Number(vm.vpUpMax)
        },
        live_chat_init_num: {
          mode: Number(vm.zbType), 
          static_value: Number(vm.zbStatic), 
          random_min_value: Number(vm.zbMin), 
          random_max_value: Number(vm.zbMax)
        }
      };
      basicValueSettingService.basicValueSet(
        {
          tenant_id: vm.tenantId,
          value: JSON.stringify(data)
        },
        function(res) {
          console.log('res', res);
          modalService.openAlert('提示', res.error_info);
        }
      )
    } else {
      modalService.openAlert('提示', '基础值范围是0~100000');
      return;
    }
  }

}

module.exports = basicValueSettingCtrl; 


