 /* 新增标签 */

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
combineCheckModalTempl = require('./combineCheckModal.html');

  combineCheckModalCtrl.$inject = ['modalService','combineCheckModalService'];

function combineCheckModalCtrl(modalService, combineCheckModalService) {
  var vm = this;

  vm.type = '';
  /*函数声明*/
  vm.openCombineCheckModalFunc = openCombineCheckModalFunc;

  /*函数定义*/
  function openCombineCheckModalFunc(type, data, callBack1) {
    var options = angular.copy(modalOptions);
    options.modalTitle = type == 1 ? '审核通过' : '审核不通过';
    options.template = combineCheckModalTempl;
    options.controller = defaultController;
    options.modalParams = data;
    vm.type = type;
    options.confirmCallback = function (result) {
      angular.isFunction(callBack1) && callBack1(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = '审核通过';
    ra.cancelBtnText = '审核不通过';


    ra.type = vm.type;
    ra.auditType = ra.modalParams.combine[0].audit_type; // 3：上下架、4：修改
    ra.historyId = ra.modalParams.combine[0].history_id;
    ra.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
    ra.operatorNo = ra.operatorInfo.operatorNo;
    ra.operatorName = ra.operatorInfo.name;
    ra.tenantId = JSON.parse(sessionStorage.getItem('tenantId')) * 1;
    ra.serviceNo = '';
    ra.serviceName = '';
    ra.remark = '';

    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function submit() {
      // 审核不通过要需要理由
      if (ra.type == 2 && !ra.remark) {
        modalService.openAlert('提示', '请填写审核意见');
        return;
      }
      if (ra.auditType == 3) {
        combineCheckModalService.combineUpDownAudit({
          tenant_id: ra.tenantId,
          combi_name:  ra.modalParams.combi_name,
          combine_info_id: ra.modalParams.combine[0].combine_info_id,
          operate_content: ra.type, // 1：通过，2：不通过
          otc_product_code: ra.serviceNo,
          otc_product_name: ra.serviceName,
          operator_no: ra.operatorNo,
          operator_name: ra.operatorName,
          remark: ra.remark
        }, function (res) {
          if (res.error_no != 0) {
            modalService.openAlert('提示', res.error_no.error_info);
          } else {
            modalService.openAlert('提示', '执行成功！');
            close(true);
          }
        }, function(err) {
          modalService.openAlert('提示', err.error_info);
        });
      } else if (ra.auditType == 4) {
        combineCheckModalService.combineModifyAudit({
          tenant_id: ra.tenantId,
          history_id: ra.historyId,
          operate_content: ra.type, // 1：通过，2：不通过
          operator_no: ra.operatorNo,
          operator_name: ra.operatorName,
          remark: ra.remark
        }, function (res) {
          if (res.error_no != 0) {
            modalService.openAlert('提示', res.error_no.error_info);
          } else {
            modalService.openAlert('提示', '执行成功！');
            close(true);
          }
        }, function(err) {
          modalService.openAlert('提示', err.error_info);
        });
      }
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = combineCheckModalCtrl;




