 // 投顾业绩数据查询

require('locale_zh');

var func = require('func');

investorAchievementQryCtrl.$inject = ['modalService', 'investorAchievementQryService', 'investorAchievementQryDownModalCtrl'];

function investorAchievementQryCtrl(modalService, investorAchievementQryService, investorAchievementQryDownModalCtrl) {
  var vm = this;

  vm.tenantId = JSON.parse(sessionStorage.getItem("tenantId"));

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = '';
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.productType = '';
  vm.dictsProductType = [
    {label: '全部', value: ''},
    {label: '观点', value: 1},
    {label: '组合', value: 2},
    {label: '问答', value: 3},
    {label: '观点包', value: 8},
    {label: '量化模型', value: 90},
    {label: '智能策略', value: 94}
  ];
  vm.keywords = '';
  vm.investorList = [];
  // 排序项
  vm.sortType = ''; // desc:降序、asc：升序
  vm.sortField = ''; // total_income：收入、focus_fans_nums：关注粉丝数、combi_nums：组合数量、viewpoint_nums：观点数量、live_nums：直播数量、branch_name：所属机构
  vm.seqIncome = false;
  vm.seqFans = false;
  vm.seqCombine = false;
  vm.seqViewpoint = false;
  vm.seqLive = false;
  vm.seqBranch = false;

  vm.keywordSearchFunc = keywordSearchFunc;
  vm.sequenceFunc = sequenceFunc;
  vm.pageChangeFunc = pageChangeFunc;
  vm.download = download;

  brokerPerformanceQryFunc();

  function brokerPerformanceQryFunc() {
    investorAchievementQryService.brokerPerformanceQry({
      tenant_id: vm.tenantId,
      keyword: vm.customerKey,
      // service_type: vm.productType,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage,
      sort_type: vm.sortType,
      sort_field: vm.sortField
    }, function (response) {
      vm.investorList = response.data_list;
      vm.totalItems = response.total_item_num;
    }, function (err) {
      modalService.openAlert('提示', err.error_info);
      vm.investorList = [];
      vm.totalItems = 0;
    })
  }

  function download() {
    investorAchievementQryDownModalCtrl.openDownModalFunc({
      tenant_id: vm.tenantId,
      keyword: vm.customerKey,
      service_type: vm.productType,
      page_no: 1,
      page_size: vm.totalItems,
      sort_type: vm.sortType,
      sort_field: vm.sortField
    });
  }

  function keywordSearchFunc() {
    vm.currentPage = 1;
    brokerPerformanceQryFunc();
  }

  function sequenceFunc(type) {
    switch (type) {
      case 0:
        vm.seqIncome = !vm.seqIncome;
        vm.sortType = vm.seqIncome ? 'desc' : 'asc';
        vm.sortField = 'total_income';
        break;
      case 1:
        vm.seqFans = !vm.seqFans;
        vm.sortType = vm.seqFans ? 'desc' : 'asc';
        vm.sortField = 'focus_fans_nums';
        break;
      case 2:
        vm.seqCombine = !vm.seqCombine;
        vm.sortType = vm.seqCombine ? 'desc' : 'asc';
        vm.sortField = 'combi_nums';
        break;
      case 3:
        vm.seqViewpoint = !vm.seqViewpoint;
        vm.sortType = vm.seqViewpoint ? 'desc' : 'asc';
        vm.sortField = 'viewpoint_nums';
        break;
      case 4:
        vm.seqLive = !vm.seqLive;
        vm.sortType = vm.seqLive ? 'desc' : 'asc';
        vm.sortField = 'live_nums';
        break;
      case 5:
        vm.seqBranch = !vm.seqBranch;
        vm.sortType = vm.seqBranch ? 'desc' : 'asc';
        vm.sortField = 'branch_name';
        break;
    }
    brokerPerformanceQryFunc();
  }

  function pageChangeFunc() {
    brokerPerformanceQryFunc();
  }

}

module.exports = investorAchievementQryCtrl; 


