storeSelectedService.$inject = ['api'];

function storeSelectedService(api) {
  var vm = this;

  vm.adviserInfoQry = adviserInfoQry; // 店铺列表信息查询
  vm.setDefaultStore = setDefaultStore; // 设置默认推荐店铺

  // 店铺信息查询
  function adviserInfoQry(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage/fun_broker_recommend',
      //url: 'fun_broker_recommend',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  // 设置默认推荐店铺
  function setDefaultStore(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage/func_set_default_mien_broker',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }
}

module.exports = storeSelectedService;


