require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var addBgModalCtrl = require('./modalPopup/addBgModal/controller');
var addBgModalService = require('./modalPopup/addBgModal/service');

//setter定义模块
module.exports = angular.module('liveBgSetting', [])
    .config(Routing)
    .controller('liveBgSettingCtrl', ctrl)
    .service('liveBgSettingService', service)
    .factory('addBgModalCtrl', addBgModalCtrl)
    .service('addBgModalService', addBgModalService)
    .name;


