/* 图片设置 module */

imageConfigRoute.$inject = ['$stateProvider'];

function imageConfigRoute($stateProvider) {
  $stateProvider.state('home.imageConfig', {
    url: '/imageConfig',
    templateUrl: './views/imageConfig.html',
    controller: 'imageConfigCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = imageConfigRoute;



