/*批量新增投顾*/

batchAddNewInvestRoute.$inject = ['$stateProvider'];

function batchAddNewInvestRoute($stateProvider) {
  $stateProvider
    .state('home.batchAddNewInvest', {
      url: '/batchAddNewInvest',
      templateUrl: './views/batchAddNewInvest.html',
      controller: 'batchAddNewInvestCtrl',
      controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
    });
}

module.exports = batchAddNewInvestRoute;



