/* name module */

templateRoute.$inject = ['$stateProvider'];

function templateRoute($stateProvider) {
    $stateProvider
        .state('template', {
            url: '/template',
            templateUrl: './views/template.html',
            controller: 'templateCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = templateRoute;



