/* name module */
require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var templateModalCtrl = require('./modalPopup/templateModal/controller');
var templateModalService = require('./modalPopup/templateModal/service');

//setter定义模块
module.exports = angular.module('app.template', [])
    .config(Routing)
    .service('templateService', service)
    .controller('templateCtrl', ctrl)
    .factory('templateModalCtrl', templateModalCtrl)
    .service('templateModalService', templateModalService)
    .name;



