orderCheckService.$inject = ['api'];
function orderCheckService(api) {
  var vm = this;

  vm.orderListQry = orderListQry;

  /**
   * 查询订单列表
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function orderListQry(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'list_order_audit_on_management',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = orderCheckService;


