/* 观点标签设置 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var addTagModalCtrl = require('./modalPopup/addTagModal/controller');
var addTagModalService = require('./modalPopup/addTagModal/service');

//setter定义模块
module.exports = angular
  .module('viewpointTagSetting', [])
  .config(Routing)
  .controller('viewpointTagSettingCtrl', ctrl)
  .service('viewpointTagSettingService', service)
  .factory('addTagModalCtrl', addTagModalCtrl)
  .service('addTagModalService', addTagModalService)
  .name;



