/* 运营基础值这是 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');

//setter定义模块
module.exports = angular
  .module('basicValueSetting', [])
  .config(Routing)
  .controller('basicValueSettingCtrl', ctrl)
  .service('basicValueSettingService', service).name;



