require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  addStockModalTempl = require('./addStockModal.html'),
  func = require('func');

addStockModalCtrl.$inject = ['modalService', 'addStockModalService'];

function addStockModalCtrl(modalService, addStockModalService) {
  var vm = this;

  vm.openAddStockModalFunc = openAddStockModalFunc;

  function openAddStockModalFunc(callBack1, callBack2) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '添加股票';
    options.template = addStockModalTempl;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function() {
      angular.isFunction(callBack1) && callBack1();
    };
    // 关闭后的回调
    options.cancelCallback = function() {
      angular.isFunction(callBack2) && callBack2();
    }

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));

    ra.maxSize = 5;
    ra.totalItems = 1;
    ra.currentPage = 1;
    ra.itemsPerPage = 10;

    ra.keyword = '';
    ra.list = [];
    ra.selectedStock = {};
    ra.ifBtnClick = true; // 添加按钮是否可点击（添加请求过程中不可点击）

    ra.getAllStockFunc = getAllStockFunc;
    ra.getStockByCodeFunc = getStockByCodeFunc;
    ra.selectedStockFunc = selectedStockFunc;
    ra.stockPoolAddFunc = stockPoolAddFunc;
    ra.cancel = cancel;
    ra.close = close;

    getAllStockFunc();

    /**
     * 获取市场股票列表
     *
     * @return  {[type]}  [return description]
     */
    function getAllStockFunc() {
      addStockModalService.getAllStock({
        offset: ra.currentPage,
        number: ra.itemsPerPage
      }, function(res) {
        ra.list = res.data.datas;
        ra.totalItems = res.data.totalSize;
        getStockRealFunc();
      }, function(res) {
        console.error('getAllStockFunc err', res);
        ra.list = [];
      });
    }

    /**
     * 条件查询股票列表
     *
     * @return  {[type]}  [return description]
     */
    function getStockByCodeFunc() {
      addStockModalService.getStockByCode({
        code: ra.keyword,
        offset: ra.currentPage,
        number: ra.itemsPerPage
      }, function(res) {
        ra.list = res.data.datas;
        ra.totalItems = res.data.totalSize;
        getStockRealFunc();
      }, function(res) {
        console.error('getStockByCodeFunc err', res);
        ra.list = [];
      });
    }

    /**
     * 获取股票行情
     *
     * @return  {[type]}  [return description]
     */
    function getStockRealFunc() {
      var codeList = ra.list.map(function(item) {
        return item.code;
      });
      addStockModalService.getStockReal({
        code: codeList.join(',')
      }, function(res) {
        res.datas.forEach(function(item, index) {
          ra.list[index].currentPrice = item.currentPrice;
          ra.list[index].changePrice = item.changePrice;
          ra.list[index].changeRation = Number((item.changeRation * 100).toFixed(2));
          ra.list[index].totalAmount = item.totalAmount;
        })
      }, function(res) {
        console.error('getStockRealFunc err', res);
      })
    }

    /**
     * 当前选中的股票
     *
     * @param   {[type]}  stock  [stock description]
     *
     * @return  {[type]}         [return description]
     */
    function selectedStockFunc(stock) {
      console.log('selectedStock', stock);
      ra.selectedStock = stock;
    }

    /**
     * 添加股票到股票池
     *
     * @return  {[type]}  [return description]
     */
    function stockPoolAddFunc() {
      if (!ra.ifBtnClick) {
        return;
      }
      if (!ra.selectedStock.code) {
        modalService.openAlert('提示', '请选择一只股票！');
        return;
      }
      ra.ifBtnClick = false;
      addStockModalService.stockPoolAdd({
        tenant_id: ra.tenantId,
        stock_code: ra.selectedStock.code,
        stock_name: ra.selectedStock.stockName,
        pinyin: ra.selectedStock.py,
        pinyin_all: ra.selectedStock.pyAll,
        code_type: ra.selectedStock.codeType,
        market: ra.selectedStock.market
      }, function(res) {
        modalService.openAlert('提示', '添加成功');
        ra.ifBtnClick = true;
      }, function(err) {
        console.log('error', err, err.error_info);
        modalService.openAlert('提示', '添加失败');
        ra.ifBtnClick = true;
      })
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = addStockModalCtrl;



