/* 敏感词库管理 module */

require('locale_zh');

var modalOptions = require('jsDir/modal_options'),
  func = require('func');
require.ensure(
  [],
  function(require) {
    XLSX = require('xlsx');
  },
  'excel'
);

sensitiveWordsCtrl.$inject = ['modalService', 'sensitiveWordsService', 'editSensitiveWordModalCtrl', '$scope', '$cookies'];

function sensitiveWordsCtrl(modalService, sensitiveWordsService, editSensitiveWordModalCtrl, $scope, $cookies) {
  var vm = this;
  var wb; //读取完成的数据

  vm.tenant_id = '';
  vm.branchDate = []; //部门信息列表
  vm.selectedBranch = ''; //已选部门信息
  vm.sensitiveData = []; //敏感词列表
  vm.selectedWord = []; //已选敏感词列表
  vm.deleteWordList = []; //要删除的敏感词列表
  vm.checkAll = false; //默认全部敏感词为非选择状态
  vm.checkItem = false; //默认每个敏感词为非选状态
  vm.keyword = ''; //关键词
  vm.adviserInfoList = []; //导入excel
  vm.list = [];
  vm.excelList = [];
  var rABS = false; //是否将文件读取为二进制字符串
  var importList = []; //导入数据

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 1; //根据接口返回决定
  vm.currentPage = 1;
  vm.itemsPerPage = 10;

  vm.selectedBranchFunc = selectedBranchFunc; //部门选择
  vm.addItem = addItem; //新增按键
  vm.deteleItem = deteleItem; //删除按键
  vm.selectAll = selectAll; //敏感词全选
  vm.selectWord = selectWord; //选择当前敏感词
  vm.searchSensitiveWord = searchSensitiveWord; //关键词搜索
  vm.pageChangeFunc = pageChangeFunc; //分页
  vm.download = download; //下载excel模板
  vm.readAsText = readAsText; //显示文本文件

  //查询部门信息
  vm.branchDate = JSON.parse(sessionStorage.getItem('allBranchListData'));
  vm.tenant_id = vm.branchDate[0].branchCode.split('_')[0];
  vm.selectedBranch = vm.branchDate[0].branchNo + vm.branchDate[0].branchName;

  init();

  //初始化
  function init() {
    if (vm.tenant_id == '') {
      vm.tenant_id = '20033';
    }
    sensitiveWordsService.getSensitiveWordsList(
      {
        source: 'cloudbroker_3',
        sign: '17AD57E731C3CF99EC72703976577EA4',
        tenant_id: vm.tenant_id,
        page_no: vm.currentPage,
        page_size: vm.itemsPerPage,
        key_word: vm.keyword
      },
      function(result) {
        vm.sensitiveData = result.data_list;
        vm.totalItems = result.total_item_num;
      },
      function(err) {
        modalService.openAlert('提示', err.error_info);
      }
    );
  }

  //部门选择
  function selectedBranchFunc(content) {
    vm.selectedBranch = content.branchNo + content.branchName;
    vm.tenant_id = content.branchCode.split('_')[0];
    init();
  }

  //全选当前页敏感词
  function selectAll() {
    if (vm.checkAll == true) {
      angular.forEach(vm.sensitiveData, function(item) {
        vm.selectedWord.push(item.sensitive_word_id);
      });
    } else {
      vm.selectedWord = [];
    }
  }

  //选择当前敏感词
  function selectWord(checkItem) {
    if (vm.selectedWord.indexOf(checkItem.sensitive_word_id) == -1) {
      vm.selectedWord.push(checkItem.sensitive_word_id);
    } else {
      removeByValue(vm.selectedWord, checkItem.sensitive_word_id);
    }
  }

  //数组中删除指定元素
  function removeByValue(arr, val) {
    for (var i = 0; i < arr.length; i++) {
      if (arr[i] == val) {
        arr.splice(i, 1);
        break;
      }
    }
  }

  //敏感词搜索
  function searchSensitiveWord() {
    sensitiveWordsService.getSensitiveWordsList(
      {
        source: 'cloudbroker_3',
        sign: '17AD57E731C3CF99EC72703976577EA4',
        tenant_id: vm.tenant_id,
        page_no: vm.currentPage,
        page_size: vm.itemsPerPage,
        key_word: vm.keyword
      },
      function(result) {
        vm.sensitiveData = result.data_list;
        vm.totalItems = result.total_item_num;
      }
    );
  }

  //新增、编辑
  function addItem(item) {
    if (item == undefined) {
      editSensitiveWordModalCtrl.openeditSensitiveWordModalFunc('', function() {
        init();
      });
    } else {
      editSensitiveWordModalCtrl.openeditSensitiveWordModalFunc(item, function() {
        init();
      });
    }
  }

  //删除
  function deteleItem() {
    if (vm.selectedWord.length == 0) {
      func.openToaster('请选择敏感词！');
    } else {
      var id = {};
      angular.forEach(vm.selectedWord, function(item) {
        id = {};
        id.sensitive_word_id = item;
        vm.deleteWordList.push(id);
      });
      var options = angular.copy(modalOptions);
      options.modalTitle = '系统提示';
      options.modalInfo = '确定要删除敏感词？';
      options.confirmCallback = deleteSensitiveWord;
      modalService.openModal(options);
    }
  }

  //删除敏感词
  function deleteSensitiveWord() {
    sensitiveWordsService.deleteSensitiveWord(
      {
        accessToken: sessionStorage.getItem('access_token'),
        source: 'cloudbroker_3',
        sign: '17AD57E731C3CF99EC72703976577EA4',
        sensitive_word_ids: vm.deleteWordList
      },
      function(result) {
        vm.deleteWordList = [];
        vm.selectedWord = [];
        vm.checkAll = false;
        delSensitiveWordCache();
        init();
      }
    );
  }

  //清缓存
  function delSensitiveWordCache() {
    sensitiveWordsService.delSensitiveWordCache(
      {},
      function(result) {
      }
    );
  }

  //下载excel模板
  function download() {
    window.open('assets/template/SensitiveWordTemplate.xlsx');
  }

  //显示文本文件
  function readAsText(event) {
    var file = event.currentTarget.files[0];
    vm.myFile = event.currentTarget.value;
    if (!file) {
      return;
    }
    var reader = new FileReader();
    reader.onload = function(e) {
      var data = e.target.result;
      if (rABS) {
        wb = XLSX.read(btoa(fixdata(data)), {
          //手动转化
          type: 'base64'
        });
      } else {
        wb = XLSX.read(data, {
          type: 'binary'
        });
      }
      //wb.SheetNames[0]是获取Sheets中第一个Sheet的名字
      //wb.Sheets[Sheet名]获取第一个Sheet的数据
      vm.adviserInfoList = JSON.stringify(XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]));
      vm.list = JSON.parse(vm.adviserInfoList);
      if (vm.list != undefined && vm.list.length != 0) {
        for (var k = 0; k < vm.list.length; k++) {
          var bbb = vm.list[k];
          for (var j in bbb) {
            var value = bbb[j];
            if (j == '敏感词') {
              delete bbb[j];
              j = 'sensitive_word';
              bbb[j] = value;
            }
            if (j == '备注') {
              delete bbb[j];
              j = 'remark';
              bbb[j] = value;
            }
            vm.excelList = bbb;
          }
          vm.excelList.tenant_id = vm.tenant_id;
          importList.push(vm.excelList);
        }
        sensitiveWordsService.importSensitiveWord(
          {
            sensitive_words: importList,
            source: 'cloudbroker_3',
            sign: '17AD57E731C3CF99EC72703976577EA4'
          },
          function(res) {
            modalService.openAlert('提示', res.error_info);
            delSensitiveWordCache();
            init();
          },
          function(err) {
            modalService.openAlert('提示', err.error_info);
            console.log('err', err);
          }
        );
      } else {
        func.openToaster('表格格式出错，请重新下载模板！');
        // 重置输入框，便于下次上传
        event.target.value = '';
        return;
      }
      // 重置输入框，便于下次上传
      event.target.value = '';
      $scope.$apply();
    };
    if (rABS) {
      reader.readAsArrayBuffer(file);
    } else {
      reader.readAsBinaryString(file);
    }
  }

  //文件流转BinaryString
  function fixdata(data) {
    var o = '',
      l = 0,
      w = 10240;
    for (; l < data.byteLength / w; ++l) o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)));
    o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
    return o;
  }

  //分页
  function pageChangeFunc() {
    init();
  }
}

module.exports = sensitiveWordsCtrl;



