
/* 密码重置 module */

resetPasswordRoute.$inject = ['$stateProvider'];

function resetPasswordRoute($stateProvider) {
    $stateProvider
        .state('home.resetPassword', {
            url: '/resetPassword',
            templateUrl: './views/resetPassword.html',
            controller: 'resetPasswordCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = resetPasswordRoute;



