/* 店铺管理 module */

storeManageService.$inject = ['api'];
function storeManageService(api) {
  var vm = this;

  vm.funcOperateBrokerSearch = funcOperateBrokerSearch;
  vm.setDefaultStore = setDefaultStore;
  vm.getDefaultStore = getDefaultStore;

  //获取店铺列表信息
  function funcOperateBrokerSearch(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'ifs',
      url: 'manage/func_operate_broker_search',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(err) {
        angular.isFunction(cb2) && cb2(err);
      }
    });
  }

  // 设置默认推荐店铺
  function setDefaultStore(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage/func_set_default_mien_broker',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(err) {
        angular.isFunction(cb2) && cb2(err);
      }
    });
  }

  // 获取默认推荐店铺
  function getDefaultStore(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage/func_get_default_mien_broker',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(err) {
        angular.isFunction(cb2) && cb2(err);
      }
    });
  }

  return vm;
}

module.exports = storeManageService;



