productAuthorityManageService.$inject = ['api'];
function productAuthorityManageService(api) {
  var vm = this;

  vm.productListQryFunc = productListQryFunc;

  /**
   * 客户贡献度数据查询
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function productListQryFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'branch_qry_in_product',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = productAuthorityManageService;


