// 观点标签设置

require('locale_zh');

var func = require('func');

viewpointPackageCheckCtrl.$inject = ['modalService', 'viewpointPackageCheckService', 'viewpointPackageCheckModalCtrl', 'viewpointPackageRemarkModalCtrl', 'viewpointPackageIntroModalCtrl'];

function viewpointPackageCheckCtrl(modalService, viewpointPackageCheckService, viewpointPackageCheckModalCtrl, viewpointPackageRemarkModalCtrl, viewpointPackageIntroModalCtrl) {
  var vm = this;

  vm.typeList = [
    {name: '查看全部', value: ''},
    {name: '待审核', value: '0'},
    {name: '审核通过', value: '1'},
    {name: '审核不通过', value: '2'}
  ];
  vm.selectType = '';
  vm.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
  vm.tenant_id = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.keywords = '';
  vm.dataList = [];
  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.searchFunc = searchFunc;
  vm.pageChangeFunc = pageChangeFunc;
  vm.selectedTypeFunc = selectedTypeFunc;
  vm.openViewpointPackageCheckModalFunc = openViewpointPackageCheckModalFunc;
  vm.openViewpointPackageRemarkModalFunc = openViewpointPackageRemarkModalFunc;
  vm.openViewpointPackageIntroModalFunc = openViewpointPackageIntroModalFunc;

  getViewpointPkgListFunc();
  
  function getViewpointPkgListFunc(param) {
    vm.selectType = param || '';
    viewpointPackageCheckService.getViewpointPkgList({
      tenant_id: vm.tenant_id,
      audit_state: vm.selectType,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage,
      keyword: vm.keywords
    }, function(res) {
      console.log('res', res);
      vm.dataList = res.data_list || [];
      vm.totalItems = res.total_num * 1;
    }, function(err) {
      console.log('err', err);
    })
  }

  function searchFunc() {
    vm.currentPage = 1;
    getViewpointPkgListFunc();
  }

  function pageChangeFunc() {
    getViewpointPkgListFunc();
  }

  function selectedTypeFunc(param) {
    getViewpointPkgListFunc(param);
  }

  function openViewpointPackageCheckModalFunc(data) {
    viewpointPackageCheckModalCtrl.openViewpointPackageCheckModalFunc(data, function() {
      getViewpointPkgListFunc();
    });
  }

  function openViewpointPackageRemarkModalFunc(content) {
    viewpointPackageRemarkModalCtrl.openViewpointPackageRemarkModalFunc(content);
  }

  function openViewpointPackageIntroModalFunc(content) {
    viewpointPackageIntroModalCtrl.openViewpointPackageIntroModalFunc(content);
  }
}

module.exports = viewpointPackageCheckCtrl;



