/* 敏感词库管理 module */
sensitiveWordsService.$inject = ['api'];

function sensitiveWordsService(api) {
  var vm = this;

  vm.getSensitiveWordsList = getSensitiveWordsList;
  vm.deleteSensitiveWord = deleteSensitiveWord;
  vm.importSensitiveWord = importSensitiveWord;
  vm.delSensitiveWordCache = delSensitiveWordCache;

  //获取敏感词列表
  function getSensitiveWordsList(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'userCenter',
      url: 'func_sensitive_word_page_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  //删除敏感词
  function deleteSensitiveWord(data, cb1) {
    api.postRequest({
      prefix_type: 'userCenter',
      url: 'func_sensitive_word_delete',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //导入敏感词
  function importSensitiveWord(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'userCenter',
      url: 'func_sensitive_word_batch_save',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  //清缓存
  function delSensitiveWordCache(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'func_del_sensitive_word_cache',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = sensitiveWordsService;



