/* 投顾管理 module */

investManageService.$inject = ['api'];
function investManageService(api) {
  var vm = this;

  vm.businessAdviserBaseQry = businessAdviserBaseQry;
  vm.businessAdviserPerformQry = businessAdviserPerformQry;
  vm.businessAdviserFrozen = businessAdviserFrozen;
  vm.businessAdviserUnfrozen = businessAdviserUnfrozen;
  vm.updateBrokerProvide = updateBrokerProvide;
  vm.updateBrokerHidden = updateBrokerHidden;

  //获取在职、限制、隐藏投顾列表信息
  function businessAdviserBaseQry(data, cb1,cb2) {
    api.postRequest({
      prefix_type: 'businessService',
      url: 'businessAdviserBaseQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }

  //获取当前展示投顾信息
  function businessAdviserPerformQry(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'businessService',
      url: 'businessAdviserPerformQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  //限制当前投顾
  function businessAdviserFrozen(data, cb1) {
    api.postRequest({
      prefix_type: 'businessService',
      url: 'businessAdviserFrozen',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //解除限制当前投顾
  function businessAdviserUnfrozen(data, cb1) {
    api.postRequest({
      prefix_type: 'businessService',
      url: 'businessAdviserUnfrozen',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  // 限制、非限制后调用
  function updateBrokerProvide(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'func_update_broker_provide',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  } 

  // 设置隐藏、非隐藏当前投顾
  function updateBrokerHidden(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'func_update_broker_hidden',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  } 

  return vm;
}

module.exports = investManageService;



