/* 投顾管理 module */

require('locale_zh');
window.ExcellentExport = require('ExcellentExport'); //导出

var avatarImg = require('img/avatar.png');
var func = require('func');

investManageCtrl.$inject = ['$state', '$stateParams', 'investManageService', 'investorDownloadModalCtrl', 'updatePwdModalCtrl'];

function investManageCtrl($state, $stateParams, investManageService, investorDownloadModalCtrl, updatePwdModalCtrl) {
  var vm = this;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.avatarImg = avatarImg; //头像图片

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = '';
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  // 获取之前所在tab，
  var session = $stateParams.params;
  vm.currentSession = session || 0; //初始化默认子模块 0：在职投顾，1：限制投顾，5：隐藏投顾
  vm.selectedIndex = 0; //默认选中列表第一项
  vm.keywords = ''; //查询关键字

  vm.adviserList = []; //正常投顾信息列表
  vm.adviserDetailInfo = {}; //投顾详细信息

  vm.pageChangeFunc = pageChangeFunc;
  vm.setCurrentSession = setCurrentSession;
  vm.adviserListQry = adviserListQry;
  vm.displayAdviserInfo = displayAdviserInfo;
  vm.frozenInvest = frozenInvest;
  vm.unfrozenInvest = unfrozenInvest;
  vm.updateBrokerHiddenFunc = updateBrokerHiddenFunc;
  vm.enterAddNewInvest = enterAddNewInvest;
  vm.enterMoreInvestInfo = enterMoreInvestInfo;
  vm.openExportDataModal = openExportDataModal;
  vm.updatePwd = updatePwd;

  adviserListQry();

  //换页方法
  function pageChangeFunc() {
    adviserListQry();
  }

  //换子模块方法
  function setCurrentSession(param) {
    vm.currentPage = 1;
    vm.keywords = '';
    vm.currentSession = param;
    adviserListQry();
  }

  //初始化在职、限制、隐藏投顾列表数据
  function adviserListQry() {
    investManageService.businessAdviserBaseQry(
      {
        keyword: vm.keywords,
        pageNo: vm.currentPage,
        pageSize: vm.itemPerPage,
        status: vm.currentSession //0：在职投顾，1：限制投顾，5：隐藏投顾
      },
      function(result) {
        vm.adviserList = result.data;
        vm.totalItems = result.totalCount;
        // exportData();
        displayAdviserInfo(0, vm.adviserList[0]);
      }
    );
  }

  //展示选中投顾数据
  function displayAdviserInfo(index, item) {
    vm.selectedIndex = index; //传入选中第几个投顾
    vm.adviserDetailInfo = item; //将选择的投顾详细信息传入
    //处理微信号信息
    if (!vm.adviserDetailInfo.weixinNo || vm.adviserDetailInfo.weixinNo == ' ') {
      vm.adviserDetailInfo.weixinNo = '暂无';
    }

    investManageService.businessAdviserPerformQry(
      {
        tenantId: item.tenantId,
        userId: item.userId
      },
      function(result) {
        console.log(result);
        vm.adviserDetailInfo.data = result.data;
      },
      function(result) {
        console.log(result.errorCode);
      }
    );
  }

  //限制投顾
  function frozenInvest() {
    investManageService.businessAdviserFrozen(
      {
        userId: vm.adviserDetailInfo.userId
      },
      function(result) {
        func.openToaster('限制成功！');
        updateBrokerProvideFunc(1);
        adviserListQry();
      }
    );
  }

  //解除限制投顾
  function unfrozenInvest() {
    investManageService.businessAdviserUnfrozen(
      {
        userId: vm.adviserDetailInfo.userId
      },
      function(result) {
        func.openToaster('解除限制成功！');
        updateBrokerProvideFunc(0);
        adviserListQry();
      }
    );
  }

  // type 0：非限制，1：限制
  function updateBrokerProvideFunc(type) {
    investManageService.updateBrokerProvide(
      {
        tenant_id: vm.tenantId,
        user_id: vm.adviserDetailInfo.userId,
        provide: type
      },
      function(res) {
        console.log('updateBrokerProvide res', res);
      }
    )
  }

  // 设置隐藏、非隐藏 type 1：隐藏，0：非隐藏
  function updateBrokerHiddenFunc(type) {
    investManageService.updateBrokerHidden(
      {
        tenant_id: vm.tenantId,
        user_id: vm.adviserDetailInfo.userId,
        hidden: type
      },
      function(res) {
        if (type == 0) {
          func.openToaster('解除隐藏成功！');
        } else if (type == 1) {
          func.openToaster('隐藏成功！');
        }
        adviserListQry();
      }
    )
  }

  //进入新增投顾页面
  function enterAddNewInvest() {
    $state.go('home.addNewInvest', {
      params: {},
      type: 0
    });
  }

  //进入更多详情
  function enterMoreInvestInfo() {
    $state.go('home.moreInvestInfo', {
      params: JSON.stringify(vm.adviserDetailInfo)
    });
  }

  //打开导出数据弹窗
  function openExportDataModal() {
    investorDownloadModalCtrl.openDownModalFunc(vm.currentSession);
  }

  function updatePwd() {
    updatePwdModalCtrl.openUpdatePwdModalFunc(vm.adviserDetailInfo);
  }

  // function exportData() {
  // investManageService.businessAdviserBaseQry(
  //   {
  // 	status: vm.currentSession   //0：在职投顾，1：限制投顾
  //   },
  //   function (result) {
  // 	vm.adviserData = result.data;
  //   }
  // );
  // }
}

module.exports = investManageCtrl;



