/* 推荐组合规则 module */

recommendCombineRuleService.$inject = ['api']
function recommendCombineRuleService(api) {
    var vm = this;

  vm.recommendCombiInfoQry = recommendCombiInfoQry;
  vm.recommendRuleQry = recommendRuleQry;
  vm.recommendCombiRuleAdd = recommendCombiRuleAdd;
  vm.combiRecommendQry = combiRecommendQry;
  vm.combiRecommend = combiRecommend;

  //组合信息查询
  function recommendCombiInfoQry(data, cb1) {
    api.postRequest({
      prefix_type: 'client',
      url: 'manage/combine_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  // 组合推荐规则查询
  function recommendRuleQry(data, cb1) {
    api.postRequest({
      prefix_type: 'client',
      url: 'manage/recommend_rule_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //组合发布规则
  function recommendCombiRuleAdd(data, cb1) {
    api.postRequest({
      prefix_type: 'client',
      url: 'manage/recommend_rule_add',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //已推荐组合查询
  function combiRecommendQry(data, cb1) {
    api.postRequest({
      prefix_type: 'client',
      url: 'manage/combine_recommend_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //推荐组合、取消推荐
  function combiRecommend(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'client',
      url: 'manage/combine_recommend',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }

  return vm;
}

module.exports = recommendCombineRuleService;



