/* 问答审核 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var QACheckDetailModalCtrl = require('./modalPopup/QACheckDetailModal/controller');
var QACheckOperateModalCtrl = require('./modalPopup/QACheckOperateModal/controller');
var QACheckOperateModalService = require('./modalPopup/QACheckOperateModal/service');

//setter定义模块
module.exports = angular
  .module('QACheck', [])
  .config(Routing)
  .controller('QACheckCtrl', ctrl)
  .service('QACheckService', service)
  .factory('QACheckDetailModalCtrl', QACheckDetailModalCtrl)
  .factory('QACheckOperateModalCtrl', QACheckOperateModalCtrl)
  .service('QACheckOperateModalService', QACheckOperateModalService).name;



