/* 直播排期预定 module */

liveManageRoute.$inject = ['$stateProvider'];

function liveManageRoute($stateProvider) {
  $stateProvider.state('home.liveManage', {
    url: '/liveManage',
    templateUrl: './views/liveManage.html',
    controller: 'liveManageCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = liveManageRoute;



