/* 组合审核 module */
combineCheckService.$inject = ['api'];
function combineCheckService(api) {
    var vm = this;

  vm.combineQry = combineQry; //查询组合
  vm.combiApplyHis = combiApplyHis; //组合申请流水查询
  vm.combineAuditPass = combineAuditPass; //组合申请通过
  vm.combineAuditNoPass = combineAuditNoPass; //组合申请不通过
  vm.saveCombiPassFunc = saveCombiPass; //保存审核信息


  function combineQry(data, cb1) {
    api.postRequest({
      prefix_type: 'manageCenter',
      url: 'T2Service/848583',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function combiApplyHis(data, cb1) {
    api.postRequest({
      prefix_type: 'manageCenter',
      url: 'T2Service/848721',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function combineAuditPass(data, cb1) {
    api.postRequest({
      prefix_type: 'manageCenter',
      url: 'T2Service/848581',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function combineAuditNoPass(data, cb1) {
    api.postRequest({
      prefix_type: 'manageCenter',
      url: 'T2Service/848582',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function saveCombiPass(data, cb1) {
    api.postRequest({
      prefix_type: 'client',
      url: 'combine_audited_apply',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }
    return vm;
}

module.exports = combineCheckService;



