/* 订阅历史 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  subscribeModalTempl = require('./subscribeModal.html');

subscribeModalCtrl.$inject = ['modalService', 'subscribeModalService'];

function subscribeModalCtrl(modalService, subscribeModalService) {
  var vm = this;

  /*函数声明*/
  vm.openSubscribeModalFunc = openSubscribeModalFunc;

  /*函数定义*/
  function openSubscribeModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '订阅历史详情';
    options.template = subscribeModalTempl;
    options.size = 'md';
    options.controller = subscribeModalController;
    options.modalParams = data; //列表传递数据
    options.confirmCallback = function(result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function subscribeModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText, ensureBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.ensureBtnText = ensureBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.cancel = cancel;
    ra.close = close;

    //分页相关
    ra.maxSize = 5;
    ra.totalItems = 0;
    ra.currentPage = 1;
    ra.itemPerPage = 10;
    ra.positionStr = 0;

    ra.subDataList = [];

    ra.subscribeMore = subscribeMore;
    // ra.pageChangeFunc = pageChangeFunc;

    subscribeMore();

    function subscribeMore() {
      subscribeModalService.subscribeMore(
        {
          pageSize: ra.itemPerPage,
          positionStr: ra.positionStr,
          tenantId: modalParams.tenantId,
          userId: modalParams.userId
        },
        function(response) {
          var len = response.data && response.data.length || 0
          if (len) {
            ra.subDataList = response.data;
            ra.positionStr = ra.subDataList[len - 1].positionStr + 1;
          } else {
            ra.subDataList = [];
          }
        },
        function(err) {
          ra.subDataList = [];
        }
      );
    }

    // function pageChangeFunc() {
    //   subscribeMore();
    // }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = subscribeModalCtrl;



