/* 风险等级设置 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var packageDetailModalCtrl = require('./modalPopup/packageDetail/controller');
var packageDetailModalService = require('./modalPopup/packageDetail/service');

//setter定义模块
module.exports = angular
  .module('servePackageCheck', [])
  .config(Routing)
  .controller('servePackageCheckCtrl', ctrl)
  .service('servePackageCheckService', service)
  .factory('packageDetailModalCtrl', packageDetailModalCtrl)
  .service('packageDetailModalService', packageDetailModalService).name;



