require ('./style.less');

var modalOptions = require('jsDir/modal_options'),
orderCheckModalTempl = require('./orderCheckModal.html'),
  func = require('func');

  orderCheckModalCtrl.$inject = ['modalService', 'orderCheckModalService'];

function orderCheckModalCtrl(modalService, orderCheckModalService) {
  var vm = this;

  vm.openModal = openModal;

  function openModal(id, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '订单审核';
    options.size = 'md';
    options.template = orderCheckModalTempl;
    options.modalParams = id;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function() {
      angular.isFunction(callback) && callback();
    };

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
    ra.operatorNo = ra.operatorInfo.operatorNo;
    ra.operatorName = ra.operatorInfo.name;
    ra.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
    ra.remark = '';
    
    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    //新增提交
    function submit(type) {
      orderCheckModalService.orderCheck({
        tenant_id: ra.tenantId,
        history_id: ra.modalParams,
        operate_content: type, // 1通过 2不通过
        operator_no: ra.operatorNo,
        operator_name: ra.operatorName,
        remark: ra.remark
      },
      function(result) {
        console.log('result', result);
        modalService.openAlert('提示', result.error_info);
        close();
      },
      function(error) {
        console.log('error', error);
        modalService.openAlert('提示', error.error_info);
      })
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = orderCheckModalCtrl;


