require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  combineModalTempl = require('./combineModal.html');

combineModalCtrl.$inject = ['modalService', 'combineModalService'];

function combineModalCtrl(modalService, combineModalService) {
  var vm = this;

  /*函数声明*/
  vm.openCombineModalFunc = openCombineModalFunc;

  /*函数定义*/
  function openCombineModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '关注历史详情';
    options.template = combineModalTempl;
    options.size = 'md';
    options.controller = defaultController;
    options.modalParams = data; //列表传递数据
    options.confirmCallback = function(result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText, ensureBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.ensureBtnText = ensureBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.cancel = cancel;
    ra.close = close;

    ra.showStore = LOCAL_CONFIG.STORE_MODE; // true：店铺模式，显示投顾，false：个人模式，不显示投顾
    //分页相关
    ra.maxSize = 5;
    ra.totalItems = 0;
    ra.currentPage = 1;
    ra.itemPerPage = 10;
    ra.positionStr = 0;

    ra.tenantId = modalParams.tenant_id;
    ra.combineList = [];

    ra.pageChangeFunc = pageChangeFunc;

    combineCollectQry();

    // 客户关注组合历史查询
    function combineCollectQry() {
      combineModalService.combineCollectQryFunc(
        {
          company_id: ra.tenantId,
          position_str: ra.positionStr,
          request_num: ra.itemPerPage
        },
        function(res) {
          var len = (res.data_list && res.data_list.length) || 0;
          if (len) {
            ra.combineList = res.data_list;
            ra.positionStr = ra.combineList[len - 1].position_str + 1;
          } else {
            ra.combineList = [];
          }
        },
        function(err) {
          ra.combineList = [];
        }
      );
    }

    // 分页
    function pageChangeFunc() {
      combineCollectQry();
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = combineModalCtrl;



