/* 导出 controller*/

require('./style.less');

var func = require('func');
var modalOptions = require('jsDir/modal_options'),
updatePwdModalTempl = require('./updatePwdModal.html');

updatePwdModalCtrl.$inject = ['modalService', 'updatePwdModalService'];

function updatePwdModalCtrl(modalService, updatePwdModalService) {
  var vm = this;
  vm.param = ''; //模态窗参数获取

  /*函数声明*/
  vm.openUpdatePwdModalFunc = openUpdatePwdModalFunc;

  /*函数定义*/
  function openUpdatePwdModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '重置密码';
    options.template = updatePwdModalTempl;
    options.size = 'md';
    options.controller = defaultController;
    vm.param = data; //传递正常投顾与限制投顾参数
    modalService.openModal(options);
  }

  /*controller定义*/
  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.userStatus = vm.param;
    ra.cancelBtnText = cancelBtnText;

    ra.userName = vm.param.aliasName;
    ra.newPwd = '';
    ra.renewPwd = '';
    ra.resetPwd = resetPwd;
    ra.cancel = cancel;
    ra.close = close;

    function resetPwd() {
      if (!ra.newPwd) {
        modalService.openAlert('提示', '请输入新密码');
        return;
      }
      if (ra.newPwd !== ra.renewPwd) {
        modalService.openAlert('提示', '两次密码不一致！');
        return;
      }
      updatePwdModalService.userSetPwd({
        sign: "17AD57E731C3CF99EC72703976577EA4",
        source: "cloudbroker_3",
        user_id: vm.param.userId,
        auth_password: func.MD5(func.MD5(ra.newPwd)).toUpperCase()
      }, function(res) {
        modalService.openAlert('提示', res.error_info);
        close();
      }, function(err) {
        modalService.openAlert('提示', err.error_info);
      })
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = updatePwdModalCtrl;



