require('locale_zh');

serviceProduct02Ctrl.$inject = ['$sce'];

function serviceProduct02Ctrl($sce) {
  var vm = this;
  vm.height = window.innerHeight - 100;
  vm.paddingTop = vm.height / 2 - vm.imgHeight / 2;
  vm.access_token = sessionStorage.getItem('access_token');
  vm.url = $sce.trustAsResourceUrl(LOCAL_CONFIG.SERVICEPRODUCT02 + '?access_token=' + vm.access_token);
}

module.exports = serviceProduct02Ctrl;


