QACheckService.$inject = ['api'];
function QACheckService(api) {
  var vm = this;

  vm.listAnswerOnManageFunc = listAnswerOnManageFunc;
  vm.askAutoCheckQryFunc = askAutoCheckQryFunc;
  vm.askAuditCheckFunc = askAuditCheckFunc;

  /**
   * 问答审核列表
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function listAnswerOnManageFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'list_answer_on_management',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  /**
   * 查询开启关闭审核状态
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function askAutoCheckQryFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'ask_auto_check_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  /**
   * 开启关闭审核
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function askAuditCheckFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'ask_auto_check',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = QACheckService;


