/* 课程新增、课程编辑 */

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  editCourseModalTempl = require('./editCourseModal.html'),
  func = require('func');

editCourseModalCtrl.$inject = ['modalService', 'editCourseModalService'];

function editCourseModalCtrl(modalService, editCourseModalService) {
  var vm = this;

  /*函数声明*/
  vm.openeditCourseModalFunc = openeditCourseModalFunc;

  /*函数定义*/
  function openeditCourseModalFunc(allCourse, date, info, tenantId, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '直播管理';
    options.template = editCourseModalTempl;
    options.controller = defaultController;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };
    //获取入参
    if (info && date) {
      vm.courseInfo = info;
    } else {
      vm.courseInfo = '';
    }
    vm.allCourse = allCourse;
    vm.dateInfo = date || '';
    vm.tenantId = tenantId;

    modalService.openModal(options);
  }

  /*controller定义*/
  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;

    if (vm.courseInfo) {
      ra.isShowCancelBtn = true;
    } else {
      ra.isShowCancelBtn = isShowCancelBtn; //默认为false
    }
    ra.submitText = '确定';
    ra.cancelBtnText = '删除';
    ra.operatorNo = JSON.parse(sessionStorage.getItem("operatorInfo")).operatorNo;
    ra.operatorName = JSON.parse(sessionStorage.getItem("operatorInfo")).name;

    /* 日期选择 */
    ra.popupStartTimeOpened = false;
    ra.popupEndTimeOpened = false;
    ra.dateOptions = {
      startingDay: 1,
      showWeeks: false,
      initDate: vm.dateInfo
    };

    ra.liveTopic = vm.courseInfo.topic;
    ra.startDate = vm.dateInfo;
    ra.startTime = vm.courseInfo.start_time || '09:00:00';  //初始化开始时间
    ra.endTime = vm.courseInfo.end_time || '15:00:00';  //初始化结束时间
    ra.shopList = [];                        //店铺列表信息
    ra.selectedShop = vm.courseInfo.broker_id || ''; //选中的店铺
    ra.liveIntro = vm.courseInfo.intro;
    ra.errorText = '';

    ra.openStartTimeFunc = openStartTimeFunc; //开始日期选择
    ra.openEndTimeFunc = openEndTimeFunc; //结束日期选择
    ra.addCourseSave = addCourseSave; //新增课程信息
    ra.submitCourse = submitCourse; //提交课程信息
    ra.deleteCourse = deleteCourse; //删除当前课程
    ra.cancel = cancel;
    ra.close = close;

    searchBrokerInfo();

    //获取店铺列表信息
    function searchBrokerInfo() {
      editCourseModalService.getOperatorBrokerList(
        {
          page_count: 10000,
          page_no: 1,
          tenant_id: vm.tenantId
        },
        function (result) {
          ra.shopList = result.data_list;
        }
      )
    }

    //新增课程
    function addCourseSave() {
      if (!ra.liveTopic) {
        modalService.openAlert("提示", "请输入直播话题");
        return;
      }
      if (!ra.selectedShop) {
        modalService.openAlert("提示", "请选择投顾");
        return;
      }
      editCourseModalService.funcChatTimetableSave(
        {
          topic_title: ra.liveTopic,
          broker_id: ra.selectedShop,
          start_time: func.getUnixTimeStamp(ra.startDate + '-' + ra.startTime) / 1000,
          end_time: func.getUnixTimeStamp(ra.startDate + '-' + ra.endTime) / 1000,
          topic_abstract: ra.liveIntro,
          operator_no: ra.operatorNo,
          operator_name: ra.operatorName, 
          tenant_id: vm.tenantId
        },
        function (result) {
          modalService.openAlert("成功", "直播预约成功");
          close();
        },
        function(err) {
          // 如果存在冲突，则查询冲突直播
          if (err.error_no == 45100) {
            funcChatTimetableListFunc();
          } else {
            modalService.openAlert("提示", err.error_info);
          }
        }
      )
    }

    //提交更新
    function submitCourse() {
      if (!ra.liveTopic) {
        modalService.openAlert("提示", "请输入直播话题");
        return;
      }
      if (!ra.selectedShop) {
        modalService.openAlert("提示", "请选择投顾");
        return;
      }
      editCourseModalService.funcChatTimetableUpdate(
        {
          topic_title: ra.liveTopic,
          broker_id: ra.selectedShop,
          chat_time_table_id: vm.courseInfo.id,
          end_time: func.getUnixTimeStamp(ra.startDate + '-' + ra.endTime) / 1000,
          start_time: func.getUnixTimeStamp(ra.startDate + '-' + ra.startTime) / 1000,
          topic_abstract: ra.liveIntro,
          operator_no: ra.operatorNo,
          operator_name: ra.operatorName,
          tenant_id: vm.tenantId
        },
        function (result) {
          modalService.openAlert("成功","直播预约修改成功");
          close();
        },
        function(err) {
          // 如果存在冲突，则查询冲突直播
          if (err.error_no == 45100) {
            funcChatTimetableListFunc();
          } else {
            modalService.openAlert("提示", err.error_info);
          }
        }
      )
    }

    // 查询冲突直播
    function funcChatTimetableListFunc() {
      ra.errorText = '';
      editCourseModalService.funcChatTimetableList(
        {
          tenant_id: vm.tenantId,
          start_time: func.getUnixTimeStamp(ra.startDate + '-' + ra.startTime) / 1000,
          end_time: func.getUnixTimeStamp(ra.startDate + '-' + ra.endTime) / 1000,
        },
        function(result) {
          var dataList = result.data_list || [];
          if (dataList.length) {
            dataList.forEach((item, index) => {
              if (index == 0) {
                ra.errorText += '“' + item.topic_title + "”";
              } else {
                ra.errorText += '、' + '“' + item.topic_title + "”";
              }
            });
            modalService.openAlert("提示","提交失败，与" + ra.errorText + "冲突。");
          }
        },
        function(err) {
          modalService.openAlert("提示", err.error_info);
        }
      );
    }

    //删除课程
    function deleteCourse() {
      editCourseModalService.funChatTimetableDel(
        {
          chat_time_table_ids: vm.courseInfo.id,
          operator_no: ra.operatorNo,
          operator_name: ra.operatorName,
          tenant_id: vm.tenantId
        },
        function (result) {
          modalService.openAlert("提示", result.error_info);
          close();
        },
        function (result) {
          modalService.openAlert("提示", result.error_info);
        }
      )
    }

    function openStartTimeFunc() {
      ra.popupStartTimeOpened = true;
    };

    function openEndTimeFunc() {
      ra.popupEndTimeOpened = true;
    };

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = editCourseModalCtrl;



