liveBgSettingService.$inject = ['api'];
function liveBgSettingService(api) {
  var vm = this;

  vm.getBgListFunc = getBgListFunc;
  vm.updatePicFunc = updatePicFunc;

  function getBgListFunc(data, cb1, cb2) {
    api.postRequest ({
      prefix_type: 'vipstu',
      url: 'func_live_bg_list',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  function updatePicFunc(data, cb1, cb2) {
    api.postRequest ({
      prefix_type: 'vipstu',
      url: 'func_live_bg_update',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = liveBgSettingService;


