/* 直播背景图设置 */

liveBgSettingRoute.$inject = ['$stateProvider'];

function liveBgSettingRoute($stateProvider) {
  $stateProvider.state('home.liveBgSetting', {
    url: '/liveBgSetting',
    templateUrl: './views/liveBgSetting.html',
    controller: 'liveBgSettingCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = liveBgSettingRoute;



