/* 直播管理 module */

require('locale_zh');
var func = require('func');
liveManageCtrl.$inject = ['modalService', 'liveManageService', 'editCourseModalCtrl', 'dayCourseModalCtrl'];

function liveManageCtrl(modalService, liveManageService, editCourseModalCtrl, dayCourseModalCtrl) {
  var vm = this;

  vm.memberNum = 0; // 直播人数（默认0，最大100000）
  vm.curYear = ''; //当日年份
  vm.curMonth = ''; //当日月份
  vm.curWeek = ''; //当日周
  vm.calendarData = []; //当月课程数组
  vm.calendarHtml = ''; //写入html日期
  vm.startTime = ''; //入参，开始时间
  vm.endTime = ''; //入参，结束时间
  vm.tenantId = JSON.parse(sessionStorage.getItem('packHead')).company_id; //券商入参

  vm.lastDateArray = []; //上月剩余日期
  vm.currentDateArray = []; //本月日期
  vm.nextDateArray = []; //下月初始日期

  vm.nextMonthFunc = nextMonthFunc;
  vm.prevMonthFunc = prevMonthFunc;
  vm.openModal = openModal;
  vm.openDayCourseModalFunc = openDayCourseModalFunc;
  vm.setMemberFunc = setMemberFunc;

  init();

  //初始化
  function init() {
    //获取年月
    var date = new Date();
    vm.curYear = date.getFullYear();
    vm.curMonth = date.getMonth() + 1;

    // 获取直播参与人数
    manageOpLiveChatNumFunc(0);
    //获取直播课程列表
    getChatTimeTableList();
  }

  //设置直播人数
  function setMemberFunc() {
    var reg = /^\d+$/;
    if (reg.test(vm.memberNum) && vm.memberNum <= 100000) {
      manageOpLiveChatNumFunc(1);
    } else {
      modalService.openAlert("提示", "直播人数范围应在0~100000");
    }
  }

  // 设置直播参与人数 param：0为查询，1为更新
  function manageOpLiveChatNumFunc(param) {
    var data = {};
    if (param) {
      data = {
        tenant_id: vm.tenantId,
        update: true,
        live_chat_num: vm.memberNum
      };
    } else {
      data = {
        tenant_id: vm.tenantId
      };
    }
    liveManageService.manageOpLiveChatNum(
      data,
      function(result) {
        vm.memberNum = result.live_chat_num;
        if (param) {
          modalService.openAlert("成功", "设置成功");
        }
      }
    );
  }

  //获取直播课程列表
  function getChatTimeTableList(year, month) {
    if (year == undefined || month == undefined) {
      //初始化时间入参
      var dateMonth = new Date(vm.curYear, vm.curMonth, 0);
      vm.startTime = vm.curYear + '-' + addZero(vm.curMonth) + '-01 00:00:00';
      vm.endTime = vm.curYear + '-' + addZero(vm.curMonth) + '-' + dateMonth.getDate() + ' 23:59:59';
    } else {
      //初始化时间入参
      var dateMonth = new Date(year, month, 0);
      vm.startTime = year + '-' + addZero(month) + '-01 00:00:00';
      vm.endTime = year + '-' + addZero(month) + '-' + dateMonth.getDate() + ' 23:59:59';
      vm.curYear = year;
      vm.curMonth = month;
    }
    //初始化日期数组
    for (var i = 1; i <= dateMonth.getDate(); i++) {
      vm.calendarData[vm.curYear + addZero(vm.curMonth) + addZero(i)] = [];
    }
    liveManageService.funcChatTimetableList(
      {
        tenant_id: vm.tenantId,
        start_time: func.getUnixTimeStamp(vm.startTime) / 1000,
        end_time: func.getUnixTimeStamp(vm.endTime) / 1000
      },
      function(result) {
        for (var i = 0; i < result.data_list.length; i++) {
          var one = {};
          one.date = func.formatDate(new Date(result.data_list[i].end_time * 1000), 'yyyy-MM-dd');
          one.id = result.data_list[i].chat_time_table_id;
          one.name = result.data_list[i].broker_name;
          one.topic = result.data_list[i].topic_title;
          one.broker_id = result.data_list[i].broker_id;
          one.start_time = func.formatDate(result.data_list[i].start_time * 1000, 'yyyy:MM:dd HH:mm:ss').split(' ')[1];
          one.end_time = func.formatDate(result.data_list[i].end_time * 1000, 'yyyy:MM:dd HH:mm:ss').split(' ')[1];
          one.intro = result.data_list[i].topic_abstract;
          vm.calendarData[func.formatDate(new Date(result.data_list[i].end_time * 1000), 'yyyyMMdd')].push(one);
        }
        if (year == undefined || month == undefined) {
          showDate(vm.curYear, vm.curMonth, vm.calendarData);
        } else {
          showDate(year, month, vm.calendarData);
        }
      },
      function() {
        showDate(vm.curYear, vm.curMonth, vm.calendarData);
      }
    );
  }

  //渲染日历数据
  function showDate(yyyy, mm, calendarData) {
    vm.lastDateArray = [];
    vm.currentDateArray = [];
    vm.nextDateArray = [];
    vm.curYear = parseInt(yyyy);
    vm.curMonth = parseInt(mm);
    var dd = new Date(parseInt(yyyy), parseInt(mm), 0);
    var daysCount = dd.getDate(); //当月天数
    vm.curWeek = new Date(parseInt(yyyy) + '/' + parseInt(mm) + '/' + 1).getDay(); //本月第一天是周几
    var lastMonth; //上一月天数
    var nextMonth; //下一月天数
    if (parseInt(mm) == 1) {
      lastMonth = new Date(parseInt(yyyy) - 1, parseInt(12), 0).getDate();
    } else {
      lastMonth = new Date(parseInt(yyyy), parseInt(mm) - 1, 0).getDate();
    }
    if (parseInt(mm) == 12) {
      nextMonth = new Date(parseInt(yyyy) + 1, parseInt(1), 0).getDate();
    } else {
      nextMonth = new Date(parseInt(yyyy), parseInt(mm) + 1, 0).getDate();
    }

    //填充本月日期
    for (var d = 1; d <= daysCount; d++) {
      var dayInfo = {};
      dayInfo.dateAll = yyyy + '-' + addZero(mm) + '-' + addZero(d);
      dayInfo.date = d;
      vm.currentDateArray.push(dayInfo);
    }

    for (var i = 0; i < daysCount; i++) {
      //计算上月空格数
      if (i % 7 == 0) {
        if (i < 7) {
          //只执行一次
          var lastDateArray = [];
          for (var j = 0; j < vm.curWeek; j++) {
            lastDateArray.push(lastMonth - j);
          }
          lastDateArray.reverse();
          vm.lastDateArray = lastDateArray;
        }
      }

      //获取每天课程信息
      vm.currentDateArray[i].course = [];
      var index = yyyy + addZero(mm) + addZero(i + 1);
      var dataDetail = calendarData[index];
      for (var l = 0; l < dataDetail.length; l++) {
        vm.currentDateArray[i].course.push(dataDetail[l]);
      }
    }

    //当月中下月剩余天数
    if (7 - ((daysCount + lastDateArray.length) % 7) < 7) {
      for (var k = 0; k < 7 - ((daysCount + lastDateArray.length) % 7); k++) {
        vm.nextDateArray.push(k + 1);
      }
    }

    var currentdate = new Date();
    var currentYear = currentdate.getFullYear();
    var currentMonth = currentdate.getMonth() + 1;

    if (yyyy == currentYear && mm == currentMonth) {
      showToday('add');
    } else {
      showToday('remove');
    }
  }

  //渲染今日样式
  function showToday(type) {
    var mydate = new Date();
    var today = mydate.getDate();
    if (type == 'add') {
      var t = setTimeout(function() {
        //给今日加class
        if (mydate.getFullYear() == vm.curYear) {
          if (mydate.getMonth() + 1 == vm.curMonth) {
            $('.f-rili-table .f-number')
              .eq(today - 1)
              .addClass('f-today');
          }
        }
      }, 1);
    } else {
      var t = setTimeout(function() {
        $('.f-rili-table .f-number')
          .eq(today - 1)
          .removeClass('f-today');
      }, 1);
    }
  }

  //下个月
  function nextMonthFunc() {
    if (vm.curMonth == 12) {
      vm.curYear = vm.curYear + 1;
      vm.curMonth = 1;
    } else {
      vm.curMonth += 1;
    }
    getChatTimeTableList(vm.curYear, vm.curMonth);
  }

  //上个月
  function prevMonthFunc() {
    if (vm.curMonth == 1) {
      vm.curYear = vm.curYear - 1;
      vm.curMonth = 12;
    } else {
      vm.curMonth = vm.curMonth - 1;
    }
    getChatTimeTableList(vm.curYear, vm.curMonth);
  }

  //打开弹窗
  function openModal(allCourse, date, course) {
    editCourseModalCtrl.openeditCourseModalFunc(allCourse, date, course, vm.tenantId, function() {
      init();
    });
  }

  // 打开弹窗
  function openDayCourseModalFunc(allCourse) {
    dayCourseModalCtrl.openDayCourseModalFunc(allCourse);
  }

  //格式化月份和日期
  function addZero(d) {
    return d < 10 ? '0' + d : '' + d;
  }

  //格式化时间
  function formatToTime2(t) {
    var reggie = /(\d{4})-(\d{2})-(\d{2}) (\d{2}):(\d{2}):(\d{2})/;
    var dateArray = reggie.exec(t);
    return dateArray[4] + ':' + dateArray[5] + ':' + dateArray[6];
  }

  //格式化日期
  function formatToDate(t) {
    var reggie = /(\d{4})-(\d{2})-(\d{2}) (\d{2}):(\d{2}):(\d{2})/;
    var dateArray = reggie.exec(t);
    return dateArray[1] + '' + dateArray[2] + '' + dateArray[3];
  }
}

module.exports = liveManageCtrl;



