require ('./style.less');

var modalOptions = require('jsDir/modal_options'),
addRiskLevelModalTempl = require('./addRiskLevel.html'),
  func = require('func');

  addRiskLevelModalCtrl.$inject = ['modalService', 'addRiskLevelModalService'];

function addRiskLevelModalCtrl(modalService, addRiskLevelModalService) {
  var vm = this;

  vm.openAddRiskLevelModalFunc = openAddRiskLevelModalFunc;

  function openAddRiskLevelModalFunc(callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '添加等级';
    options.size = 'md';
    options.template = addRiskLevelModalTempl;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function () {
      angular.isFunction(callBack) && callBack();
    };

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
    ra.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
    ra.levelName = '';
    ra.levelIntro = '';
    ra.remark = '';
    
    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    //新增提交
    function submit() {
      addRiskLevelModalService.riskLevelAddFunc({
        tenant_id: ra.tenantId,
        operator_no: ra.operatorInfo.operatorNo,
        operator_name: ra.operatorInfo.name,
        risk_level_name: ra.levelName,
        risk_level_desc: ra.levelIntro,
        remark: ra.remark
      }, function(res) {
        modalService.openAlert('提示', res.error_info);
        close();
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('提示', err.error_info);
      });
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = addRiskLevelModalCtrl;


