require('locale_zh');

jingteng01Ctrl.$inject = ['$sce'];

function jingteng01Ctrl($sce) {
  var vm = this;
  vm.height = window.innerHeight - 100;
  vm.paddingTop = vm.height / 2 - vm.imgHeight / 2;
  vm.access_token = sessionStorage.getItem('access_token');
  vm.url = $sce.trustAsResourceUrl(LOCAL_CONFIG.JINGTENG01 + '?access_token=' + vm.access_token);
}

module.exports = jingteng01Ctrl;


