/* 导出 controller*/

require('./style.less');
window.ExcellentExport = require('ExcellentExport'); //导出
var modalOptions = require('jsDir/modal_options'),
investorDownloadModalTempl = require('./investorDownloadModal.html');

  investorDownloadModalCtrl.$inject = ['modalService', 'investorDownloadModalService'];

function investorDownloadModalCtrl(modalService, investorDownloadModalService) {
  var vm = this;
  vm.param = ''; //模态窗参数获取

  /*函数声明*/
  vm.openDownModalFunc = openDownModalFunc;

  /*函数定义*/
  function openDownModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '确认导出';
    options.template = investorDownloadModalTempl;
    options.size = 'md';
    options.controller = downModalController;
    vm.param = data; //传递正常投顾与限制投顾参数
    modalService.openModal(options);
  }

  /*controller定义*/
  function downModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.userStatus = vm.param;
    ra.cancelBtnText = cancelBtnText;
    ra.content = '下载进度...0%';
    ra.customerData = '';
    ra.downFlag = true;

    ra.cancel = cancel;
    ra.close = close;

    init();

    function init() {
      investorDownloadModalService.userListFunc(
        {
          status: ra.userStatus
        },
        function(response) {
          ra.customerData = response.data;
          ra.downFlag = false;
          ra.content = '下载进度...100%';
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = investorDownloadModalCtrl;



