/* 主页 module */

homeService.$inject = ['api'];

function homeService(api) {
  var vm = this;

  vm.allBranchQry = allBranchQry;
  vm.operatorSelfQry = operatorSelfQry;
  vm.menuQry = menuQry;

  //获取组织权限列表、权限管理
  function allBranchQry(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //操作员信息查询
  function operatorSelfQry(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorSelfQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //查询导航列表
  function menuQry(data, succFunc) {
    api.postRequest({
      prefix_type: 'menuService',
      url: 'menuQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(succFunc) && succFunc(result);
      }
    })
  }


  return vm;
}

module.exports = homeService;



