/* 推荐投顾规则 module */

recommendInvestRoute.$inject = ['$stateProvider'];

function recommendInvestRoute($stateProvider) {
  $stateProvider.state('home.recommendInvest', {
    url: '/recommendInvest',
    templateUrl: './views/recommendInvest.html',
    controller: 'recommendInvestCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = recommendInvestRoute;



