/* 金融产品池管理 module */

productPoolManageRoute.$inject = ['$stateProvider'];

function productPoolManageRoute($stateProvider) {
  $stateProvider.state('home.productPoolManage', {
    url: '/productPoolManage',
    templateUrl: './views/productPoolManage.html',
    controller: 'productPoolManageCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = productPoolManageRoute;



