/* 客户管理 module */
customerManageService.$inject = ['api'];

function customerManageService(api) {
  var vm = this;

  vm.userListFunc = userListFunc;  //正常客户列表查询
  vm.performQryFunc = performQryFunc; //客户绩效查询
  vm.subQryFunc = subQryFunc;  //客户订阅查询
  // vm.signQryFunc = signQryFunc;  //客户签约查询
  vm.orderManageFunc = orderManageFunc; // 订单历史查询
  vm.combineCollectQryFunc = combineCollectQryFunc; // 关注的组合查询
  vm.frozenFunc = frozenFunc; //限制客户
  vm.frozenUnFunc = frozenUnFunc; //解除限制客户
  vm.silentFunc = silentFunc;//禁言

  function userListFunc(data,cb1,cb2) {
    api.postRequest ({
      prefix_type: 'userCenter',
      url: 'func_user_page_query',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      }
    })
  }

  function performQryFunc(data,cb1,cb2,cb3) {
    api.postRequest ({
      prefix_type: 'businessService',
      url: 'businessUserPerformQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      },
      disconnectCallback: function (response) {
        angular.isFunction(cb3) && cb3(response);
      }
    })
  }

  function subQryFunc(data,cb1,cb2,cb3) {
    api.postRequest ({
      prefix_type: 'businessService',
      url: 'businessUserSubQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      },
      disconnectCallback: function (response) {
        angular.isFunction(cb3) && cb3(response);
      }
    })
  }

  // function signQryFunc(data,cb1,cb2,cb3) {
  //   api.postRequest ({
  //     prefix_type: 'businessService',
  //     url: 'businessUserSignQry',
  //     data: data,
  //     successCallback: function (result) {
  //       angular.isFunction(cb1) && cb1(result);
  //     },
  //     failCallback: function (response) {
  //       angular.isFunction(cb2) && cb2(response);
  //     },
  //     disconnectCallback: function (response) {
  //       angular.isFunction(cb3) && cb3(response);
  //     }
  //   })
  // }

  function orderManageFunc(data,cb1,cb2,cb3) {
    api.postRequest ({
      prefix_type: 'vipstu',
      url: 'func_order_manage',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      },
      disconnectCallback: function (response) {
        angular.isFunction(cb3) && cb3(response);
      }
    })
  }

  function combineCollectQryFunc(data,cb1,cb2,cb3) {
    api.postRequest ({
      prefix_type: 'vipstu',
      url: 'func_combine_collect_info_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      },
      disconnectCallback: function (response) {
        angular.isFunction(cb3) && cb3(response);
      }
    })
  }

  function frozenFunc(data,cb1) {
    api.postRequest ({
      prefix_type: 'businessService',
      url: 'businessUserFrozen',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }

  function frozenUnFunc(data,cb1) {
    api.postRequest ({
      prefix_type: 'businessService',
      url: 'businessUserUnfrozen',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }

  function silentFunc(data,cb1) {
    api.postRequest ({
      prefix_type: 'userCenter',
      url: 'func_update_sendmsg_status',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }


  return vm;
}

module.exports = customerManageService;



