riskLevelSettingRoute.$inject = ['$stateProvider'];

function riskLevelSettingRoute($stateProvider) {
  $stateProvider.state('home.riskLevelSetting', {
    url: '/riskLevelSetting',
    templateUrl: './views/riskLevelSetting.html',
    controller: 'riskLevelSettingCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = riskLevelSettingRoute;



