/* 用户信息弹窗 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  userInfoModalTempl = require('./userInfoModal.html');

userInfoModalCtrl.$inject = ['modalService','userInfoModalService', 'editInfoModalCtrl', 'resetPasswordModalCtrl'];

function userInfoModalCtrl(modalService, userInfoModalService, editInfoModalCtrl, resetPasswordModalCtrl) {
  var vm = this;

  vm.userInfoData = {};//用户信息

  /*函数声明*/
  vm.openUserInfoModalFunc = openUserInfoModalFunc;

  /*函数定义*/
  function openUserInfoModalFunc(userInfoData, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '信息查看';
    options.template = userInfoModalTempl;
    options.controller = userInfoModalController;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };

    vm.userInfoData = userInfoData;

    modalService.openModal(options);
  }

  /*controller定义*/
  function userInfoModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.userName = vm.userInfoData.userName;
    ra.telephone = vm.userInfoData.telephone;
    if (vm.userInfoData.remark != ' ' && vm.userInfoData.remark.length != 0) {
      ra.remark = vm.userInfoData.remark;
    } else {
      ra.remark = '这个人很懒，什么都没留下！';
    }

    ra.edit = edit;
    ra.reset = reset;
    ra.close = close;
    ra.cancel = cancel;

    //进入编辑用户信息弹窗
    function edit() {
      editInfoModalCtrl.openEditInfoModalFunc(vm.userInfoData, function () {
        close();
      });
    }

    //进入修改密码弹窗
    function reset() {
      resetPasswordModalCtrl.openResetPasswordModalFunc();
      close();
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = userInfoModalCtrl;




