combineModalService.$inject = ['api'];
function combineModalService(api) {
  var vm = this;

  vm.combineCollectQryFunc = combineCollectQryFunc;

  function combineCollectQryFunc(data,cb1,cb2,cb3) {
    api.postRequest ({
      prefix_type: 'vipstu',
      url: 'func_combine_collect_info_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      },
      disconnectCallback: function (response) {
        angular.isFunction(cb3) && cb3(response);
      }
    })
  }
  
  return vm;
}

module.exports = combineModalService;


