require('locale_zh');

jingteng04Ctrl.$inject = ['$sce'];

function jingteng04Ctrl($sce) {
  var vm = this;
  vm.height = window.innerHeight - 100;
  vm.paddingTop = vm.height / 2 - vm.imgHeight / 2;
  vm.access_token = sessionStorage.getItem('access_token');
  vm.url = $sce.trustAsResourceUrl(LOCAL_CONFIG.JINGTENG04 + '?access_token=' + vm.access_token);
}

module.exports = jingteng04Ctrl;


