unifyPayment03Route.$inject = ['$stateProvider'];

function unifyPayment03Route($stateProvider) {
    $stateProvider
        .state('home.unifyPayment03', {
            url: '/unifyPayment03',
            templateUrl: './views/unifyPayment03.html',
            controller: 'unifyPayment03Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = unifyPayment03Route;



