serviceProduct03Route.$inject = ['$stateProvider'];

function serviceProduct03Route($stateProvider) {
    $stateProvider
        .state('home.serviceProduct03', {
            url: '/serviceProduct03',
            templateUrl: './views/serviceProduct03.html',
            controller: 'serviceProduct03Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = serviceProduct03Route;



