changeBranchModalService.$inject = ['api'];
function changeBranchModalService(api) {
  var vm = this;

  vm.branchMod = branchMod;

  //修改组织信息
  function branchMod(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchMod',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

}

module.exports = changeBranchModalService;



