tagManageModalService.$inject = ['api'];

function tagManageModalService(api) {
  var vm = this;

  vm.funcBrokertagUpdate = funcBrokertagUpdate;

  function funcBrokertagUpdate(data, cb1) {
    api.postRequest({
      prefix_type: 'ifs',
      url: 'manage/func_brokertag_update',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }
}

module.exports = tagManageModalService;



