// 业绩统计查询查询

require('locale_zh');
var func = require('func');

achievementStatisticsQryCtrl.$inject = ['modalService', 'achievementStatisticsQryService', 'achievementStatisticsDownloadCtrl'];

function achievementStatisticsQryCtrl(modalService, achievementStatisticsQryService, achievementStatisticsDownloadCtrl) {
  var vm = this;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  // tab切换
  vm.currentSession = 0;
  // 投顾名称
  vm.brokerName = '';
  // 机构列表
  vm.branchList = []; 
  vm.branchNo = '';
  // 服务类型列表
  vm.serviceTypeList = [
    {name: '提问', value: 1},
    {name: '观点', value: 2},
    {name: '组合', value: 4},
    {name: '量化模型', value: 90},
    {name: '智能策略', value: 94}
  ];
  vm.serviceType = '';

  /* 日期选择 */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };
  // vm.startTime = new Date();
  // vm.endTime = '';

  vm.dataList = [];
  vm.allData = {};
  // vm.dateStr = func.formatDate(vm.startTime, 'yyyy-MM-dd') + ' — ' + func.formatDate(vm.endTime || new Date(), 'yyyy-MM-dd');

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 3; // 三个投顾、三个机构

  vm.setCurrentSession = setCurrentSession;
  vm.searchFunc = searchFunc;
  vm.openStartTimeFunc = openStartTimeFunc; //开始日期选择
  vm.openEndTimeFunc = openEndTimeFunc; //结束日期选择
  vm.pageChangeFunc = pageChangeFunc;
  vm.download = download;

  getBranchQryInOrderFunc();
  getBranchAchievementStatisticQryFunc();

  function getBranchQryInOrderFunc() {
    achievementStatisticsQryService.branchQryInOrderFunc({
      tenant_id: vm.tenantId
    },function(res) {
      if (res.data_list && res.data_list.length) {
        var dataList = res.data_list;
        vm.branchList = dataList.map(function(item) {
          return {
            name: item.branch_name,
            value: item.branch_no
          }
        });
      }
    }, function(err) {
      console.log('err', err);
    });
  }

  function getBranchAchievementStatisticQryFunc() {
    // if (!vm.startTime) {
    //   modalService.openAlert('提示', '请输入起始日期');
    //   return;
    // }
    achievementStatisticsQryService.branchAchievementStatisticQryFunc({
      tenant_id: vm.tenantId,
      branch_no: vm.branchNo,
      service_type: vm.serviceType,
      // start_time: parseInt(Number(vm.startTime) / 1000),
      // end_time: parseInt(Number(vm.endTime) / 1000) || '',
      page_no: vm.currentPage,
      page_size: vm.itemPerPage,
      // page_size: vm.serviceType ? 6 : 3 // 有服务类型时传6（6 * 2行），无服务类型时传3（3 * 4行）
    },function(res) {
      console.log('res', res);
      vm.totalItems = res.total_num || 0;
      handleDataListFunc(res.data_list, res.summary);
    }, function(err) {
      modalService.openAlert('提示', '请求异常');
      console.log('err', err);
    });
  }

  function getBrokerAchievementStatisticQryFunc() {
    // if (!vm.startTime) {
    //   modalService.openAlert('提示', '请输入起始日期');
    //   return;
    // }
    achievementStatisticsQryService.brokerAchievementStatisticQryFunc({
      tenant_id: vm.tenantId,
      branch_no: vm.branchNo,
      service_type: vm.serviceType,
      broker_name: vm.brokerName,
      // start_time: parseInt(Number(vm.startTime) / 1000),
      // end_time: parseInt(Number(vm.endTime) / 1000) || '',
      page_no: vm.currentPage,
      page_size: vm.itemPerPage,
      // page_size: vm.serviceType ? 6 : 3 // 有服务类型时传6（6 * 2行），无服务类型时传3（3 * 4行）
    },function(res) {
      console.log('res', res);
      vm.totalItems = res.total_num || 0;
      handleDataListFunc(res.data_list, res.summary);
    }, function(err) {
      console.log('err', err);
    });
  }

  function handleDataListFunc(dataList, summaryList) {
    vm.dataList = [];
    // 非汇总数据
    if (dataList.length) {
      dataList.forEach(function(item) {
        if (item.achievement_statistics && item.achievement_statistics.length) {
          item.achievement_statistics.forEach(function(per) {
            vm.dataList.push({
              branchName: item.branch_name,
              brokerName: item.broker_name,
              serviceType: per.title,
              onlineNum: per.up_num,
              nosubscribeNum: per.no_subscription_num,
              checkingNum: per.wait_audit_num,
              checkFailNum: per.refuse_num,
              tradePrice: per.total_price
            })
          })
        } else {
          vm.dataList.push({
            branchName: item.branch_name,
            brokerName: item.broker_name
          })
        }
      })
    }
    // 汇总数据
    vm.allData = summaryList[0] || {};
  }

  function download() {
    achievementStatisticsDownloadCtrl.openDownModalFunc({
      current_session: vm.currentSession,
      tenant_id: vm.tenantId,
      branch_no: vm.branchNo,
      service_type: vm.serviceType,
      broker_name: vm.brokerName,
      page_no: 1,
      page_size: vm.totalItems
    })
  }

  function setCurrentSession(type) {
    // vm.dateStr = func.formatDate(vm.startTime, 'yyyy-MM-dd') + ' — ' + func.formatDate(vm.endTime || new Date(), 'yyyy-MM-dd');
    vm.currentPage = 1;
    vm.currentSession = type;
    vm.branchNo = '';
    vm.brokerName = '';
    // vm.startTime = new Date();
    // vm.endTime = '';
    if (vm.currentSession == 0) {
      getBranchAchievementStatisticQryFunc();
    } else {
      getBrokerAchievementStatisticQryFunc();
    }
  }

  function searchFunc() {
    // vm.dateStr = func.formatDate(vm.startTime, 'yyyy-MM-dd') + ' — ' + func.formatDate(vm.endTime || new Date(), 'yyyy-MM-dd');
    vm.currentPage = 1;
    if (vm.currentSession == 0) {
      getBranchAchievementStatisticQryFunc();
    } else {
      getBrokerAchievementStatisticQryFunc();
    }
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  };

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function pageChangeFunc() {
    if (vm.currentSession == 0) {
      getBranchAchievementStatisticQryFunc();
    } else {
      getBrokerAchievementStatisticQryFunc();
    }
  }

}

module.exports = achievementStatisticsQryCtrl; 


