unifyPayment04Route.$inject = ['$stateProvider'];

function unifyPayment04Route($stateProvider) {
    $stateProvider
        .state('home.unifyPayment04', {
            url: '/unifyPayment04',
            templateUrl: './views/unifyPayment04.html',
            controller: 'unifyPayment04Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = unifyPayment04Route;



