// 观点标签设置

require('locale_zh');

var func = require('func');

viewpointCheckCtrl.$inject = ['modalService', 'viewpointCheckService', 'viewpointCheckModalCtrl', 'viewpointRemarkModalCtrl', 'viewpointDetailModalCtrl'];

function viewpointCheckCtrl(modalService, viewpointCheckService, viewpointCheckModalCtrl, viewpointRemarkModalCtrl, viewpointDetailModalCtrl) {
  var vm = this;

  vm.tenant_id = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.branchNo = JSON.parse(sessionStorage.getItem('branchNoData'));

  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.ifOpenCheck = false; // 是否勾选开启审核
  vm.keyword = '';
  // vm.checkType = null; // 选中项审核状态
  vm.viewCheckList = [];

  vm.changeCheckStatus = changeCheckStatus;
  vm.searchFunc = searchFunc;
  vm.pageChangeFunc = pageChangeFunc;
  vm.withdrawViewpointFunc = withdrawViewpointFunc;
  vm.openViewpointCheckModal = openViewpointCheckModal; // 开启观点审核弹窗
  vm.openViewpointRemarlModal = openViewpointRemarlModal; // 开启审核意见弹窗
  vm.openViewpointDetailModal = openViewpointDetailModal; // 开启观点详情弹窗

  viewCheckListFunc();
  viewCheckStatus();
  
  function viewCheckListFunc(index) {
    viewpointCheckService.viewCheckList({
      // audit_states: vm.checkType,
      key_word: vm.keyword,
      page_no: vm.currentPage,
      page_count: vm.itemPerPage,
      tenant_id: vm.tenant_id,
      // branch_no: vm.branchNo,
      jsonp: 'jsonpcallback'
    }, function (response) {
      vm.totalItems = response.total_item_num;
      if (response.data_list && response.data_list.length) {
        vm.viewCheckList = response.data_list;
      }
      for (var i = 0; i < vm.viewCheckList.length; i++) {
        vm.viewCheckList[i].view_content = JSON.parse(vm.viewCheckList[i].viewpoint_content);
        vm.viewCheckList[i].view_title = vm.viewCheckList[i].view_content.title;
      }
    }, function (result) {
      console.log('viewCheckList error', result);
      vm.totalItems = 0;
    });
  }

  function viewCheckStatus() {
    viewpointCheckService.viewCheckStatus({
      tenant_id: vm.tenant_id,
      jsonp: 'jsonpcallback',
      page_count: '10'
    }, function (response) {
      vm.ifOpenCheck = response.viewpoint_check_flag * 1 ? true : false; // 0：关闭、1：开启
    }, function (err) {
      console.log('err', err);
    })
  }

  function changeCheckStatus() {
    viewpointCheckService.openAudit({
      viewpoint_check_flag: vm.ifOpenCheck ? 1 : 0,
      branch_no: vm.branchNo,
      tenant_id: vm.tenant_id,
      jsonp: 'jsonpcallback',
      page_count: 10
    }, function(response) {
      console.log('res', response);
      modalService.openAlert('提示', response.error_info);
    })
  }

  function withdrawViewpointFunc(id) {
    viewpointCheckService.withdrawViewpoint({
      tenant_id: vm.tenant_id,
      history_id: id
    }, function (response) {
      modalService.openAlert('提示', response.error_info);
      viewCheckListFunc();
    }, function (err) {
      console.log('err', err);
    })
  }

  function openViewpointCheckModal(data) {
    viewpointCheckModalCtrl.openViewpointCheckModalFunc(data, function() {
      viewCheckListFunc();
    });
  }

  function openViewpointRemarlModal(content) {
    viewpointRemarkModalCtrl.openViewpointRemarkModalFunc(content);
  }

  function openViewpointDetailModal(param) {
    viewpointDetailModalCtrl.openViewpointDetailModalFunc(param);
  }

  function searchFunc() {
    vm.viewCheckList = [];
    vm.currentPage = 1;
    viewCheckListFunc();
  }

  function pageChangeFunc() {
    viewCheckListFunc();
  }
}

module.exports = viewpointCheckCtrl;



