/* 投顾管理 module */

investManageRoute.$inject = ['$stateProvider'];

function investManageRoute($stateProvider) {
    $stateProvider
        .state('home.investManage', {
            url: '/investManage?params',
            templateUrl: './views/investManage.html',
            controller: 'investManageCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = investManageRoute;



