liveCheckRoute.$inject = ['$stateProvider'];

function liveCheckRoute($stateProvider) {
  $stateProvider.state('home.liveCheck', {
    url: '/liveCheck',
    templateUrl: './views/liveCheck.html',
    controller: 'liveCheckCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = liveCheckRoute;



