/* 操作员管理 module */
operatorManageService.$inject = ['api'];

function operatorManageService(api) {
  var vm = this;
  vm.getOperatorInfo = getOperatorInfo;//获取操作员信息
  vm.operatorDelete = operatorDelete;//删除操作员信息

  function getOperatorInfo(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function operatorDelete(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorDel',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }


  return vm;
}

module.exports = operatorManageService;



