QACheckRoute.$inject = ['$stateProvider'];

function QACheckRoute($stateProvider) {
  $stateProvider.state('home.QACheck', {
    url: '/QACheck',
    templateUrl: './views/QACheck.html',
    controller: 'QACheckCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = QACheckRoute;



