viewpointCheckService.$inject = ['api'];
function viewpointCheckService(api) {
  var vm = this;
  vm.viewCheckList = viewCheckList; //查询列表
  vm.viewCheckStatus = viewCheckStatus; //查询审核状态
  vm.openAudit = openAudit; // 修改审核状态
  vm.checkViewpoint = checkViewpoint; //设置审核通过与否
  vm.getAuditHistory = getAuditHistory; //查询观点审核流水
  vm.withdrawViewpoint = withdrawViewpoint; // 观点撤回

  function viewCheckList(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'manage',
      url: 'func_viewpoint_list_manage',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }

  function viewCheckStatus(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'manage',
      url: 'func_viewpoint_review_status_manange',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }

  function openAudit(data, cb1) {
    api.postRequest({
      prefix_type: 'manage',
      url: 'func_viewpoint_review_manange',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function checkViewpoint(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'manage',
      url: 'func_viewpoint_review_by_id_manage',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }

  function getAuditHistory(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'audit_history',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }

  function withdrawViewpoint(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage_withdraw_viewpoint_audit',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }

  return vm;
}

module.exports = viewpointCheckService;


