/* 观点展示规则 module */

viewpointDisplayRuleRoute.$inject = ['$stateProvider'];

function viewpointDisplayRuleRoute($stateProvider) {
  $stateProvider.state('home.viewpointDisplayRule', {
    url: '/viewpointDisplayRule',
    templateUrl: './views/viewpointDisplayRule.html',
    controller: 'viewpointDisplayRuleCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = viewpointDisplayRuleRoute;



