/* 组织结构设置 module */
require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var addBranchModalCtrl = require('./modalPopup/addBranchModal/controller');
var addBranchModalService = require('./modalPopup/addBranchModal/service');
var changeBranchModalCtrl = require('./modalPopup/changeBranchModal/controller');
var changeBranchModalService = require('./modalPopup/changeBranchModal/service');
var delBranchModalCtrl = require('./modalPopup/delBranchModal/controller');
var delBranchModalService = require('./modalPopup/delBranchModal/service');


//setter定义模块
module.exports = angular.module('branchManage', [])
  .config(Routing)
  .service('branchManageService', service)
  .controller('branchManageCtrl', ctrl)
  .factory('addBranchModalCtrl', addBranchModalCtrl)
  .service('addBranchModalService', addBranchModalService)
  .factory('changeBranchModalCtrl', changeBranchModalCtrl)
  .service('changeBranchModalService', changeBranchModalService)
  .factory('delBranchModalCtrl', delBranchModalCtrl)
  .service('delBranchModalService', delBranchModalService)
  .name;



