/* 订单信息查询 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var orderInfoDownloadCtrl = require('./modalPopup/orderInfoDownload/controller');
var orderInfoDownloadService = require('./modalPopup/orderInfoDownload/service');
var editOrderInfoModalCtrl = require('./modalPopup/editOrderInfoModal/controller');
var editOrderInfoModalService = require('./modalPopup/editOrderInfoModal/service');

//setter定义模块
module.exports = angular
  .module('orderInfoQry', [])
  .config(Routing)
  .controller('orderInfoQryCtrl', ctrl)
  .service('orderInfoQryService', service)
  .factory('orderInfoDownloadCtrl', orderInfoDownloadCtrl)
  .service('orderInfoDownloadService', orderInfoDownloadService)
  .factory('editOrderInfoModalCtrl', editOrderInfoModalCtrl)
  .service('editOrderInfoModalService', editOrderInfoModalService).name;



