achievementStatisticsQryRoute.$inject = ['$stateProvider'];

function achievementStatisticsQryRoute($stateProvider) {
  $stateProvider.state('home.achievementStatisticsQry', {
    url: '/achievementStatisticsQry',
    templateUrl: './views/achievementStatisticsQry.html',
    controller: 'achievementStatisticsQryCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = achievementStatisticsQryRoute;



