/* 客户信息管理 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var subscribeModalCtrl = require('./modalPopup/subscribeModal/controller');
var subscribeModalService = require('./modalPopup/subscribeModal/service');
var orderModalCtrl = require('./modalPopup/orderModal/controller');
var orderModalService = require('./modalPopup/orderModal/service');
var downModalCtrl = require('./modalPopup/downModal/controller');
var downModalService = require('./modalPopup/downModal/service');
var combineModalCtrl = require('./modalPopup/combineModal/controller');
var combineModalService = require('./modalPopup/combineModal/service');

//setter定义模块
module.exports = angular
  .module('customerManage', [])
  .config(Routing)
  .service('customerManageService', service)
  .controller('customerManageCtrl', ctrl)
  .factory('subscribeModalCtrl', subscribeModalCtrl)
  .service('subscribeModalService', subscribeModalService)
  .factory('orderModalCtrl', orderModalCtrl)
  .service('orderModalService', orderModalService)
  .factory('downModalCtrl', downModalCtrl)
  .service('downModalService', downModalService)
  .factory('combineModalCtrl', combineModalCtrl)
  .service('combineModalService', combineModalService).name;



