/* 组合审核 module */

combineCheckRoute.$inject = ['$stateProvider'];

function combineCheckRoute($stateProvider) {
  $stateProvider.state('home.combineCheck', {
    url: '/combineCheck',
    templateUrl: './views/combineCheck.html',
    controller: 'combineCheckCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = combineCheckRoute;



