/* name module */
require('locale_zh');
var func = require('func');
var modalOptions = require('jsDir/modal_options');

templateCtrl.$inject = ['templateService', 'modalService', 'templateModalCtrl', 'templateModalService', '$sce'];

function templateCtrl(templateService, modalService, templateModalCtrl, templateModalService, $sce) {
  var vm = this;

  vm.openTemplateModalFunc = templateModalCtrl.openTemplateModalFunc; //打开模态窗
  vm.pageChangeFunc = pageChangeFunc; //换页方法
  vm.openStartTimeFunc = openStartTimeFunc; //开始日期选择
  vm.openEndTimeFunc = openEndTimeFunc; //结束日期选择
  vm.selectItemFunc = selectItemFunc; //下拉列表选择
  vm.openAlertFunc = openAlertFunc; //弹出警示窗口

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = '';
  vm.currentPage = 1;
  vm.itemPerPage = 5;

  /* 日期选择 */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };

  /* 下拉列表内容 */
  vm.dropdownMenu = [{item_content:'content0'}, {item_content:'content1'}, {item_content:'content2'}, {item_content:'content3'}];
  vm.selectedContent = vm.dropdownMenu[0].item_content;

  /* popover */
  vm.htmlPopover = $sce.trustAsHtml('<div>htmlContent</div><div>htmlContent</div>');

  function pageChangeFunc() {
    console.log(vm.currentPage);
  };

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  };

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function selectItemFunc(content) {
    vm.selectedContent = content;
  };

  function openAlertFunc() {
    func.openToaster('test');
  };
}

module.exports = templateCtrl;



