/* 敏感词库管理 module */

sensitiveWordsRoute.$inject = ['$stateProvider'];

function sensitiveWordsRoute($stateProvider) {
  $stateProvider.state('home.sensitiveWords', {
    url: '/sensitiveWords',
    templateUrl: './views/sensitiveWords.html',
    controller: 'sensitiveWordsCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = sensitiveWordsRoute;



