viewpointPackageCheckRoute.$inject = ['$stateProvider'];

function viewpointPackageCheckRoute($stateProvider) {
  $stateProvider.state('home.viewpointPackageCheck', {
    url: '/viewpointPackageCheck',
    templateUrl: './views/viewpointPackageCheck.html',
    controller: 'viewpointPackageCheckCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = viewpointPackageCheckRoute;



