/* 粉丝发言监控 module */

fansSpeechMonitorService.$inject = ['api'];

function fansSpeechMonitorService(api) {
  var vm = this;

  vm.questionList = questionList;
  vm.blockQuestion = blockQuestion;
  vm.viewpointList = viewpointList;
  vm.blockViewpoint = blockViewpoint;
  vm.chatMsgList = chatMsgList;
  vm.blockChatMsg = blockChatMsg;
  vm.combineList = combineList;
  vm.blockCombine = blockCombine;

  //获取问答列表
  function questionList(data, cb1) {
    api.postRequest({
      prefix_type: 'managerService',
      url: 'questionQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //屏蔽问答数据
  function blockQuestion(data, cb1) {
    api.postRequest({
      prefix_type: 'managerService',
      url: 'questionBlock',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //获取观点列表
  function viewpointList(data, cb1) {
    api.postRequest({
      prefix_type: 'managerService',
      url: 'viewPointReplyQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //屏蔽观点数据
  function blockViewpoint(data, cb1) {
    api.postRequest({
      prefix_type: 'managerService',
      url: 'viewPointReplyBlock',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //获取直播列表
  function chatMsgList(data, cb1) {
    api.postRequest({
      prefix_type: 'managerService',
      url: 'chatMsgQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result)
      }
    });
  }

  //屏蔽直播数据
  function blockChatMsg(data, cb1) {
    api.postRequest({
      prefix_type: 'managerService',
      url: 'chatMsgBlock',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //获取组合列表
  function combineList(data, cb1) {
    api.postRequest({
      prefix_type: 'managerService',
      url: 'combinationCommentQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result)
      }
    });
  }

  //屏蔽组合数据
  function blockCombine(data, cb1) {
    api.postRequest({
      prefix_type: 'managerService',
      url: 'combinationCommentBlock',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = fansSpeechMonitorService;



