/* 观点展示规则 module */

var config = require('config');

viewpointDisplayRuleService.$inject = ['api'];

function viewpointDisplayRuleService(api) {
  var vm = this;

  vm.getViewpointListByOperate = getViewpointListByOperate;
  vm.getHotViewByOperate = getHotViewByOperate;
  vm.getHotViewRule = getHotViewRule;
  vm.setHotViewRule = setHotViewRule;
  vm.updateHotViewStatusFunc = updateHotViewStatusFunc;
  vm.imgUpload = imgUpload;

  //查询观点
  function getViewpointListByOperate(data, cb1) {
	api.postRequest({
	  prefix_type: 'vipstu/manage',
	  url: 'func_get_viewpoint_list_by_operate',
	  data: data,
	  successCallback: function (result) {
		angular.isFunction(cb1) && cb1(result);
	  }
	});
  }

  //热门观点列表
  function getHotViewByOperate(data, cb1) {
	api.postRequest({
	  prefix_type: 'vipstu/manage',
	  url: 'func_get_hot_view_by_operate',
	  data: data,
	  successCallback: function (result) {
		angular.isFunction(cb1) && cb1(result);
	  }
	});
  }

  //获取观点排序规则
  function getHotViewRule(data, cb1) {
	api.postRequest({
	  prefix_type: 'vipstu/manage',
	  url: 'func_get_hot_view_rule',
	  data: data,
	  successCallback: function (result) {
		angular.isFunction(cb1) && cb1(result);
	  }
	});
  }

  //设置观点排序规则
  function setHotViewRule(data, cb1) {
	api.postRequest({
	  prefix_type: 'vipstu/manage',
	  url: 'func_set_hot_view_rule',
	  data: data,
	  successCallback: function (result) {
		angular.isFunction(cb1) && cb1(result);
	  }
	});
  }

  //更新热门观点（更新置顶）
  function updateHotViewStatusFunc(data, cb1) {
	api.postRequest({
	  prefix_type: 'vipstu/manage',
	  url: 'update_hot_view_status',
	  data: data,
	  successCallback: function (result) {
		angular.isFunction(cb1) && cb1(result);
	  }
	});
  }

  //图片上传
  function imgUpload(data, cb1, cb2) {
    api.fileUpload({
      url: LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_upload/',
	  data: data,
	  successCallback: function (result) {
		angular.isFunction(cb1) && cb1(result);
	  },
	  failCallback: function (result) {
		angular.isFunction(cb2) && cb2(result);
	  }
	});
  }

  return vm;
}

module.exports = viewpointDisplayRuleService;



