/* 导出 controller*/

require('./style.less');
window.ExcellentExport = require('ExcellentExport'); //导出
var modalOptions = require('jsDir/modal_options'),
orderInfoDownloadTempl = require('./orderInfoDownload.html');

orderInfoDownloadCtrl.$inject = ['modalService', 'orderInfoDownloadService'];

function orderInfoDownloadCtrl(modalService, orderInfoDownloadService) {
  var vm = this;

  /*函数声明*/
  vm.openDownModalFunc = openDownModalFunc;

  /*函数定义*/
  function openDownModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '确认导出';
    options.template = orderInfoDownloadTempl;
    options.size = 'md';
    options.controller = downModalController;
    options.modalParams = data; //传递正常客户和限制客户参数
    modalService.openModal(options);
  }

  /*controller定义*/
  function downModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.cancelBtnText = cancelBtnText;
    ra.content = '下载进度...0%';
    ra.orderInfoData = '';
    ra.downFlag = true;

    ra.cancel = cancel;
    ra.close = close;

    init();

    function init() {
      orderInfoDownloadService.orderManageFunc(
        {
          tenant_id: ra.modalParams.tenant_id,
          keyword: ra.modalParams.keyword,
          begin_date: ra.modalParams.begin_date,
          end_date: ra.modalParams.end_date,
          order_status: ra.modalParams.order_status,
          third_type: ra.modalParams.third_type,
          recommend_name: ra.modalParams.recommend_name,
          page_no: ra.modalParams.page_no,
          page_size: ra.modalParams.page_size
        },
        function(response) {
          ra.orderInfoData = response.data_list;
          ra.downFlag = false;
          ra.content = '下载进度...100%';
        },
        function(err) {
          ra.content = '查询失败！';
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = orderInfoDownloadCtrl;



