UtilService.$inject = ['$cookies', 'api'];

function UtilService($cookies, api) {
  var vm = this;

  vm.sendSms = sendSms;
  vm.notLoggedIn = notLoggedIn;
  vm.readFile = readFile; //导出解析文本

  //发送验证码
  function sendSms(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorChangeSMS',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function notLoggedIn() {
    return angular.isUndefined(sessionStorage.getItem('access_token')) || sessionStorage.getItem('access_token') == '';
  }

  //导出解析文本
  function readFile() {
    var file = event.currentTarget.files[0];
    vm.myFile = event.currentTarget.value;
    if (!file) {
      return;
    }
    var reader = new FileReader();
    reader.onload = function(e) {
      var data = e.target.result;
      if (rABS) {
        wb = XLSX.read(btoa(fixdata(data)), {
          //手动转化
          type: 'base64'
        });
      } else {
        wb = XLSX.read(data, {
          type: 'binary'
        });
      }
    };
  }

  return vm;
}

module.exports = UtilService;



