// 订单统计查询查询

require('locale_zh');
var func = require('func');

orderStatisticsQryCtrl.$inject = ['modalService', 'orderStatisticsQryService', 'orderStatisticsDownloadCtrl'];

function orderStatisticsQryCtrl(modalService, orderStatisticsQryService, orderStatisticsDownloadCtrl) {
  var vm = this;
  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  // tab切换
  vm.currentSession = 0; // 0：机构订单统计查询，1：投顾订单统计查询
  // 机构列表
  vm.branchList = []; 
  vm.branchNo = '';
  // 服务类型列表
  vm.serviceTypeList = [
    {name: '提问', value: 1},
    {name: '观点', value: 2},
    // {name: '围观', value: 3},
    {name: '组合', value: 4}
  ];
  vm.serviceType = '';
  // 投顾名称
  vm.brokerName = '';

  /* 日期选择 */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };
  vm.startTime = new Date();
  vm.endTime = '';

  vm.dataList = []; // 列表数据
  vm.allData = {}; // 汇总数据
  vm.dateStr = func.formatDate(vm.startTime, 'yyyy-MM-dd') + ' — ' + func.formatDate(vm.endTime || new Date(), 'yyyy-MM-dd');

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 0; 

  vm.setCurrentSession = setCurrentSession;
  vm.openStartTimeFunc = openStartTimeFunc; //开始日期选择
  vm.openEndTimeFunc = openEndTimeFunc; //结束日期选择
  vm.searchFunc = searchFunc;
  vm.pageChangeFunc = pageChangeFunc;
  vm.download = download;

  getBranchQryInOrderFunc();
  getBranchOrderStatisticQryFunc();

  function getBranchQryInOrderFunc() {
    orderStatisticsQryService.branchQryInOrderFunc({
      tenant_id: vm.tenantId
    },function(res) {
      if (res.data_list && res.data_list.length) {
        var dataList = res.data_list;
        vm.branchList = dataList.map(function(item) {
          return {
            name: item.branch_name,
            value: item.branch_no
          }
        });
      }
    }, function(err) {
      console.log('err', err);
    });
  }

  function getBranchOrderStatisticQryFunc() {
    if (!vm.startTime) {
      modalService.openAlert('提示', '请输入起始日期');
      return;
    }
    vm.itemPerPage = vm.serviceType ? 6 : 3;
    orderStatisticsQryService.branchOrderStatisticQryFunc({
      tenant_id: vm.tenantId,
      branch_no: vm.branchNo,
      service_type: vm.serviceType,
      start_time: parseInt(Number(vm.startTime) / 1000),
      end_time: parseInt(Number(vm.endTime) / 1000) || '',
      page_no: vm.currentPage,
      page_size: vm.serviceType ? 6 : 3 // 有服务类型时传6（6 * 2行），无服务类型时传3（3 * 4行）
    },function(res) {
      console.log('res', res);
      vm.totalItems = res.total_num || 0;
      handleDataListFunc(res.data_list, res.summary);
    }, function(err) {
      modalService.openAlert('提示', '请求异常');
      console.log('err', err);
    });
  }

  function getbrokerOrderStatisticQryFunc() {
    if (!vm.startTime) {
      modalService.openAlert('提示', '请输入起始日期');
      return;
    }
    vm.itemPerPage = vm.serviceType ? 6 : 3;
    orderStatisticsQryService.brokerOrderStatisticQryFunc({
      tenant_id: vm.tenantId,
      branch_no: vm.branchNo,
      service_type: vm.serviceType,
      broker_name: vm.brokerName,
      start_time: parseInt(Number(vm.startTime) / 1000),
      end_time: parseInt(Number(vm.endTime) / 1000) || '',
      page_no: vm.currentPage,
      page_size: vm.serviceType ? 6 : 3 // 有服务类型时传6（6 * 2行），无服务类型时传3（3 * 4行）
    },function(res) {
      console.log('res', res);
      handleDataListFunc(res.data_list, res.summary);
      vm.totalItems = res.total_num || 0;
    }, function(err) {
      console.log('err', err);
    });
  }

  function handleDataListFunc(dataList, summaryList) {
    vm.dataList = [];
    // 非汇总数据
    if (dataList.length) {
      dataList.forEach(function(item) {
        if (item.order_statistic_list && item.order_statistic_list.length) {
          item.order_statistic_list.forEach(function(per) {
            vm.dataList.push({
              branchName: item.branch_name,
              brokerName: item.broker_name,
              serviceType: per.title,
              orderNum: per.total_order_num,
              orderPrice: per.total_order_price,
              orderTradeNum: per.success_order_num,
              orderTradePrice: per.success_order_price,
              orderCancelNum: per.cancel_order_num,
              orderCancelPrice: per.cancel_order_price,
              orderBackNum: per.unsubscribe_order_num,
              orderBackPrice: per.unsubscribe_order_price
            })
          })
        } else {
          vm.dataList.push({
            branchName: item.branch_name,
            brokerName: item.broker_name
          })
        }
      })
    }
    // 汇总数据
    vm.allData = summaryList[0] || {};
  }

  function setCurrentSession(type) {
    vm.dateStr = func.formatDate(vm.startTime, 'yyyy-MM-dd') + ' — ' + func.formatDate(vm.endTime || new Date(), 'yyyy-MM-dd');
    vm.currentPage = 1;
    vm.currentSession = type;
    vm.branchNo = '';
    vm.brokerName = '';
    vm.serviceType = '';
    vm.startTime = new Date();
    vm.endTime = '';
    if (vm.currentSession == 0) {
      getBranchOrderStatisticQryFunc();
    } else {
      getbrokerOrderStatisticQryFunc();
    }
  }

  function download() {
    orderStatisticsDownloadCtrl.openDownModalFunc({
      current_session: vm.currentSession,
      tenant_id: vm.tenantId,
      branch_no: vm.branchNo,
      service_type: vm.serviceType,
      broker_name: vm.brokerName,
      start_time: parseInt(Number(vm.startTime) / 1000),
      end_time: parseInt(Number(vm.endTime) / 1000) || '',
      page_no: vm.currentPage,
      page_size: vm.totalItems
    })
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  };

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function searchFunc() {
    vm.dateStr = func.formatDate(vm.startTime, 'yyyy-MM-dd') + ' — ' + func.formatDate(vm.endTime || new Date(), 'yyyy-MM-dd');
    vm.currentPage = 1;
    if (vm.currentSession == 0) {
      getBranchOrderStatisticQryFunc();
    } else {
      getbrokerOrderStatisticQryFunc();
    }
  };

  function pageChangeFunc() {
    if (vm.currentSession == 0) {
      getBranchOrderStatisticQryFunc();
    } else {
      getbrokerOrderStatisticQryFunc();
    }
  }
}

module.exports = orderStatisticsQryCtrl;



