 // 风险等级设置

require('locale_zh');
var func = require('func');
var config = require('config');

signProtocolManageCtrl.$inject = ['$stateParams', 'modalService', 'signProtocolManageService', 'signProtocolViewModalCtrl', 'downloadProtocolModalCtrl', 'downloadProgressModalCtrl'];

function signProtocolManageCtrl($stateParams, modalService, signProtocolManageService, signProtocolViewModalCtrl, downloadProtocolModalCtrl, downloadProgressModalCtrl) {
  var vm = this;

  vm.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));

  vm.customName = '';
  vm.customMobile = '';
  vm.orderId = '';
  vm.productType = '';
  vm.dictsProductType = [
    {label: '全部', value: ''},
    {label: '观点', value: 1},
    {label: '组合', value: 2},
    {label: '问答', value: 3},
    {label: '观点包', value: 8},
    {label: '量化模型', value: 98},
    {label: '智能策略', value: 99}
  ];
  vm.productName = '';
  vm.protocolType = '';
  vm.dictsProtocolType = [
    { label: "全部", value: '' },
    { label: "线下银行卡转账提示", value: 0 },
    { label: "适当性匹配协议", value: 1 },
    { label: "投顾服务协议", value: 2 },
    { label: "风险揭示协议", value: 3 },
    { label: "适当性不匹配协议c1", value: 4 },
    { label: "适当性不匹配协议非c1", value: 5 }
  ];
  vm.investorName = '';
  vm.investorMobile = '';
  vm.fundAccount = '';
  /* 日期选择 */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };
  vm.startTime = '';
  vm.endTime = '';
  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.MaxNum = 0;
  vm.fileStr = '';
  vm.list = [];

  vm.openStartTimeFunc = openStartTimeFunc; //开始日期选择
  vm.openEndTimeFunc = openEndTimeFunc; //结束日期选择
  vm.resetForm = resetForm;
  vm.searchFunc = searchFunc;
  vm.downloadProtocolFunc = downloadProtocolFunc;
  vm.openModalFunc = openModalFunc;
  vm.pageChangeFunc = pageChangeFunc;

  try {
    var paramObj = $stateParams.data && JSON.parse($stateParams.data);
    console.log('$stateParams.data', paramObj);
    // 如果带参数，则按参数条件查询
    if (paramObj) {
      vm.orderId = paramObj.transaction_id || '';
      vm.productType = paramObj.product_type || '';
      vm.productName = paramObj.product_name || '';
      vm.customName = paramObj.customer_name || '';
      vm.customMobile = paramObj.customer_mobile || '';
      vm.fundAccount = paramObj.busin_account || '';
    }
  } catch (error) {
    console.log('paramObj error:', error);
  }

  getSignProtocolListFunc();

  function getSignProtocolListFunc() {
    signProtocolManageService.signProtocolListQry(
      {
        tenant_id: vm.tenantId,
        customer_name: vm.customName,
        customer_mobile: vm.customMobile,
        order_id: vm.orderId,
        product_type: vm.productType,
        product_name: vm.productName,
        agreement_type: vm.protocolType,
        broker_name: vm.investorName,
        broker_mobile: vm.investorMobile,
        busin_account: vm.fundAccount,
        begin_time: vm.startTime && func.formatDate(vm.startTime, 'yyyyMMdd') || '',
        end_time: vm.endTime && func.formatDate(vm.endTime, 'yyyyMMdd') || '',
        page_no: vm.currentPage,
        page_count: vm.itemPerPage
      },
      function(result) {
        vm.list = result.data_list || [];
        vm.totalItems = result.total_item_num || 0;
        vm.MaxNum = result.allow_export_num || 0;
        vm.fileStr = result.file_names;
      },
      function(error) {
        modalService.openAlert('提示', error.error_info);
        vm.list = [];
        vm.totalItems = 0;
        vm.MaxNum = 0;
      }
    );
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  };

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function resetForm() {
    vm.customName = '';
    vm.customMobile = '';
    vm.orderId = '';
    vm.productType = '';
    vm.productName = '';
    vm.protocolType = '';
    vm.investorName = '';
    vm.investorMobile = '';
    vm.fundAccount = '';
    vm.startTime = '';
    vm.endTime = '';
  }

  function searchFunc() {
    vm.currentPage = 1;
    getSignProtocolListFunc();
  }

  function downloadProtocolFunc() {
    downloadProtocolModalCtrl.openModal({totalItems: vm.totalItems, MaxNum: vm.MaxNum}, function() {
      if (!vm.fileStr) {
        modalService.openAlert('提示', '暂无文件');
        return;
      }
      downloadProgressModalCtrl.openModal(vm.fileStr);
    });
  }

  function openModalFunc(filePath) {
    if (!filePath) {
      modalService.openAlert('提示', '暂无协议内容');
      return;
    }
    // 通过此url下载pdf文件并展示
    var accessToken = sessionStorage.getItem('access_token');
    var url = LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download/' + filePath + '/' + accessToken;
    signProtocolViewModalCtrl.openModal(url);
  }

  function pageChangeFunc() {
    getSignProtocolListFunc();
  }
}

module.exports = signProtocolManageCtrl; 


