operatorAddService.$inject = ['api'];
function operatorAddService(api) {
  var vm = this;
  vm.operatorAdd = operatorAdd;//增加操作员信息
  vm.allBranchQry = allBranchQry; // 查询机构列表


  function operatorAdd(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorAdd',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //获取机构列表
  function allBranchQry(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = operatorAddService;



