unifyPayment01Route.$inject = ['$stateProvider'];

function unifyPayment01Route($stateProvider) {
    $stateProvider
        .state('home.unifyPayment01', {
            url: '/unifyPayment01',
            templateUrl: './views/unifyPayment01.html',
            controller: 'unifyPayment01Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = unifyPayment01Route;



