/* 日志查询 module */

logInquiryService.$inject = ['api'];
function logInquiryService(api) {
  var vm = this;

  vm.branchQry = branchQry;   //查询机构
  vm.operatorLogQry = operatorLogQry;  //日志列表查询



  function branchQry(data,cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }

  function operatorLogQry(data,cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorLogQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }


  return vm;
}

module.exports = logInquiryService;



