 // 投顾业绩数据查询

require('locale_zh');

var func = require('func');

customerContributionQryCtrl.$inject = ['modalService', 'customerContributionQryService', 'customerContributionQryDownloadCtrl'];

function customerContributionQryCtrl(modalService, customerContributionQryService, customerContributionQryDownloadCtrl) {
  var vm = this;

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.keyword = '';
  vm.customerList = [];
  // 排序项
  vm.orderByName = ''; // totalSpeed：累计消费金额，buyCombineNum：累计购买组合总数，buyAskNum：累计提问数， buyViewpointMsgNum：累计购买观点数
  vm.sortOrder = ''; // desc：降序、asc：升序
  vm.seqConsume = false;
  vm.seqCombine = false;
  vm.seqQuestion = false;
  vm.seqViewpoint = false;

  vm.sequenceFunc = sequenceFunc;
  vm.searchFunc = searchFunc;
  vm.pageChangeFunc = pageChangeFunc;
  vm.download = download;

  getCustomerContributionQryFunc();

  function getCustomerContributionQryFunc(type) {
    // if (type === 'init') {
    //   vm.seqConsume = false;
    //   vm.seqCombine = false;
    //   vm.seqQuestion = false;
    //   vm.seqViewpoint = false;
    //   vm.orderByName = '';
    //   vm.sortOrder = '';
    // }
    customerContributionQryService.customerContributionQryFunc({
      tenant_id: vm.tenantId,
      keyword: vm.keyword,
      order_by_name: vm.orderByName,
      sort_order: vm.sortOrder,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage
    }, function(res) {
      console.log('res', res);
      vm.customerList = res.data_list;
      vm.totalItems = res.total_num;
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', '请求异常');
    })
  }

  function download() {
    customerContributionQryDownloadCtrl.openDownModalFunc({
      tenant_id: vm.tenantId,
      keyword: vm.keyword,
      order_by_name: vm.orderByName,
      sort_order: vm.sortOrder,
      page_no: 1,
      page_size: vm.totalItems
    })
  }

  function sequenceFunc(type) {
    switch (type) {
      case 0:
        vm.seqConsume = !vm.seqConsume;
        vm.sortOrder = vm.seqConsume ? 'desc' : 'asc';
        vm.orderByName = 'totalSpend';
        break;
      case 1:
        vm.seqCombine = !vm.seqCombine;
        vm.sortOrder = vm.seqCombine ? 'desc' : 'asc';
        vm.orderByName = 'buyCombineNum';
        break;
      case 2:
        vm.seqQuestion = !vm.seqQuestion;
        vm.sortOrder = vm.seqQuestion ? 'desc' : 'asc';
        vm.orderByName = 'buyAskNum';
        break;
      case 3:
        vm.seqViewpoint = !vm.seqViewpoint;
        vm.sortOrder = vm.seqViewpoint ? 'desc' : 'asc';
        vm.orderByName = 'buyViewpointMsgNum';
        break;
    }
    getCustomerContributionQryFunc();
  }

  function searchFunc() {
    vm.currentPage = 1;
    getCustomerContributionQryFunc();
  }

  function pageChangeFunc() {
    getCustomerContributionQryFunc();
  }
}

module.exports = customerContributionQryCtrl; 


