combineCheckModalService.$inject = ['api'];
function combineCheckModalService(api) {
  var vm = this;
  
  vm.combineUpDownAudit = combineUpDownAudit; // 组合上下架审核
  vm.combineModifyAudit = combineModifyAudit; // 组合修改审核

  function combineUpDownAudit(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'combine_up_down_audit',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  function combineModifyAudit(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'combine_modify_audit',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  return vm;

}

module.exports = combineCheckModalService;


