downModalService.$inject = ['api'];
function downModalService(api) {
  var vm = this;
  vm.userListFunc = userListFunc;  //正常客户列表查询

  function userListFunc(data,cb1,cb2) {
    api.postRequest ({
      prefix_type: 'userCenter',
      url: 'func_user_page_query',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (response) {
        angular.isFunction(cb2) && cb2(response);
      }
    })
  }

  return vm;
}

module.exports = downModalService;



