/* 角色管理 module */

require('locale_zh');
var modalOptions = require('jsDir/modal_options');
var func = require('func');

rolesManageCtrl.$inject = ['$cookies','rolesManageService','modalService', 'rolesModalCtrl', 'accreditModalCtrl', 'alterModalCtrl'];

function rolesManageCtrl($cookies,rolesManageService, modalService, rolesModalCtrl, accreditModalCtrl, alterModalCtrl) {
    var vm = this;

  /*角色查询参数*/
  vm.role_branchNo = '';
  vm.role_branchName = '--请选择--';
  vm.roles_type = '';
  vm.roles_value = '--请选择--';
  vm.roles_id = '';
  vm.roles_name = '';
  vm.branchInfo = '';
  vm.pageNo = 1;
  vm.isMod = false;
  vm.delete_token = sessionStorage.getItem('access_token');
  vm.operatorInfo = JSON.parse(sessionStorage.getItem("operatorInfo"));
  /* 分页 */
  vm.pagination = {
    pageSize: 5,    //页面大小
    pageNo: 1     //当前页
    //total_items: 0   //总页数
  };
  vm.maxSize = 5;
  vm.total_items = '';
  vm.currentPage = 1;
  vm.itemPerPage = 5;

  /*函数声明*/

  vm.rolesQry = rolesQry;  //查询角色信息
  vm.rolesList = rolesList;  //获取角色列表
  vm.dicts = [{"code":"1","value":"基本类"},{"code":"2","value":"管理类"},{"code":"3","value":"运营类"}];
  vm.selectBranchNoFunc = selectBranchNoFunc;
  vm.selectRoleTypeFunc = selectRoleTypeFunc;
  vm.pageChangeFunc = pageChangeFunc; //换页方法
  vm.setResponseData = setResponseData;

  /* 打开模态框 */
  vm.openRolesModalFunc = openRolesModalFunc; //打开增加模态窗
  vm.openAccreditModalFunc = accreditModalCtrl.openAccreditModalFunc; //打开授权模态窗
  vm.openAlterModalFunc = openAlterModalFunc;//alterModalCtrl.openAlterModalFunc; //打开修改模态窗
  vm.openDeleModalFunc = openDeleModalFunc; //打开删除模态窗
	
  init();

  //初始化
  function init() {
	rolesRefresh();  //刷新显示角色信息
	rolesList();  //获取角色列表
  }

  //打开增加模态窗
  function openRolesModalFunc() {
    var data = {
      branch: vm.dataMulList,
      roleType: vm.dicts
    };

    rolesModalCtrl.openRolesModalFunc(data,
      function(result) {
        vm.role_branchNo = result.branchNo;
        vm.role_branchName = result.branchName;
        vm.roles_id = result.roleId;
        rolesRefresh();
      }
    );
  }

  //打开编辑模态窗
  function openAlterModalFunc(item) {
    var data = {
      branch: item.branch,
      roleType: item.roleType,
      remark: item.remark,
      dicts: vm.dicts,
      roleId: item.roleId,
      roleName: item.roleName,
      rowCount: item.rowCount,
      branchNo: item.branchNo
    };

    alterModalCtrl.openAlterModalFunc(data,
      function(result) {
        item.roleName = result.roleName;
        item.roleType = result.roleType;
        rolesRefresh();
      }
    );
  }

  //打开删除模态窗
  function openDeleModalFunc(item) {
    rolesManageService.roleDel({
      accessToken: vm.delete_token,
      action_in: 3,
      branchNo: item.branchNo,
      menuRights: vm.operatorInfo.branchRights,
      remark: item.remark,
      roleId: item.roleId,
      roleName: item.roleName,
      roleType: item.roleType,
      rowCount: item.rowCount
    }, function (response) {
      rolesRefresh();
      var options = angular.copy(modalOptions);
      options.modalTitle = '角色删除';
      options.modalInfo = '角色删除成功！';
      modalService.openModal(options);
    });
  }

  //角色信息刷新
  function rolesRefresh () {
    var branchList = [];
    if(vm.role_branchNo > 0) {
      branchList.push(vm.role_branchNo);
    }
    rolesManageService.getRolesInfo({
      branchNoList: branchList,
      pageNo: vm.currentPage,
      pageSize: 5,
      roleId: vm.roles_id,
      roleName: vm.roles_name,
      roleType: vm.roles_type
    }, function (response) {
      setResponseData(response);
    });
  }

  //获取角色类别
  function rolesList() {
    vm.rolesInfo= JSON.parse(sessionStorage.getItem("rolesQryData"));
    return vm.rolesInfo;
  }
  //点击查询
  function rolesQry(form) {
    rolesRefresh();
  }
  //页面刷新显示角色列表信息
  function setResponseData(response){
    vm.rolesQryDate = sessionStorage.setItem("rolesQryData", JSON.stringify(response.data));
    if (response.data != undefined) {
      if (response.data[0]!= undefined && 'rowCount' in response.data[0]) {
        vm.total_items = response.data[0].rowCount;
      }else {
        vm.total_items = 0;
      }
    }
    //列表加载匹配信息
    vm.rolesList = response.data;
    vm.branchInfo= JSON.parse(sessionStorage.getItem("allBranchListData"));
    vm.dataMulList = [];
    for (var i = 0; i < vm.branchInfo.length; i++) {
      var obj = {};
      obj.branchNo = vm.branchInfo[i].branchNo;
      obj.branchName = vm.branchInfo[i].branchName;
      vm.dataMulList.push(obj);
    }
    if (vm.rolesList != undefined && vm.rolesList.length > 0) {
      for (var j = 0; j < vm.rolesList.length; j++) {
        if (vm.branchInfo != undefined && vm.branchInfo.length > 0) {
          for (var i = 0; i < vm.branchInfo.length; i++) {
            if (vm.branchInfo[i].branchNo === vm.rolesList[j].branchNo) {
              vm.rolesList[j].branch = vm.rolesList[j].branchNo + "-" + vm.branchInfo[i].branchName;
            }
          }
        }
      }
    }
  }

  // 机构选择
  function selectBranchNoFunc(branchNo, branchName) {
    vm.role_branchNo = branchNo;
    vm.role_branchName = branchName;
  }

  // 角色类型选择
  function selectRoleTypeFunc(code, value) {
    vm.roles_type = code;
    vm.roles_value = value;
  }

  //列表分页查询
  function pageChangeFunc() {
    rolesManageService.getRolesInfo({
      branchNoList: [],
      pageNo: vm.currentPage,
      pageSize: 5,
      roleId: vm.roles_id,
      roleName: vm.roles_name,
      roleType: vm.roles_type
    }, function (response) {
      vm.setResponseData(response);
    });
    vm.pageNo = vm.currentPage;
  }

}

module.exports = rolesManageCtrl;



