/*直播背景图设置 新增 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  addBgModalTempl = require('./addBgModal.html');

addBgModalCtrl.$inject = ['modalService'];

function addBgModalCtrl(modalService) {
  var vm = this;

  /*函数声明*/
  vm.openAddBgModalFunc = openAddBgModalFunc;

  /*函数定义*/
  function openAddBgModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '新增';
    options.template = addBgModalTempl;
    options.controller = addBgModalController;
    options.modalParams = data;
    options.confirmCallback = function(result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function addBgModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText, addBgModalService) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = '确认';
    ra.cancelBtnText = '取消';

    ra.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
    ra.showPath = '';
    ra.picList = []; // state，0：非激活，1：激活

    ra.readPicAddress = readPicAddress;
    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    getAllBgList();

    function getAllBgList() {
      addBgModalService.getBgListFunc({
        tenant_id: ra.tenantId,
      }, function(res) {
        var dataList = res.data_list;
        if (dataList.length) {
          dataList.forEach(function(item) {
            ra.picList.push({
              liveBgImg: item.live_bg_img,
              state: item.state
            });
          });
        }
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('提示', err.error_info);
      });
    }

    //点击添加图片
    function readPicAddress(event) {
      console.log('event', event);
      var r = new FileReader();
      var file = event.currentTarget.files[0];
      if (file.size > 2 * 1024 * 1024) {
        modalService.openAlert('提示', '图片大小不得大于2M!', '', 2000);
        return false;
      }
      if (file.type == 'image/jpeg' || file.type == 'image/jpg' || file.type == 'image/png' || file.type == 'image/bmp' || file.type == 'image/gif') {
      } else {
        modalService.openAlert('提示', '上传文件不是图片类型!', '', 2000);
        return false;
      }
      r.readAsDataURL(file);
      var a = window.URL.createObjectURL(file);

      r.onload = function(e) {
        document.getElementById('changeLoading').src = a;
        ra.picShow = this.result;
      };
      var formData = new FormData();
      formData.append('user_token', '92f17e36e6cf0a8083ff435fc708a372');
      formData.append('type', 'img');
      formData.append('file', file);
      addBgModalService.upload(formData, function(res) {
        if (ra.picList.length) {
          ra.picList.push({liveBgImg: res.oss_id, state: 0});
        } else {
          ra.picList.push({liveBgImg: res.oss_id, state: 1});
        }
      }, function(err) {
        modalService.openAlert('提示', err.error_info);
      });
    }

    function submit() {
      addBgModalService.addPicFunc({
        tenant_id: ra.tenantId,
        live_bg_json: JSON.stringify(ra.picList)
      }, function(res) {
        modalService.openAlert('提示', res.error_info);
        close();
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('提示', err.error_info);
      });
    }

    function close(result) {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close(result);
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = addBgModalCtrl;



