addStoreModalService.$inject = ['api'];

function addStoreModalService(api) {
  var vm = this;

  vm.funcBrokerListQuery = funcBrokerListQuery;
  vm.funcBrokerAdd = funcBrokerAdd;

  //店长联想
  function funcBrokerListQuery(data, cb1) {
    api.postRequest({
      prefix_type: 'ifs',
      url: 'manage/func_broker_list_query',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //新增店铺
  function funcBrokerAdd(data, cb1) {
    api.postRequest({
      prefix_type: 'ifs',
      url: 'manage/func_broker_add',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

}

module.exports = addStoreModalService;



