/* 图片设置 module */
imageConfigService.$inject = ['api'];
function imageConfigService(api) {
    var vm = this;

  vm.tenantSearchFunc = tenantSearchFunc; //查询租户信息
  vm.picSearchFunc = picSearchFunc; //查询页面展示图片
  vm.payUpdateFunc = payUpdateFunc; //广告页管理
  vm.deletePictureModalFunc = payUpdateFunc; // 删除广告图片

  //查询租户信息
  function tenantSearchFunc(data,cb1) {
    api.postRequest ({
      prefix_type: 'manage',
      url: 'func_operate_tenant_list',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }

  //查询页面展示图片
  function picSearchFunc(data,cb1) {
    api.postRequest ({
      prefix_type: 'manage',
      url: 'func_operate_adpage_list',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }

  //支付状态查询
  function payUpdateFunc(data,cb1) {
    api.postRequest ({
      prefix_type: 'manage',
      url: 'func_operate_adpage_update',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }


    return vm;
}

module.exports = imageConfigService;



