/* name module */
var func = require('func'),
  modalOptions = require('jsDir/modal_options'),
  config = require('config');

Api.$inject = ['$state', '$cookies', '$http', 'modalService'];

function Api($state, $cookies, $http, modalService) {

  var access_token = sessionStorage.getItem('access_token');

  var apiHandler = this;
  apiHandler.appendUrlPrefix = appendUrlPrefix;
  apiHandler.sendRequest = sendRequest;
  apiHandler.postRequest = postRequest;
  apiHandler.getRequest = getRequest;
  apiHandler.fileUpload = fileUpload;

  var params = {
    successCallback: function (result) {
      console.log('success' + JSON.stringify(result))
    },
    failCallback: function (response) {
      console.log("false");
      var errorOptions = angular.copy(modalOptions);
      errorOptions.modalTitle = '失败';
      errorOptions.modalParams.error_no = response && (response.errorCode || response.error_no);
      errorOptions.modalInfo = response && (response.errorMsg || response.error_info);
      modalService.openModal(errorOptions);
    },
    disconnectCallback: function (response) {
      console.log("访问失败")
    }
  };

  function isSuccessful(result) {
    return result && (result.error_no == '0' || result.errorCode == '0' || result.success) ? true : false;
  }


  /**
   * 扩展不同url前缀
   * 如根域名是'muziso.com'， 有不同扩展，client, common等，方法可随着项目更改
   * @param type
   * @returns {*}
   */
  function appendUrlPrefix(options) {
    // 统一头部添加accessToken
    var accessToken = sessionStorage.getItem('access_token') || '';
    if (!(options.headers && options.headers.accessToken)) {
      options.headers = {
        'accessToken': accessToken
      };
    }
    // 统一请求体添加accessToken
    if (!(options.data && options.data.access_token)) {
      options.data.access_token = accessToken || '';
    }

    var type = options.prefix_type;
    var url = options.url;
    if (type == 'vipstu') {
      options.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
      return LOCAL_CONFIG.API_HOME + config.OPEN_API;
    } else if (type == 'vipstu/manage') {
      options.data = JSON.stringify(options.data);
      options.headers['Content-Type'] = 'application/json;';
      return LOCAL_CONFIG.API_HSIAR + config.OPEN_API_MANAGE;

    } else if (type == 'file') {
      return LOCAL_CONFIG.API_HOME + config.UPLOADING_API;

    } else if (type === 'operatorService' ||
      type === 'branchService' ||
      type === 'menuService' ||
      type === 'roleService' ||
      type === 'nullbusinessService') {
      //特殊请求格式配置
      options.data = JSON.stringify(options.data);
      options.headers['Content-Type'] = 'application/json;charset=utf-8';
      return LOCAL_CONFIG.API_HSIAR + config.COMMON_API + type + '/';

    } else if (type === 'businessService') {
      options.data = JSON.stringify(options.data);
      options.headers['Content-Type'] = 'application/json;charset=utf-8';
      return LOCAL_CONFIG.API_HSIAR + config.COMMON_API + type + '/';

    } else if (type === 'manage') {
      if (options.url == 'export_push_model') {
        options.headers = {
          'Content-Type': 'application/json'
        };
        return LOCAL_CONFIG.API_HSIAR + config.LIGHT_API + 'client/' + type + '/';
      } else {
        return LOCAL_CONFIG.API_HOME + config.OPEN_API + type + '/';
      }

    } else if (type === 'userCenter') {
      options.data = JSON.stringify(options.data);
      options.headers['Content-Type'] = 'application/json;charset=utf-8';
      return LOCAL_CONFIG.API_HSIAR + type + '/';

    } else if (type === 'ifs') {
      options.data = JSON.stringify(options.data);
      options.headers['Content-Type'] = 'application/json';
      return LOCAL_CONFIG.API_HSIAR + type + '/' + config.OPEN_API;
      // return LOCAL_CONFIG.API_HSIAR + config.OPEN_API;  // 财达服务接口转发配置去掉ifs

    } else if (type === 'client') {
      if (options.url == 'manage/func_video_batch_add' || options.url == 'manage/func_article_batch_add') {
        options.headers['Content-Type'] = 'application/json';
        options.data = JSON.stringify(options.data);
      } else {
        options.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
      }
      if (options.url == 'manage/send_push') {
        options.transformRequest = 'angular.identity';
      }
      return LOCAL_CONFIG.API_HSIAR + config.LIGHT_API + type + '/';

    } else if (type === 'fileOpenapi') {
      return LOCAL_CONFIG.API_HOME + type + '/';

    } else if (type === 'manageCenter') {
      options.headers['Content-Type'] = 'application/x-www-form-urlencoded';
      return LOCAL_CONFIG.API_HSIAR + type + '/';

    } else if (type === 'managerService') {
      options.data = JSON.stringify(options.data);
      options.headers['Content-Type'] = 'application/json;charset=utf-8';
      return LOCAL_CONFIG.API_HSIAR + 'manageCenter/' + type + '/';

    } else if (type === 'stockMatch') {
      options.headers = {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Authorization': 'NDAwZDA4ZmUtMDQ1ZC00MmZjLThhNmYtYzZhYTIxN2MyYWJjOmMxYjAwZGUwLTMyYWMtNDBiYi05ZTcwLTAyZmUwOGJjNjA4Yg== '
      };
      return LOCAL_CONFIG.API_HSIAR + type + '/';

    } else if (type === 'compet-biz-trade') {
      options.headers = {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Authorization': sessionStorage.getItem("stockMarketToken")
      };
      return LOCAL_CONFIG.API_HSIAR + 'stockMatch/' + type + '/';
    } else if (type === 'hq') {
      options.headers['Content-Type'] = 'application/x-www-form-urlencoded';
      return LOCAL_CONFIG.API_GTN + type + '/';
    }
  }

  function sendRequest(options) {

    // 默认get请求
    options.method = (options.method === null || options.method === '' || typeof(options.method) === 'undefined') ? 'get' : options.method;
    options.data = (options.data === null || options.data === '' || typeof(options.data) === 'undefined') ? {'date': new Date().getTime()} : options.data;

    //默认入参
    if (options.url !== 'push_list' &&
      options.prefix_type !== 'fileOpenapi' &&
      options.prefix_type !== 'client' &&
      options.prefix_type !== 'manageCenter' &&
      options.url !== 'func_viewpoint_list_manage' &&
      options.url !== 'func_viewpoint_review_status_manange' &&
      options.url !== 'func_viewpoint_review_manange' &&
      options.url !== 'func_self_viewpoint_review_status' &&
      options.url !== 'func_self_viewpoint_list' &&
      options.url !== 'export_push_model' &&
      options.url !== 'func_get_combine_pool') {
      var extra_data = {
        access_token: sessionStorage.getItem('access_token')
      };
    }

    func.myExtend(options.data, extra_data);
    func.myExtend(options, params);

    //涉及到组织机构相关请求时做特殊判断，在请求头按需添加不同的值，中台返回相应内容
    if (options.prefix_type !== 'manage' && options.prefix_type !== 'fileOpenapi' && options.prefix_type !== 'client') {
      if (options.enBranchNo) {
        options.headers = {
          'accessToken': sessionStorage.getItem('access_token'),
          'enBranchCode': 0,
          'enBranchNo': options.enBranchNo
        };
      } else if (options.prefix_type == 'userCenter' || options.prefix_type == 'managerService') {
        options.headers = {
          'accessToken': sessionStorage.getItem('access_token'),
          'enBranchCode': 0
        };
      } else if (options.prefix_type === 'vipstu') {
        options.headers = {};
      } else {
        options.headers = {
          'accessToken': sessionStorage.getItem('access_token')
        };
      }
    }
    if (options.sendType && options.sendType === '1') {
      options.transformRequest = angular.identity;
      options.headers = {
        'Content-Type': undefined,
        'accessToken': sessionStorage.getItem('access_token')
      };
    }

    if (options.url === 'combine_qry' ||
      options.url === 'combine_apply_qry' ||
      options.type === 'manageCenter') {
      options.headers = {
        'accessToken': sessionStorage.getItem('access_token')
      };
    }

    if (options.prefix_type == 'client') {
      options.headers = {
        'accessToken': sessionStorage.getItem('access_token'),
        'Content-Type': 'application/json;charset=UTF-8'
      };
    }

    options.url = appendUrlPrefix(options) + options.url;

    $http({
      url: options.url,
      method: options.method,
      data: options.data,
      headers: options.headers,
      timeout: 10000,
      withCredentials: true
    }).then(function (response) {
      var result = null;
      if (response.data.errorCode || response.data.error_no) {
        result = response.data
      } else if (response.data && response.data.success) {
        result = response.data;
      } else {
        result = response.data.data[0];
      }

      if (isSuccessful(result)) {
        options.successCallback(result);
      } else {
        options.failCallback(result);
      }
    }, function (response) {
      //404等错，一般不考虑，直接在拦截器里面统一做拦截处理
      options.disconnectCallback(response);
    });
  }

  function postRequest(options) {
    options.method = 'post';
    sendRequest(options);
  }

  function getRequest(options) {
    options.method = 'get';
    sendRequest(options);
  }

  //图片上传
  function fileUpload(options) {
    func.myExtend(options, params);
    // console.log('fileUpload', options.headers);
    $http({
      method: 'post',
      url: options.url,
      data: options.data,
      transformRequest: angular.identity,
      headers: {
        "Content-Type": undefined,
        "accessToken": sessionStorage.getItem('access_token')
      }
    }).then(function (response) {
      if (response.data.errorCode || response.data.error_no) {
        var result = response.data
      } else {
        var result = response.data.data[0];
      }
      if (isSuccessful(result)) {
        options.successCallback(result);
      } else {
        options.failCallback(result);
      }
    }, function (response) {
      options.disconnectCallback(response);
    });
  }


  return apiHandler;
}


module.exports = Api;




