require('locale_zh');

jingteng05Ctrl.$inject = ['$sce'];

function jingteng05Ctrl($sce) {
  var vm = this;
  vm.height = window.innerHeight - 100;
  vm.paddingTop = vm.height / 2 - vm.imgHeight / 2;
  vm.access_token = sessionStorage.getItem('access_token');
  vm.url = $sce.trustAsResourceUrl(LOCAL_CONFIG.JINGTENG05 + '?access_token=' + vm.access_token);
}

module.exports = jingteng05Ctrl;


