nonUseStoreModalService.$inject = ['api'];

function nonUseStoreModalService(api) {
  var vm = this;

  vm.funcBrokerChangeStatus = funcBrokerChangeStatus;

  //更改启用与停用状态
  function funcBrokerChangeStatus(data, cb1) {
    api.postRequest({
      prefix_type: 'ifs',
      url: 'manage/func_broker_change_status',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }
}

module.exports = nonUseStoreModalService;



