packageDetailModalService.$inject = ['api'];

function packageDetailModalService(api) {
  var vm = this;
  
  vm.productForceDown = productForceDown;
  vm.productMoreListQry = productMoreListQry;
  
  // 服务包强制下架
  function productForceDown(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'product_force_down',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(err) {
        angular.isFunction(cb2) && cb2(err);
      }
    });
  }

  // 服务包列表查询
  function productMoreListQry(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'product_qry_in_manage',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(err) {
        angular.isFunction(cb2) && cb2(err);
      }
    });
  }
}

module.exports = packageDetailModalService;


