/* 店铺管理 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var addStoreModalCtrl = require('./modalPopup/addStoreModal/controller');
var addStoreModalService = require('./modalPopup/addStoreModal/service');
var editStoreModalCtrl = require('./modalPopup/editStoreModal/controller');
var editStoreModalService = require('./modalPopup/editStoreModal/service');
var nonUseStoreModalCtrl = require('./modalPopup/nonUseStoreModal/controller');
var nonUseStoreModalService = require('./modalPopup/nonUseStoreModal/service');
var tagManageModalCtrl = require('./modalPopup/tagManageModal/controller');
var tagManageModalService = require('./modalPopup/tagManageModal/service');

//setter定义模块
module.exports = angular
  .module('storeManage', [])
  .config(Routing)
  .service('storeManageService', service)
  .controller('storeManageCtrl', ctrl)
  .factory('addStoreModalCtrl', addStoreModalCtrl)
  .service('addStoreModalService', addStoreModalService)
  .factory('editStoreModalCtrl', editStoreModalCtrl)
  .service('editStoreModalService', editStoreModalService)
  .factory('nonUseStoreModalCtrl', nonUseStoreModalCtrl)
  .service('nonUseStoreModalService', nonUseStoreModalService)
  .factory('tagManageModalCtrl', tagManageModalCtrl)
  .service('tagManageModalService', tagManageModalService).name;



