rolesModalService.$inject = ['api'];
function rolesModalService(api) {
  var vm = this;
  vm.roleAdd = roleAdd;//增加角色信息
  vm.roleQry = roleQry;//增加角色展示信息
  vm.allBranchQry = allBranchQry; // 查询机构列表


  function roleAdd(data, cb1) {
    api.postRequest({
      prefix_type: 'roleService',
      url: 'roleAdd',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function roleQry(data, cb1) {
    api.postRequest({
      prefix_type: 'roleService',
      url: 'roleQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //获取机构列表
  function allBranchQry(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = rolesModalService;



