/*批量新增投顾*/

batchAddNewInvestService.$inject = ['api'];

function batchAddNewInvestService(api) {
  var vm = this;
  vm.saveFunc = saveFunc; //批量保存

  function saveFunc(data, cb1) {
    api.postRequest({
      prefix_type: 'businessService',
      url: 'businessAdviserBatchAdd',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = batchAddNewInvestService;



