/**
 *
 * @authors liwb (you@example.org)
 * @date    2016/12/21 10:52
 * @version $ 运行块通常用来注册全局的事件监听器。设置路由事件的监听器以及过滤未经授权的请求。
 */

/* name module */
var modalOptions = require('jsDir/modal_options');
AuthService.$inject = ['$rootScope', '$state', '$stateParams', '$cookies','utilService','modalService', '$injector'];

function AuthService($rootScope, $state, $stateParams, $cookies, utilService, modalService, $injector) {
  var vm = this;
  /**
   * 初始化服务，这样就可以在html页面中直接调用
   * @type {$state|*}
   */
  $rootScope.$state = $state;
  $rootScope.$stateParams = $stateParams;
  $rootScope.i18n = i18n;
  $rootScope.util = utilService;

  $rootScope.$on('$stateChangeSuccess', function (event, toState, toParams, fromState, fromParams) {
    //页面刷新时存储当前路由信息
    if (fromState.name == '') {
      sessionStorage.setItem('currentUrl', JSON.stringify({url: toState.name}));
    }
  });

  $rootScope.$on('$stateChangeStart', function (event, toState, toParams, fromState, fromParams) {
    var toName = toState.name;
    var uibModalStack = $injector.get('$uibModalStack');

    if (toName == "login"){
      //当前有弹窗出现时，路由修改为登录后强制关闭所有弹窗
	    uibModalStack.dismissAll();
      return;
    }

    // 如果用户不存在或用户未登录
    if (angular.isUndefined(sessionStorage.getItem('access_token'))) {
      event.preventDefault();// 取消默认跳转行为
      var options = angular.copy(modalOptions);
      options.modalInfo = '登录已超时';
      options.modalTitle = '提示';
      options.modalPriority = 'unique';
      options.confirmCallback = function (result) { //弹窗确认的回调函数
        $state.go("login");
      };
      options.cancelCallback = function (result) { //弹窗关闭的回调函数
        $state.go("login");
      };
      modalService.openModal(options);
    } else {
      //当前为登录状态并由弹窗出现时，更改路由后强制所有弹窗关闭
	    uibModalStack.dismissAll();
    }

  });
}

module.exports = AuthService;



