/* 角色新增 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  rolesModalTempl = require('./rolesModal.html');

rolesModalCtrl.$inject = ['modalService', 'rolesModalService'];

function rolesModalCtrl(modalService, rolesModalService) {
  var vm = this;

  /*函数声明*/
  vm.openRolesModalFunc = openRolesModalFunc;

  /*函数定义*/
  function openRolesModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '角色新增';
    options.template = rolesModalTempl;
    options.size = 'md';
    options.controller = rolesModalController;
    options.modalParams = data;  //列表传递数据
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function rolesModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText, ensureBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.timer = null; // 防抖函数的时间对象
    ra.keyword = ''; // 机构搜索关键字
    ra.role_modal_branchNo = '';
    ra.role_modal_branchName = '';
    ra.role_modal_name = '';
    ra.branchInfo = '';
    ra.isMod = false;
    ra.addBranchNo = '';
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.ensureBtnText = ensureBtnText;
    ra.cancelBtnText = cancelBtnText;
    ra.dataMulList = modalParams.branch;  //获取机构编码
    ra.rolesList = modalParams.roleType;  //获取角色列表
    ra.dicts = [{"code":"1","value":"基本类"},{"code":"2","value":"管理类"},{"code":"3","value":"运营类"}];
    ra.cancel = cancel;
    ra.close = close;
    ra.submit = submit;
    ra.selectBranchNoFunc = selectBranchNoFunc;
    ra.searchByKeywordFunc = searchByKeywordFunc;

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

    function submit() {
      if (ra.role_modal_branchNo && ra.role_modal_name && ra.role_modal_type) {
        ra.isMod = false;
        rolesModalService.roleAdd({
          branchNo: ra.role_modal_branchNo,
          roleName: ra.role_modal_name,
          roleType: ra.role_modal_type
        }, function (response) {
          var result = {
            roleId: response.data.roleId,
            branchNo: ra.role_modal_branchNo,
            branchName: ra.role_modal_branchName
          };
          $uibModalInstance.close(result);
          var options = angular.copy(modalOptions);
          options.modalTitle = '成功';
          options.modalInfo = '角色添加成功！';
          options.modalPriority = 'unique';
          options.size = 'md';
          modalService.openModal(options);
          close();
        });
      } else {
        modalService.openAlert('提示', '请验证表单');
      }
    }

    // 机构选择
    function selectBranchNoFunc(branchNo, branchName) {
      ra.role_modal_branchNo = branchNo;
      ra.role_modal_branchName = branchName;
    }

    // 关键字查询机构列表（防抖）
    function searchByKeywordFunc() {
      clearTimeout(ra.timer);
      ra.timer = setTimeout(function () {
        getAllBranch();
      }, 1000);
    }

    // 查询机构列表
    function getAllBranch() {
      rolesModalService.allBranchQry(
        {
          keyword: ra.keyword
        },
        function(response2) {
          ra.dataMulList = response2.data;
        }
      );
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = rolesModalCtrl;




