/* 创建侧边导航栏 */

require('./menuView.less');
var menuViewTempl = require('./menuView.html'),
    urlConfig = require('./urlConfig');
    menuIcon = require('./menuIconConfig');

menuView.$inject = ['$rootScope'];

function menuView($rootScope) {
  return {
    restrict: 'E',
    scope: {
      menuData: '=',
      unfoldSide: '='
    },
    template: menuViewTempl,
    link: function ($scope) {

      $scope.menuList = [];//侧边二级菜单
      $scope.itemList = [];//侧边三级菜单
      $scope.foldMenuList = [];//侧边栏收缩时二级菜单

      $scope.unfoldSide = false;//是否收缩侧边栏

      $scope.getUrl = getUrl;
      $scope.dropDownMenu = dropDownMenu;
      $scope.shrinkSide = shrinkSide;

      //定位到目标菜单
      $rootScope.$on('redirect', function (event, param) {
        setTimeout(function () {
          var aList = $("a[class='open-menu']");
          angular.forEach(aList, function (item, index) {
            if (item.innerText.split(' ')[1] == param) {
              $('#openMenu_' + index).addClass('selected');
              $('#itemMenu_' + index).addClass('selected');
            }
          });
        }, 300);
      });

	  //监听一级菜单数据
      $scope.$watch('menuData', function (newValue) {
        var menu = [];//全部数据数组
        var foldMenuIcon = [];//侧边栏收缩时二级菜单icon数组

        //整理菜单结构
        angular.forEach(newValue.children, function (item) {
          var dataList = {};//每一个二级菜单数据对象
          var childrenList = [];//每一二级菜单中所有三级菜单数据数组

          //如果有三级模块则配置三级模块数据
          item.children && item.children.length && angular.forEach(item.children, function (per) {
            if (per.menuUrl != ' ' && per.menuUrl != '') {
              var children = {};
              children.name = per.menuCaption;
              children.url = $scope.getUrl(per.menuId);
              childrenList.push(children);
            }
          });

          //配置二级模块数据
          dataList.name = item.menuCaption;
          dataList.icon = menuIcon[item.menuId];
          dataList.children = childrenList;
          if (item.menuUrl != ' ' && item.menuUrl != '') {
            dataList.url = $scope.getUrl(item.menuId);
          } else {
            dataList.url = $scope.getUrl(item.children[0].menuId);
          }
          menu.push(dataList);
          foldMenuIcon.push(dataList.icon);
        });

        $scope.menuList = menu;
        $scope.foldMenuList = foldMenuIcon;
      });

      //处理侧边栏是否收缩
      $scope.$watch('unfoldSide', function (newValue) {
        $scope.unfoldSide = newValue;
      });

      //通过id配置相应的url
      function getUrl(id) {
        if (id != undefined && id.length != 0) {
          var url = urlConfig[id];
          if (url != undefined && url.length != 0) {
            return url;
          } else {
            return 'home.welcome';
          }
        }
      }

      //是否显示三级菜单
      function dropDownMenu(id) {
        if ($('#itemMenu_' + id).hasClass('selected') && $('#openMenu_' + id).hasClass('selected')) {
          $('#itemMenu_' + id).removeClass('selected');
          $('#openMenu_' + id).removeClass('selected');
        } else {
          $('.open-menu').removeClass('selected');
          $('.item-menu').removeClass('selected');
          $('#itemMenu_' + id).addClass('selected');
          $('#openMenu_' + id).addClass('selected');
        }
      }

      //侧边栏收缩状态下的点击事件
      function shrinkSide() {
        // $scope.unfoldSide = !$scope.unfoldSide;
      }
    }
  }
}

module.exports = menuView;



