editPictureModalService.$inject = ['api','img'];
function editPictureModalService(api,img) {
  var vm = this;

  vm.adpagePicFunc = adpagePicFunc; //更换图片
  vm.upload = upload; //图片路径


  function adpagePicFunc(data,cb1) {
    api.postRequest ({
      prefix_type: 'manage',
      url: 'func_operate_adpage_update',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }

  function upload(data,cb1) {
    img.postImgRequest ({
      prefix_type: 'fileOpenapi',
      url: 'func_file_upload?sysno=1',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }

}

module.exports = editPictureModalService;



