/* 观点标签设置 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var combineCheckModalCtrl = require('./modalPopup/combineCheckModal/controller');
var combineCheckModalService = require('./modalPopup/combineCheckModal/service');
var combineRemarkModalCtrl = require('./modalPopup/combineRemarkModal/controller');
var combineDownModalCtrl = require('./modalPopup/combineDownModal/controller');
var combineDownModalService = require('./modalPopup/combineDownModal/service');
var combineChangeModalCtrl = require('./modalPopup/combineChangeModal/controller');

//setter定义模块
module.exports = angular
  .module('combineShelfCheck', [])
  .config(Routing)
  .controller('combineShelfCheckCtrl', ctrl)
  .service('combineShelfCheckService', service)
  .factory('combineCheckModalCtrl', combineCheckModalCtrl)
  .service('combineCheckModalService', combineCheckModalService)
  .factory('combineRemarkModalCtrl', combineRemarkModalCtrl)
  .factory('combineDownModalCtrl', combineDownModalCtrl)
  .service('combineDownModalService', combineDownModalService)
  .factory('combineChangeModalCtrl', combineChangeModalCtrl)
  .name;



