servePackageCheckService.$inject = ['api'];
function servePackageCheckService(api) {
  var vm = this;

  vm.productListQry = productListQry;
  vm.productDetailQry = productDetailQry;
  vm.productApproval = productApproval;

  // 获取服务包列表信息
  function productListQry(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'product_op_history_qry_in_manage',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(err) {
        angular.isFunction(cb2) && cb2(err);
      }
    });
  }

  // 服务包详情查询
  function productDetailQry(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'product_qry_in_manage',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(err) {
        angular.isFunction(cb2) && cb2(err);
      }
    });
  }

  // 服务包审核
  function productApproval(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'product_approval',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(err) {
        angular.isFunction(cb2) && cb2(err);
      }
    });
  }

  return vm;
}

module.exports = servePackageCheckService;


