/* 操作员冻结 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  operatorFrozenTempl = require('./operatorFrozen.html');

operatorFrozenCtrl.$inject = ['modalService', 'operatorFrozenService'];

function operatorFrozenCtrl(modalService, operatorFrozenService) {
  var vm = this;

  /*函数声明*/
  vm.operatorFrozenFunc = operatorFrozenFunc;


  /*函数定义*/
  function operatorFrozenFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '操作员冻结';
    options.template = operatorFrozenTempl;
    options.size = 'md';
    options.controller = operatorFrozenController;
    options.modalParams = data;  //列表传递数据
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function operatorFrozenController( $uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, frozenBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.isMod = false;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.frozenBtnText = frozenBtnText;

    ra.dataMulList = modalParams.branch;  //获取机构编码
    ra.branchNo = modalParams.branchNo;
    ra.dicts_type = [{"code":"1","value":"管理员"},{"code":"2","value":"普通操作员"}]; //操作员类型
    ra.dicts_status = [{"code":"0","value":"正常"},{"code":"1","value":"冻结"},{"code":"2","value":"锁定"}];
    ra.operator_modal_type = modalParams.type;
    ra.operator_modal_code = modalParams.operatorNo;
    ra.operator_modal_name = modalParams.name;
    ra.operator_modal_phone = modalParams.phoneNo;
    ra.operator_modal_remark = modalParams.remark;
    ra.operator_modal_status = modalParams.status;
    ra.status_name = ra.dicts_status[ra.operator_modal_status].value;
    ra.cancel = cancel;
    ra.close = close;
    ra.submit = submit;

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

    function submit() {
        ra.isMod = false;
        operatorFrozenService.operatorFrozen({
          branchName: ra.dataMulList,
          branchNo: ra.branchNo,
          branchRights: modalParams.branchRights,
          hsstatus: 'hsFrozen',
          name: ra.operator_modal_name,
          operatorNo: ra.operator_modal_code,
          operatorType: ra.operator_modal_type,
          registerDay: modalParams.registerDay,
          remark: ra.operator_modal_remark,
          roleRights: modalParams.roleRights,
          rowCount: modalParams.rowCount,
          status: modalParams.status,
          status_name: ra.status_name,
          telephone: ra.operator_modal_phone
        }, function (response) {
          $uibModalInstance.close();
          var options = angular.copy(modalOptions);
          options.modalTitle = '成功';
          options.modalInfo = '操作员冻结成功！';
          options.modalPriority = 'unique';
          options.size = 'md';
          modalService.openModal(options);
          close();
        });
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = operatorFrozenCtrl;




