signStatisticsQryService.$inject = ['api'];
function signStatisticsQryService(api) {
  var vm = this;

  vm.branchQryInOrderFunc = branchQryInOrderFunc;
  vm.branchSignStatisticQryFunc = branchSignStatisticQryFunc;
  vm.brokerSignStatisticQryFunc = brokerSignStatisticQryFunc;

  /**
   * 查询机构信息列表
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function branchQryInOrderFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'branch_qry_in_sign',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  /**
   * 机构签约统计查询
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function branchSignStatisticQryFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'branch_sign_statistic_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  /**
   * 投顾签约统计查询
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function brokerSignStatisticQryFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'broker_sign_statistic_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = signStatisticsQryService;


