basicValueSettingService.$inject = ['api'];
function basicValueSettingService(api) {
  var vm = this;
  vm.basicValueSet = basicValueSet;
  vm.basicValueQry = basicValueQry;

  //设置基础值
  function basicValueSet(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'basic_value_set',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //查询基础值
  function basicValueQry(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'basic_value_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }


  return vm;
}

module.exports = basicValueSettingService;


