/* 客户管理 module */

require('locale_zh');
window.ExcellentExport = require('ExcellentExport'); //导出
var avatarImg = require('img/avatar.png');
var modalOptions = require('jsDir/modal_options'),
  func = require('func');

customerManageCtrl.$inject = ['customerManageService', 'modalService', 'subscribeModalCtrl', 'orderModalCtrl', 'downModalCtrl', 'combineModalCtrl'];

function customerManageCtrl(customerManageService, modalService, subscribeModalCtrl, orderModalCtrl, downModalCtrl, combineModalCtrl) {
  var vm = this;

  vm.avatarImg = avatarImg;   //头像图片

  vm.customerList = []; //当前展示的客户信息列表
  vm.customerKey = '';  //查询关键词
  vm.selectedData = 0;  //选中的列表序号，初始为第一条
  vm.userStatus = 0;    //0：正常客户，1：限制客户
  vm.combineList = [];    // 关注组合列表
  vm.orderList = [];    // 订单历史列表
  vm.data = {
    current: "1"  // tab切换默认显示
  };

  //分页相关
  vm.maxSize = 5;
  vm.totalItems = '';
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  //客户绩效相关数据
  vm.performList = {};
  vm.askNum = '';
  vm.commentNum = '';
  vm.upvoteNum = '';
  vm.liveNum = '';
  vm.fund = '';
  vm.times = '';

  vm.noClickExport = false;  //初始化可点击导出执行下载任务

  vm.SendMsg = SendMsg;//禁言
  vm.customerQry = customerQry;  //查询用户列表信息
  vm.subscribeMore = subscribeMore;  //订阅历史点击更多
  vm.combineMore = combineMore;  // 关注历史点击更多
  vm.orderMore = orderMore;  //订单历史点击更多
  vm.customerSearch = customerSearch; //搜索框
  vm.customerFrozen = customerFrozen;  //限制
  vm.customerUnFrozen = customerUnFrozen;  //解除限制
  vm.pageChangeFunc = pageChangeFunc; //换页方法
  vm.confirmDown = confirmDownFunc; // 确定导出
  vm.actions = {
    setCurrent: function (param) {
      vm.customerKey = '';
      vm.currentPage = 1;
      vm.data.current = param;
      vm.userStatus = param - 1;
      customerQry();
    },
    setDetail: function (param) {
      vm.selectedData = param;
      performQry(); //客户绩效查询
      subQry(); //客户订阅查询
      // signQry(); //客户签约查询
      combineCollectQry();
      orderManageQry();
    }
  };

  init();
  
  //初始化
  function init() {
    customerQry();
  }

  //查询用户列表信息
  function customerQry() {
    customerManageService.userListFunc({
      key_word: vm.customerKey,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage,
      user_status: vm.userStatus,
      user_type: 3
    }, function (response) {
      vm.customerList = response.data_list;
      vm.totalItems = response.total_item_num;
      if (vm.customerList.length) {
        performQry();
        subQry();
        // signQry();
        combineCollectQry();
        orderManageQry();
      }
    }, function (result) {
      vm.customerList = [];
      vm.totalItems = 0;
    })
  }

  //确认导出
  function confirmDownFunc() {
    var data = {
      customerKey: vm.customerKey,
      userStatus: vm.userStatus
    };
    downModalCtrl.openDownModalFunc(data);
  }

  //客户绩效查询
  function performQry() {
    vm.askNum = '';
    vm.commentNum = '';
    vm.upvoteNum = '';
    vm.liveNum = '';
    customerManageService.performQryFunc({
      tenantId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].tenant_id || '',
      userId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].user_id || ''
    }, function (response) {
      vm.performList = response.data;
      vm.askNum = vm.performList.askNum;
      vm.commentNum = vm.performList.commentNum;
      vm.upvoteNum = vm.performList.upvoteNum;
      vm.liveNum = vm.performList.liveNum;
      vm.fund = vm.performList.totalousumeAmount;
      vm.times = vm.performList.totalCousumeTimes;
    })
  }

  //客户订阅查询
  function subQry() {
    customerManageService.subQryFunc({
      pageSize: 2,
      positionStr: '',
      tenantId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].tenant_id || '',
      userId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].user_id || ''
    }, function (response) {
      vm.subList = response.data;
    })
  }

  //客户签约历史查询
  // function signQry() {
  //   customerManageService.signQryFunc({
  //     pageSize: 2,
  //     positionStr: '',
  //     tenantId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].tenant_id || '',
  //     userId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].user_id || ''
  //   }, function (response) {
  //     vm.signList = response.data;
  //   })
  // }

  // 客户关注组合历史查询
  function combineCollectQry() {
    customerManageService.combineCollectQryFunc({
      company_id: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].tenant_id || '',
      position_str: 0,
      request_num: 5
    }, function(res) {
      vm.combineList = res.data_list;
    }, function(err) {
      vm.combineList = [];
    })
  }

  // 客户订单历史查询
  function orderManageQry() {
    customerManageService.orderManageFunc({
      tenantId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].tenant_id || '',
      page_no: 1,
      page_size: 5
    }, function(res) {
      vm.orderList = res.data_list;
    }, function(err) {
      vm.orderList = [];
    })
  }

  //订阅查询点击更多
  function subscribeMore() {
    var data = {
      pageSize: 5,
      tenantId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].tenant_id || '',
      userId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].user_id || ''
    };
    subscribeModalCtrl.openSubscribeModalFunc(data);
  }

  //订单查询点击更多
  function orderMore() {
    var data = {
      pageSize: 5,
      tenantId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].tenant_id || '',
      userId: vm.customerList[vm.selectedData] && vm.customerList[vm.selectedData].user_id || ''
    };
    orderModalCtrl.openOrderModalFunc(data);
  }

  // 关注历史点击更多
  function combineMore() {
    combineModalCtrl.openCombineModalFunc(vm.customerList[vm.selectedData] || {});
  }

  //限制
  function customerFrozen(item) {
    customerManageService.frozenFunc({
      userId: vm.customerList[item].user_id
    }, function (response) {
      customerQry();
      // performQry();
      // subQry();
      // signQry();
      // combineCollectQry();
      var options = angular.copy(modalOptions);
      options.modalTitle = '成功';
      options.modalInfo = '限制成功！';
      modalService.openModal(options);
    })
  }

  //解除限制
  function customerUnFrozen(item) {
    customerManageService.frozenUnFunc({
      userId: vm.customerList[item].user_id
    }, function (response) {
      customerQry();
      // performQry();
      // subQry();
      // signQry();
      // combineCollectQry();
      var options = angular.copy(modalOptions);
      options.modalTitle = '成功';
      options.modalInfo = '解除限制成功！';
      modalService.openModal(options);
    })
  }

  //禁言
  function SendMsg(item) {
    customerManageService.silentFunc({
      sendmsg_status: parseInt(vm.customerList[item].sendmsg_status) == 0 ? 1 : 0,
      sign: "17AD57E731C3CF99EC72703976577EA4",
      source: "cloudbroker_3",
      user_id: vm.customerList[item].user_id
    }, function (response) {
      vm.customerList[item].sendmsg_status == "1" ? "0" : "1";
      customerQry();
      var options = angular.copy(modalOptions);
      options.modalTitle = '成功';
      options.modalInfo = '操作成功！';
      modalService.openModal(options);
    })
  }

  //搜索框
  function customerSearch() {
    vm.selectedData = 0;
    customerManageService.userListFunc({
      key_word: vm.customerKey,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage,
      user_status: vm.userStatus,
      user_type: 3
    }, function (response) {
      vm.customerList = response.data_list;
      vm.totalItems = response.total_item_num;
      if (vm.customerList.length) {
        performQry();
        subQry();
        // signQry();
        combineCollectQry();
        orderManageQry();
      } else {
        vm.subList = [];
        vm.combineList = [];
        vm.orderList = [];
      }
    })
  }

  //分页
  function pageChangeFunc() {
    customerQry();
  }

}

module.exports = customerManageCtrl;



