require('locale_zh');

unifyPayment01Ctrl.$inject = ['$sce'];

function unifyPayment01Ctrl($sce) {
  var vm = this;
  vm.height = window.innerHeight - 100;
  vm.paddingTop = vm.height / 2 - vm.imgHeight / 2;
  vm.access_token = sessionStorage.getItem('access_token');
  vm.url = $sce.trustAsResourceUrl(LOCAL_CONFIG.UNIFYPAYMENT01 + '?access_token=' + vm.access_token);
  console.log('url', vm.url);
}

module.exports = unifyPayment01Ctrl;


