/* 编辑用户信息弹窗 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
    func = require('func'),
    editInfoModalTempl = require('./editInfoModal.html');

editInfoModalCtrl.$inject = ['modalService', 'editInfoModalService', '$interval', 'utilService'];

function editInfoModalCtrl(modalService, editInfoModalService, $interval, utilService) {
  var vm = this;

  vm.userInfo = {};//用户信息数据

  vm.openEditInfoModalFunc = openEditInfoModalFunc;

  function openEditInfoModalFunc(userInfo, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '编辑信息';
    options.template = editInfoModalTempl;
    options.controller = defaultController;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };

    vm.userInfo = userInfo;

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.userName = vm.userInfo.userName;
    ra.telephone = vm.userInfo.telephone;
    ra.newTelephone = '';
    ra.verifyCode = '';
    ra.remark = vm.userInfo.remark;
    ra.btnCodeNum = '获取';
    ra.btnUntouch = false;//倒计时按钮默认可点击

    ra.editInfo = editInfo;
    // ra.sendVerifyCode = sendVerifyCode;
    ra.cancel = cancel;
    ra.close = close;


    //修改信息
    function editInfo() {
      editInfoModalService.editInfo(
        {
          newTelephone: ra.telephone,
          oldTelephone: ra.telephone,
          operatorName: ra.userName,
          remark: ra.remark || ' ',
          verifyCode: ''
        },
        function (result) {
          func.openToaster('保存成功！');
          close();
        }
      )
      // if (ra.newTelephone && ra.verifyCode) {
      //   editInfoModalService.editInfo(
      //     {
      //       newTelephone: ra.newTelephone,
      //       oldTelephone: ra.telephone,
      //       operatorName: ra.userName,
      //       remark: ra.remark,
      //       verifyCode: ra.verifyCode
      //     },
      //     function (result) {
      //       func.openToaster('保存成功！');
      //       close();
      //     }
      //   )
      // } else {
      //   func.openToaster('请输入新手机号和验证码！');
      // }
    }

    //发送验证码
    // function sendVerifyCode() {
    //   if (ra.newTelephone) {
    //     //倒计时
    //     var number = 60;
    //     ra.btnCodeNum = number + 's';
    //     ra.btnUntouch = true;//倒计时开始按钮不可点击
    //     var promise = $interval(function() {
    //       if(number <= 0) {
    //         $interval.cancel(promise);
    //         ra.btnCodeNum = '获取';
    //         ra.btnUntouch = false;//倒计时结束按钮可点击
    //       } else {
    //         number--;
    //         ra.btnCodeNum = number + 's';
    //       }
    //     }, 1000);

    //     utilService.sendSms(
    //       {
    //         newTelephone: ra.newTelephone,
    //         oldTelephone: ra.telephone
    //       },
    //       function (result) {
    //         func.openToaster('发送验证码成功！');
    //       }
    //     )
    //   } else {
    //     func.openToaster('请输入新手机号！');
    //   }
    // }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = editInfoModalCtrl;



