/* 直播监控 module */

liveMonitorContentRoute.$inject = ['$stateProvider'];

function liveMonitorContentRoute($stateProvider) {
  $stateProvider.state('home.liveMonitorContent', {
    url: '/liveMonitorContent?topicId?topic?user',
    templateUrl: './views/liveMonitorContent.html',
    controller: 'liveMonitorContent',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = liveMonitorContentRoute;



