operatorEditService.$inject = ['api'];
function operatorEditService(api) {
  var vm = this;
  vm.operatorEdit = operatorEdit;//修改操作员信息


  function operatorEdit(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorMod',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }
  return vm;
}

module.exports = operatorEditService;



