serviceProduct02Route.$inject = ['$stateProvider'];

function serviceProduct02Route($stateProvider) {
    $stateProvider
        .state('home.serviceProduct02', {
            url: '/serviceProduct02',
            templateUrl: './views/serviceProduct02.html',
            controller: 'serviceProduct02Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = serviceProduct02Route;



