/*图片配置-修改图片 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  editPictureModalTempl = require('./editPictureModal.html');

editPictureModalCtrl.$inject = ['modalService'];

function editPictureModalCtrl(modalService) {
  var vm = this;

  /*函数声明*/
  vm.editPictureModalFunc = editPictureModalFunc;

  /*函数定义*/
  function editPictureModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = (data.item == '2' || data.item == '4') ? '修改' : '新增';
    options.template = editPictureModalTempl;
    options.controller = editPictureModalController;
    options.modalParams = data;
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function editPictureModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText, editPictureModalService) {
    var ra = this;
    var errorNo = modalParams.error_no;
    var obj = {};
    ra.picAddress = modalParams.item == '2' ? modalParams.adver.page_url : '';
    ra.loadUrl = '';
    ra.changeFlag = false;
    ra.src = modalParams.item == '1' ? '' : modalParams.src;
    ra.showPath = modalParams.item == '2' ? modalParams.path : ra.src;
    ra.picShow = modalParams.item == '1' || modalParams.item == '3' ? '' : ra.showPath;
    ra.picList = modalParams.picList;
    ra.index = modalParams.item == '2' ? modalParams.index : '';
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = '确认修改';
    ra.cancelBtnText = cancelBtnText;
    ra.sendImgFlag = true;
    ra.item = modalParams.item;

    ra.cancel = cancel;
    ra.close = close;
    ra.readPicAddress = readPicAddress; //点击添加图片
    ra.changeLoadPic = changeLoadPic; //提交改变图片

    function close(result) {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close(result);
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

    //点击添加图片
    function readPicAddress(event) {
      console.log('event', event);
      var r = new FileReader();
      var file = event.currentTarget.files[0];
      if (file.size > 2 * 1024 * 1024) {
        modalService.openAlert('提示', '图片大小不得大于2M!', '', 2000);
        return false;
      }
      if (file.type == 'image/jpeg' || file.type == 'image/jpg' || file.type == 'image/png' || file.type == 'image/bmp' || file.type == 'image/gif') {
        //$log.info("upload file type:" + imgType);
      } else {
        modalService.openAlert('提示', '上传文件不是图片类型!', '', 2000);
        return false;
      }
      r.readAsDataURL(file);
      var a = window.URL.createObjectURL(file);

      r.onload = function (e) {
        document.getElementById('changeLoading').src = a;
        ra.picShow = this.result;
      };
      ra.alter_token = '92f17e36e6cf0a8083ff435fc708a372';
      var formData = new FormData();
      formData.append("user_token", ra.alter_token);
      formData.append("type", "img");
      formData.append("file", file);
      ra.sendImgFlag = true;  //上锁
      editPictureModalService.upload(formData,
        function (response) {
          ra.imgContent = { //引用对象,img
            ossid: response.oss_id,     //名称
            format: response.format,    //格式
            size: response.size,       //大小
            link: response.oss_prefix + response.oss_id
          };
        })
    }

    function changeLoadPic() {
      if (modalParams.item == '1' || modalParams.item == '2') {
        ra.loadUrl = modalParams.load;
      }
      if (ra.imgContent && modalParams.item == '1') {
        ra.adverUrl = ra.imgContent.ossid;
      }
      if (modalParams.item == '2') {
        ra.adverUrl = ra.imgContent ? ra.imgContent.ossid : modalParams.adver.page_img;
      }
      if (ra.imgContent && modalParams.item == '3') {
        ra.loadUrl = ra.imgContent.ossid;
      }
      if (modalParams.item == '4') {
        ra.loadUrl = ra.imgContent ? ra.imgContent.ossid : modalParams.load;
      }
      if (ra.adverUrl) {
        obj = {
          page_url: ra.picAddress,
          page_img: ra.adverUrl,
          position_str: ''
        };
        if (modalParams.item == '1') {
          ra.picList.push(obj);
        }
        if (modalParams.item == '2') {
          ra.picList[ra.index] = obj;
        }
      }
      ra.changeFlag = true;
      editPictureModalService.adpagePicFunc({
        broker_id: 0,
        tenant_id: modalParams.tenant_id,
        pay_status: 1,
        loading_img_url: ra.loadUrl,
        data_list: JSON.stringify(ra.picList),
        branch_no: 1,
        jsonp: 'jsonpcallback',
        page_count: 10
      }, function (response) {
        var result = {
          src: vm.loadUrl
        };
        close(result);
      });
      ra.changeFlag = false;
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = editPictureModalCtrl;



