/* 敏感词库管理 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var editSensitiveWordModalService = require('./modalPopup/editSensitiveWordModal/service');
var editSensitiveWordModalCtrl = require('./modalPopup/editSensitiveWordModal/controller');

//setter定义模块
module.exports = angular
  .module('sensitiveWords', [])
  .config(Routing)
  .service('sensitiveWordsService', service)
  .controller('sensitiveWordsCtrl', ctrl)
  .service('editSensitiveWordModalService', editSensitiveWordModalService)
  .factory('editSensitiveWordModalCtrl', editSensitiveWordModalCtrl).name;



