/* 股票池管理 module */

stockPoolManageService.$inject = ['api'];
function stockPoolManageService(api) {
  var vm = this;

  vm.stockPoolQry = stockPoolQry;
  vm.stockPoolDel = stockPoolDel;

  /**
   * 获取股票池列表
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function stockPoolQry(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'stock_pool_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  /**
   * 股票池中删除股票
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function stockPoolDel(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'stock_pool_del',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = stockPoolManageService;



