logDetailModalService.$inject = ['api'];
function logDetailModalService(api) {
  var vm = this;
  vm.logDetail = logDetail;//操作员详情


  function logDetail(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorAdd',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }
  return vm;
}

module.exports = logDetailModalService;



