/* name module */

loginRoute.$inject = ['$stateProvider'];

function loginRoute($stateProvider) {
    $stateProvider
        .state('login', {
            url: '/login',
            templateUrl: './views/login.html',
            controller: 'loginCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = loginRoute;



