/* 主页 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var userInfoModalCtrl = require('./modalPopup/userInfoModal/controller');
var userInfoModalService = require('./modalPopup/userInfoModal/service');
var editInfoModalCtrl = require('./modalPopup/editInfoModal/controller');
var editInfoModalService = require('./modalPopup/editInfoModal/service');
var resetPasswordModalCtrl = require('./modalPopup/resetPasswordModal/controller');
var resetPasswordModalService = require('./modalPopup/resetPasswordModal/service');

//setter定义模块
module.exports = angular.module('app.home', [])
    .config(Routing)
    .service('homeService', service)
    .controller('homeCtrl', ctrl)
    .factory('userInfoModalCtrl', userInfoModalCtrl)
    .service('userInfoModalService', userInfoModalService)
    .factory('editInfoModalCtrl', editInfoModalCtrl)
    .service('editInfoModalService', editInfoModalService)
    .factory('resetPasswordModalCtrl', resetPasswordModalCtrl)
    .service('resetPasswordModalService', resetPasswordModalService)
    .name;



