require('locale_zh');

unifyPayment02Ctrl.$inject = ['$sce'];

function unifyPayment02Ctrl($sce) {
  var vm = this;
  vm.height = window.innerHeight - 100;
  vm.paddingTop = vm.height / 2 - vm.imgHeight / 2;
  vm.access_token = sessionStorage.getItem('access_token');
  vm.url = $sce.trustAsResourceUrl(LOCAL_CONFIG.UNIFYPAYMENT02 + '?access_token=' + vm.access_token);
}

module.exports = unifyPayment02Ctrl;


