/*新增投顾*/

addNewInvestRoute.$inject = ['$stateProvider'];

function addNewInvestRoute($stateProvider) {
  $stateProvider
    .state('home.addNewInvest', {
      url: '/addNewInvest?params?type',
      templateUrl: './views/addNewInvest.html',
      controller: 'addNewInvestCtrl',
      controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
    });
}

module.exports = addNewInvestRoute;



