/* 观点标签设置 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var liveCheckModalCtrl = require('./modalPopup/liveCheckModal/controller');
var liveCheckModalService = require('./modalPopup/liveCheckModal/service');
var liveIntroModalCtrl = require('./modalPopup/liveIntroModal/controller');
var liveIntroModalService = require('./modalPopup/liveIntroModal/service');
var checkRemarkModalCtrl = require('./modalPopup/checkRemarkModal/controller');
var checkRemarkModalService = require('./modalPopup/checkRemarkModal/service');

//setter定义模块
module.exports = angular
  .module('liveCheckCtrl', [])
  .config(Routing)
  .controller('liveCheckCtrl', ctrl)
  .service('liveCheckService', service)
  .factory('liveCheckModalCtrl', liveCheckModalCtrl)
  .service('liveCheckModalService', liveCheckModalService)
  .factory('liveIntroModalCtrl', liveIntroModalCtrl)
  .service('liveIntroModalService', liveIntroModalService)
  .factory('checkRemarkModalCtrl', checkRemarkModalCtrl)
  .service('checkRemarkModalService', checkRemarkModalService)
  .name;



