/* 直播监控 module */

liveMonitorContentService.$inject = ['api'];

function liveMonitorContentService(api) {
  var vm = this;

  vm.getLiveList = getLiveList;
  vm.getLiveCommentList = getLiveCommentList;
  vm.deleteLiveComment = deleteLiveComment;

  //获取直播列表
  function getLiveList(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'func_rolling_msg_list_get ',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //获取直播评论列表
  function getLiveCommentList(data, cb1) {
    api.postRequest({
      prefix_type: 'manage',
      url: 'func_chat_content',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //删除直播评论
  function deleteLiveComment(data, cb1) {
    api.postRequest({
      prefix_type: 'manage',
      url: 'func_batch_del_chat_content',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = liveMonitorContentService;



