combineOnlineModalService.$inject = ['api'];
function combineOnlineModalService(api) {
  var vm = this;
  vm.tenantId = JSON.parse(sessionStorage.getItem("tenantId"));
  vm.tenantId = parseInt(vm.tenantId);

  vm.getUserToken = getUserToken;   //获取user_token
  vm.getCombineList = getCombineList;   // 获取组合列表
  vm.setCombineOnline = setCombineOnline;   // 设置组合上下架

  function getUserToken(data, cb1) {
    api.getRequest({
      prefix_type: 'vipstu',
      url: 'func_mobile_login_register_by_tenant?customer_avatar=123&tenant_id=' + vm.tenantId + '&password=e10adc3949ba59abbe56e057f20f883e&mobile=18770043729&customer_name=adfds',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function getCombineList(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'func_combine_search',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }
  
  function setCombineOnline(data, cb1) {
    api.postRequest({
      prefix_type: 'manageCenter',
      url: 'T2Service/848577',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = combineOnlineModalService;



