/*新增投顾*/

require('locale_zh');

var func = require('func');

addNewInvestCtrl.$inject = ['$scope', '$sce', '$timeout', '$state', '$stateParams', 'addNewInvestService', 'modalService'];
function addNewInvestCtrl($scope, $sce, $timeout, $state, $stateParams, addNewInvestService, modalService) {
  var vm = this;

  vm.branchData = []; //初始化组织机构信息
  vm.selectedBranchData = []; //已选择的机构信息
  vm.surePwd = '';    //确认密码
  vm.password = '';   //密码
  vm.params = {};   //新增、编辑投顾信息请求入参
  vm.showBranchId = true;   //显示组织机构id
  vm.investType = [
    { name: '投资顾问助理', value: '0' },
    { name: '投资顾问', value: '1' },
    { name: '理财顾问', value: '2' },
    { name: '高级投顾', value: '3' },
    { name: '明星投顾', value: '4' },
    { name: '资深投顾', value: '5' },
    { name: '钻石投顾', value: '6' },
    { name: '首席投顾', value: '7' },
  ];
  vm.clickAble = 0; // 是否可点击保存按键，0：可点击，1：保存中
  vm.btnText = $sce.trustAsHtml('保存'); // 保存文本

  vm.getBranchItem = getBranchItem;
  vm.selectBranchItem = selectBranchItem;
  vm.checkForm = checkForm;

  initBranchData();

  //初始化机构数据
  function initBranchData() {

    //判断页面来源
    if ($stateParams.type == 1) {   //通过编辑进入
      vm.qryType = 1; //编辑
      vm.title = '编辑投顾';
      //接收从更多信息页面传入的投顾信息
      vm.params = JSON.parse($stateParams.params);
    } else {                        //通过新增进入
      vm.qryType = 0; //新增
      vm.title = '新增投顾';
      vm.params.weixinOwner = '0';  //初始化微信账户主题选项
      vm.password = ''; //密码
      vm.surePwd = '';  //确认密码
    }

    //获取组织机构数据
    vm.branchData = JSON.parse(sessionStorage.getItem("branchQryData"));
  }

  //保存修改
  function saveInfo() {
    if (!vm.clickAble) {
      vm.clickAble = 1;
      vm.btnText = $sce.trustAsHtml('保存<i class="icon iconloading turn-around"></i>');
      if (vm.qryType == 0) {
        addInvest();
      } else {
        modInvest();
      }
    }
  }

  //新增投顾
  function addInvest() {
    addNewInvestService.businessAdviserAdd(
      vm.params,
      function (result) {
        func.openToaster('新增投顾成功！');
        $state.go('home.investManage');
        $timeout(function() {
          vm.clickAble = 0;
          vm.btnText = $sce.trustAsHtml('保存');
          $scope.$apply();
        })
      },
      function() {
        $timeout(function() {
          vm.clickAble = 0;
          vm.btnText = $sce.trustAsHtml('保存');
          $scope.$apply();
        })
      }
    );
  }

  //编辑投顾信息
  function modInvest() {
    addNewInvestService.businessAdviserMod(
      vm.params,
      function (result) {
        func.openToaster('编辑投顾信息成功！');
        $state.go('home.investManage');
        $timeout(function() {
          vm.clickAble = 0;
          vm.btnText = $sce.trustAsHtml('保存');
          $scope.$apply();
        })
      },
      function() {
        $timeout(function() {
          vm.clickAble = 0;
          vm.btnText = $sce.trustAsHtml('保存');
          $scope.$apply();
        })
      }
    );
  }

  //租户机构选择
  function getBranchItem(item) {
    vm.selectedBranchData = [];
    vm.selectedBranchData.push(item);
    var branchCode = item.branchCode;
    if (branchCode.indexOf('_') == -1) {
      vm.params.tenantId = item.branchCode;
    } else {
      branchCode = branchCode.replace('_', '');
      vm.params.tenantId = branchCode.slice(0, 5);
    }
    vm.params.businessDept = item.branchName;
  }

  //营业部机构选择
  function selectBranchItem(item) {
    var branchCode = item.branchCode;
    if (branchCode.indexOf('_') == -1) {
      vm.params.branchNo = item.branchCode;
    } else {
      branchCode = branchCode.replace('_', '');
      vm.params.branchNo = branchCode.slice(5, branchCode.length);
    }
    vm.params.businessDept = item.branchName;
  }

  //提交前验证表单（修改为空的字段传空格）type 0：可点击，1：保存中
  function checkForm() {
    //验证申请人姓名
    if (!vm.params.applicantName) {
      $('#inputApplicantName').focus();
      $('#inputApplicantName').blur();
      modalService.openAlert('提示', '请填写申请人姓名！');
      return;
    }
    vm.params.aliasName = vm.params.applicantName;
    //验证身份证信息
    if (!vm.params.idCardNo || vm.params.idCardNo == ' ') {
      vm.params.idCardNo = '';
    }
    //验证微博号
    if (!vm.params.weiboNo || vm.params.weiboNo == ' ') {
      vm.params.weiboNo = '';
    }
    //验证微信公众号
    if (!vm.params.weixinNo || vm.params.weixinNo == ' ') {
      vm.params.weixinNo = '';
    }
    //验证QQ号
    if (!vm.params.qqNo || vm.params.qqNo == ' ') {
      vm.params.qqNo = '';
    }
    // //验证用户昵称
    // if (!vm.params.aliasName) {
    //   $('#inputNickName').focus();
    //   $('#inputNickName').blur();
    //   modalService.openAlert('提示', '请填写用户昵称！');
    //   return;
    // }
    //验证手机号
    if (!func.isMobile(vm.params.mobile)) {
      $('#inputMobile').focus();
      $('#inputMobile').blur();
      modalService.openAlert('提示', '手机号格式有误！');
      return;
    }
    //验证密码,新增表单
    if (vm.qryType == 0) {
      if (!func.isPwd(vm.password)) {
        $('#inputPassword').focus();
        $('#inputPassword').blur();
        modalService.openAlert('提示', '密码格式有误！');
        return;
      } else {
        vm.params.authPassword = func.MD5(vm.password).toUpperCase();
      }
      if (vm.password != vm.surePwd) {
        modalService.openAlert('提示', '两次输入密码不一致！');
        return;
      }
    }
    //校验投顾类型
    if (!vm.params.adviserType) {
      //类型为4，服务端无对应字典项
      modalService.openAlert('提示', '请选择投顾类型！');
      return;
    }
    //校验资格证书号
    if (!vm.params.credentialsNo) {
      $('#inputCredentialsNo').focus();
      $('#inputCredentialsNo').blur();
      modalService.openAlert('提示', '请填写资格证书号！');
      return;
    }
    //校验资格证书类型
    if (!vm.params.credentialsType) {
      $('#inputCredentialsType').focus();
      $('#inputCredentialsType').blur();
      modalService.openAlert('提示', '请填写资格证书类型！');
      return;
    }
    //验证公司名称
    if (!vm.params.companyName || vm.params.companyName == ' ') {
      vm.params.companyName = '';
    }
    //验证所在省
    if (!vm.params.provinceName || vm.params.provinceName == ' ') {
      vm.params.provinceName = '';
    }
    //验证推荐码
    if (!vm.params.referralCode || vm.params.referralCode == ' ') {
      vm.params.referralCode = '';
    }
    //验证员工编号
    if (!vm.params.employeeNo || vm.params.employeeNo == ' ') {
      vm.params.employeeNo = '';
    }
    //验证所在市
    if (!vm.params.cityName || vm.params.cityName == ' ') {
      vm.params.cityName = '';
    }
    //验证银行账户
    if (!vm.params.bankAccount || vm.params.bankAccount == ' ') {
      vm.params.bankAccount = '';
    }
    //校验从业年限
    if (!vm.params.workYear) {
      $('#inputWorkingYear').focus();
      $('#inputWorkingYear').blur();
      modalService.openAlert('提示', '请填写从业年限！');
      return;
    }
    //验证所属机构
    if (!vm.params.organization || vm.params.organization == ' ') {
      vm.params.organization = '';
    }
    //校验邮箱格式
    if (!func.isEmail(vm.params.email)) {
      $('#inputEmailAdd').focus();
      $('#inputEmailAdd').blur();
      modalService.openAlert('提示', '邮箱格式错误！');
      return;
    }
    //验证开户人
    if (!vm.params.accountHolder || vm.params.accountHolder == ' ') {
      vm.params.accountHolder = '';
    }
    //验证开户行
    if (!vm.params.openingBank || vm.params.openingBank == ' ') {
      vm.params.openingBank = '';
    }
    //校验租户
    if (!vm.params.tenantId) {
      modalService.openAlert('提示', '请选择租户！');
      return;
    }
    //校验营业部
    if (!vm.params.branchNo) {
      modalService.openAlert('提示', '请选择投营业部！');
      return;
    }
    //验证投资风格
    if (!vm.params.investmentStyle || vm.params.investmentStyle == ' ') {
      vm.params.investmentStyle = '';
    }
    //验证投资策略
    if (!vm.params.investmentStrategy || vm.params.investmentStrategy == ' ') {
      vm.params.investmentStrategy = '';
    }
    //验证投资策略简述
    if (!vm.params.investmentStrategySummary || vm.params.investmentStrategySummary == ' ') {
      vm.params.investmentStrategySummary = '';
    }
    //验证背景介绍
    if (!vm.params.backgroundIntro || vm.params.backgroundIntro == ' ') {
      vm.params.backgroundIntro = '';
    }
    saveInfo();
  }

}

module.exports = addNewInvestCtrl;



