delBranchModalService.$inject = ['api'];
function delBranchModalService(api) {
  var vm = this;

  vm.delBranch = delBranch;

  //删除机构
  function delBranch(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchDel',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

}

module.exports = delBranchModalService;



