var logoImg = require('img/logo.png');

var func = require('func'),
  sha256 = require('sha256');

loginCtrl.$inject = ['$state', '$cookies', '$timeout', 'loginService', 'modalService'];

function loginCtrl($state, $cookies, $timeout, loginService, modalService) {
  var vm = this;

  vm.logoImg = logoImg;

  var randNum = 0;

  vm.authImg = LOCAL_CONFIG.API_HSIAR + CT.COMMON_API + CT.UPLOADING_API + 'imgVerify?width=180&height=80'; //图片验证码地址
  vm.login_account = ''; //用户名
  vm.verify_code = ''; //验证码
  vm.password = ''; //密码
  vm.loginName = '确认登录';
  vm.errorMsg = '';
  vm.loginDisabled = false; //按钮禁止
  vm.stateGo = 'home.welcome'; //路由跳转

  vm.submit = submit;
  vm.reloadImgverify = reloadImgverify;

  reloadImgverify();

  //防止页面回退
  window.history.forward(1);

  //登录请求
  function submit(form) {
    if (vm.login_account && vm.password && vm.verify_code) {
      vm.loginName = '登录中……';
      vm.loginDisabled = false;
      $timeout(function() {
        loginService.login(
          {
            operatorNo: vm.login_account,
            password: sha256(vm.password),
            verifyCode: vm.verify_code
          },
          function(response) {
            loginSuccess(response);
          },
          function(response) {
            failDone(response);
            reloadImgverify();
          },
          function(response) {
            failDone(response);
          }
        );
      }, 300);
    } else {
      modalService.openAlert('提示', '账号、密码或验证码不为空！', 2000);
    }
  }

  //请求失败操作
  function failDone(from) {
    vm.errorMsg = from.errorMsg;
    vm.loginName = '登录失败';
    vm.loginDisabled = false;
  }

  //登录成功操作
  function loginSuccess(response) {
    sessionStorage.setItem('access_token', response.data.accessToken);
    $state.go(vm.stateGo);
  }

  //刷新验证码
  function reloadImgverify() {
    var time = '&date=' + new Date();
    if (!randNum) {
      randNum = 1;
      vm.authImg += time + randNum;
    } else {
      randNum = 0;
      vm.authImg += time + randNum;
    }
  }
}

module.exports = loginCtrl;



