combineShelfCheckService.$inject = ['api'];
function combineShelfCheckService(api) {
  var vm = this;
  vm.combineQry = combineQry; //查询组合
  // vm.combiApplyHis = combiApplyHis; //组合审核流水查询
  vm.combineUpDownAudit = combineUpDownAudit; // 审核通过与否

  /**
   * 组合列表
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function combineQry(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'list_combine_on_management',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  // function combiApplyHis(data, cb1, cb2) {
  //   api.postRequest({
  //     prefix_type: 'vipstu',
  //     url: 'audit_history',
  //     data: data,
  //     successCallback: function(result) {
  //       angular.isFunction(cb1) && cb1(result);
  //     },
  //     failCallback: function (result) {
  //       angular.isFunction(cb2) && cb2(result);
  //     }
  //   });
  // }

  function combineUpDownAudit(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'combine_up_down_audit',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  return vm;
}

module.exports = combineShelfCheckService;


