serviceProduct05Route.$inject = ['$stateProvider'];

function serviceProduct05Route($stateProvider) {
    $stateProvider
        .state('home.serviceProduct05', {
            url: '/serviceProduct05',
            templateUrl: './views/serviceProduct05.html',
            controller: 'serviceProduct05Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = serviceProduct05Route;



