/* 观点列表 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  templateModalTempl = require('./templateModal.html');

templateModalCtrl.$inject = ['modalService','templateModalService'];

function templateModalCtrl(modalService,templateModalService) {
  var vm = this;

  /*函数声明*/
  vm.openTemplateModalFunc = openTemplateModalFunc;

  /*函数定义*/
  function openTemplateModalFunc() {
    var options = angular.copy(modalOptions);
    options.modalTitle = 'template modal';
    options.template = templateModalTempl;
    options.controller = templateModalController;
    modalService.openModal(options);
  }

  /*controller定义*/
  function templateModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.cancel = cancel;
    ra.close = close;

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = templateModalCtrl;




