/* 组织管理--删除 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  delBranchModalTempl = require('./delBranchModal.html'),
  func = require('func');

delBranchModalCtrl.$inject = ['modalService', 'delBranchModalService'];

function delBranchModalCtrl(modalService, delBranchModalService) {
  var vm = this;

  vm.selectedBranchInfo = {};   //已选机构信息

  /*函数声明*/
  vm.opendelBranchModalFunc = opendelBranchModalFunc;

  /*函数定义*/
  function opendelBranchModalFunc(info, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '删除';
    options.template = delBranchModalTempl;
    options.controller = delBranchModalController;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };
    vm.selectedBranchInfo = info;
    modalService.openModal(options);
  }

  /*controller定义*/
  function delBranchModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, ensureBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.ensureBtnText = ensureBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function submit() {
      delBranchModalService.delBranch(
        {
          ancestorFlag: 1,
          branchNo: vm.selectedBranchInfo.branchNo,
          deepth: -1
        },
        function (result) {
          func.openToaster('操作成功！');
          close();
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = delBranchModalCtrl;




