/* 图片设置 module */

require('locale_zh');
require.ensure(
  [],
  function(require) {
    require('Swiper');
  },
  'swiper'
);
var config = require('config');
var func = require('func');

imageConfigCtrl.$inject = ['imageConfigService', 'editPictureModalCtrl', 'modalService'];

function imageConfigCtrl(imageConfigService, editPictureModalCtrl, modalService) {
  var vm = this;

  vm.picSrc = vm.picSrc == 0 ? '../assets/images/background.jpg' : vm.picSrc;
  vm.branchInfo = JSON.parse(sessionStorage.getItem('branchQryData'));
  vm.tenant_id = JSON.parse(sessionStorage.getItem('packHead')).company_id;
  vm.branchNo = vm.branchInfo[0].branchNo;
  //vm.tenant_id = '20033'; //设置默认值
  //vm.branchNo = '91032'; //设置默认值
  vm.openCharge = '1';
  vm.showAdver = false;
  vm.showLoad = false;
  vm.picList = [];
  vm.tenantList = [];
  vm.length = '';
  var mySwiper = '';
  vm.selectTenant = null;

  init();
  vm.addPicture = addPicture; //新增图片
  vm.editPicture = editPicture; //编辑loading图片
  vm.deleteImg = deleteImg; //删除图片
  vm.setPayFunc = setPayFunc; //选择支付功能
  vm.change = change;

  function init() {
    tenantSearch(); //选择租户信息
  }

  function change() {
    if (vm.selectTenant) {
      picSearch(); //loading图片展示
    }
  }

  //选择租户信息
  function tenantSearch() {
    imageConfigService.tenantSearchFunc(
      {
        branch_no: vm.branchNo,
        tenant_id: vm.tenant_id,
        jsonp: 'jsonpcallback',
        page_count: 10
      },
      function(response) {
        vm.tenantList = response.data_list;
      }
    );
  }

  //loading图片展示
  function picSearch() {
    imageConfigService.picSearchFunc(
      {
        broker_id: 0,
        tenant_id: vm.selectTenant,
        branch_no: vm.branchNo,
        jsonp: 'jsonpcallback',
        page_count: 10
      },
      function(response) {
        vm.load = response.loading_img_url;
        vm.picList = response.data_list;
        vm.picSrc = LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download/' + response.loading_img_url + '/92f17e36e6cf0a8083ff435fc708a372?sysno=1';
        if (vm.load != '') {
          vm.showLoad = true;
        }
        vm.pathList = [];
        if (vm.picList.length > 0) {
          vm.showAdver = true;
          var obj = {};
          for (var i = 0; i < vm.picList.length; i++) {
            obj = LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download/' + vm.picList[i].page_img + '/92f17e36e6cf0a8083ff435fc708a372?sysno=1';
            vm.pathList.push(obj);
          }
          vm.length = vm.pathList.length;
          setTimeout(function() {
            if (vm.pathList && vm.pathList.length > 3) {
              setSwiper();
            } else {
              if (mySwiper) {
                mySwiper.destroy(true, true);
              }
            }
          }, 100);
          vm.adver = response.data_list[0];
          vm.advertUrl = response.data_list[0].page_img;
          vm.picPath = LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download/' + vm.advertUrl + '/92f17e36e6cf0a8083ff435fc708a372?sysno=1';
        } else {
          vm.showAdver = false;
        }
        vm.payStatus = response.pay_status;
      }
    );
  }

  //支付功能设置
  function setPayFunc() {
    if (vm.selectTenant) {
      imageConfigService.payUpdateFunc(
        {
          broker_id: 0,
          pay_status: vm.openCharge,
          tenant_id: vm.selectTenant,
          loading_img_url: vm.load,
          data_list: JSON.stringify(vm.picList),
          branch_no: vm.branchNo,
          jsonp: 'jsonpcallback',
          page_count: 10
        },
        function(response) {
          if (vm.openCharge == 1) {
            func.openToaster('支付功能已开通！');
          } else {
            func.openToaster('支付功能已关闭！');
          }
          picSearch();
        }
      );
    } else {
      modalService.openAlert('提示', '请选择租户', 2000);
    }
  }
  //新增图片
  function addPicture(item) {
    if (vm.selectTenant) {
      var Data = {
        item: item,
        picList: vm.picList,
        load: vm.load,
        src: vm.picSrc,
        tenant_id: vm.selectTenant
      };
      editPictureModalCtrl.editPictureModalFunc(Data, function(result) {
        picSearch();
      });
    } else {
      modalService.openAlert('提示', '请选择租户', 2000);
    }
  }
  //编辑图片
  function editPicture(item, index) {
    var picPath = LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download/' + vm.picList[index].page_img + '/92f17e36e6cf0a8083ff435fc708a372?sysno=1';
    var adver = vm.picList[index];
    var data = {
      item: item,
      index: index,
      src: vm.picSrc,
      path: picPath,
      picList: vm.picList,
      load: vm.load,
      adver: adver,
      tenant_id: vm.selectTenant
    };
    editPictureModalCtrl.editPictureModalFunc(data, function(result) {
      picSearch();
    });
  }

  //删除广告图片
  function deleteImg(index) {
    vm.picList.splice(index, 1);
    var data = {
      broker_id: 0,
      tenant_id: vm.selectTenant,
      branch_no: vm.branchNo,
      pay_status: vm.openCharge,
      loading_img_url: vm.load,
      data_list: JSON.stringify(vm.picList)
    };
    imageConfigService.deletePictureModalFunc(data, function(result) {
      picSearch();
    });
  }

  //初始化轮播图组件
  function setSwiper() {
    mySwiper = new Swiper('.swiper-container', {
      pagination: '.swiper-pagination',
      paginationClickable: true,
      slidesPerView: 3,
      loop: false,
      prevButton: '.swiper-button-prev',
      nextButton: '.swiper-button-next'
    });
  }
}

module.exports = imageConfigCtrl;



