servePackageCheckRoute.$inject = ['$stateProvider'];

function servePackageCheckRoute($stateProvider) {
  $stateProvider.state('home.servePackageCheck', {
    url: '/servePackageCheck',
    templateUrl: './views/servePackageCheck.html',
    controller: 'servePackageCheckCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = servePackageCheckRoute;



