/* name module */
routConfig.$inject = ['$urlRouterProvider'];

function routConfig($urlRouterProvider) {
  // 在登录状态时重定向到home页，否则到登录页
  var token = sessionStorage.getItem('access_token');
  token ? $urlRouterProvider.otherwise('/home/welcome') : $urlRouterProvider.otherwise('/login');
}

module.exports = routConfig;



