/* 推荐投顾规则 module */

require('locale_zh');

var func = require('func');

recommendInvestCtrl.$inject = ['recommendInvestService', 'storeSelectedCtrl', 'storeSelectedService'];

function recommendInvestCtrl(recommendInvestService, storeSelectedCtrl, storeSelectedService) {
  var vm = this;

  /* 分页 */
  vm.maxSize = 10;
  vm.totalItems = 1;
  vm.currentPage = 1;
  vm.itemsPerPage = 10;
  //vm.start = 0;//手动分页开始截取
  //vm.end = 5;//手动分页结束截取

  vm.companyId = JSON.parse(sessionStorage.getItem('packHead')).company_id;  //获取company_id
  vm.tenant_id = JSON.parse(sessionStorage.getItem('tenantId'));

  vm.radioVale = '3'; //1-月咨询量 2-人气 3-自定义
  vm.chooseNum = ''; // 已发布投顾类型
  vm.showRule = true; //默认显示自定义规则内容
  vm.selectedStore = '';  // 选中的风采店铺
  vm.selectedAdviser = [];  //选中的adviser数据
  vm.selectedAdviserId = [];  //选中的adviserId数据

  vm.brokerManagerName = '';  //店铺管理员名称
  vm.mobile = '';  //店铺名称

  vm.pageAdviserList = [];  //每页投顾信息
  vm.adviserList = [];  //查询出的所有投顾信息
  vm.recommendedUserId = [];  //已推荐投顾的userId

  vm.cancelNum = 0; //投顾取消次数
  vm.recommendNum = 0;  //投顾推荐次数

  vm.pageChangeFunc = adviserInfoQry; // 分页
  vm.changeShowRule = changeShowRule; // 更换规则或显示自定义规则
  vm.adviserInfoQry = adviserInfoQry; // 查询店铺列表
  // vm.openStoreSelectedModal = openStoreSelectedModal;  // 打开店铺选择弹窗
  vm.releaseRule = releaseRule; // 发布
  vm.upHotInvestor = upHotInvestor; // 点击推荐投顾
  // vm.selectAdviser = selectAdviser; // 双击选择店铺
  vm.deleteAdviser = deleteAdviser; // 删除已选店铺

  init();

  //初始化
  function init() {
    // getDefaultStoreFunc();  // 获取默认推荐店铺
    getRecommendList(0); // 获取已推荐店铺
  }

  //更换规则或显示自定义规则
  function changeShowRule(flag) {
    if (flag == '3') {
      vm.showRule = true;
      getRecommendList(); // 获取已推荐店铺
      adviserInfoQry(0); // 获取店铺列表
    } else {
      vm.mobile = '';
      vm.brokerManagerName = '';
      vm.showRule = false;
    }
    if (vm.chooseNum !== '3') {
      vm.selectedAdviser = [];
      vm.selectedAdviserId = [];
    }
  }

  // // 获取默认推荐店铺
  // function getDefaultStoreFunc() {
  //   recommendInvestService.getDefaultStore(
  //     {},
  //     function (result) {
  //       vm.selectedStore = result.broker_name;
  //       console.log(result);
  //     }
  //   )
  // }

  // 获取已推荐店铺
  function getRecommendList(i) {
    vm.selectedAdviser = [];
    vm.selectedAdviserId = [];
    recommendInvestService.getRecommendQry(
      {
        size: 5,
        company_id: vm.tenant_id
      },
      function (result) {
        var arr = result.data_list || [];
        if (i === 0) {
          vm.radioVale = result.sort_type;
        }
        vm.chooseNum = result.sort_type;
        if (vm.radioVale !== '3') {
          vm.showRule = false;
        } else {
          vm.showRule = true;
        }
        if (result.sort_type === '3') {
          if (arr.length > 0) {
            arr.forEach(function (item) {
              vm.selectedAdviser.push(item.broker_name);
              vm.selectedAdviserId.push(item.broker_id);
            });
          }
          adviserInfoQry(0); // 获取店铺列表
        }
      }
    )
  }

  // 店铺列表信息查询
  function adviserInfoQry(i) {
    if (i === 0) {
      vm.currentPage = 1;
    }
    recommendInvestService.adviserInfoQry(
      {
        page_count: vm.itemsPerPage,
        page_no: vm.currentPage,
        company_id: vm.tenant_id,
        broker_manager_name: vm.brokerManagerName,
        mobile: vm.mobile
      },
      function (result) {
        vm.pageAdviserList = result.data_list;
        vm.totalItems = result.total_num;
      }
    )
  }

  // 打开店铺选择弹窗
  // function openStoreSelectedModal() {
  //   storeSelectedCtrl.openStoreSelectedModalFunc(vm.tenant_id, function (store) {
  //     console.log('openStoreSelectedModalFunc callback', store);
  //     vm.selectedStore = store.broker_name;
  //   })
  // }

  //"发布"点击事件
  function releaseRule() {
    if (vm.radioVale == '3') {
      if (vm.selectedAdviser.length != 5) {
        func.openToaster('请选择5位投顾');
        return;
      }
    }
    publishFunc(); // 发布信息
  }

  // 发布信息
  function publishFunc() {
    recommendInvestService.recommendPublish(
      {
        selected_broker: vm.selectedAdviserId.join(','),
        sort_type: vm.radioVale,
        company_id: vm.tenant_id
      },
      function (result) {
        if (result.error_no === '0') {
          func.openToaster('操作成功！');
        } else {
          func.openToaster('操作失败！');
        }
      }
    );
    getRecommendList();
  }

  // //双击选择投顾
  // function selectAdviser(item) {
  //   var obj1 = item.broker_name;
  //   var obj2 = item.broker_id;
  //   if (vm.selectedAdviser.indexOf(obj1) == -1 && vm.selectedAdviser.length < 3) {
  //     vm.selectedAdviser.push(obj1);
  //     vm.selectedAdviserId.push(obj2);
  //   }
  // }

  // 点击推荐投顾
  function upHotInvestor(item) {
    var obj1 = item.broker_name;
    var obj2 = item.broker_id;
    if (vm.selectedAdviser.length == 5) {
      func.openToaster('推荐投顾数已满！');
      return;
    }
    if (vm.selectedAdviser.indexOf(obj1) != -1) {
      func.openToaster('当前投顾已推荐！');
      return;
    }
    vm.selectedAdviser.push(obj1);
    vm.selectedAdviserId.push(obj2);
  }

  //删除已选择投顾
  function deleteAdviser(item) {
    // if (item == 'adviser0') {
    //   vm.selectedAdviser.splice(0, 1);
    //   vm.selectedAdviserId.splice(0, 1);
    // } else if (item == 'adviser1') {
    //   vm.selectedAdviser.splice(1, 1);
    //   vm.selectedAdviserId.splice(1, 1);
    // } else {
    //   vm.selectedAdviser.splice(2, 1);
    //   vm.selectedAdviserId.splice(2, 1);
    // }

    if (item == 'adviser0') {
      vm.selectedAdviser.splice(0, 1);
      vm.selectedAdviserId.splice(0, 1);
    } else if (item == 'adviser1') {
      vm.selectedAdviser.splice(1, 1);
      vm.selectedAdviserId.splice(1, 1);
    } else if (item == 'adviser2') {
      vm.selectedAdviser.splice(2, 1);
      vm.selectedAdviserId.splice(2, 1);
    } else if (item == 'adviser3') {
      vm.selectedAdviser.splice(3, 1);
      vm.selectedAdviserId.splice(3, 1);
    } else if (item == 'adviser4') {
      vm.selectedAdviser.splice(4, 1);
      vm.selectedAdviserId.splice(4, 1);
    }
  }

}

module.exports = recommendInvestCtrl;



