/* 新增图片 */
var func = require('func'),
  modalOptions = require('jsDir/modal_options');

Img.$inject = ['$cookies', '$http', 'modalService'];

function Img($cookies, $http, modalService) {

  var access_token = sessionStorage.getItem('access_token');

  var apiHandler = this;
  apiHandler.appendUrlPrefix = appendUrlPrefix;
  apiHandler.postImgRequest = postImgRequest;

  var params = {
	successCallback: function (result) {
	  console.log('success' + JSON.stringify(result))
	},
	failCallback: function (response) {
	  console.log("false");
	  var errorOptions = angular.copy(modalOptions);
	  errorOptions.modalTitle = '失败';
	  errorOptions.modalParams.error_no = response && (response.errorCode || response.error_no);
	  errorOptions.modalInfo = response && (response.errorMsg || response.error_info);
	  modalService.openModal(errorOptions);
	},
	disconnectCallback: function (response) {
	  console.log("访问失败")
	}
  };

  function isSuccessful(result) {
	return result && (result.error_no == '0' || result.errorCode == '0') ? true : false;
  }


  /**
   * 扩展不同url前缀
   * 如根域名是'muziso.com'， 有不同扩展，client, common等，方法可随着项目更改
   * @param type
   * @returns {*}
   */
  function appendUrlPrefix(options) {
	var type = options.prefix_type;
	if (type === 'fileOpenapi') {
	  return LOCAL_CONFIG.API_HOME + type + '/';
	}
  }

  function sendImgRequest(options) {

	// 默认get请求
	options.method = (options.method === null || options.method === '' || typeof(options.method) === 'undefined') ? 'get' : options.method;
	options.data = (options.data === null || options.data === '' || typeof(options.data) === 'undefined') ? {'date': new Date().getTime()} : options.data;

	func.myExtend(options, params);
	options.headers = {
		'Content-Type': undefined,
		"accessToken": sessionStorage.getItem('access_token')
	}
	options.url = appendUrlPrefix(options) + options.url;

	console.log('postImgRequest', options.headers);

	$http({
	  url: options.url,
	  method: options.method,
	  data: options.data,
	  headers: options.headers,
	  transformRequest: angular.identity,
	  //withCredentials:true,
	  timeout: 10000
	}).then(function (response) {
	  if (response.data.errorCode || response.data.error_no) {
		var result = response.data
	  } else {
		var result = response.data.data[0];
	  }
	  ;
	  if (isSuccessful(result)) {
		options.successCallback(result);
	  } else {
		options.failCallback(result);
	  }
	}, function (response) {
	  //404等错，一般不考虑，直接在拦截器里面统一做拦截处理
	  options.disconnectCallback(response);
	});
  }

  function postImgRequest(options) {
	options.method = 'post';
	sendImgRequest(options);
  }

  return apiHandler;
}


module.exports = Img;



