basicValueSettingRoute.$inject = ['$stateProvider'];

function basicValueSettingRoute($stateProvider) {
  $stateProvider.state('home.basicValueSetting', {
    url: '/basicValueSetting',
    templateUrl: './views/basicValueSetting.html',
    controller: 'basicValueSettingCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = basicValueSettingRoute;



