addStockModalService.$inject = ['api'];

function addStockModalService(api) {
  var vm = this;

  vm.getAllStock = getAllStock;
  vm.getStockByCode = getStockByCode;
  vm.getStockReal = getStockReal;
  vm.stockPoolAdd = stockPoolAdd;

  /**
   * 获取市场全部股票
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   *
   * @return  {[type]}        [return description]
   */
  function getAllStock(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'hq',
      url: 'hs_all',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  /**
   * 条件查询股票列表
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   *
   * @return  {[type]}        [return description]
   */
  function getStockByCode(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'hq',
      url: 'wizard',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  /**
   * 获取股票行情信息
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   *
   * @return  {[type]}        [return description]
   */
  function getStockReal(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'hq',
      url: 'real',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  /**
   * 股票池中添加股票
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function stockPoolAdd(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'stock_pool_add',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }
}

module.exports = addStockModalService;



