/* 面包屑导航 */

require('./style.less');

var urlList = require('./modulesUrl');

breadcrumbNav.$inject = ['$state', '$rootScope'];

function breadcrumbNav($state, $rootScope) {
  return {
    restric: 'E',
    template: '<p class="breadcrumb-nav">{{url}}</p>',
    link: function ($scope) {

      //当刷新页面时依然可以获取到路由信息
      $scope.url = urlList[$state.current.name];

      //当路由改变时调用此方法获取路由信息
      $rootScope.$on('$stateChangeStart', function (event, toState) {
        var toName = toState.name;
        $scope.url = urlList[toName];
      });
    }
  }
}

module.exports = breadcrumbNav;



