downloadProgressModalService.$inject = ['api'];
function downloadProgressModalService(api) {
  var vm = this;

  vm.signProtocolDownload = signProtocolDownload;

  /**
   * 批量下载签约协议文件
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function signProtocolDownload(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'fileOpenapi',
      url: 'func_file_download_zip',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = downloadProgressModalService;


