addBranchModalService.$inject = ['api'];
function addBranchModalService(api) {
  var vm = this;

  vm.branchAdd = branchAdd;

  //新增机构
  function branchAdd(data, cb1) {
    api.postRequest({
      prefix_type: 'branchService',
      url: 'branchAdd',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

}

module.exports = addBranchModalService;



