customerContributionQryService.$inject = ['api'];
function customerContributionQryService(api) {
  var vm = this;

  vm.customerContributionQryFunc = customerContributionQryFunc;

  /**
   * 客户贡献度数据查询
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function customerContributionQryFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'customer_contribution_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = customerContributionQryService;


