/* 股票池管理 module */

require('locale_zh');
var func = require('func');

stockPoolManageCtrl.$inject = ['modalService', 'stockPoolManageService', 'addStockModalCtrl', 'addStockModalService'];

function stockPoolManageCtrl(modalService, stockPoolManageService, addStockModalCtrl, addStockModalService) {
  var vm = this;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));

  vm.currentPage = 1;
  vm.itemPerPage = 10;
  vm.totalItems = 0;

  vm.keyword = '';
  vm.stockPoolList = [];

  vm.stockPoolQryFunc = stockPoolQryFunc;
  vm.stockPoolDelFunc = stockPoolDelFunc;
  vm.deleteStockFunc = deleteStockFunc;
  vm.openModal = openModal;

  stockPoolQryFunc();

  /**
   * 获取股票池列表
   *
   * @return  {[type]}  [return description]
   */
  function stockPoolQryFunc() {
    stockPoolManageService.stockPoolQry({
      keyword: vm.keyword,
      tenant_id: vm.tenantId,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage
    }, function(res) {
      vm.stockPoolList = res.data_list;
      vm.totalItems = res.total_item_num;
    }, function(err) {
      vm.stockPoolList = [];
      vm.totalItems = 0;
    })
  }

  /**
   * 从股票池删除股票
   *
   * @param   {[type]}  stock  [stock description]
   *
   * @return  {[type]}         [return description]
   */
  function stockPoolDelFunc(stock) {
    stockPoolManageService.stockPoolDel({
      tenant_id: vm.tenantId,
      stock_code: stock.stock_code
    }, function(res) {  
      modalService.openAlert('提示', '删除成功！');
      stockPoolQryFunc();
    }, function(err) {
      modalService.openAlert('提示', '删除失败！');
    })
  }

  /**
   * 删除股票点击事件
   *
   * @param   {[type]}  stock  [stock description]
   *
   * @return  {[type]}         [return description]
   */
  function deleteStockFunc(stock) {
    modalService.openConfirm('提示', '是否删除股票？' + stock.stock_name, function() {
      stockPoolDelFunc(stock);
    })
  }

  /**
   * 打开弹窗
   *
   * @return  {[type]}  [return description]
   */
  function openModal() {
    addStockModalCtrl.openAddStockModalFunc(function() {
      console.log('success callBack');
    }, function() {
      stockPoolQryFunc();
    })
  }

}

module.exports = stockPoolManageCtrl;



