/* 金融产品池管理 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var addProductModalCtrl = require('./modalPopup/controller');
var addProductModalService = require('./modalPopup/service');

//setter定义模块
module.exports = angular
  .module('productPoolManage', [])
  .config(Routing)
  .service('productPoolManageService', service)
  .controller('productPoolManageCtrl', ctrl)
  .factory('addProductModalCtrl', addProductModalCtrl)
  .service('addProductModalService', addProductModalService).name;



