/* 操作员管理 module */

operatorManageRoute.$inject = ['$stateProvider'];

function operatorManageRoute($stateProvider) {
    $stateProvider
        .state('home.operatorManage', {
            url: '/operatorManage',
            templateUrl: './views/operatorManage.html',
            controller: 'operatorManageCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = operatorManageRoute;



