addProtocolVersionRoute.$inject = ['$stateProvider'];

function addProtocolVersionRoute($stateProvider) {
  $stateProvider.state('home.addProtocolVersion', {
    url: '/addProtocolVersion?data',
    templateUrl: './views/addProtocolVersion.html',
    controller: 'addProtocolVersionCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = addProtocolVersionRoute;



