/* 导出 controller*/

require('./style.less');
window.ExcellentExport = require('ExcellentExport'); //导出
var modalOptions = require('jsDir/modal_options'),
  investorAchievementQryDownModalTempl = require('./investorAchievementQryDownModal.html');

  investorAchievementQryDownModalCtrl.$inject = ['modalService', 'investorAchievementQryDownModalService'];

function investorAchievementQryDownModalCtrl(modalService, investorAchievementQryDownModalService) {
  var vm = this;

  /*函数声明*/
  vm.openDownModalFunc = openDownModalFunc;

  /*函数定义*/
  function openDownModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '确认导出';
    options.template = investorAchievementQryDownModalTempl;
    options.size = 'md';
    options.controller = downModalController;
    options.modalParams = data; //传递正常客户和限制客户参数
    modalService.openModal(options);
  }

  /*controller定义*/
  function downModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.cancelBtnText = cancelBtnText;
    ra.content = '下载进度...0%';
    ra.investorData = [];
    ra.downFlag = true;

    ra.cancel = cancel;
    ra.close = close;

    init();

    function init() {
      investorAchievementQryDownModalService.brokerPerformanceQry(
        {
          tenant_id: ra.modalParams.tenant_id,
          keyword: ra.modalParams.keyword,
          service_type: ra.modalParams.productType,
          page_no: ra.modalParams.page_no,
          page_size: ra.modalParams.page_size,
          sort_type: ra.modalParams.sort_type,
          sort_field: ra.modalParams.sort_field
        },
        function(response) {
          ra.investorData = response.data_list;
          ra.downFlag = false;
          ra.content = '下载进度...100%';
        },
        function(err) {
          ra.content = '查询客户列表失败！';
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = investorAchievementQryDownModalCtrl;



