/* 客户贡献度查询 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var protocolIntroduceModalCtrl = require('./modalPopup/protocolIntroduceModal/controller');
var protocolViewModalCtrl = require('./modalPopup/protocolViewModal/controller');
var protocolStopModalCtrl = require('./modalPopup/protocolStopModal/controller');
var protocolStopModalService = require('./modalPopup/protocolStopModal/service');

//setter定义模块
module.exports = angular
  .module('protocolVersionManagment.protocolVersionManage', [])
  .config(Routing)
  .controller('protocolVersionManageCtrl', ctrl)
  .service('protocolVersionManageService', service)
  .factory('protocolIntroduceModalCtrl', protocolIntroduceModalCtrl)
  .factory('protocolViewModalCtrl', protocolViewModalCtrl)
  .factory('protocolStopModalCtrl', protocolStopModalCtrl)
  .service('protocolStopModalService', protocolStopModalService).name;



