/* 粉丝发言监控 module */

require('locale_zh');
var func = require('func');

fansSpeechMonitorCtrl.$inject = ['fansSpeechMonitorService', '$sce', 'modalService'];

function fansSpeechMonitorCtrl(fansSpeechMonitorService, $sce, modalService) {
  var vm = this;

  vm.typeList = ['观点', '问答', '直播', '组合']; //类型列表
  vm.keyword = '';  //关键字
  vm.selectedType = '';  //默认已选评论类型
  vm.dataList = []; //数据列表

  vm.checkAll = false; //默认为非全选状态
  vm.checkItem = false;  //默认每条数据为非选状态
  vm.selectList = []; //选中的数据

  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 1;  //根据接口返回决定
  vm.currentPage = 1;
  vm.itemsPerPage = 10;

  vm.selectedTypeFunc = selectedTypeFunc;
  vm.blockRemark = blockRemark;
  vm.pageChangeFunc = pageChangeFunc;
  vm.keywordSearch = keywordSearch;
  vm.selectAll = selectAll;
  vm.selectItem = selectItem;

  init();

  //初始化
  function init() {
    vm.selectedType = vm.typeList[0];
    viewpointListQry();
  }

  //根据类型请求
  function qryByType() {
    vm.selectList = [];
    if (vm.selectedType == '观点') {
      viewpointListQry();
    } else if (vm.selectedType == '问答') {
      questionListQry();
    } else if (vm.selectedType == '直播') {
      liveListQry();
    } else {
      combineListQry();
    }
  }

  //选择评论类型
  function selectedTypeFunc(item) {
    vm.selectedType = item;
    vm.currentPage = 1;
    qryByType();
  }

  //屏蔽言论
  function blockRemark() {
    if (vm.selectedType == '观点') {
      blockViewpointData();
    } else if (vm.selectedType == '问答') {
      blockQuestionData();
    } else if (vm.selectedType == '直播') {
      blockLiveData();
    } else {
      blockCombineData();
    }
  }

  //获取观点列表
  function viewpointListQry() {
    fansSpeechMonitorService.viewpointList(
      {
        enBranchCode: '0',
        keyword: vm.keyword,
        pageNo: vm.currentPage,
        pageSize: vm.itemsPerPage
      },
      function (result) {
        vm.dataList = result.data;
        vm.totalItems = result.totalCount;
        renderRichText(vm.dataList);
        angular.forEach(vm.dataList, function (item) {
		  item.replyTime = item.replyTime.substring(0, item.replyTime.length - 2);
		});
      }
    )
  }

  //屏蔽观点数据
  function blockViewpointData() {
	if (vm.selectList.length != 0) {
	  fansSpeechMonitorService.blockViewpoint(
		{
		  replyIds: vm.selectList,
		  enBranchCode: '0'
		},
		function (result) {
		  vm.selectList = [];
		  modalService.openAlert('提示', '操作成功！');
		  viewpointListQry();
		}
	  )
	} else {
	  modalService.openAlert('提示', '请先选中要屏蔽的言论！');
	}
  }

  //获取问答列表
  function questionListQry() {
    fansSpeechMonitorService.questionList(
      {
        enBranchCode: '0',
        keyword: vm.keyword,
        pageNo: vm.currentPage,
        pageSize: vm.itemsPerPage
      },
      function (result) {
        vm.dataList = result.data;
        vm.totalItems = result.totalCount;
        renderRichText(vm.dataList);
        angular.forEach(vm.dataList, function (item) {
		  item.createTime = item.createTime.substring(0, item.createTime.length - 2);
		});
      }
    )
  }

  //屏蔽问答数据
  function blockQuestionData() {
	if (vm.selectList.length != 0) {
	  fansSpeechMonitorService.blockQuestion(
		{
		  questionIds: vm.selectList,
		  enBranchCode: '0'
		},
		function (result) {
		  vm.selectList = [];
		  modalService.openAlert('提示', '操作成功！');
		  questionListQry();
		}
	  )
	} else {
	  modalService.openAlert('提示', '请先选中要屏蔽的言论！');
	}
  }

  //获取直播列表
  function liveListQry() {
    fansSpeechMonitorService.chatMsgList(
      {
        enBranchCode: '0',
        keyword: vm.keyword,
        pageNo: vm.currentPage,
        pageSize: vm.itemsPerPage
      },
      function (result) {
        vm.dataList = result.data;
        vm.totalItems = result.totalCount;
        renderRichText(vm.dataList);
        angular.forEach(vm.dataList, function (item) {
		  item.sendTime = item.sendTime.substring(0, item.sendTime.length - 2);
		});
      }
    )
  }

  //屏蔽直播数据
  function blockLiveData() {
    if (vm.selectList.length != 0) {
	  fansSpeechMonitorService.blockChatMsg(
		{
		  chatMsgIds: vm.selectList,
		  enBranchCode: '0'
		},
		function (result) {
		  vm.selectList = [];
		  modalService.openAlert('提示', '操作成功！');
		  liveListQry();
		}
	  )
    } else {
      func.openAlert('提示', '请先选中要屏蔽的言论！');
    }
  }

  //获取组合列表
  function combineListQry() {
    fansSpeechMonitorService.combineList(
      {
        enBranchCode: '0',
        keyword: vm.keyword,
        pageNo: vm.currentPage,
        pageSize: vm.itemsPerPage
      },
      function (result) {
        vm.dataList = result.data;
        vm.totalItems = result.totalCount;
        renderRichText(vm.dataList);
        angular.forEach(vm.dataList, function (item) {
		  item.createTime = func.timeConverter(item.createTime);
		});
      }
    )
  }

  //屏蔽组合数据
  function blockCombineData() {
	if (vm.selectList.length != 0) {
	  fansSpeechMonitorService.blockCombine(
		{
		  commentIds: vm.selectList,
		  enBranchCode: '0'
		},
		function (result) {
		  vm.selectList = [];
		  modalService.openAlert('提示', '操作成功！');
		  combineListQry();
		}
	  )
	} else {
	  modalService.openAlert('提示', '请先选中要屏蔽的言论！');
	}
  }

  //关键词搜索
  function keywordSearch() {
    selectedTypeFunc(vm.selectedType);
  }

  //分页
  function pageChangeFunc() {
    qryByType();
  }

  //全选
  function selectAll() {
    if (vm.checkAll == true) {
      angular.forEach(vm.dataList, function (item) {
        var itemId = '';
        if (vm.selectedType == '观点') {
          itemId = item.replyId;
        } else if (vm.selectedType == '问答') {
          itemId = item.customerId;
        } else if (vm.selectedType == '直播') {
          itemId = item.chatMsgId;
        } else {
          itemId = item.commentId;
        }
        vm.selectList.push(itemId);
      });
    } else {
      vm.selectList = [];
    }
  }

  //单选
  function selectItem(item) {
    var itemId = '';
    if (vm.selectedType == '观点') {
      itemId = item.replyId;
    } else if (vm.selectedType == '问答') {
      itemId = item.questionId;
    } else if (vm.selectedType == '直播') {
      itemId = item.chatMsgId;
    } else {
      itemId = item.commentId;
    }
    if (vm.selectList.indexOf(itemId) == -1) {
      vm.selectList.push(itemId);
    } else {
      removeByValue(vm.selectList, itemId);
    }
  }

  //数组中删除指定元素
  function removeByValue(arr, val) {
    for (var i = 0; i < arr.length; i++) {
      if (arr[i] == val) {
        arr.splice(i, 1);
        break;
      }
    }
  }

  //渲染content
  function renderRichText(data) {
    vm.htmlContent = [];  //初始化弹窗内容数据
    var html = '';
    angular.forEach(data, function (item) {
      if (item.replyContent) {                          //观点评论内容
        try {
          var content = JSON.parse(item.replyContent);
          if (content[0].body) {
            html = $sce.trustAsHtml('<div class="popover-text"><span class="content">' + autoMatchStockAndEmoji(content[0].body) + '</span></div>');
            vm.htmlContent.push(html);
          }
        } catch (e) {
          html = $sce.trustAsHtml('<div class="popover-text"><span class="error-text">' + autoMatchStockAndEmoji(item.replyContent) + '</span></div>');
          vm.htmlContent.push(html);
        }

      } else if (item.questionContent) {                //问答评论内容
        html = $sce.trustAsHtml('<div class="popover-text"><span class="content">' + autoMatchStockAndEmoji(item.questionContent) + '</span></div>');
        vm.htmlContent.push(html);

      } else if (item.msgContent) {                     //直播评论内容
        var content = JSON.parse(item.msgContent);
        if (content.content) {
          var obj = JSON.parse(content.content);
          html = $sce.trustAsHtml('<div class="popover-text"><span class="content">' + autoMatchStockAndEmoji(obj[0].body) + '</span></div>');
          vm.htmlContent.push(html);
        }

      } else {                                          //组合评论内容commentContent
        html = $sce.trustAsHtml('<div class="popover-text"><span class="content">' + autoMatchStockAndEmoji(item.commentContent) + '</span></div>');
        vm.htmlContent.push(html);
      }
    });
  }

  //匹配股票和表情
  function autoMatchStockAndEmoji(str){
    return func.autoMatchStock(func.showEmoji(str));
  }
}

module.exports = fansSpeechMonitorCtrl;



