addTagModalService.$inject = ['api'];
function addTagModalService(api) {
  var vm = this;
  
  vm.viewpointTagAddFunc = viewpointTagAddFunc;

  /**
   * 新增观点标签
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function viewpointTagAddFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'viewpoint_tag_add',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

}

module.exports = addTagModalService;


