/* 观点展示规则 module */

require('locale_zh');

var config = require('config'),
  func = require('func');

viewpointDisplayRuleCtrl.$inject = ['$cookies', 'viewpointDisplayRuleService', 'modalService'];

function viewpointDisplayRuleCtrl($cookies, viewpointDisplayRuleService, modalService) {
  var vm = this;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));;
  vm.viewpointRule = '';  //观点规则默认值
  vm.viewpointId = '';  //投顾编号
  vm.adviserName = '';  //投顾姓名
  vm.keywords = '';   //关键字
  vm.adviserTel = ''; //投顾手机号
  vm.viewpointList = [];  //获取到的观点列表
  vm.hotViewpointList = [];   //获取到的热门（置顶）观点列表
  vm.selectedViewpoint = [];  //已置顶的观点列表

  vm.newHotVPList = [];	//将发布的热门观点列表`
  vm.newImgList = [];	//将发布的图片配置列表

  vm.srcArr = [];	//获取到的图片路径

  /* 分页 */
  vm.maxSize = 10;
  vm.totalItems = 1;  //根据接口返回决定
  vm.currentPage = 1;
  vm.itemsPerPage = 10;

  vm.getViewpointList = getViewpointList;
  vm.setViewpointRule = setViewpointRule;
  vm.changeHotViewpoint = changeHotViewpoint;
  vm.uploadFile = uploadFile;
  vm.deleteSelectedViewpoint = deleteSelectedViewpoint;
  vm.releaseViewpointRule = releaseViewpointRule;
	vm.pageChangeFunc = pageChangeFunc;

	init();

  function init() {
	getViewpointRole();
	getViewpointInfo();
  }

  //获取观点相关信息（观点列表和热门观点）
  function getViewpointInfo() {
	getViewpointList();
	getHotViewpointList();
  }

  //获取、查询观点列表
  function getViewpointList() {
	viewpointDisplayRuleService.getViewpointListByOperate(
	  {
		broker_manage_name: vm.adviserName,
		mobile: vm.adviserTel,
		page_count: vm.itemsPerPage,
		page_no: vm.currentPage,
		tenant_id: vm.tenantId,
		viewpoint_id: vm.viewpointId,
		viewpoint_keyword: vm.keywords
	  },
	  function (result) {
		vm.totalItems = result.total_num;
		vm.viewpointList = result.data_list;
	  }
	)
  }

  //获取热门观点列表（置顶的）
  function getHotViewpointList() {
	viewpointDisplayRuleService.getHotViewByOperate(
	  {
		tenant_id: vm.tenantId
	  },
	  function (result) {
		vm.hotViewpointList = result.data_list;
		if (vm.hotViewpointList.length != 0) {
		  angular.forEach(vm.hotViewpointList, function (item) {
			vm.selectedViewpoint.push(item.viewpoint_id + '-' + item.viewpoint_title || '(无标题)');
			vm.newHotVPList.push(item.viewpoint_id);
		  });
		} else {
		  vm.selectedViewpoint = [];
		  vm.newHotVPList = [];
		}
	  }
	)
  }

  //获取观点规则
  function getViewpointRole() {
	viewpointDisplayRuleService.getHotViewRule(
	  {
		tenant_id: vm.tenantId
	  },
	  function (result) {
		vm.viewpointRule = result.hot_view_rule;

		//格式化图片文件
		var imgList = result.hot_view_imgs.split(',');
		angular.forEach(imgList, function (item) {
		  if (item != '') {
		    vm.newImgList.push(item);
		  }
		});
		if (vm.newImgList.length != 0) {
		  angular.forEach(vm.newImgList, function (item, index) {
			vm.srcArr[index] = LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download/' + item + "/" + sessionStorage.getItem('access_token');
		  });
		}
	  }
	)
  }

  //发布观点规则
  function setViewpointRule() {
    var imgParams = '';
	if (vm.newImgList.length != 0) {
	  angular.forEach(vm.newImgList, function (item) {
		imgParams = imgParams.concat(item + ',');
	  });
	}
	viewpointDisplayRuleService.setHotViewRule(
	  {
		hot_view_imgs: imgParams,
		hot_view_rule: vm.viewpointRule,	//0：点赞数规则、1：阅读数规则、2：发布时间规则
		tenant_id: vm.tenantId
	  },
	  function (result) {
		console.log(result);
	  }
	)
  }

  //更新热门观点(新置顶的观点)
  function updateHotViewStatus() {
    var hotViewpointStr = '';
    if (vm.newHotVPList.length != 0) {
	  angular.forEach(vm.newHotVPList, function (item) {
		hotViewpointStr += item + ',';
	  });
	} else {
	  hotViewpointStr = ',,';
	}
	viewpointDisplayRuleService.updateHotViewStatusFunc(
	  {
		hot_view_flag: 1,	//0：取消置顶、1：置顶
		viewpoint_id: hotViewpointStr,
		tenant_id: vm.tenantId
	  },
	  function (result) {
		func.openToaster('发布规则成功！');
		vm.hotViewpointList = [];   //将之前的热门观点数据置空
		vm.selectedViewpoint = [];  //将之前的已选中观点数据置空
		vm.newHotVPList = [];	//将之前要发布的热门观点数据置空
		vm.newImgList = [];		//将已设置的图片列表清空
		init();
	  }
	)
  }

  //上传图片
  function uploadFile(imgId) {
	$('#' + imgId).click();
	document.getElementById(imgId).onchange = function () {
	  var operatorNo = JSON.parse(sessionStorage.getItem('operatorInfo')).operatorNo;
	  var file = $('#' + imgId)[0].files[0];
	  var formData = new FormData();
	  formData.append("file", file);
	  formData.append("user_token", "92f17e36e6cf0a8083ff435fc708a372");
	  formData.append("customer_id", operatorNo || '');
	  formData.append("type", "img");

	  viewpointDisplayRuleService.imgUpload(formData, function (result) {
		var imgSrc = LOCAL_CONFIG.API_HOME + config.FILE_OPEN_API + 'func_file_download/' + result.oss_id + "/" + sessionStorage.getItem('access_token');
		if (imgId == 'img1') {
		  vm.srcArr[0] = imgSrc;
		  vm.newImgList[0] = result.oss_id;
		} else if (imgId == 'img2') {
		  vm.srcArr[1] = imgSrc;
		  vm.newImgList[1] = result.oss_id;
		} else if (imgId == 'img3') {
		  vm.srcArr[2] = imgSrc;
		  vm.newImgList[2] = result.oss_id;
		}
	  }, function (result) {
		console.log('fail');
	  });
	}
  }

  //新增热门(置顶)观点
  function changeHotViewpoint(item) {
	if (vm.newHotVPList.length < 3) {		// 如果当前热门观点已满时，则不可再置顶
	  if (vm.newHotVPList.indexOf(item.viewpoint_id) == -1) {	//如果选择置顶的观点不在当前热门观点中
		vm.newHotVPList.push(item.viewpoint_id);
		vm.selectedViewpoint.push(item.viewpoint_id + '-' + item.viewpoint_title);
	  } else {												//如果选择置顶的观点中在当前热门观点中
		modalService.openConfirm('提示', '请选择不同的观点！');
	  }
	} else {
	  modalService.openConfirm('提示', '当前热门观点已满！');
	}
  }

  //删除已选观点
  function deleteSelectedViewpoint(index) {
	vm.selectedViewpoint.splice(index, 1);
	vm.hotViewpointList.splice(index, 1);
	vm.newHotVPList.splice(index, 1);
  }

  function releaseViewpointRule() {
	setViewpointRule();
	updateHotViewStatus();
  }

  //换页
  function pageChangeFunc() {
	getViewpointList();
  }

}

module.exports = viewpointDisplayRuleCtrl;



