require('./style.less');

var modalOptions = require('jsDir/modal_options'),
    resetPasswordModalTempl = require('./resetPasswordModal.html'),
    func = require('func'),
    sha256 = require('sha256');

resetPasswordModalCtrl.$inject = ['modalService', 'resetPasswordModalService'];

function resetPasswordModalCtrl(modalService, resetPasswordModalService) {
  var vm = this;

  // vm.newPwd = '';
  // vm.oldPwd = '';

  vm.openResetPasswordModalFunc = openResetPasswordModalFunc;

  function openResetPasswordModalFunc() {
    var options = angular.copy(modalOptions);
    options.modalTitle = '重置密码';
    options.template = resetPasswordModalTempl;
    options.controller = defaultController;
    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.iconInvisible = true;//默认新密码不可见
    ra.iconName = 'icon icon-invisible';//默认icon类名
    ra.inputType = 'password';//默认新密码为不可见
    ra.newPwd = '';
    ra.oldPwd = '';

    ra.changeIcon = changeIcon;
    ra.resetPwd = resetPwd;
    ra.cancel = cancel;
    ra.close = close;

    //更改新密码是否可见
    function changeIcon() {
      ra.iconInvisible = !ra.iconInvisible;
      if (ra.iconInvisible) {
        ra.iconName = 'icon icon-invisible';
        ra.inputType = 'password';
      } else {
        ra.iconName = 'icon icon-visible';
        ra.inputType = 'text';
      }
    }

    //修改密码
    function resetPwd() {
      if ((ra.newPwd != undefined && ra.oldPwd != undefined) && (ra.newPwd.length != 0 && ra.oldPwd.length != 0)) {
        resetPasswordModalService.resetPassword(
          {
            newPassword: sha256(ra.newPwd),
            oldPassword: sha256(ra.oldPwd),
            operator_no: '',
            remark: ''
          },
          function (result) {
            func.openToaster('修改密码成功！');
            close();
          }
        )
      } else {
        func.openToaster('新旧密码不能为空');
      }
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = resetPasswordModalCtrl;



