/* 组织管理--修改 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  changeBranchModalTempl = require('./changeBranchModal.html'),
  func = require('func');

changeBranchModalCtrl.$inject = ['modalService', 'changeBranchModalService'];

function changeBranchModalCtrl(modalService, changeBranchModalService) {
  var vm = this;

  vm.selectedBranchInfo = {};   //已选机构信息

  /*函数声明*/
  vm.openchangeBranchModalFunc = openchangeBranchModalFunc;

  /*函数定义*/
  function openchangeBranchModalFunc(info, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '修改';
    options.template = changeBranchModalTempl;
    options.controller = changeBranchModalController;
    vm.selectedBranchInfo = info;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function changeBranchModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, submitText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.submitText = submitText;
    ra.cancelBtnText = cancelBtnText;

    ra.branchNo = vm.selectedBranchInfo.branchNo;   //机构编码
    ra.branchName = vm.selectedBranchInfo.branchName;   //机构名称
    ra.branchType = vm.selectedBranchInfo.branchType;   //机构类别
    ra.typeOptions = [{"code":"1","value":"管理机构"},{"code":"2","value":"产品机构"},{"code":"3","value":"专门机构"},{"code":"4","value":"营业部"}];   //可选机构类型
    ra.chargeName = vm.selectedBranchInfo.chargeName;   //联系人
    ra.chargeTelephone = vm.selectedBranchInfo.chargeTelephone;    //联系电话
    ra.branchCode = vm.selectedBranchInfo.branchCode;   //对应机构编码
    ra.remark = vm.selectedBranchInfo.remark;   //备注

    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function submit() {
      if (ra.branchName == '' || ra.branchName == undefined) {
        func.openToaster('机构名称不能为空！');
      }
      if (ra.branchType == '' || ra.branchType == null) {
        func.openToaster('机构类型不能为空！');
      }
      changeBranchModalService.branchMod(
        {
          action_in: 2,
          branchCode: ra.branchCode,
          branchName: ra.branchName,
          branchNo: ra.branchNo,
          branchType: ra.branchType,
          chargeName: ra.chargeName,
          chargeTelephone: ra.chargeTelephone,
          menuRights: vm.selectedBranchInfo.menuRights,
          remark: ra.remark,
          upperBranchNo: vm.selectedBranchInfo.upperBranchNo
        },
        function (result) {
          func.openToaster('操作成功！');
          close();
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = changeBranchModalCtrl;




