require ('./style.less');

var modalOptions = require('jsDir/modal_options'),
QACheckOperateModalTempl = require('./QACheckOperateModal.html'),
  func = require('func');

QACheckOperateModalCtrl.$inject = ['modalService', 'QACheckOperateModalService'];

function QACheckOperateModalCtrl(modalService, QACheckOperateModalService) {
  var vm = this;

  vm.type = '';
  vm.answer_id = '';
  vm.openQACheckOperateFunc = openQACheckOperateFunc;

  function openQACheckOperateFunc(type, answerId, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = type == 1 ? '审核通过' : '审核不通过'; // 1-审批通过。2-审批不通过
    options.template = QACheckOperateModalTempl;
    vm.answer_id = answerId;
    vm.type = type;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function () {
      angular.isFunction(callBack) && callBack();
    };

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.tenant_id = JSON.parse(sessionStorage.getItem('tenantId'));
    var infoData = JSON.parse(window.sessionStorage.getItem('operatorInfo'));
    ra.operateName = infoData.name || '';
    ra.operateNo = infoData.operatorNo || '';
    ra.type = vm.type;
    ra.remark = '';
    
    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    //新增提交
    function submit() {
      QACheckOperateModalService.askAuditFunc({
        tenant_id: ra.tenant_id,
        answer_id: vm.answer_id || '',
        audit_state: ra.type, // 1-审批通过。2-审批不通过
        operator_no: ra.operateNo,
        operator_name: ra.operateName,
        remark: ra.remark
      }, function(res) {
        console.log('res', res);
        close();
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('提示', err.error_info);
      });
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = QACheckOperateModalCtrl;


