/* 投顾业绩数据查询 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var investorAchievementQryDownModalCtrl = require('./modalPopup/investorAchievementQryDownModal/controller');
var investorAchievementQryDownModalService = require('./modalPopup/investorAchievementQryDownModal/service');

//setter定义模块
module.exports = angular
  .module('investorAchievementQry', [])
  .config(Routing)
  .controller('investorAchievementQryCtrl', ctrl)
  .service('investorAchievementQryService', service)
  .factory('investorAchievementQryDownModalCtrl', investorAchievementQryDownModalCtrl)
  .service('investorAchievementQryDownModalService', investorAchievementQryDownModalService).name;



