/* 组织管理--删除 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  operatorDelTempl = require('./operatorDel.html'),
  func = require('func');

operatorDelCtrl.$inject = ['modalService'];

function operatorDelCtrl(modalService) {
  var vm = this;

  /*函数声明*/
  vm.openOperatoeDelModalFunc = openOperatoeDelModalFunc;

  /*函数定义*/
  function openOperatoeDelModalFunc(callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '删除';
    options.template = operatorDelTempl;
    options.controller = operatorDelController;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function operatorDelController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, ensureBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.ensureBtnText = ensureBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function submit() {
      close();
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = operatorDelCtrl;




