/* 投顾推荐配置 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');
var storeSelectedCtrl = require('./modalPopup/storeSelected/controller');
var storeSelectedService = require('./modalPopup/storeSelected/service');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');

//setter定义模块
module.exports = angular
  .module('recommendInvest', [])
  .config(Routing)
  .service('recommendInvestService', service)
  .controller('recommendInvestCtrl', ctrl)
  .factory('storeSelectedCtrl', storeSelectedCtrl)
  .service('storeSelectedService', storeSelectedService).name;



