require('locale_zh');

serviceProduct05Ctrl.$inject = ['$sce'];

function serviceProduct05Ctrl($sce) {
  var vm = this;
  vm.height = window.innerHeight - 100;
  vm.paddingTop = vm.height / 2 - vm.imgHeight / 2;
  vm.access_token = sessionStorage.getItem('access_token');
  vm.url = $sce.trustAsResourceUrl(LOCAL_CONFIG.SERVICEPRODUCT05 + '?access_token=' + vm.access_token);
}

module.exports = serviceProduct05Ctrl;


