/* 直播监控 module */

liveMonitorListRoute.$inject = ['$stateProvider'];

function liveMonitorListRoute($stateProvider) {
  $stateProvider.state('home.liveMonitorList', {
    url: '/liveMonitorList',
    templateUrl: './views/liveMonitorList.html',
    controller: 'liveMonitorList',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = liveMonitorListRoute;



