require ('./style.less');

var modalOptions = require('jsDir/modal_options'),
  storeSelectedModalTempl = require('./storeSelected.html'),
  func = require('func');

storeSelectedModalCtrl.$inject = ['modalService', 'storeSelectedService'];

function storeSelectedModalCtrl(modalService, storeSelectedService) {
  var vm = this;

  vm.openStoreSelectedModalFunc = openStoreSelectedModalFunc;

  function openStoreSelectedModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '选择店铺';
    options.template = storeSelectedModalTempl;
    options.modalParams = data;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function () {
      angular.isFunction(callBack) && callBack(vm.selectedStoreParam);
    };

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.maxSize = 5;
    ra.totalItems = 1;
    ra.currentPage = 1;
    ra.itemsPerPage = 5;

    ra.tenant_id = ra.modalParams;    // 操作员所属机构信息
    ra.brokerManagerName = '';    // 店铺管理员名称
    ra.brokerName = '';   // 店铺名称

    vm.selectedStoreParam = {};  // 所选店铺参数
    ra.store = {};  // 所选店铺（绑定值）
    ra.pageAdviserList = [];    // 店铺展示列表

    ra.getStoreList = getStoreList;
    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    getStoreList();

    // 查询店铺列表
    function getStoreList() {
      storeSelectedService.adviserInfoQry(
        {
          page_count: ra.itemsPerPage,
          page_no: ra.currentPage,
          company_id: ra.tenant_id,
          broker_manager_name: ra.brokerManagerName,
          broker_name: ra.brokerName
        },
        function (result) {
          ra.pageAdviserList = result.data_list;
          ra.totalItems = result.total_num;
        }
      )
    }

    // 设置默认推荐店铺
    function setDefaultStoreFunc(id) {
      storeSelectedService.setDefaultStore(
        {
          broker_id: id
        },
        function (result) {
          console.log(result);
        }
      )
    }

    //新增提交
    function submit() {
      vm.selectedStoreParam = ra.store;
      if (vm.selectedStoreParam.broker_id) {
        setDefaultStoreFunc(vm.selectedStoreParam.broker_id);
        close();
      } else {
        func.openToaster('请选择推荐店铺！');
      }
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = storeSelectedModalCtrl;


