serviceProduct01Route.$inject = ['$stateProvider'];

function serviceProduct01Route($stateProvider) {
    $stateProvider
        .state('home.serviceProduct01', {
            url: '/serviceProduct01',
            templateUrl: './views/serviceProduct01.html',
            controller: 'serviceProduct01Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = serviceProduct01Route;



