combineShelfCheckRoute.$inject = ['$stateProvider'];

function combineShelfCheckRoute($stateProvider) {
  $stateProvider.state('home.combineShelfCheck', {
    url: '/combineShelfCheck',
    templateUrl: './views/combineShelfCheck.html',
    controller: 'combineShelfCheckCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = combineShelfCheckRoute;



