/* 客户贡献度查询 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var orderCheckModalCtrl = require('./modalPopup/orderCheckModal/controller');
var orderCheckModalService = require('./modalPopup/orderCheckModal/service');
var orderRemarkModalCtrl = require('./modalPopup/orderRemarkModal/controller');

//setter定义模块
module.exports = angular
  .module('orderCheck', [])
  .config(Routing)
  .controller('orderCheckCtrl', ctrl)
  .service('orderCheckService', service)
  .factory('orderCheckModalCtrl', orderCheckModalCtrl)
  .service('orderCheckModalService', orderCheckModalService)
  .factory('orderRemarkModalCtrl', orderRemarkModalCtrl).name;



