unifyPayment02Route.$inject = ['$stateProvider'];

function unifyPayment02Route($stateProvider) {
    $stateProvider
        .state('home.unifyPayment02', {
            url: '/unifyPayment02',
            templateUrl: './views/unifyPayment02.html',
            controller: 'unifyPayment02Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = unifyPayment02Route;



