require ('./style.less');

var modalOptions = require('jsDir/modal_options'),
    addProductModalTempl = require('./addProductModal.html'),
    func = require('func');

addProductModalCtrl.$inject = ['modalService', 'addProductModalService', '$http'];

function addProductModalCtrl(modalService, addProductModalService, $http) {
  var vm = this;

  vm.openAddProductModalFunc = openAddProductModalFunc;

  function openAddProductModalFunc(callBack1, callBack2) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '添加产品';
    options.template = addProductModalTempl;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function() {
      angular.isFunction(callBack1) && callBack1();
    };
    // 关闭后的回调
    options.cancelCallback = function() {
      angular.isFunction(callBack2) && callBack2();
    }

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.maxSize = 5;
    ra.totalItems = 1;
    ra.currentPage = 1;
    ra.itemsPerPage = 10;

    ra.keyword = '';
    ra.productList = [];
    ra.checkedList = [];

    ra.getProductionMallFunc = getProductionMallFunc;
    ra.updateProductListFunc = updateProductListFunc;
    ra.selectedItem = selectedItem;
    ra.cancel = cancel;
    ra.close = close;

    getProductionMallFunc();

    /**
     * 获取金融产品池
     *
     * @return  {[type]}  [return description]
     */
    function getProductionMallFunc() {
      ra.checkedList = [];
      addProductModalService.getProductList({
        access_token: sessionStorage.getItem('access_token'),
        keyword: ra.keyword,
        page_no: ra.currentPage,
        page_count: ra.itemsPerPage
      }, function(res) {
        ra.productList = res.data_list;
        ra.totalItems = res.total_num;
      }, function(err) {
        ra.productList = [];
        ra.totalItems = 0;
      })
    }

    /**
     * 添加到金融产品列表
     *
     * @return  {[type]}  [return description]
     */
    function updateProductListFunc() {
      var idList = [];
      if (ra.checkedList.length) {
        idList = ra.checkedList.map(function(item) {
          return item.counterfund_id;
        });
      } else {
        modalService.openAlert('提示', '请选择产品！');
        return;
      }
      addProductModalService.updateProductList({
        counterfund_id: idList.join(',') || '',
        is_public: '1'
      }, function(res) {
        modalService.openAlert('提示', res.error_info);
      }, function(err) {
        modalService.openAlert('提示', res.error_info);
      })
    }

    function selectedItem(item) {
      ra.checkedList.push(item);
      console.log('checkedList', ra.checkedList);
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = addProductModalCtrl;



