/* 组合审核 module */

require('locale_zh');
var passImg = require('img/already-pass.png');
var noPassImg = require('img/no-pass.png');
var md5Func = require('../services/md5');
var func = require('func');
combineCheckCtrl.$inject = ['combineCheckService', 'combineOnlineModalCtrl'];
function combineCheckCtrl(combineCheckService, combineOnlineModalCtrl) {
  var vm = this;

  vm.passImg = passImg;   //审核通过图片
  vm.noPassImg = noPassImg;   //审核未通过图片

  /* 分页 */
  vm.maxSize = 10;
  vm.totalItems = '';
  vm.currentPage = 1;
  vm.itemPerPage = 10;
  vm.showTable = false;
  vm.showTableHis = '点击加载更多';
  vm.showClickList = false;
  vm.typeList = [{"code": "", "value": "审核状态"}, {"code": "0", "value": "未审核"}, {"code": "1", "value": "审核通过"}, {"code": "2", "value": "审核不通过"}];

  vm.branchInfo = JSON.parse(sessionStorage.getItem("branchQryData"));
  vm.companyId = JSON.parse(sessionStorage.getItem('packHead')).company_id;
  vm.companyNo = JSON.parse(sessionStorage.getItem('packHead')).branch_no;
  vm.branchNo = vm.branchInfo[0].branchNo;
  vm.adviserType = null; //筛选类型默认为全部
  vm.combiCheckKey = '';  //搜索框置空
  vm.requestNum = 10;
  vm.combineCheckList = [];//组合列表数据
  vm.combiCheckDetail = {}; //组合详情数据
  vm.pageArr = [];
  vm.combiApplyHisList = [];//组合申请流水数据
  vm.positionStr = '';
  vm.selectedData = 0; //默认选中第一项
  vm.selectedHis = 0; //审核历史默认选中第一项
  vm.checkerList = [];  //审核人列表
  vm.auditOpinion = ''; //审核意见
  vm.checkData = [
    {"code": "0", "value": "未审核"},
    {"code": "1", "value": "审核通过"},
    {"code": "2", "value": "审核不通过"}
  ];
  vm.status = [
    {"code": "0", "value": "默认"},
    {"code": "1", "value": "保守"},
    {"code": "2", "value": "稳健"},
    {"code": "3", "value": "积极型"},
    {"code": "4", "value": "激进型"},
    {"code": "5", "value": "主题型"},
    {"code": "6", "value": "价值型"},
    {"code": "7", "value": "趋势型"}
  ];
  // 风险等级
  vm.riskLevel = ['默认', '低风险', '中低风险', '中风险', '中高风险', '高风险', '极高风险'];

  vm.combineCheckSearch = combineCheckSearch; //点击查询
  vm.showMore = showMore; //详情点击加载更多
  vm.combiApplyHis = combiApplyHis; //组合申请历史查询
  vm.pageChangeFunc = pageChange; //分页
  vm.setDetail = showCombiDetails; //显示组合详情
  vm.combineAudit = combineAudit; //组合审核
  vm.checkHisRemark = checkHisRemark; // 审核历史点击获取意见
  vm.openCombineOnlineModalFunc = openCombineOnlineModalFunc; //打开组合上下架设置弹窗

  init();

  function init() {
    combineQry(); //组合申请查询
  }

  function combineCheckSearch(type) {
    vm.currentPage = 1;
    vm.adviserType = type;
    combineQry();
  }

  //组合申请查询
  function combineQry() {
    combineCheckService.combineQry({
      verify_status: vm.adviserType,
      branch_no: vm.companyNo,
      businsys_no: 1000,
      combi_name: vm.combiCheckKey,
      request_num: vm.requestNum,
      position_str: vm.positionStr,
      sendercomp_id: vm.companyId,
      company_id: vm.companyId
    }, function (response) {
      qryTotalItems();
      vm.combineCheckList = response.data;

      if (vm.combineCheckList && vm.combineCheckList.length > 0) {
        showCombiDetails(0);
        for (var k = 0; k < vm.requestNum; k++) {
          for (var t = 0; t < 3; t++) {
            if (vm.combineCheckList[k].verify_status == vm.checkData[t].code) {
              vm.combineCheckList[k].verify_status = vm.checkData[t].value;
            }
          }
          for (var m = 0; m < 8; m++) {
            if (vm.combineCheckList[k].invest_style == vm.status[m].code) {
              vm.combineCheckList[k].invest_style = vm.status[m].value;
            }
          }
        }
      } else {
        vm.combiCheckDetail = [];
        vm.combiApplyHisList = [];
        vm.checkerList = [];
        vm.auditOpinion = '';
      }
    })
  }

  function qryTotalItems() {
    combineCheckService.combineQry({
      verify_status: vm.adviserType,
      combi_name: vm.combiCheckKey,
      businsys_no: 1000,
      company_id: vm.companyId,
      branch_no: vm.companyNo,
      sendercomp_id: vm.companyId,
      request_num: 1000
    }, function (response) {
      if (response.data) {
        vm.totalItems = response.data.length;
        getPositionStr(response.data);
      }
    })
  }

  //计算总页数,填充position_str,否则用户如果直接点击页码无法翻页
  function getPositionStr(arr) {
    var numPages = (vm.totalItems + vm.requestNum - 1) / vm.requestNum;
    for (var i = 1; i <= numPages; i++) {
      if (i < parseInt(numPages)) {
        vm.pageArr[i + 1] = arr[vm.requestNum * i - 1].position_str;
        continue;
      }
      return;
    }
  }

  // 打开弹窗
  function openCombineOnlineModalFunc() {
    combineOnlineModalCtrl.openCombineOnlineModalFunc();
  }

  function showCombiDetails(index) {
    vm.selectedData = index;
    vm.combiCheckDetail = vm.combineCheckList[index];
    vm.combiCheckDetail.fare_per_month_str = vm.combiCheckDetail.fare_per_month_str ? vm.combiCheckDetail.fare_per_month_str.split('|')[0] : '';
    combiApplyHis(vm.combiCheckDetail);
  }

  function combiApplyHis(obj) {
    combineCheckService.combiApplyHis({
      businsys_no: 1000,
      company_id: vm.companyId,
      user_id: obj.user_id,
      sendercomp_id: vm.companyId
    }, function (response) {
      vm.combiApplyHisList = [];
      vm.checkerList = [];
      angular.forEach(response.data, function (data) {
        //过滤数据，不处理verify_status=0的数据
        if (data['verify_status'] != '0') {
          //处理审核人字段信息
          if (data.remark.indexOf('审核人') > 0) {
            vm.checkerList.push(data.remark.substr(data.remark.lastIndexOf('审核人') + 4));
          } else if (data.remark.indexOf('自动审核') > 0) {
            vm.checkerList.push('自动审核');
          } else {
            vm.checkerList.push('暂无');
          }
          //处理审核状态字段信息
          for (var t = 0; t < 3; t++) {
            if (data.verify_status == vm.checkData[t].code) {
              data.verify_status = vm.checkData[t].value;
            }
          }
          vm.combiApplyHisList.push(data);
        }
      });
      //默认显示审核历史中第一条审核意见
      if (vm.combiCheckDetail.verify_status != '未审核' && vm.combiApplyHisList.length > 0) {
        vm.selectedHis = 0;
        combiHisObj(vm.combiApplyHisList[0]);
      } else {
        vm.auditOpinion = '';
      }
    })
  }

  //获取当前审核记录的审核意见
  function combiHisObj(obj) {
    if (obj && obj.verify_status != '未审核') {
      vm.auditOpinion = obj.remark && obj.remark.indexOf('审核人') > 0 ? obj.remark.substr(0, obj.remark.lastIndexOf(',')) : obj.remark;
    } else {
      vm.auditOpinion = '';
    }
  }

  //选择某条审核记录
  function checkHisRemark(i) {
    vm.selectedHis = i;
    combiHisObj(vm.combiApplyHisList[i]);
  }

  //点击更多
  function showMore() {
    vm.showTable = !vm.showTable;
    vm.showTableHis = vm.showTable == false ? '点击加载更多' : '点击收起';
  }

  //组合审核
  function combineAudit(flag) {
    vm.operatorInfo = JSON.parse(sessionStorage.getItem("operatorInfo"));
    vm.operatorName = vm.operatorInfo ? vm.operatorInfo.name : '';
    var audit = {
      apply_no: vm.combiCheckDetail.apply_no,
      apply_date: vm.combiCheckDetail.apply_date,
      sendercomp_id: vm.companyId,
      remark: vm.auditOpinion + ",审核人：" + vm.operatorName
    };
    if (flag === 'yes') {		//审核通过
      combineCheckService.combineAuditPass(audit, function (response) {
        if (response.data[0].combi_code) {
          saveCombiPass(vm.combiCheckDetail, response.data[0].combi_code);
        }
      });
    } else {					//审核不通过
      combineCheckService.combineAuditNoPass(audit, function (response) {
        combineQry();
        func.openToaster('审核不通过操作成功！');
      });
    }
  }

  //保存审核信息
  function saveCombiPass(item, code) {
    combineCheckService.saveCombiPassFunc({
      apply_no: item.apply_no,
      user_id: item.user_id,
      company_id: item.sendercomp_id,
      combi_name: item.combi_name,
      combi_code: code,
      sign: md5Func.hex_md5(item.sendercomp_id.concat('&' + code, '&' + item.user_id)).toUpperCase()
    }, function (response) {
      combineQry();
      func.openToaster('审核通过操作成功！');
    })
  }

  //分页
  function pageChange() {
    vm.positionStr = vm.pageArr[vm.currentPage];
    combineQry();
  }

}

module.exports = combineCheckCtrl;



