welcomeRoute.$inject = ['$stateProvider'];

function welcomeRoute($stateProvider) {
    $stateProvider
        .state('home.welcome', {
            url: '/welcome',
            templateUrl: './views/welcome.html',
            controller: 'welcomeCtrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = welcomeRoute;



