/* Banner设置 module */
require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var editPictureModalCtrl = require('./modalPopup/editPictureModal/controller');
var editPictureModalService = require('./modalPopup/editPictureModal/service');

//setter定义模块
module.exports = angular
  .module('imageConfig', [])
  .config(Routing)
  .service('imageConfigService', service)
  .controller('imageConfigCtrl', ctrl)
  .factory('editPictureModalCtrl', editPictureModalCtrl)
  .service('editPictureModalService', editPictureModalService).name;



