viewpointCheckModalService.$inject = ['api'];
function viewpointCheckModalService(api) {
  var vm = this;
  
  vm.checkViewpoint = checkViewpoint; // 观点审核

  function checkViewpoint(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'manage',
      url: 'func_viewpoint_review_by_id_manage',
      data: data,
      successCallback: function(result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function(response) {
        angular.isFunction(cb2) && cb2(response);
      }
    });
  }

}

module.exports = viewpointCheckModalService;


