// 观点标签设置

require('locale_zh');

var func = require('func');

viewpointTagSettingCtrl.$inject = ['modalService', 'viewpointTagSettingService', 'addTagModalCtrl'];

function viewpointTagSettingCtrl(modalService, viewpointTagSettingService, addTagModalCtrl) {
  var vm = this;

  vm.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
  vm.tenant_id = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.keywords = '';
  vm.dataList = [];
  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.searchTag = searchTag;
  vm.addTag = addTag;
  vm.deleteTag = deleteTag;
  vm.pageChangeFunc = pageChangeFunc;
  
  getViewpointListFunc();

  function getViewpointListFunc() {
    vm.dataList = [];
    viewpointTagSettingService.listViewpointTagFunc({
      tenant_id: vm.tenant_id,
      keyword: vm.keywords,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage
    }, function(res) {
      console.log('res', res);
      if (res.data_list && res.data_list.length) {
        vm.dataList = res.data_list;
        vm.totalItems = res.total_num * 1;
      }
    }, function(err) {
      console.log('err', err);
    })
  }

  function searchTag() {
    vm.currentPage = 1;
    getViewpointListFunc();
  }

  function addTag() {
    addTagModalCtrl.openAddTagModalFunc(function() {
      vm.keywords = '';
      vm.currentPage = 1;
      getViewpointListFunc();
    });
  }

  function deleteTag(item) {
    viewpointTagSettingService.viewpointTagDelFunc({
      operator_name: vm.operatorInfo.name,
      operator_no: vm.operatorInfo.operatorNo,
      tenant_id: vm.tenant_id,
      viewpoint_tag_ids: item.viewpoint_tag_id
    }, function(res) {
      console.log('res', res);
      if (res.error_no == '0') {
        modalService.openAlert('提示', '执行成功！');
        getViewpointListFunc();
      } else {
        modalService.openAlert('提示', res.error_info);
      }
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('提示', '执行错误！');
    })
  }

  function pageChangeFunc() {
    getViewpointListFunc();
  }
}

module.exports = viewpointTagSettingCtrl;



