require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  combineOnlineModalTempl = require('./combineOnlineModal.html');

combineOnlineModalCtrl.$inject = ['modalService', 'combineOnlineModalService'];

function combineOnlineModalCtrl(modalService, combineOnlineModalService) {
  var vm = this;

  /*函数声明*/
  vm.openCombineOnlineModalFunc = openCombineOnlineModalFunc;


  /*函数定义*/
  function openCombineOnlineModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '组合上下架';
    options.template = combineOnlineModalTempl;
    options.size = 'md';
    options.controller = combineOnlineModalController;
    options.modalParams = data;
    options.confirmCallback = function (result) {
      angular.isFunction(callback) && callback(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function combineOnlineModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, isShowCancelBtn, confirmBtnText, cancelBtnText, ensureBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;

    // 风险等级
    ra.riskLevel = ['默认', '低风险', '中低风险', '中风险', '中高风险', '高风险', '极高风险'];
    // 上下架状态
    ra.onlineType = [{"code": "2", "value": "上下架状态"}, {"code": "0", "value": "上架"}, {"code": "4", "value": "下架"}];
    ra.onlineTypeData = '2'; // 上下架条件查询入参
    ra.userToken = '';
    ra.itemsPerPage = 5;
    ra.positionStr = 0;     // 当前页最后一项数据的定位串
    ra.positionStrList = [0];    // 定位串历史数组，用于查询上一页数据
    ra.combineName = '';     // 筛选条件组合名称
    ra.combineCreator = '';     // 筛选条件组合创建人
    ra.sendercompId = JSON.parse(sessionStorage.getItem('packHead')).sendercomp_id;
    ra.combineList = [];    // 组合列表

    ra.getUserTokenFunc = getUserTokenFunc;
    ra.getCombineListFunc = getCombineListFunc;
    ra.searchCombineByKeyword = searchCombineByKeyword;
    ra.pageChangeFunc = pageChangeFunc;
    ra.setCombineOnlineFunc = setCombineOnlineFunc;
    ra.filterCombineStatusFunc = filterCombineStatusFunc;
    ra.cancel = cancel;
    ra.close = close;

    init();

    function init() {
      ra.getUserTokenFunc();
    }

    // 获取user_token
    function getUserTokenFunc() {
      combineOnlineModalService.getUserToken({
      }, function (result) {
        ra.userToken = result.user_token;
        ra.getCombineListFunc();
      })
    }

    // 查询组合列表
    function getCombineListFunc() {
      combineOnlineModalService.getCombineList({
        user_token: ra.userToken,
        combi_name: ra.combineName,
        alias_name: ra.combineCreator,
        combi_status: ra.onlineTypeData,
        query_flag: 1,
        position_str: ra.positionStr,
        request_num: ra.itemsPerPage
      }, function (result) {
        ra.combineList = result.data_list;
        // 获取最后一条数据的定位串
        ra.lastPositionStr = parseInt(result.data_list[result.data_list.length - 1].position_str);
      })
    }

    // 条件搜索
    function searchCombineByKeyword(status) {
      ra.positionStr = 0;
      ra.positionStrList = [0];   // 重置定位串历史数组
      ra.onlineTypeData = status;
      ra.getCombineListFunc();
    }

    // 分页查询，将上一次请求的定位串记录，上翻页时使用
    function pageChangeFunc(type) {
      if (type == 'pre' && ra.positionStr > 1) {
        ra.positionStrList.pop();
        ra.positionStr = ra.positionStrList[ra.positionStrList.length - 1];
        ra.getCombineListFunc();
      } else if (type == 'next' && ra.combineList.length) {
        ra.positionStr = ra.lastPositionStr;
        ra.positionStrList.push(ra.positionStr);
        ra.getCombineListFunc();
      }
    }

    // 设置组合上下架
    function setCombineOnlineFunc(combine) {
      combineOnlineModalService.setCombineOnline({
        combi_code: combine.combi_code,
        combi_status: combine.combi_status == '0' ? '4' : '0',
        sendercomp_id: ra.sendercompId,
        user_id: combine.user_id
      }, function (result) {
        modalService.openAlert('提示', '执行成功！');
        ra.getCombineListFunc();
      })
    }

    // 匹配上下架状态，0：上架状态，4：下架状态
    function filterCombineStatusFunc(status) {
      // 反向匹配，执行反向操作
      if (status == '0') {
        return '下架';
      } else if (status == '4') {
        return '上架';
      }
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = combineOnlineModalCtrl;


