/* 直播监控 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');

//setter定义模块
module.exports = angular
  .module('liveMonitor.liveMonitorContent', [])
  .config(Routing)
  .service('liveMonitorContentService', service)
  .controller('liveMonitorContent', ctrl).name;



