/* 金融产品池管理 module */

require('locale_zh');

var func = require('func');

productPoolManageCtrl.$inject = ['productPoolManageService', 'addProductModalCtrl', 'addProductModalService', 'modalService'];

function productPoolManageCtrl(productPoolManageService, addProductModalCtrl, addProductModalService, modalService) {
  var vm = this;

  vm.currentPage = 1;
  vm.itemPerPage = 10;
  vm.totalItems = 0;

  vm.keyword = '';
  vm.productList = [];

  vm.getProductListFunc = getProductListFunc;
  vm.deleteFunc = deleteFunc;
  vm.deleteProductListFunc = deleteProductListFunc;
  vm.pageChangeFunc = pageChangeFunc;
  vm.openModal = openModal;

  getProductListFunc();

  /**
   * 获取金融产品列表
   *
   * @return  {[type]}  [return description]
   */
  function getProductListFunc() {
    productPoolManageService.getProductList({
      access_token: sessionStorage.getItem('access_token'),
      is_public: '1',   // 0：只看隐藏（删除状态的部分）、1：只看公开、不传：看所有
      keyword: vm.keyword,
      page_no: vm.currentPage,
      page_count: vm.itemPerPage
    }, function(res) {
      vm.productList = res.data_list;
      vm.totalItems = res.total_num;
    }, function(err) {
      vm.productList = [];
      vm.totalItems = 0;
    })
  }

  /**
   * 删除金融产品
   *
   * @return  {[type]}  [return description]
   */
  function deleteProductListFunc(item) {
    productPoolManageService.updateProductList({
      counterfund_id: item.counterfund_id,
      is_public: '0'  // 0：不公开、1：公开
    }, function(res) {
      modalService.openAlert('提示', '删除成功！');
      getProductListFunc();
    }, function(err) {
      modalService.openAlert('提示', '删除失败！');
    })
  }

  /**
   * 删除操作
   *
   * @param   {[type]}  item  [item description]
   *
   * @return  {[type]}        [return description]
   */
  function deleteFunc(item) {
    modalService.openConfirm('提示', '删除产品代码' + item.prod_code + '产品名称' + item.prod_name, function() {
      deleteProductListFunc(item);
    })
  }

  /**
   * 分页
   *
   * @return  {[type]}  [return description]
   */
  function pageChangeFunc() {
    getProductListFunc();
  }

  /**
   * 打开弹窗
   *
   * @return  {[type]}  [return description]
   */
  function openModal() {
    addProductModalCtrl.openAddProductModalFunc(function() {
      console.log('openAddProductModalFunc');
    }, function() {
      getProductListFunc();
    })
  }

}

module.exports = productPoolManageCtrl;



