/* 业绩统计查询 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var achievementStatisticsDownloadCtrl = require('./modalPopup/achievementStatisticsDownload/controller');
var achievementStatisticsDownloadService = require('./modalPopup/achievementStatisticsDownload/service');

//setter定义模块
module.exports = angular
  .module('achievementStatisticsQry', [])
  .config(Routing)
  .controller('achievementStatisticsQryCtrl', ctrl)
  .service('achievementStatisticsQryService', service)
  .factory('achievementStatisticsDownloadCtrl', achievementStatisticsDownloadCtrl)
  .service('achievementStatisticsDownloadService', achievementStatisticsDownloadService).name;



