require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  operatorTempl = require('./operatorAdd.html'),
  sha256 = require('sha256');

operatorAddCtrl.$inject = ['modalService', 'operatorAddService'];

function operatorAddCtrl(modalService, operatorAddService) {
  var vm = this;

  /*函数声明*/
  vm.operatorAddFunc = operatorAddFunc;


  /*函数定义*/
  function operatorAddFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '操作员新增';
    options.template = operatorTempl;
    options.size = 'md';
    options.controller = operatorAddController;
    options.modalParams = data;  //列表传递数据
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function operatorAddController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, saveBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.timer = null; // 防抖函数的时间对象
    ra.keyword = ''; // 机构搜索关键字
    ra.branchNo = '';
    ra.branchName = '';

    ra.isMod = false;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.saveBtnText = saveBtnText;

    ra.dataMulList = modalParams.branch;  //获取机构编码
    console.log(modalParams);
    ra.dicts_type = [{"code":"1","value":"管理员"},{"code":"2","value":"普通操作员"}]; //操作员类型
    ra.cancel = cancel;
    ra.close = close;
    ra.submit = submit;
    ra.selectBranchNoFunc = selectBranchNoFunc;
    ra.searchByKeywordFunc = searchByKeywordFunc;

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

    function submit() {
      if (ra.branchNo && ra.operator_modal_type && ra.operator_modal_code &&
          ra.operator_modal_name && ra.operator_modal_password && ra.operator_modal_confirm &&
          ra.operator_modal_phone && (ra.operator_modal_password === ra.operator_modal_confirm)) {
        ra.isMod = false;
        operatorAddService.operatorAdd({
            branchNo: ra.branchNo,
            check_password: sha256(ra.operator_modal_confirm),
            name: ra.operator_modal_name,
            operatorNo: ra.operator_modal_code,
            operatorType: ra.operator_modal_type,
            password: sha256(ra.operator_modal_password),
            remark: ra.operator_modal_remark,
            telephone: ra.operator_modal_phone
          }, function (response) {
            var result = {
              operatorNo: ra.operator_modal_code,
              branchNo: ra.branchNo,
              branchName: ra.branchName
            };
            $uibModalInstance.close(result);
            var options = angular.copy(modalOptions);
            options.modalTitle = '成功';
            options.modalInfo = '操作员新增成功！';
            options.modalPriority = 'unique';
            options.size = 'md';
            modalService.openModal(options);
            close();
          });
      } else {
        modalService.openAlert('提示', '请验证表单');
      }
    }

    // 机构选择
    function selectBranchNoFunc(branchNo, branchName) {
      ra.branchNo = branchNo;
      ra.branchName = branchName;
    }

    // 关键字查询机构列表（防抖）
    function searchByKeywordFunc() {
      clearTimeout(ra.timer);
      ra.timer = setTimeout(function () {
        getAllBranch();
      }, 1000);
    }

    // 查询机构列表
    function getAllBranch() {
      operatorAddService.allBranchQry(
        {
          keyword: ra.keyword
        },
        function(response2) {
          ra.dataMulList = response2.data;
          console.log('dataList', ra.dataMulList);
        }
      );
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = operatorAddCtrl;




