require('locale_zh');
var bgImg = require('img/box.png');

welcomeCtrl.$inject = [];

function welcomeCtrl() {
  var vm = this;

  vm.bgImg = bgImg;
  vm.imgWidth = (window.innerWidth - 200) / 3;
  vm.imgHeight = vm.imgWidth / 2.5;
  vm.height = window.innerHeight - 64;
  vm.paddingTop = vm.height / 2 - vm.imgHeight / 2;
  
  
}

module.exports = welcomeCtrl;


