/* 金融股票池管理 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var addStockModalCtrl = require('./modalPopup/addStockModal/controller');
var addStockModalService = require('./modalPopup/addStockModal/service');

//setter定义模块
module.exports = angular
  .module('stockPoolManage', [])
  .config(Routing)
  .service('stockPoolManageService', service)
  .controller('stockPoolManageCtrl', ctrl)
  .factory('addStockModalCtrl', addStockModalCtrl)
  .service('addStockModalService', addStockModalService).name;



