 /* 新增标签 */

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
viewpointDetailModalTempl = require('./viewpointDetailModal.html');
var func = require('func');

viewpointDetailModalCtrl.$inject = ['modalService', '$sce'];

function viewpointDetailModalCtrl(modalService, $sce) {
  var vm = this;

  /*函数声明*/
  vm.openViewpointDetailModalFunc = openViewpointDetailModalFunc;

  /*函数定义*/
  function openViewpointDetailModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '观点详情';
    options.template = viewpointDetailModalTempl;
    options.controller = defaultController;
    options.modalParams = data;
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.param = JSON.parse(modalParams.viewpoint_content);
    ra.content = $sce.trustAsHtml(func.showEmoji(ra.param.content));
    ra.author = ra.param.author;
    ra.title = ra.param.title;
    ra.time = modalParams.send_time;

    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function submit() {
      close()
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = viewpointDetailModalCtrl;




