/* 推荐投顾规则 module */

recommendInvestService.$inject = ['api'];

function recommendInvestService(api) {
  var vm = this;

  // vm.getDefaultStore = getDefaultStore; // 获取默认推荐店铺
  vm.adviserInfoQry = adviserInfoQry; // 店铺列表信息查询
  vm.recommendPublish = recommendPublish; // 保存店铺推荐
  vm.getRecommendQry = getRecommendQry; // 获取已推荐店铺列表
  
  // // 获取默认推荐店铺
  // function getDefaultStore(data, cb1) {
  //   api.postRequest({
  //     prefix_type: 'vipstu',
  //     url: 'manage/func_get_default_mien_broker',
  //     data: data,
  //     successCallback: function (result) {
  //       angular.isFunction(cb1) && cb1(result);
  //     }
  //   });
  // }

  // 店铺信息查询
  function adviserInfoQry(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage/fun_broker_recommend',
      //url: 'fun_broker_recommend',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  // 保存推荐店铺列表（发布）
  function recommendPublish(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage/fun_save_broker_recommend_list',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }
  
  // 获取已推荐店铺列表
  function getRecommendQry(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage/fun_get_broker_recommend_list',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = recommendInvestService;



