/* 角色修改 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  alterModalTempl = require('./alterModal.html');
var func = require('func');

alterModalCtrl.$inject = ['modalService', 'alterModalService'];

function alterModalCtrl(modalService, alterModalService) {
  var vm = this;

  /*函数声明*/
  vm.openAlterModalFunc = openAlterModalFunc;


  /*函数定义*/
  function openAlterModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '角色修改';
    options.modalParams = data;  //列表传递数据
    options.template = alterModalTempl;
    options.controller = alterModalController;

    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };

    modalService.openModal(options);
  }


  /*controller定义*/
  function alterModalController($cookies, $uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, ensureBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.isMod = false;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.ensureBtnText = ensureBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.alter_token = sessionStorage.getItem('access_token');
    ra.operatorInfo = JSON.parse(sessionStorage.getItem("operatorInfo"));
    ra.alter_branch = modalParams.branch;
    ra.alter_branchNo = modalParams.branchNo;
    ra.alter_roleType = modalParams.roleType;
    ra.alter_remark = modalParams.remark;
    ra.alter_roleId = modalParams.roleId;
    ra.alter_roleName = modalParams.roleName;
    ra.alter_roleType = modalParams.roleType;
    ra.alter_rowCount = modalParams.rowCount;
    ra.alter_dics = modalParams.dicts;

    ra.cancel = cancel;
    ra.close = close;
    ra.alterSubmit = alterSubmit;

    function close(result) {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close(result);
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

    function alterSubmit() {
      alterModalService.roleMode({
        access_token: ra.alter_token,
        branchNo: ra.alter_branchNo,
        menuRights: ra.operatorInfo.branchRights,
        remark: ra.alter_remark,
        roleId: ra.alter_roleId,
        roleName: ra.alter_roleName,
        roleType: ra.alter_roleType,
        rowCount: ra.alter_rowCount
      }, function (response) {
        var result = {
          roleName: ra.alter_roleName,
          roleType: ra.alter_roleType
        };
        close(result);
        func.openToaster('操作成功！');
      });
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = alterModalCtrl;




