/* 观点标签设置 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var viewpointPackageCheckModalCtrl = require('./modalPopup/viewpointPackageCheckModal/controller');
var viewpointPackageCheckModalService = require('./modalPopup/viewpointPackageCheckModal/service');
var viewpointPackageIntroModalCtrl = require('./modalPopup/viewpointPackageIntroModal/controller');
var viewpointPackageRemarkModalCtrl = require('./modalPopup/viewpointPackageRemarkModal/controller');

//setter定义模块
module.exports = angular
  .module('viewpointPackageCheckCtrl', [])
  .config(Routing)
  .controller('viewpointPackageCheckCtrl', ctrl)
  .service('viewpointPackageCheckService', service)
  .factory('viewpointPackageCheckModalCtrl', viewpointPackageCheckModalCtrl)
  .service('viewpointPackageCheckModalService', viewpointPackageCheckModalService)
  .factory('viewpointPackageIntroModalCtrl', viewpointPackageIntroModalCtrl)
  .factory('viewpointPackageRemarkModalCtrl', viewpointPackageRemarkModalCtrl)
  .name;



