require ('./style.less');

var modalOptions = require('jsDir/modal_options'),
protocolIntroduceModalTempl = require('./protocolIntroduceModal.html'),
  func = require('func');

  protocolIntroduceModalCtrl.$inject = ['modalService'];

function protocolIntroduceModalCtrl(modalService) {
  var vm = this;

  vm.openModal = openModal;

  function openModal(callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '适当性匹配确认协议';
    options.size = 'md';
    options.template = protocolIntroduceModalTempl;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function () {
      angular.isFunction(callBack) && callBack();
    };

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;
    
    ra.selected = 0; // 0：银行卡转账提示， 1：其他类型协议

    ra.changeSession = changeSession;
    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function changeSession(type) {
      ra.selected = type;
    }

    //新增提交
    function submit() {
      close();
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = protocolIntroduceModalCtrl;


