 // 风险等级设置

require('locale_zh');

protocolVersionManageCtrl.$inject = ['$state', '$scope', '$timeout', '$stateParams', 'modalService', 'protocolVersionManageService', 'protocolIntroduceModalCtrl', 'protocolViewModalCtrl', 'protocolStopModalCtrl'];

function protocolVersionManageCtrl($state, $scope, $timeout, $stateParams, modalService, protocolVersionManageService, protocolIntroduceModalCtrl, protocolViewModalCtrl, protocolStopModalCtrl) {
  var vm = this;

  vm.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
  vm.operatorNo = vm.operatorInfo.operatorNo;
  vm.operatorName = vm.operatorInfo.name;
  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.list = [];

  vm.openIntorduceModalFunc = openIntorduceModalFunc;
  vm.openViewModalFunc = openViewModalFunc;
  vm.openStopModalFunc = openStopModalFunc;
  vm.linkToAddProtocolPage = linkToAddProtocolPage;
  vm.pageChangeFunc = pageChangeFunc;

  getProtocolListFunc();

  function getProtocolListFunc() {
    protocolVersionManageService.protocolListQry({
      tenant_id: vm.tenantId,
      page_no: vm.currentPage,
      page_count: vm.itemPerPage,
      agreement_id: ''
    }, function(res) {
      console.log('res',  res);
      vm.list = res.data_list || [];
      vm.totalItems = res.total_item_num || 0;
    }, function(err) {
      console.log('err',  err);
      vm.list = [];
      vm.totalItems = 0;
    })
  }

  function openIntorduceModalFunc() {
    protocolIntroduceModalCtrl.openModal();
  }

  function openViewModalFunc(content) {
    protocolViewModalCtrl.openModal(content);
  }

  function openStopModalFunc(data) {
    if (data.agreement_status == 1) { // 启用
      protocolVersionManageService.protocolStop({
        tenant_id: vm.tenantId,
        agreement_id: data.agreement_id,
        agreement_status: 0, //0：启用，1：停用
        operator_no: vm.operatorNo,
        operator_name: vm.operatorName,
        agreement_type: data.agreement_type
      }, function(res) {
        console.log('protocolStop res', res);
        modalService.openAlert('提示', res.error_info);
        getProtocolListFunc();
      }, function(err) {
        console.log('protocolStop err', err);
        modalService.openAlert('提示', err.error_info);
      })
    } else { // 停用
      protocolStopModalCtrl.openModal(data, function() {
        getProtocolListFunc();
      });
    }
  }

  function linkToAddProtocolPage(data, type) {
    if (type) { // 新增
      $state.go('home.addProtocolVersion');
    } else { // 编辑
      $state.go('home.addProtocolVersion', {
        data: encodeURIComponent(JSON.stringify(data))
      });
    }
  }

  function pageChangeFunc() {
    getProtocolListFunc();
  }
}

module.exports = protocolVersionManageCtrl; 


