combineDownModalService.$inject = ['api'];
function combineDownModalService(api) {
  var vm = this;

  vm.getCombineList = getCombineList;   // 获取组合列表
  vm.setCombineUnderline = setCombineUnderline;   // 组合强制下架

  function getCombineList(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'combine_force_down_list',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }
  
  function setCombineUnderline(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'combine_force_down',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    });
  }

  return vm;
}

module.exports = combineDownModalService;



