/* 敏感词管理 module */

require("./style.less");

var modalOptions = require("jsDir/modal_options"),
  editSensitiveWordModalTempl = require("./editSensitiveWordModal.html");

editSensitiveWordModalCtrl.$inject = [
  "modalService",
  "editSensitiveWordModalService"
];

function editSensitiveWordModalCtrl(
  modalService,
  editSensitiveWordModalService
) {
  var vm = this;

  vm.wordInfo = {}; //敏感词信息

  /*函数声明*/
  vm.openeditSensitiveWordModalFunc = openeditSensitiveWordModalFunc;

  /*函数定义*/
  function openeditSensitiveWordModalFunc(item, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = "编辑";
    options.template = editSensitiveWordModalTempl;
    options.controller = defaultController;
    vm.wordInfo = item;
    options.confirmCallback = function() {
      angular.isFunction(callback) && callback();
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function defaultController(
    $uibModalInstance,
    $uibModalStack,
    modalTitle,
    modalInfo,
    modalParams,
    modalTime
  ) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = false;
    ra.submitText = "确定";

    ra.branchDate = JSON.parse(sessionStorage.getItem("allBranchListData"));
    ra.tenant_id = ra.branchDate[0].branchCode.split("_")[0] || "";
    ra.sensitiveWord = vm.wordInfo.sensitive_word || ""; //敏感词
    ra.remark = vm.wordInfo.remark || ""; //备注

    ra.confirmFunc = confirmFunc; //确定方法
    ra.addSensitiveWord = addSensitiveWord; //新增敏感词
    ra.updateSensitiveWord = updateSensitiveWord; //更新敏感词
    ra.cancel = cancel;
    ra.close = close;

    //确定方法
    function confirmFunc() {
      if (vm.wordInfo == "") {
        addSensitiveWord();
      } else {
        updateSensitiveWord();
      }
    }

    //新增敏感词
    function addSensitiveWord() {
      if (ra.sensitiveWord != "") {
        editSensitiveWordModalService.addNewSensitiveWord(
          {
            source: "cloudbroker_3",
            sign: "17AD57E731C3CF99EC72703976577EA4",
            tenant_id: ra.tenant_id,
            sensitive_word: ra.sensitiveWord,
            remark: ra.remark
          },
          function(result) {
            delSensitiveWordCache();
          }
        );
      } else {
        modalService.openConfirm("提示", "敏感词不能为空！");
      }
    }

    //更新敏感词
    function updateSensitiveWord() {
      if (ra.sensitiveWord != "") {
        editSensitiveWordModalService.updateSensitiveWord(
          {
            remark: ra.remark,
            sensitive_word: ra.sensitiveWord,
            sensitive_word_id: vm.wordInfo.sensitive_word_id,
            sign: "17AD57E731C3CF99EC72703976577EA4",
            source: "cloudbroker_3"
          },
          function(result) {
            delSensitiveWordCache();
          }
        );
      } else {
        modalService.openConfirm("提示", "敏感词不能为空！");
      }
    }

    //清缓存
    function delSensitiveWordCache() {
      editSensitiveWordModalService.delSensitiveWordCache(
        {},
        function(result) {
          close();
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss("cancel");
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == "2009") {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = editSensitiveWordModalCtrl;



