/* 角色管理 module */
rolesManageService.$inject = ['api'];

function rolesManageService(api) {
  var vm = this;
  vm.getRolesInfo = getRolesInfo;//获取角色信息
  vm.roleDel = roleDel;//删除角色信息

  function getRolesInfo(data, cb1) {
    api.postRequest({
      prefix_type: 'roleService',
      url: 'roleQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function roleDel(data, cb1) {
    api.postRequest({
      prefix_type: 'roleService',
      url: 'roleDel',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = rolesManageService;



