require ('./style.less');

var modalOptions = require('jsDir/modal_options'),
packageDetailTempl = require('./packageDetail.html'),
  func = require('func');

  packageDetailModalCtrl.$inject = ['modalService', 'packageDetailModalService'];

function packageDetailModalCtrl(modalService, packageDetailModalService) {
  var vm = this;

  vm.openPackageDetailModalFunc = openPackageDetailModalFunc;

  function openPackageDetailModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '服务包下架';
    options.template = packageDetailTempl;
    options.modalParams = data;
    options.controller = defaultController;

    //成功后的回调
    options.confirmCallback = function () {
      angular.isFunction(callBack) && callBack();
    };

    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    /* 分页 */
    ra.maxSize = 10;
    ra.totalItems = 0;
    ra.currentPage = 1;
    ra.itemPerPage = 5;

    ra.brokerName = '';     // 筛选条件创建人
    ra.productName = '';     // 筛选条件服务包名
    ra.productStatus = '';    // 筛选条件状态
    ra.statusList = [
      { value: '', label: '全部' }, 
      { value: 1, label: '出售中（已上架）' }, 
      { value: 2, label: '仓库中（已下架）' }, 
      { value: 3, label: '已删除' },
      { value: 4, label: '上架待审核' },
      { value: 5, label: '下架待审核' },
    ];
    
    ra.tenantId = JSON.parse(sessionStorage.getItem("tenantId"));
    ra.operatorNo = JSON.parse(sessionStorage.getItem("operatorInfo")).operatorNo;
    ra.operatorName = JSON.parse(sessionStorage.getItem("operatorInfo")).name;
    ra.productList = [];

    ra.getListFunc = getListFunc;
    ra.productDownFunc = productDownFunc;
    ra.cancel = cancel;
    ra.close = close;

    getListFunc();

    function getListFunc() {
      packageDetailModalService.productMoreListQry({
        tenant_id: ra.tenantId,
        page_no: ra.currentPage,
        page_size: ra.itemPerPage,
        broker_name: ra.brokerName,
        product_name: ra.productName,
        product_states: ra.productStatus // 1-出售中（已上架）。2-仓库中（已下架）。3-已删除。4-上架待审核。5-下架待审核。
      }, function(res) {
        console.log('res', res);
        ra.productList = res.data_list || [];
        if (ra.productList.length) {
          ra.productList.forEach(function(item) {
            item.productPrice = (JSON.parse(item.product_price)[0].price / 100).toFixed(2);
          });
        }
      }, function(err) {
        console.log('err', err);
      })
    }

    function productDownFunc(id) {
      packageDetailModalService.productForceDown({
        tenant_id: ra.tenantId,
        operator_no: ra.operatorNo,
        operator_name: ra.operatorName,
        product_ids: id
      }, function(res) {
        console.log('res', res);
        modalService.openAlert('提示', res.error_info);
        getListFunc();
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('提示', err.error_info);
      })
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = packageDetailModalCtrl;


