/* 订单统计查询 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var orderStatisticsDownloadCtrl = require('./modalPopup/orderStatisticsDownload/controller');
var orderStatisticsDownloadService = require('./modalPopup/orderStatisticsDownload/service');

//setter定义模块
module.exports = angular
  .module('orderStatisticsQry', [])
  .config(Routing)
  .controller('orderStatisticsQryCtrl', ctrl)
  .service('orderStatisticsQryService', service)
  .factory('orderStatisticsDownloadCtrl', orderStatisticsDownloadCtrl)
  .service('orderStatisticsDownloadService', orderStatisticsDownloadService).name;



