/* 角色授权 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  accrreditModalTempl = require('./accreditModal.html');

accreditModalCtrl.$inject = ['modalService', 'accreditModalService'];

function accreditModalCtrl(modalService, accreditModalService) {
  var vm = this;

  /*函数声明*/
  vm.openAccreditModalFunc = openAccreditModalFunc;

  /*函数定义*/
  function openAccreditModalFunc(x) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '角色授权';
    options.template = accrreditModalTempl;
    options.controller = accreditModalController;
    modalService.openModal(options);
    vm.accreBranchNo = x.branchNo;
    vm.accreRoleType = x.roleType;
    vm.accreRoleId = x.roleId;
    vm.accreRoleName = x.roleName;
  }

  /*controller定义*/
  function accreditModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText,modalService) {
    var ra = this;
    var errorNo = modalParams.error_no;
    vm.selectedBranchInfo = {}; //已选组织信息
    vm.selectedBranchNo = 0;    //选中营业部的branchNo
    ra.showBranchId = true;
    ra.rightTreeSearchInfo = '';  //通过权限树搜索的信息
    ra.branchSearch = '';   //查询的模块名称或id
    ra.accreBranchNo = vm.accreBranchNo;
    ra.accreRoleType = vm.accreRoleType;
    ra.accreRoleId = vm.accreRoleId;
    ra.accreRoleName = vm.accreRoleName;

    ra.ownList = [];  //当前权限信息结果集
    ra.addList = [];  //新增权限结果集，空数组
    ra.delList = [];  //删除权限结果集，空数组
    ra.addRights = '';  //新增权限结果，请求入参
    ra.delRights = '';  //删除权限结果，请求入参

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.changeRight = changeRight;
    ra.roleMenuSet = roleMenuSet;
    ra.getItemClicked = getItemClicked;
    ra.choseBranchByNameOrId = choseBranchByNameOrId;
    ra.cancel = cancel;
    ra.close = close;

    menuQry();
    ownListQry();

    //当前权限信息查询
    function ownListQry() {
      accreditModalService.roleMenuQry({
        ancestorFlag: 1,
        branchNo: JSON.stringify(ra.accreBranchNo),
        deepth: -1,
        roleId: ra.accreRoleId
      }, function (response) {
        ra.ownList = response.data;
      });
    }

    //默认加载部门信息
    function menuQry() {
      accreditModalService.roleMenuQry({
        ancestorFlag: 1,
        branchNo: JSON.stringify(ra.accreBranchNo),
        deepth: -1,
        roleId: 0
      }, function (response) {
        ra.allBranchList = response.data;
      })
    }

    //改变权限树的回调
    function changeRight(addResult, delResult) {
      //增加菜单
      var addList = [];
      if (addResult.length > 0) {
        for (var i = 0; i <addResult[addResult.length - 1]; i++) {
          addList.push(0);
        }
        for (var i = 0; i <= addResult.length; i++) {
          addList[addResult[i] - 1] = 1;
        }
        addList = addList.join('');
        ra.addRights = addList;
      }

      //删除菜单
      var delList = [];
      if (delResult.length > 0) {
        for (var i = 0; i < delResult[delResult.length - 1]; i++) {
          delList.push(0);
        }
        for (var i = 0; i <= delResult.length; i++) {
          delList[delResult[i] - 1] = 1;
        }
        delList = delList.join('');
        ra.delRights = delList;
      }
    }

    //点击营业部
    function getItemClicked(item) {
      console.log(item);
    }

    //选择营业部
    function choseBranchByNameOrId() {
      ra.rightTreeSearchInfo = ra.branchSearch;
    }

    //保存营业部权限修改
    function roleMenuSet() {
      accreditModalService.roleMenuSet({
        branchNo: JSON.stringify(ra.accreBranchNo),
        addMenuRights: ra.addRights,
        delMenuRights: ra.delRights,
        roleId: ra.accreRoleId
      }, function (response) {
        var options = angular.copy(modalOptions);
        options.modalTitle = '成功';
        options.modalInfo = '角色授权成功！';
        modalService.openModal(options);
        close();
      })
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = accreditModalCtrl;




