/* 操作员管理--密码重置 module */

require('locale_zh');

var modalOptions = require('jsDir/modal_options'),
	sha256 = require('sha256');
var func = require('func');

resetPasswordCtrl.$inject = ['resetPasswordService', 'modalService', '$scope'];

function resetPasswordCtrl(resetPasswordService, modalService, $scope) {
  var vm = this;
  vm.resetOperatorName = "";
  vm.resetOperatorNo = '';
  vm.isMod = false;
  vm.dicts_status = [{"code":"0","value":"正常"},{"code":"1","value":"冻结"},{"code":"2","value":"锁定"}];
  vm.newPassword = '';	//新密码
  vm.confirmPassword = '';	//确认新密码

  //操作员信息
  vm.operatorNo = '';
  vm.operatorName = '';
  vm.operatorStatus = '';
  vm.operatorPhone = '';
  vm.branch = '';

  //函数声明
  vm.operatorQry = operatorQry;  //点击查询
  vm.setResponseData = setResponseData;  //查询结果
	vm.savePassword = savePassword;  //保存新密码

  function operatorQry() {
    vm.operatorName = '';
    vm.operatorStatus = '';
    vm.operatorPhone = '';
    vm.branch = '';
	if (!vm.resetOperatorNo && !vm.resetOperatorName) {
	  modalService.openAlert('提示', '未输入操作员信息！');
	} else {
	  resetPasswordService.operatorQry({
		branchNoList: [],
		checkEnBranchNo: 1,
		name: vm.resetOperatorName,
		operatorNo: vm.resetOperatorNo,
		remark: ''
	  }, function (response) {
		setResponseData(response);
	  });
	}
  }

  function setResponseData(response) {
	if (response.data.length == 0) {
	  var options = angular.copy(modalOptions);
	  options.modalTitle = '警告';
	  options.modalInfo = '操作员不存在';
	  modalService.openModal(options);
	}

	//加载操作员信息
	var operatorData = response.data[0];
	var branchNo = operatorData.branchNo;
	var branchInfo = JSON.parse(sessionStorage.getItem("allBranchListData"));
	vm.operatorName = operatorData.name;
	vm.operatorNo = operatorData.operatorNo;
	vm.operatorStatus = vm.dicts_status[operatorData.status].value;
	vm.operatorPhone = operatorData.telephone;
	for (var i = 0; i < branchInfo.length; i++) {
	  if (branchNo == branchInfo[i].branchNo) {
		vm.branch = branchInfo[i].branchNo + "-" + branchInfo[i].branchName;
	  }
	}
  }

  function savePassword() {
    if (!vm.operatorName) {
	  modalService.openAlert('提示', '请先查询操作员信息！');
	} else {
	  if (vm.newPassword == vm.confirmPassword && vm.newPassword != '') {
		resetPasswordService.operatorPwdRst({
		  newPassword: sha256(vm.newPassword),
		  operatorNo: vm.operatorNo,
		  remark: ''
		}, function (response) {
		  var options = angular.copy(modalOptions);
		  options.modalTitle = '成功';
		  options.modalInfo = '操作成功';
		  modalService.openModal(options);
		  //密码输入框置空
		  vm.newPassword = '';
		  vm.confirmPassword = '';
		  $scope.resetPassword.newPassword.$touched = false;
		})
	  }
	}
  }


}

module.exports = resetPasswordCtrl;



