/* 客户贡献度查询 */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var customerContributionQryDownloadCtrl = require('./modalPopup/customerContributionQryDownload/controller');
var customerContributionQryDownloadService = require('./modalPopup/customerContributionQryDownload/service');

//setter定义模块
module.exports = angular
  .module('customerContributionQry', [])
  .config(Routing)
  .controller('customerContributionQryCtrl', ctrl)
  .service('customerContributionQryService', service)
  .factory('customerContributionQryDownloadCtrl', customerContributionQryDownloadCtrl)
  .service('customerContributionQryDownloadService', customerContributionQryDownloadService).name;



