require('./style.less');

var modalOptions = require('jsDir/modal_options'),
    nonUseStoreModalTempl = require('./nonUseStoreModal.html'),
    func = require('func');

nonUseStoreModalCtrl.$inject = ['modalService', 'nonUseStoreModalService'];

function nonUseStoreModalCtrl(modalService, nonUseStoreModalService) {
  var vm = this;

  vm.brokerStatus = '';//获取停用、启用状体
  vm.brokerId = '';//获取broker_id
  vm.status = '';//broker_type

  vm.openNonUseStoreModalFunc = openNonUseStoreModalFunc;

  function openNonUseStoreModalFunc(item, status, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '系统提示';
    options.template = nonUseStoreModalTempl;
    options.controller = defaultController;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };

    //获取传入的参数
    vm.brokerStatus = item.broker_status;
    vm.brokerId = item.broker_id;
    vm.status = status;
    modalService.openModal(options);
  }

  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;

    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.brokerId = vm.brokerId;
    ra.brokerType = vm.status;
    //判断启用或停用
    ra.brokerStatus = vm.brokerStatus;
    if (ra.brokerStatus == 1) {
      ra.content = '确定要停用此店铺吗？';
    } else if (ra.brokerStatus == 2) {
      ra.content = '确定要启用此店铺吗？';
    }

    ra.submitQry = submitQry;
    ra.cancel = cancel;
    ra.close = close;

    //提交请求
    function submitQry() {
      nonUseStoreModalService.funcBrokerChangeStatus(
        {
          broker_id: ra.brokerId,
          broker_type: ra.brokerType
        },
        function (result) {
          close();
        }
      )
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = nonUseStoreModalCtrl;



