/* 操作员管理 module */

require('./style.less');

//引入模块的路由配置
var Routing = require('./route');

// 引入service、controller、directive等文件
var ctrl = require('./controller');
var service = require('./service');
var operatorAddService = require('./modalPopup/operatorAdd/service');
var operatorAddCtrl = require('./modalPopup/operatorAdd/controller');
var operatorAccreditService = require('./modalPopup/operatorAccredit/service');
var operatorAccreditCtrl = require('./modalPopup/operatorAccredit/controller');
var operatorEditService = require('./modalPopup/operatorEdit/service');
var operatorEditCtrl = require('./modalPopup/operatorEdit/controller');
var operatorFrozenService = require('./modalPopup/operatorFrozen/service');
var operatorFrozenCtrl = require('./modalPopup/operatorFrozen/controller');
var operatorUnFrozenService = require('./modalPopup/operatorUnFrozen/service');
var operatorUnFrozenCtrl = require('./modalPopup/operatorUnFrozen/controller');
var operatorDelCtrl = require('./modalPopup/operatorDel/controller');

//setter定义模块
module.exports = angular
  .module('operatorManage', [])
  .config(Routing)
  .service('operatorManageService', service)
  .controller('operatorManageCtrl', ctrl)
  .factory('operatorAddCtrl', operatorAddCtrl)
  .service('operatorAddService', operatorAddService)
  .factory('operatorAccreditCtrl', operatorAccreditCtrl)
  .service('operatorAccreditService', operatorAccreditService)
  .factory('operatorEditCtrl', operatorEditCtrl)
  .service('operatorEditService', operatorEditService)
  .factory('operatorFrozenCtrl', operatorFrozenCtrl)
  .service('operatorFrozenService', operatorFrozenService)
  .factory('operatorUnFrozenCtrl', operatorUnFrozenCtrl)
  .service('operatorUnFrozenService', operatorUnFrozenService)
  .factory('operatorDelCtrl', operatorDelCtrl).name;



