/* 课程新增、课程编辑 */

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  dayCourseModalTempl = require('./dayCourseModal.html'),
  func = require('func');

dayCourseModalCtrl.$inject = ['modalService', 'dayCourseModalService'];

function dayCourseModalCtrl(modalService, dayCourseModalService) {
  var vm = this;

  /*函数声明*/
  vm.openDayCourseModalFunc = openDayCourseModalFunc;

  /*函数定义*/
  function openDayCourseModalFunc(course, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '当日直播';
    options.template = dayCourseModalTempl;
    options.controller = defaultController;
    options.modalParams = course;
    options.confirmCallback = function () {
      angular.isFunction(callback) && callback();
    };

    modalService.openModal(options);
  }

  /*controller定义*/
  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.dataList = modalParams;
    ra.modalTime = modalTime;

    ra.submitText = '确定';
    ra.cancelBtnText = '删除';

    ra.cancel = cancel;
    ra.close = close;

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = dayCourseModalCtrl;



