/* 订单历史 controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  orderModalTempl = require('./orderModal.html');

  orderModalCtrl.$inject = ['modalService', 'orderModalService'];

function orderModalCtrl(modalService, orderModalService) {
  var vm = this;

  /*函数声明*/
  vm.openOrderModalFunc = openOrderModalFunc;


  /*函数定义*/
  function openOrderModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '订单历史详情';
    options.template = orderModalTempl;
    options.size = 'md';
    options.controller = orderModalController;
    options.modalParams = data;  //列表传递数据
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function orderModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText, ensureBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.ensureBtnText = ensureBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.cancel = cancel;
    ra.close = close;

    //分页相关
    ra.maxSize = 5;
    ra.totalItems = 0; 
    ra.currentPage = 1;
    ra.itemPerPage = 10;

    ra.orderList = [];

    ra.pageChangeFunc = pageChangeFunc;

    getOrderManageFunc();

    function getOrderManageFunc() {
      orderModalService.orderManageFunc({
        tenantId: ra.modalParams.tenantId,
        page_no: ra.currentPage,
        page_size: ra.itemPerPage
      }, function(res) {
        ra.orderList = res.data_list || [];
        ra.totalItems = res.total_item_num * 1;
      }, function(err) {
        ra.orderList = [];
        ra.totalItems = 0;
      })
    }

    function pageChangeFunc() {
      getOrderManageFunc();
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = orderModalCtrl;



