investorAchievementQryRoute.$inject = ['$stateProvider'];

function investorAchievementQryRoute($stateProvider) {
  $stateProvider.state('home.investorAchievementQry', {
    url: '/investorAchievementQry',
    templateUrl: './views/investorAchievementQry.html',
    controller: 'investorAchievementQryCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = investorAchievementQryRoute;



