/* 编辑订单信息 controller*/

require('./style.less');
var modalOptions = require('jsDir/modal_options'),
editOrderInfoModalTempl = require('./editOrderInfoModal.html'),
func = require('func');

editOrderInfoModalCtrl.$inject = ['modalService', 'editOrderInfoModalService'];

function editOrderInfoModalCtrl(modalService, editOrderInfoModalService) {
  var vm = this;

  /*函数声明*/
  vm.openEditOrderInfoModalFunc = openEditOrderInfoModalFunc;

  /*函数定义*/
  function openEditOrderInfoModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '修改订单';
    options.template = editOrderInfoModalTempl;
    options.size = 'md';
    options.controller = downModalController;
    options.modalParams = data;
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function downModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, confirmBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.orderStatus = Number(ra.modalParams.order_status);
    ra.orderStatusList = [
      { value: 0, label: '待支付' }, 
      { value: 1, label: '已支付' }, 
      { value: 2, label: '已失效' }, 
      { value: 3, label: '待退款' },
      { value: 4, label: '已退款' },
      { value: 5, label: '已评价' }
    ];

    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function submit() {
      editOrderInfoModalService.changeOrderInfoFunc(
        {
          tenant_id: ra.modalParams.tenant_id,
          order_id: ra.modalParams.order_id,
          order_status: ra.orderStatus
        },
        function(res) {
          func.openToaster('操作成功！');
          close();
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = editOrderInfoModalCtrl;



