editInfoModalService.$inject = ['api'];

function editInfoModalService(api) {
  var vm = this;

  vm.editInfo = editInfo;

  function editInfo(data, cb1) {
    api.postRequest({
      prefix_type: 'operatorService',
      url: 'operatorChangeTel',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    })
  }

}

module.exports = editInfoModalService;



