customerContributionQryRoute.$inject = ['$stateProvider'];

function customerContributionQryRoute($stateProvider) {
  $stateProvider.state('home.customerContributionQry', {
    url: '/customerContributionQry',
    templateUrl: './views/customerContributionQry.html',
    controller: 'customerContributionQryCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = customerContributionQryRoute;



