liveCheckService.$inject = ['api'];
function liveCheckService(api) {
  var vm = this;
  vm.manageLiveChatAuditHistory = manageLiveChatAuditHistory; // 直播审核列表

  function manageLiveChatAuditHistory(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'manage_live_chat_audi_history',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = liveCheckService;


