orderManage01Route.$inject = ['$stateProvider'];

function orderManage01Route($stateProvider) {
    $stateProvider
        .state('home.orderManage01', {
            url: '/orderManage01',
            templateUrl: './views/orderManage01.html',
            controller: 'orderManage01Ctrl',
            controllerAs: 'vm'  //使用vm而不是使用$scope去操作视图
        });
}

module.exports = orderManage01Route;



