/* 组织管理 module */

branchManageRoute.$inject = ['$stateProvider'];

function branchManageRoute($stateProvider) {
  $stateProvider.state('home.branchManage', {
    url: '/branchManage',
    templateUrl: './views/branchManage.html',
    controller: 'branchManageCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = branchManageRoute;



