require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  logDetailTempl = require('./logDetail.html'),
  sha256 = require('sha256'),
  func = require('func');

logDetailModalCtrl.$inject = ['modalService', 'logDetailModalService'];

function logDetailModalCtrl(modalService, logDetailModalService) {
  var vm = this;

  /*函数声明*/
  vm.logDetailFunc = logDetailFunc;


  /*函数定义*/
  function logDetailFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '详细信息';
    options.template = logDetailTempl;
    options.size = 'md';
    options.controller = logDetailController;
    options.modalParams = data;  //列表传递数据
    modalService.openModal(options);
  }

  /*controller定义*/
  function logDetailController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.logDate = ra.modalParams.sysDate || '';
    ra.logTime = ra.modalParams.sysTime || '';
    ra.logNum = ra.modalParams.serialNo || '';
    ra.operatorCode = ra.modalParams.operatorNo || '';
    ra.branchCode = ra.modalParams.branch || '';
    ra.logOper = ra.modalParams.interfaceName || '';
    ra.logRemark = ra.modalParams.remark || '';

    ra.cancel = cancel;
    ra.close = close;

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = logDetailModalCtrl;




