accreditModalService.$inject = ['api'];
function accreditModalService(api) {
  var vm = this;
  vm.roleMenuQry = menuQry;//授权获取菜单信息;
  vm.roleMenuSet = roleMenuSet; //点击保存

  function menuQry(data, cb1) {
    api.postRequest({
      prefix_type: 'menuService',
      url: 'menuQry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  function roleMenuSet(data, cb1) {
    api.postRequest({
      prefix_type: 'roleService',
      url: 'roleMenuSet',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }


  return vm;


}

module.exports = accreditModalService;



