orderStatisticsDownloadService.$inject = ['api'];
function orderStatisticsDownloadService(api) {
  var vm = this;
  vm.branchOrderStatisticQryFunc = branchOrderStatisticQryFunc;
  vm.brokerOrderStatisticQryFunc = brokerOrderStatisticQryFunc;

  /**
   * 机构订单统计查询
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function branchOrderStatisticQryFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'branch_order_statistic_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  /**
   * 投顾订单统计查询
   *
   * @param   {[type]}  data  [data description]
   * @param   {[type]}  cb1   [cb1 description]
   * @param   {[type]}  cb2   [cb2 description]
   *
   * @return  {[type]}        [return description]
   */
  function brokerOrderStatisticQryFunc(data, cb1, cb2) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'broker_order_statistic_qry',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      },
      failCallback: function (result) {
        angular.isFunction(cb2) && cb2(result);
      }
    })
  }

  return vm;
}

module.exports = orderStatisticsDownloadService;



