viewpointCheckRoute.$inject = ['$stateProvider'];

function viewpointCheckRoute($stateProvider) {
  $stateProvider.state('home.viewpointCheck', {
    url: '/viewpointCheck',
    templateUrl: './views/viewpointCheck.html',
    controller: 'viewpointCheckCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = viewpointCheckRoute;



