/* 直播监控 module */

require('locale_zh');
var func = require('func');

liveMonitorList.$inject = ['$sce', '$state', '$stateParams', 'modalService', 'liveMonitorListService'];

function liveMonitorList($sce, $state, $stateParams, modalService, liveMonitorListService) {
  var vm = this;

  //获取branchNo和tenantId
  vm.branchNo = JSON.parse(sessionStorage.getItem('branchNoData'));
  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.roomStatus = ''; // 直播状态
  vm.keyword = ''; //关键字
  /* 日期选择 */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };
  vm.startTime = '';
  vm.endTime = '';
  /* 分页 */
  vm.maxSize = 5;
  vm.totalItems = 1; //根据接口返回决定
  vm.currentPage = 1;
  vm.itemsPerPage = 10;

  vm.liveList = [];

  vm.keywordSearch = keywordSearch;
  vm.openStartTimeFunc = openStartTimeFunc; //开始日期选择
  vm.openEndTimeFunc = openEndTimeFunc; //结束日期选择
  vm.enterLiveContent = enterLiveContent; // 进入直播间
  vm.pageChangeFunc = pageChangeFunc;

  qryLiveList();

  //获取直播列表
  function qryLiveList() {
    liveMonitorListService.getLiveList(
      {
        keyword: vm.keyword,
        room_status: vm.roomStatus == '' ? null : vm.roomStatus, // 1：进行中，2：已结束
        send_time_start: vm.startTime ? (func.formatDate(vm.startTime, 'yyyy-MM-dd') + ' 00:00:00') : '',
        send_time_end: vm.endTime ? (func.formatDate(vm.endTime, 'yyyy-MM-dd') + ' 23:59:59') : '',
        page_no: vm.currentPage,
        page_count: vm.itemsPerPage,
        branch_no: vm.branchNo,
        tenant_id: vm.tenantId,
        jsonp: 'jsonpcallback'
      },
      function(result) {
				vm.liveList = result.data_list;
        vm.totalItems = result.total_item_num;
      }
    );
  }

  function keywordSearch() {
    vm.currentPage = 1;
    qryLiveList();
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  };

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function enterLiveContent(data) {
    $state.go('home.liveMonitorContent', {
      topicId: data.topic_id,
      topic: data.topic,
      user: data.broker_name
    });
  }

  //分页
  function pageChangeFunc() {
    qryLiveList();
  }
}

module.exports = liveMonitorList;



