orderCheckRoute.$inject = ['$stateProvider'];

function orderCheckRoute($stateProvider) {
  $stateProvider.state('home.orderCheck', {
    url: '/orderCheck',
    templateUrl: './views/orderCheck.html',
    controller: 'orderCheckCtrl',
    controllerAs: 'vm' //使用vm而不是使用$scope去操作视图
  });
}

module.exports = orderCheckRoute;



