/* 敏感词管理 module */

editSensitiveWordModalService.$inject = ['api'];

function editSensitiveWordModalService(api) {
  var vm = this;

  vm.addNewSensitiveWord = addNewSensitiveWord;
  vm.updateSensitiveWord = updateSensitiveWord;
  vm.delSensitiveWordCache = delSensitiveWordCache;

  //新增敏感词
  function addNewSensitiveWord(data, cb1) {
    api.postRequest({
      prefix_type: 'userCenter',
      url: 'func_sensitive_word_save',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //更新敏感词
  function updateSensitiveWord(data, cb1) {
    api.postRequest({
      prefix_type: 'userCenter',
      url: 'func_sensitive_word_update',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  //清缓存
  function delSensitiveWordCache(data, cb1) {
    api.postRequest({
      prefix_type: 'vipstu',
      url: 'func_del_sensitive_word_cache',
      data: data,
      successCallback: function (result) {
        angular.isFunction(cb1) && cb1(result);
      }
    });
  }

  return vm;
}

module.exports = editSensitiveWordModalService;



