 /* 新增标签 */

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  addTagModalTempl = require('./addTagModal.html');

addTagModalCtrl.$inject = ['modalService','addTagModalService'];

function addTagModalCtrl(modalService,addTagModalService) {
  var vm = this;

  /*函数声明*/
  vm.openAddTagModalFunc = openAddTagModalFunc;

  /*函数定义*/
  function openAddTagModalFunc(callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '新增标签';
    options.template = addTagModalTempl;
    options.controller = addTagModalController;
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller定义*/
  function addTagModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.cancelBtnText = cancelBtnText;

    ra.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
    ra.tenantId = JSON.parse(sessionStorage.getItem('tenantId')) * 1;
    ra.tagName = '';
    ra.tagInfo = '';
    ra.remark = '';

    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function submit() {
      addTagModalService.viewpointTagAddFunc({
        tenant_id: ra.tenantId,
        operator_name: ra.operatorInfo.name,
        operator_no: ra.operatorInfo.operatorNo,
        viewpoint_tag_name: ra.tagName,
        viewpoint_tag_instruction: ra.tagInfo,
        remark: ra.remark
      }, function(res) {
        console.log('res', res);
        modalService.openAlert('提示', '执行成功！');
        close();
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('提示', err.error_info);
      })
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = addTagModalCtrl;




